import React, { memo, useCallback, useMemo } from "react";
import { File, IAttachment, styled } from "@true/core";
import { IMapItem, MapList } from "../../MapList";
import FastImage from "react-native-fast-image";
import { Dimensions, View } from "react-native";
import { FileItemView } from "../input/InputFileView";

const WIDTH = (Dimensions.get("window").width - 105) / 3;

export const AttachmentView = memo((props: { attachments: IAttachment[] }) => {
  const { attachments } = props;

  const renderItem = useCallback(
    ({ item, index }: IMapItem<IAttachment>) => {
      return <AttachItem attachment={item} />;
    },
    [attachments]
  );

  return (
    <View style={{ flexDirection: "row", flexWrap: "wrap", gap: 8 }}>
      <MapList data={attachments} renderItem={renderItem} />
    </View>
  );
});

const AttachItem = memo((props: { attachment: IAttachment }) => {
  const { attachment } = props;

  if (!attachment.url) return null;

  const isImage = useMemo(
    () => File.getMime({ uri: attachment.url })?.split("/")[0] === "image",
    [attachment.url]
  );

  return (
    <SItemContainer>
      {isImage ? (
        <SImage
          style={{ width: WIDTH, height: WIDTH }}
          source={{ uri: attachment.url }}
        />
      ) : (
        <FileItemView file={attachment} size={WIDTH} />
      )}
    </SItemContainer>
  );
});

const SItemContainer = styled.View``;
const SImage = styled(FastImage)`
  border-radius: 4px;
  border-color: ${(p) => p.theme.neutral5};
  border-width: 1px;
`;
