import { memo } from "react";

export interface IMapItem<T> {
  item: T;
  index: number;
}

interface Props<T> {
  data: T[];
  renderItem: (props: IMapItem<T>) => JSX.Element;
}

const _MapList = <T extends unknown>({ data, renderItem }: Props<T>) => {
  if (!data || !data.length) return null;
  return data.map((item, index) =>
    renderItem({ item, index })
  ) as unknown as JSX.Element;
};

export const MapList = memo(_MapList) as typeof _MapList;
