import React, { useMemo } from "react";
import { SharedValue, useSharedValue } from "react-native-reanimated";
import constate from "constate";

export type IMap = { [id: string]: number };

interface IValue {
  headerHeightMap: SharedValue<IMap>;
  tabBarHeightMap: SharedValue<IMap>;
  aHeightMap: SharedValue<IMap>;
}

interface Props {
  children?: any;
  onCollapse?: (offset: number) => void;
}

const Context = (props: Props) => {
  const { onCollapse } = props;

  const headerHeightMap = useSharedValue<IMap>({});
  const tabBarHeightMap = useSharedValue<IMap>({});
  const aHeightMap = useSharedValue<IMap>({});

  const totalIndexMap = useSharedValue<any>({});
  const totalScrollMap = useSharedValue<any>({});

  const totalScrollVal = useSharedValue<number>(0);
  const totalLevelIndex = useSharedValue("");
  const totalTop = useSharedValue(0);

  return useMemo(
    () => ({
      headerHeightMap,
      tabBarHeightMap,
      aHeightMap,
      onCollapse,
      totalIndexMap,
      totalScrollMap,
      totalScrollVal,
      totalLevelIndex,
      totalTop,
    }),
    [
      headerHeightMap,
      aHeightMap,
      tabBarHeightMap,
      onCollapse,
      totalIndexMap,
      totalScrollMap,
      totalScrollVal,
      totalLevelIndex,
      totalTop,
    ]
  );
};

export const [TotalProvider, useTotalScrollableTabView] = constate(
  Context,
  (val) => val
);
