import { memo, useCallback, useMemo } from "react";
import { IAttachment, styled } from "@true/core";
import { ActivityIndicator, StyleSheet } from "react-native";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";
import FastImage from "react-native-fast-image";

interface Props {
  attachment: IAttachment;
}

export const AttachmentImage = memo(({ attachment }: Props) => {
  const isLoading = useSharedValue(true);
  const isSticker = useSharedValue(false);

  const uri = useMemo(() => {
    let _uri = "";
    if (attachment.type === "gif") {
      _uri = attachment?.src || "";
    } else if (attachment.type === "link") {
      _uri = attachment?.url || "";
    }

    if (_uri && _uri.includes("stickers")) {
      isSticker.value = true;
    }

    return _uri;
  }, [attachment]);

  const animatedLoadingStyle = useAnimatedStyle(() => ({
    zIndex: isLoading.value ? 1 : -1,
    opacity: isLoading.value ? 1 : 0,
    justifyContent: "center",
    alignItems: "center",
  }));

  const animatedStyle = useAnimatedStyle(() => ({
    width: isSticker.value ? 150 : "100%",
    height: isSticker.value ? 150 : 200,
  }));

  const onLoadEnd = useCallback(() => {
    isLoading.value = false;
  }, []);

  if (!uri) return null;

  return (
    <SWrapper style={animatedStyle}>
      <Animated.View
        style={[StyleSheet.absoluteFillObject, animatedLoadingStyle]}
      >
        <ActivityIndicator color={"red"} />
      </Animated.View>
      <SFastImage
        source={{ uri }}
        resizeMode={"contain"}
        onLoadEnd={onLoadEnd}
        onError={onLoadEnd}
      />
    </SWrapper>
  );
});

const SWrapper = styled(Animated.View)`
  background-color: ${(p) => p.theme.neutral9};
`;

const SFastImage = styled(FastImage)`
  width: 100%;
  height: 100%;
`;
