import React, {
  memo,
  NamedExoticComponent,
  PropsWithChildren,
  useEffect,
  useMemo,
} from "react";
import { MyThemeProvider } from "./MyThemeProvider";
import { AppInfo } from "@true/core";
import { Persistor } from "redux-persist";
import { PersistGate } from "redux-persist/integration/react";
import { Preload } from "./Preload";
import { ActivityIndicator } from "react-native";

interface Props extends PropsWithChildren {
  component: NamedExoticComponent<object>;
  primaryColor?: string;
  persistor?: Persistor;
}

export const MyMiniApp = memo(
  ({ component, primaryColor, persistor }: Props) => {
    // useEffect(() => {
    //   AppInfo.preSyncData().then();
    // }, []);

    const Comp = useMemo(() => component, [component]);

    return (
      <MyThemeProvider primaryColor={AppInfo.primaryColor || primaryColor}>
        <PersistGate
          persistor={persistor}
          loading={<ActivityIndicator color={AppInfo.primaryColor}/>}
        >
          <Comp />
        </PersistGate>
      </MyThemeProvider>
    );
  }
);
