import React, { memo, useCallback, useMemo, useState } from "react";
import { TextProps, View, ViewStyle } from "react-native";
import HTML, {
  CustomTagRendererRecord,
  MixedStyleDeclaration,
  RenderersProps,
} from "react-native-render-html";
import { WebView } from "react-native-webview";
import TableRenderer, { tableModel } from "@native-html/table-plugin";
import {
  objectStyles,
  translate,
  useBoolean,
  useLinkPress,
  useTheme,
  useWindow,
} from "@true/core";
import {
  BottomMenuContainer,
  BottomMenuHeader,
  BottomMenuItem,
  BottomMenuModal,
} from "../BottomMenu";
import { PhotoViewModal } from "../PhotoView";
import { ULOLProps, ULOLView } from "./ULOLView";
import { ImageProps, ImageView } from "./ImageView";
import { defaultBaseStyles, defaultTagsStyles, styles } from "./styles";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { useAnimatedStyle } from "react-native-reanimated";
// import linkifyHtml from "linkify-html";

// const urlRegex = /\bhttps?:\/\/[a-z0-9\.\-_](:\d+)?[^ \n\t<>()\[\]]*/i;

// const linkifyOptions = {
//   validate: {
//     url: function (value: string) {
//       return urlRegex.test(value);
//     },
//     email: false,
//   },
//   ignoreTags: ["a", "script", "style"],
// };

const renderers = (props: ULOLProps & ImageProps) => {
  return {
    table: TableRenderer,
    ol: ULOLView(props as ULOLProps),
    ul: ULOLView(props as ULOLProps),
    img: ImageView(props as ImageProps),
  } as CustomTagRendererRecord;
};

const customHTMLElementModels = {
  table: tableModel,
};

const renderersProps = (onLinkPress: (href: string) => void) => {
  return {
    a: {
      onPress: (_: any, href: string, __: any, ___: any) => onLinkPress(href),
    },
    img: {
      enableExperimentalPercentWidth: true,
    },
  } as Partial<RenderersProps>;
};

interface Props {
  htmlContent: string;
  containerStyle?: ViewStyle | ViewStyle[];
  baseFont?: Readonly<Record<string, ViewStyle>>;
  goToEdit?: () => void;
  canEdit?: boolean;
  defaultTextProps?: TextProps;
  onLinkPress?: (url: string) => void;
  imageScale?: number;
  imageOpacity?: number;
  imageTapHandlerWaitFor?: React.Ref<unknown> | React.Ref<unknown>[];
  isImageTapGestureHandler?: boolean;
}

// const regex = new RegExp("(file|https)?:\\/\\/.*\\.(?:png|jpg|jpeg)", "gi");

export const HTMLRenderer = memo(
  ({
    htmlContent,
    containerStyle,
    baseFont,
    goToEdit,
    canEdit,
    defaultTextProps,
    onLinkPress,
    imageScale,
    imageOpacity,
    imageTapHandlerWaitFor,
    isImageTapGestureHandler,
  }: Props) => {
    const theme = useTheme();
    const [isVisible, showVisible, hideVisible] = useBoolean(false);
    const [visibleImage, showVisibleImage, hideVisibleImage] =
      useBoolean(false);

    // const images = useMemo(() => {
    //   return htmlContent.match(regex) || [];
    // }, [htmlContent]);
    const [images, setImages] = useState<string[]>([]);

    const [href, setHref] = useState("");

    const showLink = useLinkPress();

    const _onLinkPress = useCallback(
      (href: string) => {
        if (onLinkPress) {
          return onLinkPress(href);
        }
        showLink(href).then();
      },
      [onLinkPress]
    );

    const onImagePress = useCallback(
      (_href: string) => {
        setHref(_href);
        if (canEdit) {
          showVisible();
        } else {
          showVisibleImage();
        }
      },
      [href, canEdit]
    );

    const htmlContentStr = useMemo(
      // () => (htmlContent ? linkifyHtml(htmlContent, linkifyOptions) : ""),
      () => (htmlContent ? htmlContent : ""),
      [htmlContent]
    );

    const indexImage = useMemo(() => {
      const _index = images.indexOf(href);
      return _index > -1 ? _index : 0;
    }, [images, href]);

    const viewPhoto = useCallback(() => {
      // Linking.openURL(href);
      images.length && showVisibleImage();
    }, [images]);

    const onGoToEdit = useCallback(
      (value: string) => {
        goToEdit && goToEdit();
      },
      [goToEdit]
    );

    const key = useMemo(() => Math.random().toString(), []);

    const { aBottom } = useAnimatedSafeAreaInsets();
    const animatedStyles = useAnimatedStyle(() => ({
      paddingBottom: aBottom.value,
    }));

    const { width } = useWindow();

    return (
      <>
        <HTML
          key={key}
          defaultTextProps={defaultTextProps}
          contentWidth={width}
          renderersProps={renderersProps(_onLinkPress)}
          renderers={renderers({
            baseFontStyle: baseFont ? baseFont : {},
            key,
            onImagePress,
            setImages,
            images,
            imageScale,
            imageOpacity,
            imageTapHandlerWaitFor,
            isTapGestureHandler: isImageTapGestureHandler,
          })}
          source={{ html: htmlContentStr }}
          tagsStyles={{
            ...defaultTagsStyles(theme),
            ...(baseFont as Readonly<Record<string, MixedStyleDeclaration>>),
          }}
          baseStyle={{
            ...defaultBaseStyles(theme),
            ...(containerStyle
              ? objectStyles(containerStyle)
              : styles.container),
          }}
          WebView={WebView}
          defaultWebViewProps={{
            style: {
              opacity: 0.99,
            },
          }}
          customHTMLElementModels={customHTMLElementModels}
        />
        <BottomMenuModal isVisible={isVisible} onClose={hideVisible}>
          <BottomMenuContainer containerStyle={animatedStyles}>
            <BottomMenuHeader
              onClose={hideVisible}
              title={translate("common.Action")}
            />
            <View style={styles.optionContainer}>
              <BottomMenuItem
                label={"common.ViewPhoto"}
                selected={false}
                onPress={viewPhoto}
              />
              {canEdit && (
                <BottomMenuItem
                  label={"common.EditContent"}
                  selected={false}
                  onPress={onGoToEdit}
                />
              )}
            </View>
          </BottomMenuContainer>
        </BottomMenuModal>
        <PhotoViewModal
          isVisible={visibleImage}
          onCloseRequest={hideVisibleImage}
          images={images}
          initialIndex={indexImage}
        />
      </>
    );
  }
);
