import React, { memo } from "react";
import {
  ActivityIndicator,
  StyleSheet,
  Text,
  TextStyle,
  TouchableOpacity,
  View,
  ViewStyle,
} from "react-native";
import { flatStyles, Fonts, styled, translate, useTheme } from "@true/core";

const SActivityIndicator = styled(ActivityIndicator).attrs((props) => ({
  color: props.color ? props.color : props.theme.primaryColor,
}))``;

export const NavRightButtonGroup = styled.View`
  flex-direction: row;
  height: 100%;
  margin-right: -8px;
`;

interface Props {
  onPress?: () => void;
  title: string;
  containerStyle?: ViewStyle;
  textStyle?: TextStyle;
  loading?: boolean;
  loadingColor?: string;
  disabled?: boolean;
  disabledColor?: string;
  position?: string | "left" | "right";
  activeBold?: boolean;
}

export const NavActionButton = memo(
  ({
    title,
    activeBold,
    position,
    textStyle,
    onPress,
    loading,
    loadingColor,
    disabled,
    disabledColor,
    containerStyle,
  }: Props) => {
    const theme = useTheme();
    return (
      <TouchableOpacity
        disabled={disabled || loading}
        style={flatStyles(
          styles.container,
          {
            paddingLeft: position === "left" ? 16 : 0,
            paddingRight: position === "right" ? 16 : 0,
          },
          containerStyle
        )}
        onPress={onPress}
      >
        <Text
          style={flatStyles(
            styles.title,
            { color: theme.primaryColor },
            {
              fontFamily: activeBold ? Fonts.Bold : Fonts.Normal,
            },
            disabled ? { color: disabledColor || theme.neutral6 } : null,
            textStyle ? textStyle : null,
            loading ? { opacity: 0 } : null
          )}
        >
          {title}
        </Text>
        {loading && (
          <View
            style={flatStyles(styles.indicator, {
              left: position === "left" ? 16 : 0,
              right: position === "right" ? 16 : 0,
            })}
          >
            <SActivityIndicator color={loadingColor} />
          </View>
        )}
      </TouchableOpacity>
    );
  }
);

const styles = StyleSheet.create({
  title: {
    fontSize: 15,
  },
  container: {
    height: "100%",
    paddingLeft: 18,
    justifyContent: "center",
  },
  indicator: {
    top: 0,
    height: "100%",
    position: "absolute",
    alignItems: "center",
    justifyContent: "center",
  },
});

interface OptionalTitleProps extends Omit<Props, "title"> {
  title?: string;
}

export const NavCancelButton = (props: OptionalTitleProps) => {
  return (
    <NavActionButton
      position={"left"}
      title={translate("common.Cancel")}
      {...props}
    />
  );
};

export const NavCreateButton = (props: OptionalTitleProps) => {
  const disabled = typeof props.disabled === "boolean" ? props.disabled : true;
  return (
    <NavActionButton
      title={translate("common.Create")}
      position={"right"}
      activeBold={true}
      {...props}
      disabled={disabled}
    />
  );
};
