import React, { memo } from "react";
import { View } from "react-native";
import { EViewTypes } from "./RecycleList";

interface Props {
  renderItem: (data: any, index?: number, rawIndex?: number) => JSX.Element;
  renderSection?: (
    data: any,
    onToggle?: () => void,
    index?: number,
    rawIndex?: number
  ) => JSX.Element | null;
  data: any[] | Record<string, any>;
  item: any;
  index: number;
  toggle: (section: string) => void;
}

export const ItemRender = memo((props: Props) => {
  const { renderItem, renderSection, index, item, toggle } = props;

  if (item === "lastItem") return null;

  if (!renderSection) {
    return <View key={index}>{renderItem(item, index)}</View>;
  }

  if (item.type === EViewTypes.SECTION && renderSection) {
    return (
      <View key={index}>
        {renderSection(item.data, () => toggle(item.index), item.index, index)}
      </View>
    );
  }

  if (item.type === EViewTypes.ITEM) {
    return <View key={index}>{renderItem(item.data, item.index, index)}</View>;
  }

  return null;
});
