import React, { memo, PropsWithChildren, useCallback, useState } from "react";
import BottomSheet, {
  BottomSheetBackdrop,
  BottomSheetFooterProps,
  useBottomSheet,
} from "@gorhom/bottom-sheet";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { Keyboard, StyleSheet } from "react-native";
import { BottomSheetContent } from "./BottomSheetContent";
import { CameraRollPicker } from "../CameraRollPicker";
import {
  SharedValue,
  useAnimatedReaction,
  useDerivedValue,
  useSharedValue,
} from "react-native-reanimated";
import { useCommentsProvider } from "./Provider";
import {
  getComments,
  getCommentsByQuery,
  interactManager,
  requestPostComment,
  useAnimatedKeyboard,
  useAsyncFn,
  useAutoToastError,
  useTheme,
} from "@true/core";
import { StickerBox } from "../StickerBox";
import { useDeepCompareEffect } from "react-use";
import {
  getCmtReplyHid,
  setCmtReplyHid,
} from "../../components/Comment/UIComps/CommentReplyView";
import { useKeyboardAvoidingView } from "../../components/AnimatedKeyboardAvoidingView";

export const CommentBottomSheet = memo((props: PropsWithChildren) => {
  const {
    bottomSheetRef,
    selectedImages,
    onSelectedImages,
    cameraRollRef,
    files,
    hid,
    token,
    appName,
    type,
    repliedItem,
    setMap,
    listHeight,
    itemLayout,
  } = useCommentsProvider();

  const { scrollRef, disableAutoScroll } =
    useKeyboardAvoidingView();

  const { aTop } = useAnimatedSafeAreaInsets();
  const { maxKeyboardHeight } = useAnimatedKeyboard();

  const animatedSnapPoints = useDerivedValue(
    () => [maxKeyboardHeight.value || 1, "100%"],
    []
  );

  const _a = useSharedValue(0);
  const [animatedFooter, setAnimatedBottomFooter] =
    useState<SharedValue<number>>(_a);

  const [{ loading, error }, onPost] = useAsyncFn(requestPostComment);

  const _onSendSticker = useCallback(
    async (url: string) => {
      const replyHid = getCmtReplyHid();
      const replyToken = getComments(replyHid)?.token;

      onPost(
        {
          hid: replyHid || hid,
          token: replyToken || token,
          comment: "-- @sticker " + url,
          content: "-- @sticker " + url,
          file: [],
        },
        appName,
        type
      ).then((res) => {
        setCmtReplyHid("");
        if (type === "comment") {
          if (replyHid) {
            repliedItem.value = {
              ...repliedItem.value,
              [replyHid]: true,
            };
            const newComment = getCommentsByQuery(replyHid);

            setMap((prev) => ({
              ...prev,
              [replyHid]: newComment,
            }));
            const lastItem = newComment[newComment.length - 1];
            interactManager(() => {
              disableAutoScroll();
              scrollRef.current?.scrollToItem({
                item: lastItem,
                animated: true,
                viewOffset:
                  listHeight.value - (itemLayout.current?.[lastItem] || 0),
              });
            }, 100);
          } else {
            interactManager(() => {
              scrollRef.current?.scrollToEnd({ animated: true });
            }, 100);
          }
        } else {
          interactManager(() => {
            scrollRef.current?.scrollToOffset({ offset: 0, animated: true });
          }, 100);
        }
      });
    },
    [hid, token, appName, type]
  );

  useAutoToastError(error);
  const theme = useTheme();

  return (
    <BottomSheet
      style={[styles.bottomSheet, { backgroundColor: theme.backgroundColor }]}
      ref={bottomSheetRef}
      enablePanDownToClose={false}
      onAnimate={(from, to) => {
        if (from == 1 && (to == 0 || to == -1)) {
          Keyboard.dismiss();
        }
      }}
      backdropComponent={(props) => (
        <BottomSheetBackdrop
          {...props}
          appearsOnIndex={1}
          disappearsOnIndex={0}
        />
      )}
      footerComponent={(props) => {
        return (
          <FootComp
            {...props}
            setAnimatedBottomFooter={setAnimatedBottomFooter}
          />
        );
      }}
      index={-1}
      topInset={aTop}
      snapPoints={animatedSnapPoints}
      handleStyle={{
        backgroundColor: theme.backgroundColor,
        borderTopLeftRadius: 14,
        borderTopRightRadius: 14,
      }}
      handleIndicatorStyle={{
        backgroundColor: theme.neutral2,
      }}
    >
      <BottomSheetContent
        contents={[
          <CameraRollPicker
            ref={cameraRollRef}
            isBottomSheet={true}
            onSelectImages={onSelectedImages}
            selected={selectedImages}
            isMarked={true}
            maxFiles={3 - files.length}
            toastShowMaxFile={3}
          />,
          <StickerBox
            onStickerSend={_onSendSticker}
            animatedBottomFooter={animatedFooter}
          />,
        ]}
      />

      <AnimatedIndex />
    </BottomSheet>
  );
});

const AnimatedIndex = memo(() => {
  const { animatedIndex: a, animatedCurrentIndex: b } = useBottomSheet();
  const { animatedIndex, animatedCurrentIndex } = useCommentsProvider();

  useAnimatedReaction(
    () => ({ _a: a?.value, _b: b?.value }),
    (cur) => {
      animatedIndex.value = cur._a;
      animatedCurrentIndex.value = cur._b;
    },
    []
  );

  return null;
});

const FootComp = memo(
  ({
    animatedFooterPosition,
    setAnimatedBottomFooter,
  }: BottomSheetFooterProps & { setAnimatedBottomFooter: any }) => {
    // const {setAnimatedBottomFooter} = useInputBottomSheet();

    useDeepCompareEffect(() => {
      setAnimatedBottomFooter(animatedFooterPosition);
    }, [animatedFooterPosition]);

    return null;
  }
);

const styles = StyleSheet.create({
  bottomSheet: {
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    elevation: 10,
    borderRadius: 14,
  },
});

