import { useSharedValue } from "react-native-reanimated";
import { useMemo } from "react";
import constate from "constate";

const provider = () => {
  const imageLayout = useSharedValue<{[id: string]: {w: number, h: number}}>({})
  return useMemo(() => ({
    imageLayout
  }), [imageLayout])
}

export const [HTMLImageProvider, useHTMLImage] = constate(provider, val => val)
