import React, { PureComponent } from "react";
import { StyleSheet, View } from "react-native";
import { flatStyles } from "@true/core";

interface OwnProps {
  width: number;
  height: number;
  backgroundColor: string;
}

type Props = OwnProps;

export class AddIcon extends PureComponent<Props> {
  render() {
    const { width, height, backgroundColor } = this.props;

    return (
      <View style={styles.container}>
        <View
          style={flatStyles(styles.vertical, { height, backgroundColor })}
        />
        <View
          style={flatStyles(styles.horizontal, { width, backgroundColor })}
        />
      </View>
    );
  }
}

const styles = StyleSheet.create({
  container: {
    justifyContent: "center",
    alignItems: "center",
  },
  vertical: {
    width: 2,
    position: "absolute",
  },
  horizontal: {
    height: 2,
    position: "absolute",
  },
});
