import React, { memo, PropsWithChildren } from "react";
import { Platform, StyleSheet, View } from "react-native";
import { AnimatedKeyboardAvoidingView } from "../../AnimatedKeyboardAvoidingView";
import {
  BottomMenuContainer,
  BottomMenuHeader,
  BottomMenuModal,
} from "../../BottomMenu";
import {styled} from "@true/core";
import { useStatusBarStackEntry } from "@true/core";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { useDerivedValue } from "react-native-reanimated";

interface Props extends PropsWithChildren {
  title: string;
  visible: boolean;
  withDivider?: boolean;
  onHide: () => void;
  headerComponent?: React.ReactElement;
}

export const BottomTemplate = memo((props: Props) => {
  const {
    title,
    visible,
    onHide,
    headerComponent,
    withDivider = false,
  } = props;
  useStatusBarStackEntry(visible);

  const { aBottom } = useAnimatedSafeAreaInsets();
  const negativeBottom = useDerivedValue(() => -aBottom.value, [aBottom]);

  return (
    <BottomMenuModal
      isVisible={visible}
      onClose={onHide}
      propagateSwipe={true}
      noChangeStatusbarColor={true}
    >
      <SKeyboardAvoidingView
        behavior={Platform.OS === "ios" ? "padding" : undefined}
        keyboardVerticalOffset={negativeBottom}
      >
        <BottomMenuContainer containerStyle={styles.fullscreen}>
          {title ? (
            <BottomMenuHeader
              title={title}
              onClose={onHide}
              noDivider={!withDivider}
            />
          ) : headerComponent ? (
            headerComponent
          ) : (
            <View style={{ height: 8 }} />
          )}
          <>{props.children}</>
        </BottomMenuContainer>
      </SKeyboardAvoidingView>
    </BottomMenuModal>
  );
});

export const SKeyboardAvoidingView: typeof AnimatedKeyboardAvoidingView = styled(
  AnimatedKeyboardAvoidingView
)`
  background-color: ${(p) => p.theme.backgroundColor};
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
`;

const styles = StyleSheet.create({
  fullscreen: {
    paddingBottom: 0,
    width: "100%",
    maxHeight: "100%",
  },
});
