import React, { memo } from "react";
import { ViewProps } from "react-native";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from "react-native-reanimated";
import { getHeight, styled } from "@true/core";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { UI } from "../../services";

interface Props extends ViewProps {
  duration?: number;
}

export const EmptyHeader = memo((props: Props) => {
  const { duration = 100 } = props;
  const { aTop } = useAnimatedSafeAreaInsets();
  const animatedHeight = useSharedValue(getHeight(UI.headerHeight));
  const animatedStyles = useAnimatedStyle(() => ({
    paddingTop: duration
      ? withTiming(aTop.value, { duration: 100 })
      : aTop.value,
    height: duration
      ? withTiming(aTop.value + animatedHeight.value, { duration: 100 })
      : aTop.value + animatedHeight.value,
  }));

  return (
    <SContainer {...props} style={[props.style, animatedStyles]}>
      {props.children}
    </SContainer>
  );
});

const SContainer = styled(Animated.View)`
  justify-content: center;
`;
