import React, { memo, useState } from "react";
import { styled } from "@true/core";
import { useCommentsProvider } from "./Provider";
import Animated, {
  runOnJS,
  SharedValue,
  useAnimatedStyle,
} from "react-native-reanimated";

interface Props {
  contents: JSX.Element[];
}

export const BottomSheetContent = memo((props: Props) => {
  const { contents } = props;
  const { currentBottomSheetIndex } = useCommentsProvider();

  return (
    <SContainer>
      {contents.map((item, index) => (
        <Item
          index={index}
          item={item}
          currentIndex={currentBottomSheetIndex}
        />
      ))}
    </SContainer>
  );
});

const Item = memo(
  ({
    index,
    item,
    currentIndex,
  }: {
    index: number;
    item: JSX.Element;
    currentIndex: SharedValue<number>;
  }) => {
    const [mounted, setMounted] = useState(false);

    const itemStyle = useAnimatedStyle(() => {
      if (!mounted && index == currentIndex.value) runOnJS(setMounted)(true);
      return {
        height: index == currentIndex.value ? "100%" : 0,
        // opacity: index == currentIndex.value ? 1 : 0,
      };
    }, [index, mounted]);

    return mounted ? (
      <SItemContainer style={itemStyle}>{item}</SItemContainer>
    ) : null;
  }
);

const SContainer = styled.View`
  flex: 1;
  background-color: ${(p) => p.theme.backgroundColor};
`;

const SItemContainer = styled(Animated.View)``;
