import {
  createNavigatorFactory,
  ParamListBase,
  StackNavigationState,
  useNavigationBuilder,
} from '@react-navigation/native';
import * as React from 'react';
import {HoldMenuRouter, HoldMenuRouterOptions} from './HoldMenuRouter';
import {HoldMenuView} from './HoldMenuView';
import type {
  HoldMenuActionHelpers,
  HoldMenuNavigationEventMap,
  HoldMenuNavigationOptions,
  HoldMenuNavigationState,
  HoldMenuNavigatorProps,
} from './type';

function HoldMenuNavigator({
  id,
  children,
  screenListeners,
  screenOptions,
  ...rest
}: HoldMenuNavigatorProps) {
  const {state, descriptors, navigation, NavigationContent} =
    useNavigationBuilder<
      HoldMenuNavigationState<ParamListBase>,
      HoldMenuRouterOptions,
      HoldMenuActionHelpers<ParamListBase>,
      HoldMenuNavigationOptions,
      HoldMenuNavigationEventMap
    >(HoldMenuRouter, {
      id,
      children,
      screenListeners,
      screenOptions,
    });

  return (
    <NavigationContent>
      <HoldMenuView
        {...rest}
        state={state}
        navigation={navigation}
        descriptors={descriptors}
      />
    </NavigationContent>
  );
}

export const createHoldMenuNavigator = createNavigatorFactory<
  StackNavigationState<ParamListBase>,
  HoldMenuNavigationOptions,
  HoldMenuNavigationEventMap,
  typeof HoldMenuNavigator
>(HoldMenuNavigator);
