import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";

import {
  ActivityIndicator,
  Animated,
  Dimensions,
  NativeScrollEvent,
  NativeSyntheticEvent,
  ScrollView,
  StyleSheet,
  TouchableWithoutFeedback,
  View,
} from "react-native";

import useDoubleTapToZoom from "../../hooks/useDoubleTapToZoom";
import useImageDimensions from "../../hooks/useImageDimensions";

import { getImageStyles, getImageTransform } from "../../utils";
import { ImageSource } from "../../@types";
import { ImageLoading } from "./ImageLoading";
import FastImage from "react-native-fast-image";
import {
  getBottomSpace,
  getStatusBarHeight,
} from "react-native-iphone-x-helper";
import {
  Extrapolate,
  runOnJS,
  SharedValue,
  useAnimatedReaction,
} from "react-native-reanimated";
import { useAsyncFn } from "react-use";
import { styled } from "@true/core";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";

const SWIPE_CLOSE_VELOCITY = 1.55;
const SCREEN = Dimensions.get("screen");
const SCREEN_WIDTH = SCREEN.width;
const SCREEN_HEIGHT =
  SCREEN.height - 56 - getStatusBarHeight() - getBottomSpace();

type Props = {
  imageSrc: ImageSource;
  onRequestClose: () => void;
  onZoom: (scaled: boolean) => void;
  onLongPress: (image: ImageSource) => void;
  delayLongPress: number;
  swipeToCloseEnabled?: boolean;
  doubleTapToZoomEnabled?: boolean;
  scrollValueY: any;
  requestFn?: (image: string) => Promise<string>;
  index: number;
  currentIndex: number;
  onRequestCloseEnhanced: () => void;
  onDownload: (url: string) => void;
  onShare: (url: string) => void;
  imageOpacityHeader: any;
};

const FS = Animated.createAnimatedComponent(FastImage);

const SFS = styled(FS)`
  width: 100%;
  flex: 1 1;
` as any;

const ImageItem = ({
  imageSrc: pImageSrc,
  onZoom,
  onRequestClose,
  onLongPress,
  delayLongPress,
  swipeToCloseEnabled = true,
  doubleTapToZoomEnabled = true,
  scrollValueY,
  requestFn,
  index,
  currentIndex,
  onDownload,
  onRequestCloseEnhanced,
  onShare,
  imageOpacityHeader,
}: Props) => {
  const scrollViewRef = useRef<ScrollView>(null);
  const [loaded, setLoaded] = useState(false);
  const [scaled, setScaled] = useState(false);
  // const imageDimensions = useImageDimensions(imageSrc);
  const handleDoubleTap = useDoubleTapToZoom(scrollViewRef, scaled, {
    width: SCREEN_WIDTH,
    height: SCREEN_HEIGHT,
  });

  const [{ value }, onRequestFn] = useAsyncFn(requestFn);

  const imageSrc = useMemo(
    () => (requestFn ? { uri: value } : pImageSrc),
    [pImageSrc, requestFn, value]
  ) as unknown as { uri: string };

  if (requestFn) {
    useEffect(() => {
      if (!loaded && currentIndex === index) {
        onRequestFn(pImageSrc?.uri as string).then(() => {
          setLoaded(true);
        });
      }
    }, [pImageSrc?.uri, currentIndex, index]);
  }

  // const [translate, scale] = getImageTransform(imageDimensions, {
  //   width: SCREEN_WIDTH,
  //   height: SCREEN_HEIGHT,
  // });
  // const scrollValueY = new Animated.Value(0);
  // const scaleValue = new Animated.Value(scale || 1);
  // const translateValue = new Animated.ValueXY(translate);
  // const maxScale = scale && scale > 0 ? Math.max(1 / scale, 1) : 1;

  const imageOpacity = scrollValueY.interpolate({
    inputRange: [-SCREEN_HEIGHT / 2, 0, SCREEN_HEIGHT / 2],
    outputRange: [0.5, 1, 0.5],
    extrapolate: Extrapolate.CLAMP,
  });

  // const imagesStyles = getImageStyles(
  //   imageDimensions,
  //   translateValue,
  //   scaleValue
  // );
  // const imageStylesWithOpacity = { ...imagesStyles };

  const onScrollEndDrag = useCallback(
    ({ nativeEvent }: NativeSyntheticEvent<NativeScrollEvent>) => {
      const velocityY = nativeEvent?.velocity?.y || 0;
      const offsetY = nativeEvent?.contentOffset?.y || 0;
      const scaled = nativeEvent?.zoomScale > 1;

      onZoom(scaled);
      setScaled(scaled);
      if (
        !scaled &&
        swipeToCloseEnabled &&
        (Math.abs(velocityY) > SWIPE_CLOSE_VELOCITY || Math.abs(offsetY) > 50)
      ) {
        onRequestClose();
      }
    },
    [scaled, onZoom, swipeToCloseEnabled]
  );

  const onScroll = ({
    nativeEvent,
  }: NativeSyntheticEvent<NativeScrollEvent>) => {
    const offsetY = nativeEvent?.contentOffset?.y || 0;

    if (nativeEvent?.zoomScale > 1) {
      return;
    }

    scrollValueY.setValue(offsetY);
  };

  const onLongPressHandler = useCallback(() => {
    onLongPress(imageSrc);
  }, [imageSrc, onLongPress]);

  const { aTop } = useAnimatedSafeAreaInsets();

  return (
    // @ts-ignore
    <View style={{ flex: 1 }}>
      <SHeader style={{ opacity: imageOpacityHeader, paddingTop: aTop.value }}>
        <SHeaderLeft>
          {!loaded ? (
            <ActivityIndicator color={"#eee"} />
          ) : (
            <SCloseButton onPress={() => onDownload(imageSrc?.uri)}>
              <Icon source={require("../../assets/ic_download.png")} />
            </SCloseButton>
          )}
          <SShareButton onPress={() => onShare(imageSrc?.uri)}>
            <Icon source={require("../../assets/ic_share.png")} />
          </SShareButton>
        </SHeaderLeft>
        <SCloseButton onPress={onRequestCloseEnhanced}>
          <Icon source={require("../../assets/ic_close.png")} />
        </SCloseButton>
      </SHeader>
      <Animated.View style={{ transform: [{ scale: imageOpacity }] }}>
        <ScrollView
          ref={scrollViewRef}
          style={styles.listItem}
          pinchGestureEnabled
          nestedScrollEnabled={true}
          showsHorizontalScrollIndicator={false}
          showsVerticalScrollIndicator={false}
          maximumZoomScale={1}
          contentContainerStyle={styles.imageScrollContainer}
          scrollEnabled={swipeToCloseEnabled}
          onScrollEndDrag={onScrollEndDrag}
          scrollEventThrottle={1}
          {...(swipeToCloseEnabled && {
            onScroll,
          })}
        >
          {!loaded && <ImageLoading />}
          <TouchableWithoutFeedback
            onPress={doubleTapToZoomEnabled ? handleDoubleTap : undefined}
            onLongPress={onLongPressHandler}
            delayLongPress={delayLongPress}
          >
            {requestFn ? (
              <SFS source={{ uri: value }} resizeMode={"contain"} />
            ) : (
              <SFS
                // @ts-ignore
                source={imageSrc}
                resizeMode={"contain"}
                onLoad={() => setLoaded(true)}
              />
            )}
          </TouchableWithoutFeedback>
        </ScrollView>
      </Animated.View>
    </View>
  );
};

const styles = StyleSheet.create({
  listItem: {
    width: SCREEN_WIDTH,
    height: SCREEN_HEIGHT,
  },
  imageScrollContainer: {
    height: SCREEN_HEIGHT,
  },
});

const SHeader = styled(Animated.View)`
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  z-index: 1;
  padding: 16px 8px;
  background-color: #000;
`;

const SHeaderLeft = styled.View`
  flex-direction: row;
  align-items: center;
`;

const SCloseButton = styled.TouchableOpacity`
  height: 32px;
  width: 32px;
  align-items: center;
  justify-content: center;
`;

const SShareButton = styled(SCloseButton)`
  margin-left: 16px;
`;

const Icon = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: #fff;
`;

export default React.memo(ImageItem);
