import React, {
  memo,
  PropsWithChildren,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import BottomSheet from "@gorhom/bottom-sheet";
import { MyKeyboardAvoidingView } from "../MyKeyboardAvoidingView";
import { AnimatedKeyboardAvoidingViewRef } from "../AnimatedKeyboardAvoidingView";
import constate from "constate";
import { useSharedValue } from "react-native-reanimated";
import { CameraRollPickerRef } from "../CameraRollPicker";
import { FileType } from "@true/core";
import { IRawComment } from "./UIComps/Item";
import { Keyboard, View } from "react-native";
import { setCmtReplyHid } from "./UIComps/CommentReplyView";

export interface CommentProviderProps extends PropsWithChildren {
  hid?: string;
  token?: string;
  appName?: string;
  type?: "comment" | "post";
}

const _provider = (props: CommentProviderProps) => {
  const bottomSheetRef = useRef<BottomSheet>(null);
  // const scrollRef = useRef<FlashList<any>>(null);
  const keyboardRef = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const cameraRollRef = useRef<CameraRollPickerRef>(null);
  const currentBottomSheetIndex = useSharedValue<number>(-2);
  const animatedIndex = useSharedValue<number>(-1);
  const animatedCurrentIndex = useSharedValue<number>(-1);
  const topInset = useSharedValue(0);

  const [selectedImages, setSelectedImages] = useState<FileType[]>([]);
  const onSelectedImages = useCallback((images: FileType[]) => {
    setSelectedImages(images);
  }, []);

  const [files, setFiles] = useState<FileType[]>([]);

  const [map, setMap] = useState<Record<string, IRawComment[]>>({});
  const listHeight = useSharedValue(0);

  const mountedItem = useSharedValue<{ [id: string]: boolean }>({});
  const repliedItem = useSharedValue<{ [id: string]: boolean }>({});
  const itemLayout = useRef<{ [id: string]: number }>({});

  const onCloseBottomSheet = useCallback(() => {
    currentBottomSheetIndex.value = -2;
    keyboardRef.current?.enableCollapse();
    bottomSheetRef.current?.close();
    Keyboard.dismiss();
    keyboardRef.current?.collapse();
  }, []);

  return useMemo(
    () => ({
      bottomSheetRef,
      keyboardRef,
      currentBottomSheetIndex,
      selectedImages,
      onSelectedImages,
      animatedIndex,
      animatedCurrentIndex,
      cameraRollRef,
      map,
      setMap,
      files,
      setFiles,
      topInset,
      onCloseBottomSheet,
      mountedItem,
      repliedItem,
      listHeight,
      itemLayout,
      ...props,
    }),
    [
      bottomSheetRef,
      keyboardRef,
      currentBottomSheetIndex,
      selectedImages,
      onSelectedImages,
      animatedIndex,
      animatedCurrentIndex,
      cameraRollRef,
      map,
      setMap,
      files,
      setFiles,
      topInset,
      onCloseBottomSheet,
      mountedItem,
      repliedItem,
      listHeight,
      itemLayout,
      props,
    ]
  );
};

const [_Provider, useCommentsProvider] = constate(_provider, (val) => val);

const _KeyboardAvoidingView = memo((props: PropsWithChildren) => {
  const { keyboardRef } = useCommentsProvider();

  return (
    <MyKeyboardAvoidingView ref={keyboardRef}>
      {props.children}
    </MyKeyboardAvoidingView>
  );
});

export const Provider = memo(({ children, ...rest }: CommentProviderProps) => {
  useEffect(() => {
    return () => {
      setCmtReplyHid("");
    };
  });

  return (
    <_Provider {...rest}>
      <_KeyboardAvoidingView children={children} />
    </_Provider>
  );
});

export const TopInsetTagUserView = memo(() => {
  const { topInset } = useCommentsProvider();

  return (
    <View
      style={{ height: 0, position: "absolute", top: 0 }}
      onLayout={(e) => {
        //@ts-ignore
        e.target?.measure((x, y, w, h, px, py) => {
          topInset.value = py;
        });
      }}
    />
  );
});

export { useCommentsProvider };
