import React, { memo, useCallback, useEffect, useRef } from "react";
import { Keyboard, TextInput, View } from "react-native";
import { interactManager, MaxKeyboard, useAnimatedKeyboard } from "@true/core";
import { KeyboardEvents } from "react-native-keyboard-controller";

export const InitKeyboardView = memo((props: { isInForm?: boolean }) => {
  const ref = useRef<TextInput | null>(null);

  if (!props.isInForm) {
    useEffect(() => {
      (async () => {
        const height = await MaxKeyboard.getHeight();
        if (height == 0) {
          ref.current?.focus();
          interactManager(() => {
            Keyboard.dismiss();
          }, 100);
        }
      })();
    }, []);
  }

  const { maxKeyboardHeight, extendedKeyboardHeight } = useAnimatedKeyboard();

  const onKeyboardShowHandler = useCallback((e: { height: number }) => {
    if (e.height) {
      maxKeyboardHeight.value = e.height;
      extendedKeyboardHeight.value = e.height;
      MaxKeyboard.setHeight(e.height).then();
    }
  }, []);

  useEffect(() => {
    let show = KeyboardEvents.addListener(
      "keyboardDidShow",
      onKeyboardShowHandler
    );

    return () => show.remove();
  }, []);

  if (props.isInForm) return null;

  return (
    <View
      style={{
        opacity: 0,
        height: 0,
        width: 0,
        overflow: "hidden",
        position: "absolute",
        bottom: -500,
      }}
    >
      <TextInput ref={ref} />
    </View>
  );
});
