import { memoForwardRef, styled } from "@true/core";
import React, { RefObject, useImperativeHandle } from "react";
import { UIForm } from "../UIForm";
import { UIUserPickerInputProps } from "./types";
import { ReText } from "react-native-redash";
import { CONTENT_FONT_SIZE, CONTENT_LINE_HEIGHT } from "../UIForm/constant";
import { UserPickerInputProvider, useUserPickerInput } from "./provider";
import { useDerivedValue } from "react-native-reanimated";
import { UsersRow } from "./UsersRow/UsersRow";
import { BottomSheet } from "./BottomSheet/BottomSheet";
import { IC_ADD_USERS } from "../UIForm/assets";
import { BottomSheetModalMethods } from "@gorhom/bottom-sheet/src/types";

export interface UIUserPickerInput {
  onClearValue: () => void;
  bottomSheetRef: RefObject<BottomSheetModalMethods>;
}

const _UIUserPickerInput = memoForwardRef(
  (props: UIUserPickerInputProps, ref: RefObject<UIUserPickerInput>) => {
    const { type } = props;

    const { selectedIds, bottomSheetRef, onClearIds } = useUserPickerInput();

    const textVal = useDerivedValue(
      () => (selectedIds.value.length ? "1" : ""),
      []
    );

    // const mounted = useMounted(100);

    useImperativeHandle(ref, () => ({
      onClearValue: onClearIds,
      bottomSheetRef: bottomSheetRef,
    }));

    return (
      <>
        <UIForm.Input
          {...props}
          ContentElement={
            <UsersRow
              ids={selectedIds}
              size={24}
              type={type}
              maxUsers={6}
              placeholder={props.placeholder || "---"}
            />
          }
          textValue={textVal}
          onPress={() => bottomSheetRef.current?.present()}
          inputType={"selector"}
          onClearValue={onClearIds}
          rightIconSource={IC_ADD_USERS}
        />
        <BottomSheet ref={bottomSheetRef} {...props} />
      </>
    );
  }
);

export const UIUserPickerInput = memoForwardRef(
  (props: UIUserPickerInputProps, ref: RefObject<UIUserPickerInput>) => {
    return (
      <UserPickerInputProvider {...props}>
        <_UIUserPickerInput ref={ref} {...props} />
      </UserPickerInputProvider>
    );
  }
);

const SReText = styled(ReText)`
  color: ${(p) => p.theme.neutral1};
  font-size: ${CONTENT_FONT_SIZE}px;
  line-height: ${CONTENT_LINE_HEIGHT}px;
`;

