import { useInvalid } from "./useInvalid";
import {
  requestLoadComment,
  useAsyncFn,
  useAutoToastError,
  useCommentsByQuery,
  usePostsByQuery,
} from "@true/core";
import { useCallback, useEffect, useMemo, useState } from "react";
import { CommentProviderProps } from "../Provider";

export const useRequest = (props?: CommentProviderProps) => {
  const { type, hid, token, appName } = useInvalid(props);

  const commentsList = (
    type == "comment" ? useCommentsByQuery : usePostsByQuery
  )(hid) as string[];

  const [mounted, setMounted] = useState(false);

  const _requestLoadComment = useCallback(
    async (params?: { method: "prev"; position: number }) => {
      return await requestLoadComment(
        { hid, token, ...(params || {}) },
        appName,
        type
      );
    },
    [hid, token, appName, type]
  );

  const [
    { loading: commentLoadingFirst, error: commentFirstError },
    loadCommentFirst,
  ] = useAsyncFn(_requestLoadComment);
  const [
    { loading: commentRefreshing, error: commentRefreshError },
    refreshComment,
  ] = useAsyncFn(_requestLoadComment);
  const [
    { loading: commentLoadingMore, error: commentLoadMoreError },
    loadMoreComment,
  ] = useAsyncFn(_requestLoadComment);

  useEffect(() => {
    loadCommentFirst().then(() => {
      setMounted(true);
    });
  }, []);

  useAutoToastError(commentFirstError);
  useAutoToastError(commentRefreshError);
  useAutoToastError(commentLoadMoreError);

  return useMemo(
    () => ({
      commentsList,
      commentLoadingFirst: !mounted || commentLoadingFirst,
      commentRefreshing,
      commentLoadingMore,
      refreshComment,
      loadMoreComment,
    }),
    [
      commentsList,
      mounted,
      commentLoadingFirst,
      commentRefreshing,
      commentLoadingMore,
      refreshComment,
      loadMoreComment,
    ]
  );
};
