import { ImageSourcePropType } from "react-native";
import React, { memo, useCallback, useMemo } from "react";
import { useBoolean, useMe, useUser } from "@true/core";
import { SimpleClickableItem } from "../ReuseItem";
import { ProfileLightBox } from "../ProfileModal";

interface CreatedByRowProps {
  userId: string | undefined;
  label?: string;
  divider?: boolean;
  icon?: ImageSourcePropType;
}

export const CreatedByRow = memo(function Position({
  userId,
  label = "common.create_by",
  divider,
  icon,
}: CreatedByRowProps) {
  const [visible, show, hide] = useBoolean(false);
  const user = useUser(userId);
  const me = useMe();
  const username = useMemo(() => {
    const nameArr = user?.name.split(" ") || [];
    const _length = nameArr.length || 0;
    const _fName = nameArr[0];
    const _lName = _length > 1 ? nameArr[_length - 1] + " " : "";
    return _lName + _fName;
  }, [user]);

  const onPress = useCallback(() => {
    if (userId) {
      show();
    }
  }, [visible, userId]);

  return (
    <>
      <SimpleClickableItem
        label={label}
        onPress={onPress}
        disabled={userId == me?.id}
        content={username != "undefined" ? username : "N/A"}
        divider={divider}
        icon={icon}
        isBigIcon={true}
      />
      {userId && (
        <ProfileLightBox
          userKey={userId}
          isVisible={visible}
          onCloseRequest={hide}
        />
      )}
    </>
  );
});
