import * as React from "react";
import {
  createContext,
  RefObject,
  useCallback,
  useContext,
  useImperativeHandle,
  useMemo,
  useRef,
} from "react";

import {
  findNodeHandle,
  FlatList,
  Keyboard,
  LayoutChangeEvent,
  NativeScrollEvent,
  NativeSyntheticEvent,
  Platform,
  ScrollView,
  StyleSheet,
  TextInput,
  UIManager,
  ViewProps,
  ViewStyle,
} from "react-native";
import Animated, {
  interpolate,
  runOnJS,
  SharedValue,
  useAnimatedReaction,
  useAnimatedRef,
  useAnimatedStyle,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
  withTiming,
  scrollTo,
} from "react-native-reanimated";
import {
  interactManager,
  memoForwardRef,
  useAnimatedKeyboard,
  useAnimatedWindow,
} from "@true/core";
import { FlashList } from "@shopify/flash-list";

interface ViewLayout {
  x: number;
  y: number;
  width: number;
  height: number;
}

interface Props extends ViewProps {
  /**
   * Specify how to react to the presence of the keyboard.
   */
  behavior?: "height" | "position" | "padding";

  /**
   * Style of the content container when `behavior` is 'position'.
   */
  contentContainerStyle?: ViewStyle;

  /**
   * Controls whether this `KeyboardAvoidingView` instance should take effect.
   * This is useful when more than one is on the screen. Defaults to true.
   */
  enabled?: boolean;

  /**
   * Distance between the top of the user screen and the React Native view. This
   * may be non-zero in some cases. Defaults to 0.
   */
  keyboardVerticalOffset?: SharedValue<number> | number;

  hasCollapseMethod?: boolean;

  scrollInputAdditionalOffset?: SharedValue<number> | number;

  animatedIndex?: SharedValue<number>;

  animatedCurrentIndex?: SharedValue<number>;

  scrollRef?: React.RefObject<ScrollView | FlashList<any> | FlatList>;
}

export interface AnimatedKeyboardAvoidingViewRef {
  scrollRef: React.RefObject<ScrollView & FlashList<any> & FlatList>;
  collapse: () => void;
  expand: () => void;
  onScroll: (e: NativeSyntheticEvent<NativeScrollEvent>) => void;
  onBeginDrag: () => void;
  disableCollapse: () => void;
  enableCollapse: () => void;
  disableAutoScroll: () => void;
  enableAutoScroll: () => void;
  animatedKeyboardIndex: SharedValue<number>;
  bottomHeight: SharedValue<number>;
}

const Context = createContext<AnimatedKeyboardAvoidingViewRef | null>(null);

export const useKeyboardAvoidingView = () => {
  return useContext(Context) as AnimatedKeyboardAvoidingViewRef;
};

/**
 * View that moves out of the way when the keyboard appears by automatically
 * adjusting its height, position, or bottom padding.
 */
export const AnimatedKeyboardAvoidingView = memoForwardRef(
  (props: Props, ref: RefObject<AnimatedKeyboardAvoidingViewRef>) => {
    const {
      behavior,
      children,
      contentContainerStyle,
      enabled = true,
      // eslint-disable-next-line no-unused-vars
      keyboardVerticalOffset = 0,
      style,
      onLayout,
      animatedIndex,
      animatedCurrentIndex,
      hasCollapseMethod,
      scrollInputAdditionalOffset,
      scrollRef: pScrollRef,
      ...rest
    } = props;

    const scrollRef =
      (pScrollRef as React.RefObject<ScrollView & FlashList<any> & FlatList>) ||
      useAnimatedRef<FlashList<any> & ScrollView & FlatList>();

    const isBottomSheet = useSharedValue(false);
    const scrollValue = useSharedValue<number>(0);
    const endScrollVal = useSharedValue(0);

    const disabledCollapse = useSharedValue(false);
    const disabledAutoScroll = useSharedValue(true);

    const _frame = useSharedValue<ViewLayout | null>(null);
    const viewRef = useAnimatedRef<Animated.View>();
    const _initialFrameHeight = useSharedValue<number>(0);

    const bottom = useSharedValue(0);
    const maxBottom = useSharedValue(0);

    const getAnimatedValue = useCallback(
      (value: SharedValue<number> | number) => {
        "worklet";
        return typeof value !== "number" ? value?.value || 0 : value;
      },
      []
    );

    const { keyboardHeight, extendedKeyboardHeight } = useAnimatedKeyboard();

    const { height: windowHeight } = useAnimatedWindow();

    const _updateBottomIfNecessary = useWorkletCallback(
      (
        _keyboardHeight: number,
        _windowHeight: number,
        _aFrame: ViewLayout | null
      ) => {
        "worklet";

        if (_aFrame == null) {
          bottom.value = 0;
          return;
        }

        const keyboardY =
          _windowHeight -
          _keyboardHeight -
          getAnimatedValue(keyboardVerticalOffset);

        // Calculate the displacement needed for the view such that it
        // no longer overlaps with the keyboard
        const height = Math.max(
          (_aFrame?.y || 0) + (_aFrame?.height || 0) - keyboardY,
          0
        );

        bottom.value = withTiming(height, {
          duration: Platform.OS == "ios" ? 250 : 150,
        });

        if (maxBottom.value < height) {
          maxBottom.value = height;
        }
      },
      [bottom, _frame, maxBottom]
    );

    const _onLayout = useCallback(
      (event: LayoutChangeEvent) => {
        _frame.value = event.nativeEvent.layout;

        if (!_initialFrameHeight.value) {
          // save the initial frame height, before the keyboard is visible
          _initialFrameHeight.value = _frame.value?.height;
        }

        if (onLayout) {
          onLayout(event);
        }
      },
      [_frame, _initialFrameHeight, _updateBottomIfNecessary, onLayout]
    );

    /**
     * enable collapsable if input is focused
     * and enable autoScroll if input is blurred
     */
    // const _enableAutoScroll = useWorkletCallback((keyboardHeight: number) => {
    //   "worklet";
    //
    //   if (keyboardHeight == 0) {
    //     disabledAutoScroll.value = false;
    //   }
    // }, []);

    /**
     * Keyboard handler
     * */
    useAnimatedReaction(
      () => ({
        aFrame: _frame.value,
        aKeyboardHeight: keyboardHeight.value,
        aWindowHeight: windowHeight.value,
      }),
      (cur) => {
        if (
          (hasCollapseMethod == true || disabledCollapse.value) &&
          cur.aKeyboardHeight == 0
        ) {
          return;
        }

        _updateBottomIfNecessary(
          cur.aKeyboardHeight,
          cur.aWindowHeight,
          cur.aFrame
        );
      },
      [hasCollapseMethod]
    );

    const bottomHeight = useDerivedValue(() => {
      if (
        disabledCollapse.value &&
        animatedIndex &&
        animatedCurrentIndex &&
        animatedCurrentIndex.value > -1
      ) {
        bottom.value = interpolate(
          animatedIndex.value,
          [-1, 0, 0],
          [0, maxBottom.value]
        );
      }
      return enabled ? bottom.value : 0;
    }, [
      enabled,
      bottom,
      disabledCollapse,
      animatedIndex,
      animatedCurrentIndex,
    ]);

    const expand = useCallback(() => {
      _updateBottomIfNecessary(
        extendedKeyboardHeight.value,
        windowHeight.value,
        _frame.value
      );
    }, []);

    const collapse = useCallback(() => {
      _updateBottomIfNecessary(0, windowHeight.value, _frame.value);
      Keyboard.dismiss();
    }, [windowHeight, _frame, windowHeight]);

    const onScroll = useCallback(
      (e: NativeSyntheticEvent<NativeScrollEvent>) => {
        endScrollVal.value =
          e.nativeEvent.contentSize.height -
          e.nativeEvent.layoutMeasurement.height;
        if (isBottomSheet.value) return;
        scrollValue.value = e.nativeEvent.contentOffset.y;
      },
      [scrollValue.value]
    );

    const onBeginDrag = useCallback(() => {
      isBottomSheet.value = false;
      disabledAutoScroll.value = false;
    }, []);

    const disableCollapse = useCallback(() => {
      disabledCollapse.value = true;
    }, []);

    const enableCollapse = useCallback(() => {
      disabledCollapse.value = false;
    }, []);

    const disableAutoScroll = useCallback(() => {
      disabledAutoScroll.value = true;
    }, []);

    const enableAutoScroll = useWorkletCallback(() => {
      disabledAutoScroll.value = false;
    }, []);

    const animatedKeyboardIndex = useDerivedValue(() => {
      return interpolate(bottomHeight.value, [0, maxBottom.value], [0, 1]);
    }, []);

    const returnValue = useMemo(
      () => ({
        collapse,
        expand,
        scrollRef,
        onScroll,
        disableCollapse,
        enableCollapse,
        disableAutoScroll,
        enableAutoScroll,
        animatedKeyboardIndex,
        bottomHeight,
        onBeginDrag,
      }),
      [
        collapse,
        scrollRef,
        expand,
        onScroll,
        disableCollapse,
        disableAutoScroll,
        enableAutoScroll,
        animatedKeyboardIndex,
        bottomHeight,
        onBeginDrag,
      ]
    );

    useImperativeHandle(ref, () => returnValue, [returnValue]);

    switch (behavior) {
      case "height": {
        // Note that we only apply a height change when there is keyboard present,
        // i.e. this.state.bottom is greater than 0. If we remove that condition,
        // this.frame.height will never go back to its original value.
        // When height changes, we need to disable flex.
        const animatedStyles = useAnimatedStyle(() => {
          return _frame.value != null && bottom.value > 0
            ? {
                height: _initialFrameHeight.value - bottomHeight.value,
              }
            : {};
        });

        return (
          <Context.Provider value={returnValue}>
            <Animated.View
              ref={viewRef}
              style={[style, { flex: 0 }, animatedStyles]}
              onLayout={_onLayout}
              {...rest}
            >
              {children}
            </Animated.View>
          </Context.Provider>
        );
      }

      case "position": {
        const animatedStyle = useAnimatedStyle(() => ({
          bottom: bottomHeight.value,
          // height: withDelay(300, withTiming(windowHeight.value - keyboardHeight.value, {duration: 0})),
        }));
        // const animatedViewStyle = useAnimatedStyle(() => ({
        //   height: keyboardHeight.value + 120,
        // }));
        return (
          <Context.Provider value={returnValue}>
            <Animated.View
              ref={viewRef}
              style={[style, { justifyContent: "flex-end", flex: 1 }]}
              onLayout={_onLayout}
              {...rest}
            >
              <Animated.View
                style={[
                  contentContainerStyle,
                  { justifyContent: "flex-end", flex: 1 },
                  animatedStyle,
                ]}
              >
                {/*<Animated.View style={animatedViewStyle}/>*/}
                {children}
              </Animated.View>
            </Animated.View>
          </Context.Provider>
        );
      }

      case "padding": {
        const currentBottomHeight = useSharedValue(0);

        const isAutoScroll = useDerivedValue(() => {
          /** get keyboard height when bottomSheet  */
          const _keyboardHeight =
            animatedIndex && animatedIndex?.value > -1
              ? maxBottom.value
              : bottomHeight.value;

          return (
            !disabledAutoScroll.value &&
            !(
              endScrollVal.value - maxBottom.value < scrollValue.value &&
              _keyboardHeight == 0
            )
            // && scrollValue.value !== null
          );
        }, []);

        const autoScoll = useCallback((_bottomHeight: number) => {
          "worklet";
          isBottomSheet.value = true;
          const _scrollVal = scrollValue.value + _bottomHeight;

          if (scrollRef) {
            scrollTo(scrollRef as any, 0, _scrollVal, false);
          }
        }, []);

        useAnimatedReaction(
          () => bottomHeight.value,
          (_bottomHeight) => {
            /** scroll to current position if scrollValue available
             * and autoScroll is enabled
             */
            if (isAutoScroll.value) return autoScoll(_bottomHeight);
            /* --get value after animation-- */
            if (_bottomHeight == maxBottom.value)
              return (currentBottomHeight.value = maxBottom.value);
            if (_bottomHeight == 0) return (currentBottomHeight.value = 0);
          },
          []
        );

        const animatedStyles = useAnimatedStyle(() => ({
          paddingBottom: bottomHeight.value,
        }));

        const JSFunc = useCallback(
          (_bottomHeight: number) => {
            interactManager(
              () => {
                if (Platform.OS !== "ios") return;

                const currentlyFocusedField = TextInput.State
                  .currentlyFocusedInput
                  ? findNodeHandle(TextInput.State.currentlyFocusedInput())
                  : TextInput.State.currentlyFocusedField();

                const responder = scrollRef.current?.getScrollResponder?.();

                if (!currentlyFocusedField || !responder) return;
                // Check if the TextInput will be hidden by the keyboard
                //@ts-ignore
                UIManager?.viewIsDescendantOf(
                  currentlyFocusedField,
                  // @ts-ignore
                  responder?.getInnerViewNode(),
                  (isAncestor: any) => {
                    if (isAncestor) {
                      UIManager.measureInWindow(
                        currentlyFocusedField,
                        (
                          x: number,
                          y: number,
                          width: number,
                          height: number
                        ) => {
                          // const textInputBottomPosition = y + height;

                          scrollRef.current
                            ?.getScrollResponder()
                            .scrollResponderScrollNativeHandleToKeyboard(
                              currentlyFocusedField,
                              75 +
                                getAnimatedValue(scrollInputAdditionalOffset) -
                                getAnimatedValue(keyboardVerticalOffset)
                            );
                        }
                      );
                    }
                  }
                );
              },
              0,
              true
            );
          },
          [keyboardVerticalOffset, animatedIndex]
        );

        useAnimatedReaction(
          () => currentBottomHeight.value,
          (cur) => {
            if (isAutoScroll.value) return autoScoll(cur);
            runOnJS(JSFunc)(cur);
          },
          [keyboardVerticalOffset]
        );

        return (
          <Context.Provider value={returnValue}>
            <Animated.View
              ref={viewRef}
              style={StyleSheet.compose(style, animatedStyles)}
              onLayout={_onLayout}
              {...rest}
            >
              {children}
            </Animated.View>
          </Context.Provider>
        );
      }

      default: {
        return (
          <Context.Provider value={returnValue}>
            <Animated.View
              ref={viewRef}
              onLayout={_onLayout}
              style={style}
              {...rest}
            >
              {children}
            </Animated.View>
          </Context.Provider>
        );
      }
    }
  }
);
