// This file is statusbar helper for iPhoneX

import { styled,  ThemeContext } from "@true/core";
import * as React from "react";
import { memo, useContext, useEffect, useMemo } from "react";
import { StatusBar, StatusBarProps, ViewStyle } from "react-native";
import { ETheme, useThemeStore } from "@true/core";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";

const TheView = styled(Animated.View)`
  background-color: ${(props) => props.theme.primaryColor};
`;

interface Props {
  statusBarProps?: StatusBarProps;
  style?: ViewStyle;
}
export const StatusBarView = memo(({ statusBarProps = {}, style }: Props) => {
  const theme = useContext(ThemeContext);
  const { themeName } = useThemeStore();

  const sStatusBarProps: StatusBarProps = useMemo(() => {
    return {
      barStyle: `${
        themeName === ETheme.light ? ETheme.dark : ETheme.light
      }-content`,
      backgroundColor: theme.primaryColor,
      ...statusBarProps,
    };
  }, [statusBarProps]);

  useEffect(() => {
    StatusBar.pushStackEntry(sStatusBarProps);

    return () => {
      StatusBar.popStackEntry(sStatusBarProps);
    };
  }, [sStatusBarProps]);

  const { aTop } = useAnimatedSafeAreaInsets();
  const animatedStyles = useAnimatedStyle(() => ({
    height: aTop.value,
  }));

  return <TheView style={[style, animatedStyles]} />;
});
