import {
  IC_EMPTY_LIST,
  memoForwardRef,
  setMultiRef,
  styled,
  translate,
  useAnimatedWindow,
} from "@true/core";
import { useAuToScroll } from "./hooks/useAutoScroll";
import Animated, {
  interpolate,
  SharedValue,
  useAnimatedReaction,
  useAnimatedScrollHandler,
  useAnimatedStyle,
  withDelay,
  withTiming,
} from "react-native-reanimated";
import React, { memo, useRef } from "react";
import { Image, NativeScrollEvent, View } from "react-native";
import { StickyHeader } from "./StickyHeader";
import { ScrollView as ReScrollView } from "react-native-gesture-handler";
import { FlashList } from "@shopify/flash-list";
import { UIFlashList, UIFlastListProps } from "../Common";

const __List = (
  props: {
    index: number;
    noItemImageSource?: any;
    disabledLoadData?: boolean;
    onCollapse?: (offset: number) => void;
    singleListIndex?: number;
    onScroll?: (e: NativeScrollEvent) => void;
    onScrollBeginDrag?: (e: NativeScrollEvent) => void;
    onMomentumScrollBegin?: (e: NativeScrollEvent) => void;
    onScrollEndDrag?: (e: NativeScrollEvent) => void;
    onMomentumScrollEnd?: (e: NativeScrollEvent) => void;
    changeParamsLoading?: boolean;
    loadMorable?: boolean;
    // tabs?: ITab[];
    // currentIndex?: number;
    // setCurrentIndex?: (val: number) => void;
  } & Omit<
    UIFlastListProps<string | number, any>,
    | "refreshable"
    | "loadMorable"
    | "onScroll"
    | "onScrollBeginDrag"
    | "onMomentumScrollBegin"
    | "onScrollEndDrag"
    | "onMomentumScrollEnd"
  >,
  ref: any
) => {
  const _ref = useRef<FlashList<any>>(null);

  const {
    noItemImageSource,
    index,
    ListHeaderComponent,
    disabledLoadData,
    loading,
    onCollapse,
    singleListIndex,
    onScroll: propsOnScroll,
    onScrollBeginDrag: propsOnScrollBeginDrag,
    onMomentumScrollBegin,
    onScrollEndDrag,
    onMomentumScrollEnd,
    ListEmptyComponent,
    changeParamsLoading,
    loadMorable = true,
    // tabs,
    // currentIndex,
    // setCurrentIndex,
    ...rest
  } = props;

  const {
    _headerHeight,
    // footerHeight,
    // onContentSizeChange,
    onAutoScroll,
    onScrollBeginDrag,
    onContainerListLayout,
    _aHeight,
    _minHeight,
    containerHeight,
  } = useAuToScroll({
    _ref,
    index,
    singleListIndex,
  });

  const headerStyle = useAnimatedStyle(() => {
    return {
      height: withDelay(100, withTiming(_headerHeight.value, { duration: 0 })),
    };
  }, []);

  /**
   * return animatedValue in onCollapse
   */
  useAnimatedReaction(
    () => _aHeight.value,
    (_height) => {
      onCollapse?.(interpolate(_height, [0, _minHeight.value], [0, 1]));
    },
    [onCollapse]
  );

  const containerStyle = useAnimatedStyle(() => {
    return {
      minHeight: containerHeight.value - _minHeight.value,
    };
  }, []);

  // const footerStyle = useAnimatedStyle(() => {
  //   return {
  //     height: footerHeight.value,
  //   };
  // }, []);

  /**
   * auto scroll other tab when dragging current tab
   */
  const onScroll = useAnimatedScrollHandler(
    {
      onScroll: (e) => {
        onAutoScroll(e);
        propsOnScroll?.(e);
      },
      onBeginDrag: (e) => {
        propsOnScrollBeginDrag?.(e);
        onScrollBeginDrag();
      },
      onMomentumBegin: (e) => {
        onMomentumScrollBegin?.(e);
      },
      onMomentumEnd: (e) => {
        onMomentumScrollEnd?.(e);
      },
      onEndDrag: (e) => {
        onScrollEndDrag?.(e);
      },
    },
    [
      propsOnScroll,
      onAutoScroll,
      propsOnScrollBeginDrag,
      onMomentumScrollEnd,
      onMomentumScrollBegin,
      onScrollEndDrag,
    ]
  );

  return (
    <>
      <StickyHeader
        index={index}
        loadingFirst={loading || changeParamsLoading}
        _headerHeight={_headerHeight}
        singleListIndex={singleListIndex}
      />
      <SContainer
        onLayout={onContainerListLayout}
        // onTouchMove={(e) => {
        //   pressable.value = false;
        // }}
        // onTouchEnd={(e) => {
        //   interactManager(() => (pressable.value = true), 100, true);
        // }}
        // style={{ opacity: loadingFirst ? 0 : 1 }}
      >
        <UIFlashList
          ref={setMultiRef(_ref, ref)}
          {...rest}
          loading={loading}
          ListHeaderComponent={
            <View>
              <Animated.View style={headerStyle} />
              <View>
                {ListHeaderComponent as JSX.Element}
                {/*{currentIndex && tabs?.length && setCurrentIndex ? (*/}
                {/*  <SingleTabBar*/}
                {/*    tabs={tabs}*/}
                {/*    currentIndex={currentIndex}*/}
                {/*    setCurrentIndex={setCurrentIndex}*/}
                {/*    stickyHeaderRender={null}*/}
                {/*  />*/}
                {/*) : null}*/}
              </View>
            </View>
          }
          ListEmptyComponent={
            <EmptyView
              _headerHeight={_headerHeight}
              _minHeight={_minHeight}
              noItemImageSource={noItemImageSource}
              ListEmptyComponent={ListEmptyComponent as any}
            />
          }
          // ListFooterComponent={<Animated.View style={footerStyle} />}
          // onContentSizeChange={onContentSizeChange}
          keyboardDismissMode={"on-drag"}
          onScroll={onScroll}
          animatedContentContainerStyle={containerStyle}
          renderScrollComponent={ReScrollView}
          progressViewOffset={disabledLoadData ? -1000 : _headerHeight.value}
          refreshable={true}
          loadMorable={loadMorable}
        />
      </SContainer>
    </>
  );
};

export const _List = memoForwardRef(__List) as typeof __List;

const EmptyView = memo(
  ({
    noItemImageSource,
    _headerHeight,
    _minHeight,
    ListEmptyComponent,
  }: {
    noItemImageSource?: any;
    _headerHeight: SharedValue<number>;
    _minHeight: SharedValue<number>;
    ListEmptyComponent?: JSX.Element;
  }) => {
    const { height: windowHeight } = useAnimatedWindow();

    const animatedStyle = useAnimatedStyle(() => {
      // console.log(_headerHeight.value)
      return {
        top:
          _minHeight.value -
          (windowHeight.value - _headerHeight.value) / 2 -
          20,
      };
    }, []);

    return (
      <Animated.View
        style={[
          animatedStyle,
          {
            height: 0,
            overflow: "visible",
          },
        ]}
      >
        <View style={{ height: 1000, alignItems: "center" }}>
          {ListEmptyComponent ? (
            ListEmptyComponent
          ) : (
            <>
              <Image source={noItemImageSource || IC_EMPTY_LIST} />
              <SText style={{ paddingTop: 16 }}>
                {translate("policy.no_more_doc")}
              </SText>
            </>
          )}
        </View>
      </Animated.View>
    );
  }
);

const SContainer = styled(Animated.View)`
  flex: 1;
`;
const SText = styled.Text`
  color: ${(p) => p.theme.neutral1};
`;
const SSeparateView = styled.View`
  background-color: ${(p) => p.theme.neutral6};
  height: 8px;
`;
