import React, { memo, useCallback } from "react";
import {
  IOnReplyPress,
  RawCommentItem,
  RawCommentItemProps,
} from "./UIComps/Item";
import {
  getCommentsByQuery,
  getPostsByQuery,
  requestLoadComment,
  useComments,
  usePosts,
} from "@true/core";
import { CommentProviderProps } from "./Provider";
import { useInvalid } from "./hooks/useInvalid";

interface Props extends Omit<RawCommentItemProps, "appName" | "type"> {
  isExpandable?: boolean;
  comment: string;
  appName?: string;
  type?: "comment" | "post";
}

export const CommentItem = memo((props: CommentProviderProps & Props) => {
  const { type, appName } = useInvalid(props);

  const comment =
    type === "comment"
      ? useComments((props.comment || "") as string)
      : usePosts((props.comment || "") as string);

  if (comment == undefined) return null;

  const onReplyPress = useCallback(
    ({ comment, onAddReplies, requestReply }: IOnReplyPress) => {
      if (comment) {
        if (props.isExpandable) {
          requestReply(
            { hid: comment.hid, token: comment.token },
            appName,
            type
          ).then(() => {
            const _comment =
              type === "comment"
                ? getCommentsByQuery(comment.hid)
                : getPostsByQuery(comment.hid);
            onAddReplies(_comment as string[]);
          });
        } else {
          console.log("tag reply");
        }
      }
    },
    [appName, type]
  );

  return (
    <RawCommentItem
      {...props}
      comment={comment}
      onReplyPress={onReplyPress}
      onToggle={props.onToggle}
      requestReply={requestLoadComment}
      type={type}
      appName={appName}
    />
  );
});
