import React, {
  ComponentType,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import {
  ActivityIndicator,
  Animated,
  Dimensions,
  Modal,
  ModalProps,
  StyleSheet,
  View,
  VirtualizedList,
} from "react-native";

import ImageItem from "./components/ImageItem/ImageItem";
import StatusBarManager from "./components/StatusBarManager";
import useImageIndexChange from "./hooks/useImageIndexChange";
import useRequestClose from "./hooks/useRequestClose";
import { ImageSource } from "./@types";
import { File, flatStyles, styled } from "@true/core";
import SimpleToast from "react-native-simple-toast";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
// import { useAnimatedStyle } from "react-native-reanimated";

const SWIPE_CLOSE_OFFSET = 65;

type Props = {
  images: ImageSource[];
  keyExtractor?: (imageSrc: ImageSource, index: number) => string;
  imageIndex: number;
  visible: boolean;
  onRequestClose: () => void;
  onLongPress?: (image: ImageSource) => void;
  onImageIndexChange?: (imageIndex: number) => void;
  presentationStyle?: ModalProps["presentationStyle"];
  animationType?: ModalProps["animationType"];
  backgroundColor?: string;
  swipeToCloseEnabled?: boolean;
  doubleTapToZoomEnabled?: boolean;
  delayLongPress?: number;
  HeaderComponent?: ComponentType<{ imageIndex: number }>;
  FooterComponent?: ComponentType<{ imageIndex: number }>;
  requestFn?: (image: string) => Promise<string>;
};

const DEFAULT_ANIMATION_TYPE = "fade";
const DEFAULT_BG_COLOR = "#000";
const DEFAULT_DELAY_LONG_PRESS = 800;
const SCREEN = Dimensions.get("screen");
const SCREEN_WIDTH = SCREEN.width;

const SHeader = styled(Animated.View)`
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  z-index: 1;
  padding: 16px 8px;
  background-color: #000;
`;

const SHeaderLeft = styled.View`
  flex-direction: row;
  align-items: center;
`;

const SCloseButton = styled.TouchableOpacity`
  height: 32px;
  width: 32px;
  align-items: center;
  justify-content: center;
`;

const SShareButton = styled(SCloseButton)`
  margin-left: 16px;
`;

const Icon = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: #fff;
`;

function ImageViewing({
  images,
  keyExtractor,
  imageIndex,
  visible,
  onRequestClose,
  onLongPress = () => {},
  onImageIndexChange,
  animationType = DEFAULT_ANIMATION_TYPE,
  backgroundColor = DEFAULT_BG_COLOR,
  presentationStyle,
  swipeToCloseEnabled,
  doubleTapToZoomEnabled,
  delayLongPress = DEFAULT_DELAY_LONG_PRESS,
  requestFn,
}: Props) {
  const imageList = React.createRef<VirtualizedList<ImageSource>>();
  const [, onRequestCloseEnhanced] = useRequestClose(onRequestClose);
  const [currentImageIndex, onScroll] = useImageIndexChange(imageIndex, SCREEN);
  const [loading, setLoading] = useState(false);
  const scrollValueY = new Animated.Value(0);
  useEffect(() => {
    if (onImageIndexChange) {
      onImageIndexChange(currentImageIndex);
    }
  }, [currentImageIndex]);

  const imageOpacity = scrollValueY.interpolate({
    inputRange: [-SWIPE_CLOSE_OFFSET, 0, SWIPE_CLOSE_OFFSET],
    outputRange: [0.8, 1, 0.8],
  });

  const imageOpacityHeader = scrollValueY.interpolate({
    inputRange: [-SWIPE_CLOSE_OFFSET / 2, 0, SWIPE_CLOSE_OFFSET / 2],
    outputRange: [0.3, 1, 0.3],
  });

  const onZoom = useCallback(
    (isScaled: boolean) => {
      // @ts-ignore
      imageList?.current?.setNativeProps({ scrollEnabled: !isScaled });
    },
    [imageList]
  );

  const actions = useMemo(() => {
    // @ts-ignore
    return {
      share: (url: string) =>
        File.share({
          url,
          cacheDownload: true,
        }),
      download: async (url: string) => {
        setLoading(true);
        await File.download({
          url,
        });
        setLoading(false);
        // SimpleToast.show("Photo downloaded successfully");
      },
    };
  }, [images, currentImageIndex]);

  const { aTop } = useAnimatedSafeAreaInsets();
  // const animatedStyles = useAnimatedStyle(() => ({
  //   paddingTop: aTop.value,
  // }));

  if (!visible) {
    return null;
  }

  return (
    <Modal
      transparent={true}
      visible={visible}
      presentationStyle={presentationStyle}
      animationType={animationType}
      onRequestClose={onRequestCloseEnhanced}
      supportedOrientations={["portrait"]}
      hardwareAccelerated
    >
      <StatusBarManager presentationStyle={presentationStyle} />
      <View style={flatStyles(styles.container)}>
        <Animated.View
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            zIndex: -1,
            opacity: imageOpacity,
            backgroundColor,
          }}
        />
        <VirtualizedList
          ref={imageList}
          data={images}
          horizontal
          pagingEnabled
          maxToRenderPerBatch={requestFn ? undefined : 1}
          showsHorizontalScrollIndicator={false}
          showsVerticalScrollIndicator={false}
          initialScrollIndex={imageIndex}
          getItem={(_: any, index: string | number) => images[index as number]}
          getItemCount={() => images.length}
          getItemLayout={(_: any, index: number) => ({
            length: SCREEN_WIDTH,
            offset: SCREEN_WIDTH * index,
            index,
          })}
          renderItem={({ item: imageSrc, index }: any) => (
            <ImageItem
              onZoom={onZoom}
              requestFn={requestFn}
              imageSrc={imageSrc}
              onRequestClose={onRequestCloseEnhanced}
              onLongPress={onLongPress}
              delayLongPress={delayLongPress}
              swipeToCloseEnabled={swipeToCloseEnabled}
              doubleTapToZoomEnabled={doubleTapToZoomEnabled}
              scrollValueY={scrollValueY}
              index={index}
              currentIndex={currentImageIndex}
              onDownload={actions.download}
              onShare={actions.share}
              onRequestCloseEnhanced={onRequestCloseEnhanced}
              imageOpacityHeader={imageOpacityHeader}
            />
          )}
          onMomentumScrollEnd={onScroll}
          keyExtractor={(imageSrc: ImageSource, index: number) =>
            keyExtractor
              ? keyExtractor(imageSrc, index)
              : // @ts-ignore
                imageSrc.uri || `${imageSrc}`
          }
        />
      </View>
    </Modal>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "transparent",
  },
  header: {
    position: "absolute",
    width: "100%",
    zIndex: 1,
    top: 0,
  },
  footer: {
    position: "absolute",
    width: "100%",
    zIndex: 1,
    bottom: 0,
  },
});

const EnhancedImageViewing = (props: Props) => (
  <ImageViewing key={props.imageIndex} {...props} />
);

export default EnhancedImageViewing;
