import React, { memo, useCallback, useEffect, useState } from "react";
import {
  KeyboardAvoidingView,
  Platform,
  ScrollView,
  StatusBar,
} from "react-native";
import Modal from "react-native-modal";
import { Colors, styled, translate, useLatest, useMounted } from "@true/core";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { TableHeadOptions } from "./types";
import { BaseDropList } from "./BaseDropList";
import { Loading } from "../Common/Loading";
import { MyTextInput } from "../Inputs";
import { DynamicHeader } from "../Header";
import { MyButton } from "../Button";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";

export interface Props {
  visible: boolean;
  rowData: any[];
  headOptions: TableHeadOptions[];
  onClose?: () => void;
  index?: number;
  onSave?: (data: any[], index?: number) => void;
}

export const TableRowCreator = memo((props: Props) => {
  const { onClose, visible, rowData, index, onSave, headOptions } = props;
  const [formData, setFormData] = useState(rowData);
  const interactionReady = useMounted();
  const latestFormData = useLatest(formData);

  const onUpdateDataItem = useCallback((data: any, index: number) => {
    const newData = [...latestFormData.current];
    newData[index] = data;
    setFormData(newData);
  }, []);

  useEffect(() => {
    setFormData(rowData);
  }, [rowData]);

  const onSavePress = useCallback(() => {
    const latestData = [...latestFormData.current];
    const normalizeData = headOptions.map((_, index) =>
      typeof latestData[index] === "undefined" ? "" : latestData[index]
    );
    onSave && onSave(normalizeData, index);
    setFormData([]);
  }, [index, onSave, headOptions]);

  const { aBottom } = useAnimatedSafeAreaInsets();
  const animatedStyles = useAnimatedStyle(() => ({
    marginBottom: aBottom.value + 16,
  }));

  return (
    <Modal
      isVisible={visible}
      onBackButtonPress={onClose}
      onBackdropPress={onClose}
      style={{
        margin: 0,
        top: Platform.OS == "android" ? -(StatusBar.currentHeight || 0) : 0,
        bottom: 0,
        position: "absolute",
        flex: 1,
        width: "100%",
        alignItems: "center",
        justifyContent: "center",
      }}
      animationIn="slideInUp"
      animationOut="slideOutDown"
      animationInTiming={500}
      animationOutTiming={500}
      backdropOpacity={0.0}
    >
      <SSafeView>
        <DynamicHeader
          title={
            typeof index !== "undefined"
              ? translate("comp.edit_row")
              : translate("comp.add_new_row")
          }
          onGoBack={onClose}
        />

        {interactionReady ? (
          <SScrollView keyboardShouldPersistTaps="always">
            {headOptions.map((option, index) => (
              <SInputContainer key={index}>
                <SViewInputContainer>
                  {option.options ? (
                    <BaseDropList
                      title={option.name}
                      options={option.options}
                      placeHolder={translate("common/select")}
                      borderStyle="none"
                      keyIdentifier={""}
                      content={formData[index] || ""}
                      displayItemData={(data: string) => data}
                      onSelectValue={(text) => {
                        onUpdateDataItem(text, index);
                      }}
                    />
                  ) : (
                    <MyTextInput
                      title={option.name}
                      key={index}
                      placeholder={"---"}
                      keyName={"name"}
                      value={formData[index] || ""}
                      onTextChange={(key, text) => {
                        onUpdateDataItem(text, index);
                      }}
                      containerStyle={{ flex: 1 }}
                    />
                  )}
                </SViewInputContainer>
              </SInputContainer>
            ))}
          </SScrollView>
        ) : (
          <Loading />
        )}
        <KeyboardAvoidingView
          behavior={Platform.OS == "ios" ? "padding" : "height"}
        >
          {interactionReady && (
            <STouchSave
              style={animatedStyles}
              backgroundColor={Colors.blue1}
              onPress={onSavePress}
              title={translate("comp.save")}
              textColor={Colors.white}
            />
          )}
        </KeyboardAvoidingView>
      </SSafeView>
    </Modal>
  );
});

const SSafeView = styled.View`
  width: 100%;
  height: 100%;
  background-color: ${($props) => $props.theme.neutral8};
`;

const SInputContainer = styled.View`
  width: 100%;
  padding-bottom: 8px;
`;

const SViewInputContainer = styled.View`
  flex-direction: row;
  align-items: center;
`;

const SScrollView = styled(ScrollView)`
  padding-top: 16px;
  padding-left: 16px;
  padding-right: 16px;
`;

const AnimatedMyButton = Animated.createAnimatedComponent(MyButton);

const STouchSave = styled(AnimatedMyButton)`
  margin-right: 16px;
  margin-left: 16px;
`;
