import React, { memo, useCallback, useState } from "react";
import { DynamicHeader, HeaderActionText } from "../../Header";
import { RichTextEditor } from "../../RichTextEditor";
import { ScreenWrapper } from "../../ScreenWrap";
import {
  Fetch,
  goBack,
  interactManager,
  KeyboardService,
  translate,
  useAsyncFn,
  useNavigationParams,
  useRefState,
} from "@true/core";

export interface RichTextEditorScreenProps {
  title: string;
  onSubmit: (value: string) => void | Promise<void>;
  value?: string;
  placeholder?: string;
  isUploadImageWhenPick?: boolean;
}

export const RichTextEditorScreen = memo(() => {
  const { title, onSubmit, value, placeholder, isUploadImageWhenPick } =
    useNavigationParams<RichTextEditorScreenProps>();
  const richText = React.useRef<{
    imgBase64: string;
  }>(null);
  const [update, forceUpdate] = useState(false);
  const [content, _setContent] = useRefState<string>(value || "");
  const setContent = useCallback((value: string) => {
    _setContent(value);
    forceUpdate((p) => !p);
  }, []);

  const [{ loading }, _onSubmit] = useAsyncFn(async () => {
    if (isUploadImageWhenPick) {
      await onSubmit(content);
    } else {
      await onSubmit(richText.current?.imgBase64 || content);
    }
    KeyboardService.dismiss();
    interactManager(() => goBack());
  }, [onSubmit, content, update, richText.current, isUploadImageWhenPick]);

  // const [
  //   {loading: uploadLoading, value: uploadValue, error: uploadErr},
  //   onInsertImage,
  // ] = useAsyncFn(async (uri: string, callback: (value: string) => void) => {
  //   // console.log('uri', uri);
  //   // let data = await uploadImageFile(uri);
  //   // callback(uri);
  // }, []);
  //
  // useAutoToastError(uploadErr);
  return (
    <ScreenWrapper>
      <DynamicHeader
        isAnimatedDuration={true}
        title={title}
        rightComponent={
          <HeaderActionText
            loading={loading}
            hideContentWhileLoading={true}
            onPress={_onSubmit}
            text={translate("Save")}
          />
        }
      />
      <RichTextEditor
        ref={richText}
        value={content}
        onValueChanged={setContent}
        isUploadImageWhenPick={isUploadImageWhenPick}
        toolbarItem={[
          "bold",
          "italic",
          "underline",
          "h1",
          "h2",
          "ul",
          "ol",
          "image",
        ]}
        placeholder={placeholder}
      />
    </ScreenWrapper>
  );
});

export const uploadImageFile = async (filePath: string) => {
  const params: Record<string, any> = {};

  params.file = {
    path: filePath,
    uri: filePath,
    filename: "file.jpg",
    name: "image.jpg",
    mime: "image/jpeg",
    width: 650,
    height: 638,
    size: "34983",
    type: "image/jpeg",
    creationDate: "1589422842",
    modificationDate: "1592211943",
    localIdentifier: "E626C78D-2B77-402F-AB1A-CCF6E6842BA8/L0/001",
  };

  const {
    data: { image },
  } = await Fetch.postWithToken<{
    image: string;
  }>("@api/ajax/api/file/upload", params);

  return image;
};

export * from "./index";
