import React, { memo, PropsWithChildren, useMemo } from "react";
import {
  DefaultTheme,
  defaultTheme,
  ThemeProvider,
  useLanguage,
  useThemeStore,
} from "@true/core";

interface Props extends PropsWithChildren {
  primaryColor: string;
}

export const MyThemeProvider = memo((props: Props) => {
  const { themeName } = useThemeStore();
  useLanguage();

  const mergedTheme = useMemo(
    () => ({
      ...defaultTheme[themeName],
      primaryColor: props.primaryColor,
      name: themeName,
    }),
    [themeName, props.primaryColor]
  );
  return (
    <ThemeProvider theme={mergedTheme as DefaultTheme}>
      <>{props.children}</>
    </ThemeProvider>
  );
});
