import { memoForwardRef, TakeCameraOptions } from "@true/core";
import React, { RefObject, useMemo } from "react";
import { BottomSheetModal, BottomSheetView } from "@gorhom/bottom-sheet";
import { MyBottomSheet, useAnimatedSnapPoints } from "../../MyBottomSheet";
import { UIAttachmentInputProps } from "./types";
import PickFileContent from "../../PickFileActionsSheet/PickFileContent";
import { useUIAttachment } from "./provider";
import { MAX_FILE } from "./index";

const takeCameraOptions: TakeCameraOptions = {
  mediaType: "photo",
  forceJpg: true,
};

export const PickFileBottomSheet = memoForwardRef(
  (props: UIAttachmentInputProps, ref: RefObject<BottomSheetModal>) => {
    const { multiple, maxFile = MAX_FILE } = props;

    const { animatedSnapPoints, onViewLayout } = useAnimatedSnapPoints();

    const pickImageOptions = useMemo(() => {
      return {
        compressImageQuality: 1,
        multiple: true,
        maxFiles: maxFile,
        changeFileNameExtensionFromHEICToJpg: true,
      };
    }, [maxFile]);

    const { files, onAddFile } = useUIAttachment();

    return (
      <MyBottomSheet ref={ref} snapPoints={animatedSnapPoints}>
        <BottomSheetView onLayout={onViewLayout("ok")}>
          <PickFileContent
            onFilePicked={onAddFile}
            pickFileOptions={{ multiple }}
            pickImageOptions={
              multiple
                ? {
                    ...pickImageOptions,
                    maxFiles:
                      maxFile - ((files.value && files.value.length) || 0),
                  }
                : { multiple: false }
            }
            takeCameraOptions={takeCameraOptions}
            includeTakeCamera={true}
          />
        </BottomSheetView>
      </MyBottomSheet>
    );
  }
);
