import { BottomSheetView } from "@gorhom/bottom-sheet";
import { BottomSheetFlashList } from "../MyBottomSheet";
import { SearchBar, SearchBarRef } from "../SearchBar";
import React, { memo, useCallback, useEffect, useRef, useState } from "react";
import {
  getGiphy,
  getGiphyByQuery,
  requestQueryGiphy,
  requestTrendingGiphy,
  styled,
  useAnimatedWindow,
  useAsyncFn,
  useAutoToastError,
} from "@true/core";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { ActivityIndicator, TouchableOpacity, View } from "react-native";
import FastImage from "react-native-fast-image";
import { SimpleTabBar } from "../SimpleTabBar";

const TYPE: ("gif" | "sticker")[] = ["gif", "sticker"];

interface Props {
  onSearchFocus?: () => void;
}

export const Giphy = memo(({onSearchFocus}: Props) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  const [mounted, setMounted] = useState(0);

  const [{ error, loading }, firstLoad] = useAsyncFn(requestTrendingGiphy);
  const [{ error: tabError, loading: tabLoading }, tabLoad] =
    useAsyncFn(requestTrendingGiphy);
  const [{ error: queryError, loading: queryLoading }, queryLoad] =
    useAsyncFn(requestQueryGiphy);

  // const giphyList = useGiphyByQuery('trending');

  const [list, setList] = useState<(string | number)[]>([]);

  // const [text, setText] = useState('');
  const searchRef = useRef<SearchBarRef | null>(null);

  useEffect(() => {
    firstLoad({ type: "gif" }).then(() => {
      setList(getGiphyByQuery("trending/gif"));
      setMounted((prev) => prev + 1);
    });
  }, []);

  const onSearchChange = useCallback(
    (text: string) => {
      // setText(text);
      if (text) {
        queryLoad({ search: text, type: TYPE[currentIndex] }).then(() => {
          setList(getGiphyByQuery("search/" + TYPE[currentIndex]));
        });
      } else {
        setList(getGiphyByQuery("trending/" + TYPE[currentIndex]));
      }
    },
    [currentIndex]
  );

  const onTabChange = useCallback(
    (index: number) => {
      searchRef.current?.clearText();
      setCurrentIndex(index);

      if (mounted < 2) {
        return tabLoad({ type: TYPE[index] }).then(() => {
          setMounted((prev) => prev + 1);
          setList(getGiphyByQuery("trending/" + TYPE[index]));
        });
      } else {
        setList(getGiphyByQuery("trending/" + TYPE[index]));
      }
    },
    [mounted, searchRef]
  );

  const renderItem = useCallback(({ item }: { item: string | number }) => {
    return <Item item={item as string} />;
  }, []);

  useAutoToastError(error);
  useAutoToastError(queryError);
  useAutoToastError(tabError);

  return (
    <>
      <BottomSheetView>
        <SSearchBar
          ref={searchRef}
          onSearchTextChange={onSearchChange}
          isOnModal={true}
          onFocus={onSearchFocus}
        />
      </BottomSheetView>
      <BottomSheetView>
        <SimpleTabBar tabs={["gif", "sticker"]} onChangeTab={onTabChange} />
      </BottomSheetView>

      {loading ? <ActivityIndicator /> : null}
      {queryLoading || tabLoading ? (
        <SView>
          <ActivityIndicator />
        </SView>
      ) : null}
      <View style={{ flex: 1, marginLeft: 2 }}>
        <BottomSheetFlashList
          style={{ paddingLeft: 2 }}
          data={list as any}
          renderItem={renderItem}
          estimatedItemSize={100}
          numColumns={3}
          keyboardDismissMode={"on-drag"}
        />
      </View>
    </>
  );
});

const Item = memo(({ item }: { item: string }) => {
  const { width: windowWidth } = useAnimatedWindow();

  const animatedStyle = useAnimatedStyle(() => {
    const scale = (windowWidth.value - 2) / 3 - 2;
    return {
      width: scale,
      height: scale,
    };
  }, []);

  return (
    <SItemContainer style={animatedStyle}>
      <FastImage
        style={{ height: "100%", width: "100%" }}
        source={{ uri: getGiphy?.(item)?.images.fixed_width_small.url }}
      />
    </SItemContainer>
  );
});

const AnimatedBtn = Animated.createAnimatedComponent(TouchableOpacity);

const SItemContainer = styled(AnimatedBtn)`
  background-color: ${(p) => p.theme.neutral7};
  margin-top: 2px;
`;

const SView = styled.View`
  position: absolute;
  width: 100%;
  top: 100px;
  z-index: 10;
`;

const SSearchBar = styled(SearchBar)`
  margin: 0 16px 8px 16px;
`
