import {nanoid} from 'nanoid/non-secure';
import {
  ParamListBase,
  Router,
  StackRouter,
  StackActionType,
  StackActions,
  StackRouterOptions,
} from '@react-navigation/native';
import type {HoldMenuNavigationState} from './type';

export type HoldMenuRouterOptions = StackRouterOptions;

export type HoldMenuActionType = StackActionType;

export const HoldMenuActions = {
  ...StackActions,
  // snapTo(index: number): BottomSheetActionType {
  //   return {type: 'SNAP_TO', index};
  // },
};

export function HoldMenuRouter(
  routerOptions: StackRouterOptions,
): Router<HoldMenuNavigationState<ParamListBase>, HoldMenuActionType> {
  const baseRouter = StackRouter(routerOptions) as unknown as Router<
    HoldMenuNavigationState<ParamListBase>,
    HoldMenuActionType
  >;
  return {
    ...baseRouter,
    type: 'hold-menu',
    getInitialState(options) {
      const state = baseRouter.getInitialState(options);

      return {
        ...state,
        stale: false,
        type: 'hold-menu',
        key: `hold-menu-${nanoid()}`,
      };
    },
    getStateForAction(state, action, options) {
      return baseRouter.getStateForAction(state, action, options);
    },
    getRehydratedState(
      partialState,
      {routeNames, routeParamList, routeGetIdList},
    ) {
      if (partialState.stale === false) {
        return partialState;
      }

      const state = baseRouter.getRehydratedState(partialState, {
        routeNames,
        routeParamList,
        routeGetIdList,
      });

      return {
        ...state,
        type: 'hold-menu',
        key: `hold-menu-${nanoid()}`,
      };
    },
    actionCreators: HoldMenuActions,
  };
}
