import { useCallback, useEffect, useMemo, useState } from "react";
import {
  runOnJS,
  SharedValue,
  useAnimatedReaction,
  useSharedValue,
} from "react-native-reanimated";
import { interactManager, useAsyncFn, useAutoToastError } from "@true/core";
import { ISingleListProps } from "../SingleTabView/SingleList";
import { useAnimatedScrollableView } from "../index";

interface Props extends ISingleListProps {
  index: number;
  currentIndex?: SharedValue<number>;
}

export const useRequest = ({
  index,
  query,
  tab,
  useItemByQuery,
  disabledLoadData,
  requestFn,
  params,
  mainKey = "tab",
  getItemByQuery,
  onDataChange,
  currentIndex,
  singleListIndex,
  loadMoreParams,
}: Props) => {
  /**
   * Validate undefined props
   */
  if (!requestFn) throw new Error("ScrollableTabList: requestFn is null");
  if (!useItemByQuery)
    throw new Error("ScrollableTabList: useItemByQuery is null");
  if (!getItemByQuery)
    throw new Error("ScrollableTabList: getItemByQuery is null");

  /**
   * get query to set in redux
   */
  const _query = useMemo(
    () => (query || "") + (query && tab ? "/" : "") + (tab || "") || "all",
    [query, tab]
  );

  /**
   * useAnimatedScrollableView
   */
  const { staticIndex } = useAnimatedScrollableView();

  const [mounted, setMounted] = useState(false);

  const [itemList, setItems] = useState<(number | string)[]>([]);

  const isFirstLoad = useSharedValue(true);
  const isAction = useSharedValue(false);
  const [changeParamsLoading, setChangeParamsLoading] = useState(false);

  const isLoadMorable = useSharedValue(true);
  const page = useSharedValue(1);

  if (useItemByQuery) {
    const _itemList = useItemByQuery(_query);
    useEffect(() => {
      if (
        !isAction.value &&
        !isFirstLoad.value &&
        JSON.stringify(_itemList) !== JSON.stringify(itemList)
      ) {
        // console.log("set");
        setItems(_itemList);
        onDataChange?.(_itemList);
      }
    }, [_itemList]);
  }

  /**
   * request function
   */
  const [{ loading: loadingFirst, error: firstError }, onFirstLoad] =
    useAsyncFn(disabledLoadData ? async () => {} : requestFn, [
      disabledLoadData,
    ]);
  const [{ loading: changeTabLoading, error: changeTabError }, onChangeTab] =
    useAsyncFn(disabledLoadData ? async () => {} : requestFn, [
      disabledLoadData,
    ]);
  const [{ loading: refreshing, error: refreshError }, onRefresh] = useAsyncFn(
    disabledLoadData ? async () => {} : requestFn,
    [disabledLoadData]
  );
  const [{ loading: loadingMore, error: loadMoreError }, onLoadMore] =
    useAsyncFn(disabledLoadData ? async () => {} : requestFn, [
      disabledLoadData,
    ]);

  useAutoToastError(firstError);
  useAutoToastError(changeTabError);
  useAutoToastError(refreshError);
  useAutoToastError(loadMoreError);

  /**
   * get params before submit
   */
  const submitParams = useMemo(() => {
    let result: any = {};

    if (params) {
      for (let key in params) {
        if (key !== mainKey && params[key]) result[key] = params[key];
      }
    }

    if (tab) result[mainKey] = tab;

    return result;
  }, [params, mainKey, tab]);

  /**
   * get prev params to determinded when params change
   */
  // const [prevParams, setPrevParams] = useState(submitParams);
  const prevParams = useSharedValue(submitParams);

  /**
   * First load
   */
  const _firstLoad = useCallback(() => {
    interactManager(() => {
      onFirstLoad(submitParams, _query).then(() => {
        let _items = getItemByQuery(_query);
        setItems(_items);
        onDataChange?.(_items);
        isFirstLoad.value = false;
        isAction.value = false;
        setMounted(true);
      });
    }, 300);
  }, [submitParams, _query]);

  useAnimatedReaction(
    () => ({
      _staticIndex: staticIndex.value,
      _currentIndex: currentIndex?.value,
    }),
    ({ _staticIndex, _currentIndex }) => {
      if (index == _staticIndex && isFirstLoad.value) {
        if (
          _currentIndex === undefined ||
          singleListIndex === undefined ||
          _currentIndex === singleListIndex
        ) {
          // console.log("first Load");
          isAction.value = true;
          runOnJS(_firstLoad)();
        }
      }
    },
    [index, singleListIndex, submitParams, _query]
  );

  /**
   * Change Tab Load
   */
  const _refresh = useCallback(
    (timeout: number, isClear?: boolean) => {
      isAction.value = true;
      if (!isClear) setChangeParamsLoading(true);
      interactManager(
        () => {
          onChangeTab(submitParams, _query).then(() => {
            let _items = getItemByQuery(_query);
            setItems(_items);
            onDataChange?.(_items);
            isAction.value = false;
            setChangeParamsLoading(false);
          });
        },
        timeout,
        isClear
      );
    },
    [tab, submitParams, onDataChange, _query]
  );

  useAnimatedReaction(
    () => ({
      _index: staticIndex.value,
      _prevParams: prevParams.value,
      _currentIndex: currentIndex?.value,
    }),
    ({ _index, _prevParams, _currentIndex }, prev) => {
      // only fetch after first load
      if (!isFirstLoad.value) {
        // fetch when on current tab
        if (
          _index == index &&
          (_currentIndex === undefined || _currentIndex == singleListIndex)
        ) {
          // fetch when change params
          if (JSON.stringify(submitParams) !== JSON.stringify(_prevParams)) {
            // console.log("change params");
            page.value = 1;
            isLoadMorable.value = true;
            prevParams.value = submitParams;
            runOnJS(_refresh)(300);
          }
          // fetch when change tab
          else if (
            _index !== prev?._index ||
            _currentIndex !== prev?._currentIndex
          ) {
            // console.log("change tab", submitParams, _query);
            runOnJS(_refresh)(2000, true);
          }
        }
      }
    },
    [submitParams, singleListIndex]
  );

  /**
   * onRefresh
   */
  const onLoadData = useCallback(
    (isRefreshing: boolean, isLoadingMore: boolean) => {
      if (isRefreshing) {
        isAction.value = true;
        onRefresh(submitParams, _query).then(() => {
          let _items = getItemByQuery(_query);
          page.value = 1;
          isLoadMorable.value = true;
          setItems(_items);
          onDataChange?.(_items);
          isAction.value = false;
        });
        return;
      }

      if (isLoadingMore && isLoadMorable.value) {
        isAction.value = true;
        page.value++;

        onLoadMore({
          ...submitParams,
          ...(loadMoreParams?.(getItemByQuery(_query), page.value + 1) || {
            page: page.value + 1,
          }),
        }, _query).then(() => {
          let _items = getItemByQuery(_query);
          isLoadMorable.value = _items.length > itemList.length;
          setItems(_items);
          onDataChange?.(_items);
          isAction.value = false;
        });
        return;
      }
    },
    [
      submitParams,
      onRefresh,
      onDataChange,
      onLoadMore,
      _query,
      loadMoreParams,
      itemList.length,
      isLoadMorable,
      page,
    ]
  );

  return useMemo(
    () => ({
      loadingFirst,
      refreshing,
      changeTabLoading,
      loadingMore,
      onLoadData,
      itemList,
      mounted,
      changeParamsLoading,
    }),
    [
      loadingFirst,
      refreshing,
      changeTabLoading,
      loadingMore,
      onLoadData,
      itemList,
      mounted,
      changeParamsLoading,
    ]
  );
};
