import { useCallback, useState } from "react";
import { useNavigationParams, useSet } from "@true/core";
import { useNavigation } from "@react-navigation/native";
import { UserPickerModalProps } from "./types";
import constate from "constate";
import { useSelectedUserIds } from "./hook";

const useUserPickerModalValue = () => {
  const { value, onSubmit, multiple, onGoBack, onSave } =
    useNavigationParams<UserPickerModalProps>();
  const { selectedUserIds, useUsers, returnType, showError } =
    useSelectedUserIds(value);
  const navigation = useNavigation();

  const searchTextState = useState("");
  const [selectedIds, { toggle, has, remove }] = useSet<string>(
    new Set(selectedUserIds as any[])
  );

  const onPressSubmit = useCallback(() => {
    const usernameString = [...selectedIds]
      .map((userId) => {
        const username = useUsers?.[userId]?.username;
        return username ? `@${username}` : undefined;
      })
      .filter(Boolean)
      .join(" ");

    const usernameArray = [...selectedIds]
      .map((userId) => {
        const username = useUsers?.[userId]?.username;
        return username || undefined;
      })
      .filter(Boolean);

    const userIdArray = [...selectedIds];

    const param =
      returnType === "usernameString"
        ? usernameString
        : returnType === "usernameArray"
        ? usernameArray
        : returnType === "userIdArray"
        ? userIdArray
        : undefined;

    onSubmit?.(param);
    onSave?.(param);
    onGoBack ? onGoBack() : navigation.goBack(); // close modal
    if (returnType === "undefined") showError();
  }, [onSubmit, selectedIds, onGoBack, returnType, onSave]);

  const onUserPress = useCallback(
    (id: string) => {
      // searchTextState[1]('');
      if (multiple) return toggle(id);
      const usernameString = useUsers?.[id]
        ? "@" + useUsers?.[id]?.username
        : "";
      const usernameArray = useUsers?.[id] ? [useUsers?.[id]?.username] : [];
      const userIdArray = useUsers?.[id] ? [id] : [];

      const param =
        returnType === "usernameString"
          ? usernameString
          : returnType === "usernameArray"
          ? usernameArray
          : returnType === "userIdArray"
          ? userIdArray
          : id;

      onSubmit?.(param);
      onSave?.(param);
      onGoBack ? onGoBack() : navigation.goBack(); // close modal;
      // if (returnType === "undefined") showError();
    },
    [
      toggle,
      multiple,
      searchTextState[1],
      onGoBack,
      useUsers,
      returnType,
      onSave,
    ]
  );

  const usersTray = {
    selectedIds,
    remove,
    has,
  };

  return {
    onPressSubmit,
    onUserPress,
    isUserSelected: has, //check if user is selected
    searchTextState,
    usersTray,
  };
};

const [
  UserPickerModalProvider,
  useOnPressSubmit,
  useOnUserPress,
  useIsUserSelected,
  useSearchTextState,
  useUsersTray,
] = constate(
  useUserPickerModalValue,
  (value) => value.onPressSubmit,
  (value) => value.onUserPress,
  (value) => value.isUserSelected,
  (value) => value.searchTextState,
  (value) => value.usersTray
);

const UserPickerModalContext = {
  useOnPressSubmit,
  useOnUserPress,
  useIsUserSelected,
  useSearchTextState,
  useUsersTray,
};

export { UserPickerModalProvider, UserPickerModalContext };
