import {
  getMe,
  getUser,
  interactManager,
  matchFilter,
  memoForwardRef,
  styled,
  TPrevFn,
  useUserByQuery,
} from "@true/core";
import Animated, {
  measure,
  runOnJS,
  SharedValue,
  useAnimatedRef,
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from "react-native-reanimated";
import React, {
  memo,
  useCallback,
  useImperativeHandle,
  useRef,
  useState,
} from "react";
import { FlashList } from "@shopify/flash-list";
import { UserAvatar } from "../Avatar";

export interface TagUserView {
  onSelectionChange: (event: any) => void;
}
interface Props {
  content: SharedValue<string>;
  setTaggedUsers: (value: TPrevFn<string[]>) => void;
  taggedUsers: string[];
  userList?: string[];
  topInset?: number | SharedValue<number>;
}

const getRawValue = (value?: number | SharedValue<number>) => {
  "worklet";
  if (value === undefined || value === null) return 600;
  if (typeof value === "number") return value;
  return value.value;
};

export const TagUserView = memoForwardRef<Props, TagUserView>(
  function TagUserView(props, ref) {
    const { content, setTaggedUsers, taggedUsers, userList, topInset } = props;

    const contentHeight = useSharedValue(0);
    const selection = useSharedValue({ end: 0, start: 0 });

    const [startIndex, setStartIndex] = useState(-1);

    const timeout = useRef<any>(null);
    const timeout2 = useRef<any>(null);
    const timeout3 = useRef<any>(null);

    const [data, setData] = useState<any[]>([]);

    const bottomViewRef = useAnimatedRef<Animated.View>();

    const users = useUserByQuery("allIds");

    /**
     * set user baseed on @tag text
     * if username is tagged, modal will not be shown
     */
    const setUser = useCallback(
      (selectedText: string, isSet: boolean) => {
        interactManager(
          () => {
            if (selectedText.indexOf("@") !== -1 && isSet) {
              const _user = (userList?.length ? userList : users)
                .map((id) => getUser(id))
                .filter((item) => {
                  return (
                    item?.id != getMe()?.id &&
                    (item
                      ? matchFilter(
                          selectedText.substring(1),
                          item,
                          ["username"],
                          []
                        ) === "isMatch"
                      : false)
                  );
                });
              return setData(_user.map((item) => item?.id));
            }

            return setData([]);
          },
          300,
          true,
          timeout2
        );
      },
      [timeout2, users, userList]
    );

    const _setStartIndex = useCallback(
      (i: number) => {
        interactManager(
          () => {
            setStartIndex(i);
          },
          300,
          true,
          timeout3
        );
      },
      [timeout3]
    );

    const aniamtedSTyle = useAnimatedStyle(() => {
      /**
       * get tagged text from @ to selection.value.end
       */
      let selectedText = "";
      let taggedIndex = 0;

      for (let i = 0; i < selection.value.end; i++) {
        const char = content.value?.[i];

        selectedText += char;
        if (char === " ") {
          selectedText = "";
        }

        if (char === "\n") {
          selectedText = "";
        }

        if (char === "@") {
          selectedText = "@";
          taggedIndex++;
          runOnJS(_setStartIndex)(i + 1);
        }
      }

      /**
       * get boolean value if username is tagged
       */
      let isSetUser = true;

      if (taggedUsers)
        for (let taggedUser of taggedUsers) {
          if (
            content.value.split("@")[taggedIndex].indexOf(taggedUser) !== -1
          ) {
            isSetUser = isSetUser && false;
            break;
          }
        }

      runOnJS(setUser)(selectedText, isSetUser);

      const bottomInset = measure(bottomViewRef)?.pageY || 0;

      return {
        height:
          selectedText.indexOf("@") !== -1
            ? withTiming(
                Math.min(
                  bottomInset - getRawValue(topInset),
                  contentHeight.value
                ),
                {
                  duration: 100,
                }
              )
            : withTiming(0, { duration: 100 }),
      };
    }, [taggedUsers, setUser]);

    const onItemPres = useCallback(
      (item: string) => {
        const username = getUser(item)?.username;
        if (!username) return;

        setTaggedUsers((prev) => [...new Set([...prev, username])]);

        content.value = content.value.replaceBetween(
          startIndex,
          selection.value.end,
          username + " "
        );
      },
      [startIndex, selection, setTaggedUsers]
    );

    const onSelectionChange = useCallback((e: any) => {
      selection.value = e.nativeEvent.selection;
    }, []);

    useImperativeHandle(
      ref,
      () => ({
        onSelectionChange: onSelectionChange,
      }),
      [onSelectionChange]
    );

    return (
      <SContainer style={aniamtedSTyle}>
        <FlashList
          data={[...data, "lastItem"]}
          estimatedItemSize={50}
          keyboardShouldPersistTaps={"handled"}
          onContentSizeChange={(w, h) => {
            interactManager(
              () => {
                contentHeight.value = h;
              },
              50,
              true,
              timeout
            );
          }}
          renderItem={({ item }) => <Item item={item} onPress={onItemPres} />}
        />
        <Animated.View ref={bottomViewRef} style={{ height: 0 }} />
      </SContainer>
    );
  }
);

const Item = memo(
  ({ item, onPress }: { item: string; onPress: (item: string) => void }) => {
    const user = getUser(item);

    const _onPress = useCallback(() => {
      onPress(item);
    }, [item, onPress]);

    return item === "lastItem" ? null : (
      <STouchableOpacity onPress={_onPress}>
        <UserAvatar userKey={item} size={40} />
        <SItemView>
          <SText>{user?.name}</SText>
          <SSubText>@{user?.username}</SSubText>
        </SItemView>
      </STouchableOpacity>
    );
  }
);

const SContainer = styled(Animated.View)``;
const STouchableOpacity = styled.TouchableOpacity`
  padding: 8px 12px;
  flex-direction: row;
  align-items: center;
`;
const SText = styled.Text`
  color: ${(p) => p.theme.neutral1};
  line-height: 22px;
  font-size: 16px;
`;
const SSubText = styled.Text`
  color: ${(p) => p.theme.neutral2};
  line-height: 20px;
  font-size: 12px;
`;
const SItemView = styled.View`
  margin-left: 12px;
`;
