import React, { memo, useCallback } from "react";
import Animated, {
  SharedValue,
  useAnimatedStyle,
} from "react-native-reanimated";
import { styled, translate } from "@true/core";
import { ActionProps } from "./FloatingModal";
import { Touchable } from "./Touchable";

export const IOSFloatingModal = memo(
  ({
    onClose,
    actions,
    _onPress,
    value,
  }: {
    onClose: () => void;
    actions?: ActionProps[];
    _onPress: (name: string) => void;
    value: SharedValue<number>;
  }) => {
    const onPress = useCallback(
      (name: string) => {
        _onPress && _onPress(name);
      },
      [_onPress]
    );

    const _onClose = useCallback(() => {
      onClose && onClose();
    }, [onClose]);

    const animatedStyles = useAnimatedStyle(() => ({
      opacity: value.value,
      transform: [{ scale: value.value }],
    }));

    return (
      <Animated.View style={animatedStyles}>
        <STouchableWithoutFeedback onPress={_onClose}>
          <SDropBack />
        </STouchableWithoutFeedback>

        <SWrapOptions>
          <SAnchor />
          {actions?.map((item) => {
            return (
              <STouchable onPress={() => onPress(item?.name)}>
                <SIcon>{item.icon}</SIcon>
                <SText>{translate(item?.text || "")}</SText>
              </STouchable>
            );
          })}
        </SWrapOptions>
      </Animated.View>
    );
  }
);

const SWrapOptions = styled.View`
  background-color: ${(p) => p.theme.backgroundColor};
  padding: 4px;
  border-radius: 8px;
  bottom: 70px;
  right: 0;
`;

export const STouchable = styled(Touchable)`
  flex-direction: row;
  align-items: center;
  padding: 12px;
`;

export const SText = styled.Text`
  font-size: 15px;
`;

export const SIcon = styled.View`
  margin-right: 8px;
`;

export const SAnchor = styled.View`
  position: absolute;
  background-color: ${(p) => p.theme.backgroundColor};
  height: 30px;
  width: 30px;
  transform: rotate(45deg) scaleY(0.7);
  bottom: -6px;
  right: 15px;
`;

const STouchableWithoutFeedback = styled.TouchableWithoutFeedback`
  position: absolute;
`;

const SDropBack = styled.View`
  position: absolute;
  background-color: black;
  opacity: 0.4;
  top: -100000px;
  left: -100000px;
  right: -100000px;
  bottom: -100000px;
`;
