import axios from 'axios';
import { EmojiGroup } from "../types";

const APIKEY = 'Zl5GZLHbEnJPt0nBy01QGYWFRkrcD1K7';

interface Params {
  q: string; // a
  offset: number; // 0
  limit?: number;
}
const URL_GIPHY = 'https://api.giphy.com/v1/gifs/search';

export const requestGetGif = async (params: Params) => {
  try {
    // console.log({api_key: APIKEY, limit: 32, ...params, rating: 'g'});
    const res = await axios.get(URL_GIPHY, {
      params: {api_key: APIKEY, limit: 32, ...params, rating: 'g'},
    });
    return res?.data?.data?.map((item: any) => {
      return {
        ...item.images.original,
        preview_gif: item?.images?.preview_gif?.url
      }
    });
  } catch (e) {
    return [];
  }
};

const baseGifKeys = [
  "tuzki",
  "puppy",
  "kitty",
  "cute",
  "cotton",
  "nerd",
  "owl",
  "bigeye",
  "alien",
]


const baseGifRange: {[id: string]: number} = {
  tuzki: 15,
  puppy: 16,
  kitty: 16,
  cute: 24,
  cotton: 24,
  nerd: 15,
  owl: 24,
  bigeye: 16,
  alien: 43,
}

export const getBaseGifs = () => {
  const url = 'https://share-true.true-cdn.com/stickers/'
  const gifs: {[id: string]: EmojiGroup} = {}
  for (let key of baseGifKeys) {
    gifs[key] = {
      end: baseGifRange[key],
      name: key,
      start: 0,
      type: key,
      item: [...Array(baseGifRange[key]).keys()].map(index => ({url: url + key + '/' +(index+1)+'.gif'})),
      file: ""
    }
  }
  return gifs
}
