import { useCallback, useMemo, useState } from "react";
import {
  AppInfo,
  createClient,
  EAuthStage,
  KeyboardService,
  manualLogin,
  useAsyncFn,
  useAuthStore,
  useAutoToastError,
  wait,
} from "@true/core";
import { RecaptchaV2, RecaptchaV3 } from "../Recaptcha";

export const useManualLogin = (
  param: { email: string; password: string; code_2fa?: string },
  onGoBack?: () => void
) => {
  const [v3, setV3] = useState("");
  const [isVerifiedCaptcha, setIsVerifiedCaptcha] = useState(false);
  const [failSafeV3, setFailSafeV3] = useState(0);
  const [twoFA, setTwoFA] = useState(false);

  const { setAuthStage, authStage } = useAuthStore();

  const onAuthError = useCallback(
    async (e: any) => {
      console.log("createClient", e.message);

      if (e.message === "ERROR_RECAPTCHA_V3_FAILED") {
        setFailSafeV3(1);
        await wait(100);
        await onGetRecaptcha();
      }

      if (e.message === "ERROR_2FA_EMPTY") {
        setTwoFA(true);
      }

      setAuthStage(EAuthStage.ERROR);
      setIsVerifiedCaptcha(false);
    },
    [param, v3]
  );

  const [{ error, loading }, onCreateClient] = useAsyncFn(async () => {
    KeyboardService.dismiss();
    try {
      const _v3 = await RecaptchaV3.requestToken();
      if (_v3) {
        setV3(_v3);
        setAuthStage(EAuthStage.CHECKING);
        await createClient({
          email: param.email,
          password: param.password,
          "catpcha-version": "v3",
          recaptcha_response: _v3,
          code_2fa: param.code_2fa,
        });
        onGoBack?.();
        setAuthStage(EAuthStage.SIGNED);
        await AppInfo.preSyncData();
      }
    } catch (e) {
      onAuthError(e).then();
      throw e;
    }
  }, [param]);

  const [{ error: recaptchaError }, onGetRecaptcha] = useAsyncFn(async () => {
    const _v2 = await RecaptchaV2.requestToken();
    if (_v2) {
      setIsVerifiedCaptcha(true);
      await onManualLogin(_v2);
    }
  }, [v3, param]);

  const [{ error: loginError, loading: loginLoading }, onManualLogin] =
    useAsyncFn(
      async (_v2: string) => {
        setAuthStage(EAuthStage.CHECKING);
        try {
          await manualLogin({
            email: param.email,
            password: param.password,
            "catpcha-version": "v3",
            recaptcha_response: v3,
            "g-recaptcha-response": _v2,
            "v3-failsafe": 1,
          });
          onGoBack?.();
          setAuthStage(EAuthStage.SIGNED);
          await AppInfo.preSyncData();
        } catch (e) {
          onAuthError(e).then();
          throw e;
        }
      },
      [v3, param]
    );

  useAutoToastError(error);
  useAutoToastError(loginError);

  return useMemo(
    () => ({
      isVerifiedCaptcha,
      onCreateClient,
      onGetRecaptcha,
      failSafeV3,
      loading: loading || loginLoading,
      twoFA,
    }),
    [
      isVerifiedCaptcha,
      onCreateClient,
      onGetRecaptcha,
      failSafeV3,
      loading,
      loginLoading,
      twoFA,
    ]
  );
};
