import React, { memo } from "react";
import { ScrollView, View } from "react-native";
import { UsersRow } from "./UsersRow";
import { styled } from "@true/core";
import Animated, {
  SharedValue,
  useAnimatedStyle,
  withTiming,
} from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";

export const BottomUserRow = memo(
  ({
    ids,
    type,
    onClearPress,
    onItemPress,
  }: {
    ids: SharedValue<string[]>;
    type: "user" | "group";
    onClearPress?: (id: string) => void;
    onItemPress?: (id: string) => void;
  }) => {
    const { aBottom } = useAnimatedSafeAreaInsets();

    const animatedStyle = useAnimatedStyle(
      () => ({
        height: ids.value.length
          ? withTiming(aBottom.value + 63)
          : withTiming(0),
      }),
      []
    );

    return (
      <SContainer style={animatedStyle}>
        <SDivider />
        <ScrollView
          style={{ height: 43, marginVertical: 8 }}
          horizontal={true}
          keyboardShouldPersistTaps={"handled"}
        >
          <View style={{ width: 16 }} />
          <UsersRow
            hasClearBtn={true}
            ids={ids}
            size={40}
            type={type}
            onClearPress={onClearPress}
            onItemPress={onItemPress}
          />
        </ScrollView>
      </SContainer>
    );
  }
);

const SContainer = styled(Animated.View)``;
const SDivider = styled.View`
  height: 1px;
  background-color: ${(p) => p.theme.neutral7};
`;
