import React, { memo, useCallback, useMemo } from "react";
import { useSingleTabView } from "./provider";
import { useRequest } from "../hooks/useRequest";
import { _List } from "../_List";
import { AnimatedScrollableTabCommonProps } from "../index";
import { NativeScrollEvent } from "react-native";
import { IFlashItem, IFlashSection } from "../../Common";
import Animated from "react-native-reanimated";

export interface ISingleListProps extends AnimatedScrollableTabCommonProps {
  singleListIndex?: number;
  tab?: string;
  map?: Record<string, (string | number)[]>;
  onDataChange?: (val: (number | string)[]) => void;
  mapKey?: string;
  ref?: any;
  onScroll?: (event: NativeScrollEvent) => void;
  setData?: (prevData: (string | number)[]) => (string | number)[];
  loadMorable?: boolean;
}

export const SingleList = memo(
  (props: { singleListIndex: number } & ISingleListProps) => {
    const {
      index,
      tabs,
      currentIndex,
      renderItem: _renderItem,
      params: _params,
      requestFn: _requestFn,
      query: _query,
      getItemByQuery: _getItemByQuery,
      useItemByQuery: _useItemByQuery,
      noItemImageSource: _noItemImageSource,
      disabledLoadData: _disabledLoadData,
      itemHeight: _itemHeight,
      numColumns: _numColumns,
      ListHeaderComponent: _ListHeaderComponent,
      ListEmptyComponent: _ListEmptyComponent,
      mainKey = "tab",
      renderSection: _renderSection,
      loadMoreParams: _loadMoreParams,
      loadMorable: _loadMorable,
      setMap: _setMap,
      showsVerticalScrollIndicator: _showsVerticalScrollIndicator,
    } = useSingleTabView();

    /**
     * set props by values in props or values in Context
     */
    const params = useMemo(
      () => props.params || _params,
      [props.params, _params]
    );

    const requestFn = useMemo(
      () => props.requestFn || _requestFn,
      [props.requestFn, _requestFn]
    );

    const query = useMemo(() => props.query || _query, [props.query, _query]);

    const getItemByQuery = useMemo(
      () => props.getItemByQuery || _getItemByQuery,
      [props.getItemByQuery, _getItemByQuery]
    );

    const useItemByQuery = useMemo(
      () => props.useItemByQuery || _useItemByQuery,
      [props.useItemByQuery, _useItemByQuery]
    );

    const noItemImageSource = useMemo(
      () => props.noItemImageSource || _noItemImageSource,
      [props.noItemImageSource, _noItemImageSource]
    );

    const disabledLoadData = useMemo(
      () => props.disabledLoadData || _disabledLoadData,
      [props.disabledLoadData, _disabledLoadData]
    );

    const itemHeight = useMemo(
      () => props.itemHeight || _itemHeight,
      [props.itemHeight, _itemHeight]
    );

    const numColumns = useMemo(
      () => props.numColumns || _numColumns,
      [props.numColumns, _numColumns]
    );

    const ListHeaderComponent = useMemo(
      () => props.ListHeaderComponent || _ListHeaderComponent,
      [props.ListHeaderComponent, _ListHeaderComponent]
    );

    const ListEmptyComponent = useMemo(
      () => props.ListEmptyComponent || _ListEmptyComponent,
      [props.ListEmptyComponent, _ListEmptyComponent]
    );

    const renderItem = useMemo(
      () => props.renderItem || _renderItem,
      [props.renderItem, _renderItem]
    );
    const renderSection = useMemo(
      () => props.renderSection || _renderSection,
      [props.renderSection, _renderSection]
    );
    const loadMoreParams = useMemo(
      () => props.loadMoreParams || _loadMoreParams,
      [props.loadMoreParams, _loadMoreParams]
    );
    const loadMorable = useMemo(
      () => props.loadMorable || _loadMorable,
      [props.loadMorable, _loadMorable]
    );

    const setMap = useMemo(
      () => _setMap || props.setMap,
      [_setMap, props.setMap]
    );
    const showsVerticalScrollIndicator = useMemo(
      () => _showsVerticalScrollIndicator || props.showsVerticalScrollIndicator,
      [_showsVerticalScrollIndicator, props.showsVerticalScrollIndicator]
    );

    /**
     * request hook
     */
    const {
      loadingFirst,
      refreshing,
      changeParamsLoading,
      itemList,
      mounted,
      loadingMore,
      onLoadData,
    } = useRequest({
      singleListIndex: props.singleListIndex,
      currentIndex,
      index,
      params,
      requestFn,
      query,
      getItemByQuery,
      useItemByQuery,
      noItemImageSource,
      disabledLoadData,
      itemHeight,
      numColumns,
      ListHeaderComponent,
      tab: props.tab,
      mainKey,
      loadMoreParams,
    });

    const __renderItem = useCallback(
      ({ item, index }: IFlashItem<any>) => {
        return (renderItem?.(item, index, false) || null) as JSX.Element;
      },
      [renderItem]
    );

    const __renderSection = useCallback(
      ({ item, index, toggle }: IFlashSection<any>) => {
        return (
          <Animated.View>{renderSection(item, toggle, index)}</Animated.View>
        );
      },
      [itemList.length]
    );

    return (
      <_List
        {...props}
        ListEmptyComponent={ListEmptyComponent}
        loadingMore={loadingMore}
        singleListIndex={props.singleListIndex}
        index={index}
        data={setMap?.(itemList)?.data || props.setData?.(itemList) || itemList}
        estimatedItemSize={itemHeight || 100}
        renderItem={__renderItem}
        renderSection={renderSection ? __renderSection : undefined}
        loading={loadingFirst || !mounted}
        changeParamsLoading={changeParamsLoading}
        refreshing={refreshing}
        loadMorable={loadMorable}
        onLoadData={onLoadData}
        ListHeaderComponent={ListHeaderComponent}
        map={setMap?.(itemList)?.map || props.map}
        numColumns={numColumns}
        showsVerticalScrollIndicator={showsVerticalScrollIndicator}
      />
    );
  }
);
