// @ts-ignore
import jwt from "jwt-encode";
import React, { memo, useCallback, useMemo, useRef } from "react";
import RNWebview, {
  WebViewNavigation,
  WebViewProps,
} from "react-native-webview";
import { Platform } from "react-native";
import { Core, Fetch, parseQueryString, styled } from "@true/core";
import { useNavigation, useRoute } from "@react-navigation/native";
import { BackButton } from "./BackButton";
import { AnimatedBottomSpace } from "../AnimatedBottomSpace";

import moment from "moment";
import { AnimatedStatusBar } from "@true/ui";

export interface WebviewProps {
  uri: string;
}

export const Webview = memo(function Webview() {
  const webviewRef = useRef<RNWebview>(null);
  const lastPressedBack = useRef(Date.now());
  const lastNavigationEvent = useRef<WebViewNavigation | null>(null);
  const { uri } = useRoute().params as unknown as WebviewProps;
  const navigation = useNavigation();
  const goBack = useCallback(() => {
    navigation.goBack();
  }, [navigation]);

  const realUri = useMemo(() => {
    const httpUri = Fetch.__getRealUrl(uri);

    const time = moment().unix();

    let urls = httpUri.split("?");
    const params = urls[1] ? parseQueryString(urls[1] || "") : {};
    // params.access_token = Core.accessToken
    // params.__code = 'native';
    params.wv_jwt = jwt(
      {
        access_token: Core.accessToken,
        iat: time,
        exp: time,
      },
      Core.clientSecret
    );
    params.wv_client = Core.clientKey;

    const extParams = Object.keys(params)
      .map((key) => `${key}=${params[key]}`)
      .join("&");

    return `${urls[0]}?${extParams}`;
  }, [uri]);

  const onBackButtonPress = useCallback(() => {
    // If double press, go back
    const now = Date.now();
    if (now - lastPressedBack.current < 750) {
      return goBack();
    }

    if (!lastNavigationEvent.current?.canGoBack) {
      return goBack();
    }

    lastPressedBack.current = now;
    webviewRef.current?.goBack();
  }, []);

  const onNavigationStateChange = useCallback((event: WebViewNavigation) => {
    if (!event.canGoBack && event.navigationType === "backforward") {
      return goBack();
    }

    lastNavigationEvent.current = event;
  }, []);

  const props = useMemo((): WebViewProps => {
    return {
      source: { uri: realUri },
      // originWhitelist: ['*' + '.' + Core.sysUrl],
      scalesPageToFit: false,
      javaScriptEnabled: true,
      cacheEnabled: true,
      geolocationEnabled: true,
      domStorageEnabled: true,
      scrollEnabled: true,
      onNavigationStateChange,
    };
  }, [realUri]);

  return (
    <ScreenWrapper>
      <AnimatedStatusBar />
      <RNWebview
        mixedContentMode={"compatibility"}
        ref={webviewRef}
        {...props}
      />
      {Platform.OS === "ios" && <BackButton onPress={onBackButtonPress} />}
      <AnimatedBottomSpace />
    </ScreenWrapper>
  );
});

const ScreenWrapper = styled.View`
  flex: 1;
  background-color: ${(p) => p.theme.backgroundColor};
`;

export const WebViewScreen = Webview;
