import { CommentProviderProps, useCommentsProvider } from "../Provider";
import { useMemo } from "react";

export const useInvalid = (props?: CommentProviderProps) => {
  const {
    hid: aHid,
    token: aToken,
    appName: aAppName,
    type: aType,
  } = props || {};

  const {
    hid: bHid,
    token: bToken,
    type: bType,
    appName: bAppName,
  } = useCommentsProvider();

  const type = useMemo(() => aType || bType, [aType, bType]) as
    | "comment"
    | "post";
  const hid = useMemo(() => aHid || bHid, [aHid, bHid]) as string;
  const token = useMemo(() => aToken || bToken, [aToken, bToken]) as string;
  const appName = useMemo(
    () => aAppName || bAppName,
    [aAppName, bAppName]
  ) as string;

  // if (!hid) throw new Error("there no 'hid' prop");
  // if (!token) throw new Error("there no 'token' prop");
  if (!appName) throw new Error("there no 'appName' prop");
  if (!type) throw new Error("there no 'type' prop");

  return useMemo(
    () => ({
      type,
      hid,
      token,
      appName,
    }),
    [type, hid, token, appName]
  );
};
