import { Fonts, styled, useTheme } from "@true/core";
import React, { memo } from "react";
import Animated, {
  SharedValue,
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";

interface Props {
  tabs: string[];
  onChangeTab?: (index: number) => void;
}

export const SimpleTabBar = memo((props: Props) => {
  const { tabs, onChangeTab } = props;

  const current = useSharedValue(0);

  return (
    <SContainer>
      {tabs.map((item, index) => (
        <Item
          current={current}
          item={item}
          index={index}
          onPress={onChangeTab}
        />
      ))}
    </SContainer>
  );
});

const SContainer = styled.View`
  flex-direction: row;
  padding-left: 16px;
  border-bottom-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
  margin-bottom: 12px;
`;

const Item = memo(
  ({
    item,
    index,
    onPress,
    current,
  }: {
    item: string;
    index: number;
    onPress?: (index: number) => void;
    current: SharedValue<number>;
  }) => {
    const theme = useTheme();

    const animatedLineStyle = useAnimatedStyle(() => {
      return {
        opacity: current.value == index ? 1 : 0,
      };
    });
    const animatedTextStyle = useAnimatedStyle(() => {
      return {
        color: current.value == index ? theme.neutral1 : theme.neutral3,
        fontFamily: current.value == index ? Fonts.Medium : Fonts.Regular,
      };
    });

    return (
      <SItemContainer
        onPress={() => {
          onPress?.(index);
          current.value = index;
        }}
      >
        <SItemTextWrapper>
          <SItemText style={animatedTextStyle}>{item}</SItemText>
        </SItemTextWrapper>

        <SUnderline style={animatedLineStyle} />
      </SItemContainer>
    );
  }
);

const SItemContainer = styled.TouchableOpacity``;
const SItemTextWrapper = styled.View`
  padding: 8px 16px;
`;
const SItemText = styled(Animated.Text)`
  color: ${(p) => p.theme.neutral1};
  text-align: center;
`;

const SUnderline = styled(Animated.View)`
  width: 100%;
  height: 2px;
  background-color: ${(p) => p.theme.neutral1};
`;
