import React, { memo, useCallback, useMemo } from "react";
import constate from "constate";
import { AnimatedScrollableTabCommonProps } from "../index";
import { LayoutChangeEvent, View } from "react-native";
import { ISingleListProps } from "./SingleList";
import { useSharedValue } from "react-native-reanimated";

export interface ITab {
  content: (p: { singleListIndex: number } & ISingleListProps) => JSX.Element;
  title: string;
  stickyHeaderRender?: JSX.Element;
}

interface Props extends AnimatedScrollableTabCommonProps {
  tabs: ITab[];
  index: number;
  stickyHeaderRender?: JSX.Element;
  children?: any;
}

const _provider = (props: Props) => {
  // const { tabs, stickyHeaderRender, index } = props;

  // const { header: pHeader } = useSingleTabView();
  // const {
  //   aScrollVal,
  //   headerHeight: rHeaderHeight,
  //   tabBarHeight: rTabBarHeight,
  //   top: rTop,
  // } = useAnimatedScrollableView();

  // const [currentIndex, setCurrentIndex] = useState(0);
  const currentIndex = useSharedValue(0);
  // const top = useSharedValue<number>(0);
  const singleHeaderHeight = useSharedValue<number>(0);
  const singleTabBarHeight = useSharedValue<number>(0);

  const onHeaderLayout = useCallback((e: LayoutChangeEvent) => {
    singleHeaderHeight.value = e.nativeEvent.layout.height;
  }, []);
  const onTabBarLayout = useCallback((e: LayoutChangeEvent) => {
    singleTabBarHeight.value = e.nativeEvent.layout.height;
  }, []);

  // useAnimatedReaction(
  //   () => aScrollVal.value,
  //   (_aScrollVal) => {
  //     const _raw = rTop.value + rHeaderHeight.value;
  //     console.log('scroll', _aScrollVal, _raw)
  //     top.value = _raw;
  //     // Math.min(0 - _aScrollVal, _raw);
  //   },
  //   []
  // );

  return useMemo(
    () => ({
      ...props,
      currentIndex,
      // setCurrentIndex,
      // top: rTop,
      singleHeaderHeight,
      singleTabBarHeight,
      onTabBarLayout,
      onHeaderLayout,
    }),
    [
      currentIndex,
      props,
      singleHeaderHeight,
      onHeaderLayout,
      onTabBarLayout,
      singleTabBarHeight,
    ]
  );
};

const [_SingleTabProvider, useSingleTabView] = constate(
  _provider,
  (val) => val
);

export const SingleTabProvider = memo((props: Props) => {
  return (
    <View style={{ flex: 1, overflow: "hidden" }} pointerEvents={"box-none"}>
      <_SingleTabProvider {...props} />
    </View>
  );
});

export { useSingleTabView };
