import { flatStyles } from '@true/core';
import React, {PropsWithChildren} from 'react';
import {ScrollView, ScrollViewProps, StyleSheet, ViewStyle} from 'react-native';
import {KeyboardDismissContainer} from './KeyboardDismissContainer';

interface Props extends PropsWithChildren<ScrollViewProps> {
  innerRef?: any;
  containerStyle?: ViewStyle | ViewStyle[];
  flex?: number;
}

export const FormScroll = (props: Props) => {
  const {innerRef, containerStyle, flex} = props;
  return (
    <ScrollView
      style={{flex}}
      contentContainerStyle={{flex}}
      ref={innerRef}
      scrollEventThrottle={50}
      keyboardShouldPersistTaps="always"
      showsVerticalScrollIndicator={false}
      showsHorizontalScrollIndicator={false}
      {...props}>
      <KeyboardDismissContainer
        style={flatStyles(styles.contentContainer, containerStyle)}>
        {props.children}
      </KeyboardDismissContainer>
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  contentContainer: {
    flex: 1,
    minHeight: '100%',
  },
});

export * from './provider';
