import React, {memo, useState} from 'react';
import {styled} from '@true/core';
import {StickerBoxContent} from './StickerBoxContent';
import {StickerHeaderTab} from './StickerHeaderTab';
import {StickerTabEnum} from './types';
import {GifScreen} from './GifScreen';
import {BuzzComponent} from './BuzzComponent';
import Animated, {
  SharedValue,
  SlideInDown,
  SlideOutDown,
} from 'react-native-reanimated';

export interface StickerBoxProps {
  onStickerSend: (url: string) => Promise<void>;
  animatedBottomFooter: SharedValue<number>;
}
export const StickerBox = memo(function StickerBox({
  onStickerSend,
  animatedBottomFooter,
}: StickerBoxProps) {
  const [currentTab, setCurrentTab] = useState(StickerTabEnum.emoji);

  return (
    <Wrapper
      entering={SlideInDown.duration(200)}
      exiting={SlideOutDown.duration(200)}>
      <StickerHeaderTab currentTab={currentTab} onTabChange={setCurrentTab} />
      {currentTab === StickerTabEnum.emoji ? (
        <StickerBoxContent
          onStickerPress={onStickerSend}
          animatedBottomFooter={animatedBottomFooter}
        />
      ) : null}
      {currentTab === StickerTabEnum.gif ? (
        <GifScreen
          onGifPress={onStickerSend}
          animatedBottomFooter={animatedBottomFooter}
        />
      ) : null}
      {currentTab === StickerTabEnum.buzz ? (
        <BuzzComponent onBuzzPress={onStickerSend} />
      ) : null}
    </Wrapper>
  );
});

const Wrapper = styled(Animated.View)`
  flex: 1;
`;
