import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import {
  normalizeStringForSearch,
  stripSpecialCharacters,
  ToastService,
} from "@true/core";
import _ from "lodash";

interface IOption {
  label: string;
  value: any;
  disabled?: boolean;
}

interface Props {
  value: any;
  keyName: string;
  onValueChange: (keyName: string, value: any) => void;
  onSave?: (value: any) => void;
  multiple?: boolean;
  options: IOption[];
  visible?: boolean;
  returnType?: "string" | "array";
  isSubmitButton?: boolean;
  defaultOptionLength?: number;
}

export const useSelectorInput = (props: Props) => {
  const {
    value,
    onSave,
    keyName,
    onValueChange,
    multiple,
    options,
    returnType = "string",
    isSubmitButton,
    defaultOptionLength,
  } = props;

  // convert options to dict
  const [initOptionMap, newOptions] = useMemo(() => {
    let map: Record<string, any> = {};
    let arr = [];
    for (let i = 0; i < options.length; i++) {
      const option = options[i];
      map[option.value] = option;
      arr[i] = { ...option, label: stripSpecialCharacters(`${option.label}`) };
    }
    return [map, arr];
  }, [options]);

  const optionMap = useRef(initOptionMap);
  const temp = useRef(value);
  const [textSearch, setTextSearch] = useState("");
  const [isSeeMore, setSeeMore] = useState(false);
  const isPressing = useRef<number | null>(null);
  const [, forceUpdate] = useState(false);

  useEffect(() => {
    temp.current = value;
    const newOption = { ...initOptionMap };
    const valueArr = Array.isArray(value)
      ? value
      : typeof value === "string"
      ? value.split(",").filter(Boolean)
      : [value];

    for (let val of valueArr) {
      newOption[val] = { ...optionMap.current[val], isSelected: true };
    }
    optionMap.current = {
      ...optionMap.current,
      ...newOption,
    };
    forceUpdate((p) => !p);
  }, [options]);

  const tempArray = useCallback((temp: React.MutableRefObject<any>) => {
    if (Array.isArray(temp.current)) {
      return temp.current;
    }
    if (typeof temp.current === "string")
      return temp.current.split(",").filter(Boolean);
    return [temp.current];
  }, []);

  const setMultiOptionMap = useCallback(
    (_value: any, isSelected: boolean) => {
      optionMap.current = {
        ...optionMap.current,
        [_value]: {
          ...optionMap.current[_value],
          isSelected,
        },
      };
    },
    [optionMap]
  );

  // console.log(initOptionMap);

  const setSingleOptionMap = useCallback(
    (_value: any, isSelected: boolean) => {
      optionMap.current = {
        ...initOptionMap,
        [_value]: {
          ...initOptionMap[_value],
          isSelected,
        },
      };
      forceUpdate((p) => !p);
    },
    [initOptionMap]
  );

  // submit value
  const onSubmit = useCallback(
    (_value?: any) => {
      if (typeof value === "undefined" && multiple)
        return ToastService.simpleShow(
          "Cannot set value because default value is undefined"
        );
      onValueChange(keyName, _value || temp.current);
      onSave?.(_value || temp.current);
    },
    [temp, keyName, onValueChange, onSave, multiple, value]
  );

  // set temp to data after a certain time
  const _onSubmit = useCallback(
    (newVal: any, isPressing: any) => {
      if (!isSubmitButton) {
        if (!multiple) {
          onValueChange(keyName, newVal);
          onSave?.(newVal);
        } else {
          clearTimeout(isPressing.current);
          isPressing.current = setTimeout(() => {
            onValueChange(keyName, newVal);
            onSave?.(newVal);
          }, 600);
        }
      }
    },
    [isSubmitButton, onValueChange, onSave, keyName]
  );

  // select value
  const onSelect = useCallback(
    (_value: any) => {
      if (multiple) {
        if (optionMap.current[_value]) setMultiOptionMap(_value, true);
        const _tempArr = tempArray(temp);
        const arr = [..._tempArr, _value];
        const newVal = returnType === "array" ? arr : arr.join(",");
        temp.current = newVal;
        _onSubmit(newVal, isPressing);
        return newVal;
      } else {
        if (optionMap.current[_value]) setSingleOptionMap(_value, true);
        temp.current = _value;
        _onSubmit(_value, isPressing);
        return _value;
      }
    },
    [
      multiple,
      tempArray,
      optionMap,
      returnType,
      onSubmit,
      _onSubmit,
      isPressing,
    ]
  );

  // deselect value
  const onDeSelect = useCallback(
    (_value: any) => {
      if (multiple) {
        if (optionMap.current[_value]) setMultiOptionMap(_value, false);
        const _tempArr = tempArray(temp);
        const arr = _tempArr.filter((item) => `${item}` != `${_value}`);
        const newVal = returnType === "array" ? arr : arr.join(",");
        temp.current = newVal;
        _onSubmit(newVal, isPressing);
        return newVal;
      } else {
        if (optionMap.current[_value]) setSingleOptionMap(_value, false);
        temp.current = typeof value === "string" ? "" : undefined;
        _onSubmit(typeof value === "string" ? "" : undefined, isPressing);
        return typeof value === "string" ? "" : undefined;
      }
    },
    [
      multiple,
      tempArray,
      optionMap,
      value,
      returnType,
      onSubmit,
      _onSubmit,
      isPressing,
    ]
  );

  // toggle value
  const onToggle = useCallback(
    (_value: any) => {
      if (!optionMap.current?.[_value].isSelected) return onSelect(_value);
      return onDeSelect(_value);
    },
    [optionMap, onSelect, onDeSelect]
  );

  // get is selected
  const getIsSelected = useCallback(
    (value: any) => {
      return optionMap.current[value]?.isSelected || false;
    },
    [optionMap]
  );

  // return value in string
  const valueString = useMemo(() => {
    if (Array.isArray(value)) {
      return value
        .map((item) =>
          stripSpecialCharacters(`${initOptionMap?.[item]?.label || ""}`)
        )
        .join(", ");
    }
    if (typeof value === "string")
      return value
        .split(",")
        .filter(Boolean)
        .map((item) =>
          stripSpecialCharacters(`${initOptionMap?.[item]?.label || ""}`)
        )
        .join(", ");
    return stripSpecialCharacters(`${initOptionMap?.[value]?.label || ""}`);
  }, [value, initOptionMap]);

  // onClearSelect
  const onClearSelect = useCallback(() => {
    if (Array.isArray(value)) {
      onSave?.([]);
      return onValueChange(keyName, []);
    }
    if (typeof value === "string") {
      onSave?.("");
      return onValueChange(keyName, "");
    }
    if (!multiple) {
      onSave?.(undefined);
      return onValueChange(keyName, undefined);
    }
  }, [keyName, onValueChange, value, onSave]);

  // set value if change value type
  useEffect(() => {
    if (multiple) {
      const _tempArr = tempArray(temp);
      temp.current = returnType === "array" ? _tempArr : _tempArr.join(",");
    }
  }, [returnType]);

  // search
  const getOptions = useMemo(() => {
    if (!isSeeMore && defaultOptionLength) {
      return [...newOptions].slice(defaultOptionLength);
    }
    let allIds: IOption[] = [];
    newOptions.map((item) => {
      // @ts-ignore
      if (
        normalizeStringForSearch(item?.label).includes(
          normalizeStringForSearch(textSearch)
        )
      ) {
        allIds.push(item);
      }
    });
    return allIds;
  }, [textSearch, newOptions, isSeeMore, defaultOptionLength]);

  const onSearch = useCallback(
    _.debounce((text: string) => {
      setTextSearch(text);
    }, 200),
    []
  );

  const toggleSeeMore = useCallback(() => {
    setSeeMore((prev) => !prev);
  }, []);

  const onDismiss = useCallback(() => {
    setTextSearch("");
    setSeeMore(false);
  }, []);

  return {
    temp,
    onSelect,
    onDeSelect,
    onToggle,
    onSubmit,
    getIsSelected,
    getOptions,
    onSearch,
    onDismiss,
    valueString,
    onClearSelect,
    toggleSeeMore,
    isSeeMore,
  };
};
