import React, {memo, useCallback, useEffect, useMemo, useState} from 'react';
import {styled, translate} from '@true/core';
import {StyleSheet} from 'react-native';
import {FileType} from '@true/core';
import {stripSpecialCharacters, formatFloat} from '@true/core';
import {
  MyDateTimeInput,
  MySelectorInput,
  MyTextInput,
  MyAttachments,
  MyCheckbox,
  MyTableInput,
} from './index';
import {ImageSourcePropType} from 'react-native';

export interface ICustomFieldForm {
  id: string;
  name: string;
  value: string;
  type: TypeEnum;
  required: string;
  placeholder: string;
  file_name?: string;
  file_url?: string;
  options: string[];
  enabled: number; // 0 | 1,
}

export interface FilterBoxOption {
  value: string | number | undefined;
  label: string;
  noTranslate?: boolean;
  icon?: ImageSourcePropType;
}

interface Props {
  form: ICustomFieldForm;
  value: any;
  onValueChange: (name: string, value: string) => void;
  formList: ICustomFieldForm[];
  loading?: boolean;
  onSave?: (
    keyName: string,
    item: any,
    value: any,
    onDone?: () => void,
  ) => void;
}

export enum TypeEnum {
  int = 'int',
  text = 'text',
  textarea = 'textarea',
  date = 'date',
  datetime = 'datetime',
  float = 'float',
  file = 'file',
  select = 'select',
  selectM = 'select-m',
  title = 'title',
  input_table = 'input-table',
  checkbox = 'checkbox',
}

export const MyCustomField = memo(function ({
  form,
  formList,
  value,
  onValueChange,
  loading,
  onSave,
}: Props) {
  const type = useMemo(() => form.type, [form]);
  const formFileRequest = useMemo(() => {
    let data: any[] = [];
    (formList || []).map(item => {
      if (item.type === 'file') return data.push(item);
    });
    return data;
  }, [formList]);
  const [files, setFiles] = useState<FileType[]>([]);

  useEffect(() => {
    formFileRequest &&
      formFileRequest.map(item => {
        if (item.id === form.id && item.file_fid) {
          setFiles([
            {
              ...item,
              id: item?.file_fid,
              name: item?.file_name,
              url: item?.file_url,
              image: item?.display,
            },
          ]);
        }
      });
  }, [formFileRequest, form]);

  const onSaveResult = useCallback(
    (value: any, onDone?: () => void) => {
      const key = 'custom_' + form.id;
      onSave && onSave(key, form, value, onDone);
    },
    [onSave, form],
  );

  const setParamKeyValue = useCallback(
    (keyString: string, value: any) => {
      const key = 'custom_' + form.id;
      if (form.id == 'int' || form.id == 'float') {
        onValueChange(key, value?.replace(/,/g, ''));
      } else if (form.type === TypeEnum.file) {
        onValueChange(key, value?.[0]);
        onSaveResult(value?.[0]);
      } else {
      }
      onValueChange(key, value);
    },
    [form, value, onValueChange, onSaveResult],
  );

  const formatDot = useMemo(() => {
    if (type == TypeEnum.file) return '';
    //  const removeText = dataValue?.replace(/[^0-9\.,]/g, "");
    const dataValue = value['custom_' + form.id] || '';
    const dataValueSplit = dataValue.split('.');
    const valueFormat = dataValueSplit?.[0]?.replace(/,/g, '');
    const valueShow = formatFloat(valueFormat);
    return String(valueShow);
  }, [value, form, type]);

  const formatValueFloat = useMemo(() => {
    if (type == TypeEnum.file) return '';

    const dataValue = value['custom_' + form.id] || '';
    const dataValueSplit = dataValue.split('.');
    const valueFormat = dataValueSplit?.[0]?.replace(/,/g, '');
    const valueShow = formatFloat(valueFormat);
    return `${valueShow}${
      dataValueSplit.length == 2 ? '.' + dataValueSplit[1] : ''
    }`;
  }, [value, form]);

  const getOption = useMemo(() => {
    let option: FilterBoxOption[] = [];
    form.options &&
      form?.options.map(item => {
        const value = stripSpecialCharacters(item);
        option.push({label: value, value: value});
      });
    return option;
  }, [form]);

  const isRequired = useMemo(() => {
    return form.required == '1';
  }, [form.required]);

  const _FormName = useMemo(() => {
    return stripSpecialCharacters(form?.name) || '';
  }, [form?.name]);

  return (
    <SViewContainer>
      {type == TypeEnum.int && (
        <MyTextInput
          key={form.id}
          containerStyle={styles.labelInput}
          title={_FormName}
          placeholder={'---'}
          keyName={_FormName}
          value={formatDot}
          onTextChange={setParamKeyValue}
          keyboardType={'number-pad'}
          required={isRequired}
          onSave={onSaveResult}
        />
      )}

      {type == TypeEnum.text && (
        <MyTextInput
          key={form.id}
          containerStyle={styles.labelInput}
          title={_FormName}
          placeholder={'---'}
          keyName={_FormName}
          value={value['custom_' + form.id] || ''}
          onTextChange={setParamKeyValue}
          required={isRequired}
          onSave={onSaveResult}
        />
      )}

      {type == TypeEnum.textarea && (
        <MyTextInput
          key={form.id}
          containerStyle={styles.labelInput}
          title={_FormName}
          placeholder={'---'}
          keyName={_FormName}
          value={value['custom_' + form.id]}
          onTextChange={setParamKeyValue}
          multiline={true}
          required={isRequired}
          onSave={onSaveResult}
        />
      )}

      {type == TypeEnum.date && (
        <MyDateTimeInput
          onValueChange={setParamKeyValue}
          keyName={_FormName}
          title={_FormName}
          placeholder={'---'}
          mode={'date'}
          value={value['custom_' + form.id] || ''}
          containerStyle={styles.labelInput}
          required={isRequired}
          showClearButton={true}
          type={'border'}
          onSave={onSaveResult}
        />
      )}

      {type == TypeEnum.datetime &&
        (onSave ? (
          <MyDateTimeInput
            onValueChange={setParamKeyValue}
            keyName={_FormName}
            title={_FormName}
            placeholder={'---'}
            mode={'datetime'}
            value={value['custom_' + form.id] || ''}
            containerStyle={styles.labelInput}
            required={isRequired}
            showClearButton={!isRequired}
            onSave={onSaveResult}
          />
        ) : (
          <SViewDateTime>
            <SViewDate style={styles.dateSpace}>
              <MyDateTimeInput
                onValueChange={(keyName, value) =>
                  setParamKeyValue(`custom_${form.id}-date`, value)
                }
                keyName={_FormName}
                title={_FormName}
                placeholder={'---'}
                mode={'date'}
                value={value[`custom_${form.id}-date`] || ''}
                containerStyle={styles.labelInput}
                required={isRequired}
                showClearButton={true}
              />
            </SViewDate>

            <SViewDate>
              <MyDateTimeInput
                onValueChange={(keyName, value) =>
                  setParamKeyValue(`custom_${form.id}-time`, value)
                }
                keyName={_FormName}
                title={_FormName}
                placeholder={'---'}
                mode={'time'}
                value={value[`custom_${form.id}-time`] || ''}
                containerStyle={styles.labelInput}
                required={isRequired}
                showClearButton={true}
              />
            </SViewDate>
          </SViewDateTime>
        ))}

      {type == TypeEnum.float && (
        <MyTextInput
          key={form.id}
          containerStyle={styles.labelInput}
          title={_FormName}
          placeholder={'---'}
          keyName={_FormName}
          value={formatValueFloat}
          onTextChange={setParamKeyValue}
          keyboardType={'decimal-pad'}
          multiline={true}
          required={isRequired}
          onSave={onSaveResult}
        />
      )}

      {type == TypeEnum.title && (
        <SViewCustomTitle>
          <STextCustomTitle>{_FormName}</STextCustomTitle>
        </SViewCustomTitle>
      )}

      {type == TypeEnum.file && (
        <MyAttachments
          keyName={_FormName}
          containerStyle={styles.labelInput}
          value={files}
          title={_FormName}
          placeholder={translate('common.attachments')}
          onValueChange={setParamKeyValue}
          multiple={false}
          required={isRequired}
        />
      )}

      {type == TypeEnum.select && (
        <SViewContainer>
          <MySelectorInput
            keyName={_FormName}
            containerStyle={styles.labelInput}
            title={_FormName}
            options={getOption}
            value={value['custom_' + form.id] || ''}
            onValueChange={setParamKeyValue}
            placeholder={'---'}
            required={isRequired}
            showClearButton={true}
            isSearch={true}
            onSave={onSaveResult}
          />
        </SViewContainer>
      )}

      {type == TypeEnum.selectM && (
        <SViewContainer>
          <MySelectorInput
            containerStyle={styles.labelInput}
            title={_FormName}
            options={getOption}
            value={value['custom_' + form.id] || ''}
            keyName={_FormName}
            onValueChange={setParamKeyValue}
            placeholder={'---'}
            required={isRequired}
            showClearButton={true}
            isSearch={true}
            multiple={true}
            onSave={onSaveResult}
          />
        </SViewContainer>
      )}

      {type == TypeEnum.input_table && (
        <SViewContainer>
          <MyTableInput
            containerStyle={styles.labelInput}
            title={_FormName}
            item={form}
            onSave={onSaveResult}
            onValueChange={setParamKeyValue}
            value={value['custom_' + form.id] || ''}
            required={isRequired}
          />
        </SViewContainer>
      )}
      {type == TypeEnum.checkbox && (
        <SViewContainer>
          <MyCheckbox
            containerStyle={styles.labelInput}
            title={_FormName}
            onSave={onSaveResult}
            onValueChange={setParamKeyValue}
            value={value['custom_' + form.id] || ''}
            required={isRequired}
            keyName={_FormName}
            options={getOption}
          />
        </SViewContainer>
      )}
    </SViewContainer>
  );
});

const styles = StyleSheet.create({
  labelInput: {
    marginTop: 8,
    width: '100%',
  },
  labelSelectInput: {
    paddingLeft: 4,
  },
  dateSpace: {
    marginRight: 16,
  },
});

const SViewDateTime = styled.View`
  flex-direction: row;
`;

const SViewDate = styled.View`
  flex: 1;
  flex-direction: row;
`;

const SViewContainer = styled.View`
  flex: 1;
`;

const SViewCustomTitle = styled.View`
  justify-content: center;
`;

const STextCustomTitle = styled.Text`
  margin-top: 20px;
  font-size: 14px;
  line-height: 20px;
  color: ${props => props.theme.neutral3};
`;
