import React, { memo, useMemo } from "react";
import FastImage from "react-native-fast-image";
import { TouchableOpacity } from "react-native";
import { ProfileLightBox } from "../ProfileModal";
import { IM_DEFAULT_AVATAR, useBoolean, useUser } from "@true/core";

const buildImageStyle = (size: number) => {
  const radius = size / 2;
  const imageStyle: any = {
    width: size,
    height: size,
    borderRadius: radius,
    overflow: "hidden",
    backgroundColor: "#E0E0E0",
  };
  return imageStyle;
};

interface Props {
  userKey: string | number;
  size: number;
  disableClick?: boolean;
}

export const Avatar = memo(({ userKey, size = 28, disableClick }: Props) => {
  const [isShowUserInfo, showUserInfor, hideUserInfo] = useBoolean(false);
  const user = useUser(userKey);

  const gavatarUri = useMemo(() => user?.gavatar || undefined, [user]);

  const imageStyle = useMemo(() => buildImageStyle(size), [size]);

  return (
    <>
      <TouchableOpacity
        {...{ ["round" + size]: true }}
        disabled={!!disableClick}
        onPress={showUserInfor}
      >
        <FastImage
          style={imageStyle}
          source={
            gavatarUri
              ? {
                  uri: gavatarUri,
                  priority: FastImage.priority.low,
                  cache: FastImage.cacheControl.web,
                  borderRadius: size / 2,
                  width: size,
                  height: size,
                }
              : IM_DEFAULT_AVATAR
          }
        />
      </TouchableOpacity>
      {gavatarUri && (
        <ProfileLightBox
          isVisible={isShowUserInfo}
          userKey={userKey}
          onCloseRequest={hideUserInfo}
        />
      )}
    </>
  );
});

export * from "./UserAvatar";
export * from "./AvatarList";
