import React, { memo, useMemo } from "react";
import { ReplyView } from "../../ReplyView";
import { useSharedValue } from "react-native-reanimated";
import { getComments, interactManager, useZustand } from "@true/core";

export const CommentReplyView = memo(() => {
  const replyHid = useCmtReplyHid();
  const visible = useSharedValue(false);

  const comment = useMemo(() => {
    visible.value = !!replyHid;
    return getComments(replyHid);
  }, [replyHid]);

  const username = useMemo(() => comment?.username, [comment?.user_id]);
  const attachment = useMemo(() => {
    const firstAttachment = comment?.attachment;
    return {
      uri: firstAttachment?.src,
      name: firstAttachment?.name,
      isImage: !!firstAttachment?.type,
    };
  }, [comment]);
  const content = useMemo(() => comment?.content, [comment?.content]);

  return (
    <ReplyView
      visible={visible}
      username={username}
      attachment={attachment}
      content={content}
      onClose={() => {
        interactManager(() => setCmtReplyHid(""), 200);
      }}
    />
  );
});

export const [useCmtReplyHid, getCmtReplyHid, setCmtReplyHid] = useZustand("");
