interface Props extends MyDynamicSelectorInputProps {
  isSearch?: boolean;
  loading?: boolean;
  contentContainer?: ViewStyle | ViewStyle[];
  itemContainerStyle?: ViewStyle | ViewStyle[];
  itemTextStyle?: TextStyle | TextStyle[];
  renderIcon?: (icon: ImageSourcePropType) => React.ReactElement | null;
  typeSelect?: "normal" | "border" | "tick";
  map?: any;
  _options?: IOption[];
  lastElements?: IOption[];
}

const _MySelectorInput = memo((props: Props) => {
  const { loading = false, isSearch, title, options, _options, map } = props;

  const { getOptions, onSearch, getIsSelected, onToggle } =
    useMySelectorInput();

  const { animatedSnapPoints, onListContentSize, onViewLayout } =
    useAnimatedSnapPoints(
      title ? [`CONTENT_HEIGHT+${getHeight(60)}`] : undefined,
      [getOptions.length]
    );

  const { animatedStyles } = useBottomSpace();

  const bottomRef = useRef<BottomSheetModal>(null);

  const renderItem = useCallback(
    (item: IOption, index?: number) => {
      return (
        <Item
          bottomRef={bottomRef}
          getIsSelected={getIsSelected}
          onToggle={onToggle}
          data={item}
          {...props}
        />
      );
    },
    [props, bottomRef.current]
  );

  const renderSection = useCallback(
    (data: unknown, onToggle?: () => void, index?: number) => {
      return <SectionItem data={data} onToggle={onToggle} key={index} />;
    },
    []
  );

  const renderMenu = useCallback(() => {
    return (
      <>
        {isSearch && (
          <BottomSheetView onLayout={onViewLayout("search")}>
            <SSearchBar onSearchTextChange={onSearch} isOnModal />
          </BottomSheetView>
        )}
        <RecycleList
          isBottomSheet
          data={_options || getOptions}
          mapKey={"value"}
          map={map}
          itemHeight={100}
          renderItem={renderItem}
          renderSection={map ? renderSection : undefined}
          loading={false}
          isRefreshing={false}
          isLoadMore={false}
          error={false}
          refreshable={false}
          loadMorable={false}
          onContentSizeChange={onListContentSize}
          estimatedItemSize={44}
          ListFooterComponent={<Animated.View style={animatedStyles} />}
        />
        {/*<BottomSheetFlashList*/}
        {/*  data={data}*/}
        {/*  onContentSizeChange={onListContentSize}*/}
        {/*  renderItem={_renderItem}*/}
        {/*  estimatedItemSize={44}*/}
        {/*  ListFooterComponent={<Animated.View style={animatedStyles} />}*/}
        {/*/>*/}
      </>
    );
  }, [
    isSearch,
    renderItem,
    loading,
    getOptions,
    options.length,
    map,
    _options,
  ]);

  return (
    <MyDynamicSelectorInput
      {...props}
      options={getOptions}
      ref={bottomRef}
      hasFlashList={true}
      renderMenu={renderMenu}
      snapPoints={animatedSnapPoints}
    />
  );
});

export const MySelectorInput = memo((props: Props) => {
  return (
    <MySelectorInputProvider {...props}>
      <_MySelectorInput {...props} />
    </MySelectorInputProvider>
  );
});

const SectionItem = memo(
  ({ data, onToggle }: { data: any; onToggle?: () => void }) => {
    const toggle = useSharedValue(1);

    const _onToggle = useCallback(() => {
      "worklet";
      toggle.value = toggle.value === 1 ? 0 : 1;
      onToggle && onToggle();
    }, [onToggle]);

    const rotateValue = useDerivedValue(() => {
      const rotatePosition = interpolate(toggle.value, [0, 1], [180, 0]);
      return withTiming(rotatePosition, {
        duration: 500,
      });
    }, [toggle]);

    const animatedIconStyle = useAnimatedStyle(() => {
      return { transform: [{ rotate: `${rotateValue.value}deg` }] };
    }, [rotateValue]);

    const animatedStyle = useAnimatedStyle(() => {
      return { height: interpolate(toggle.value, [0, 1], [1, 0]) };
    });

    return (
      <>
        <SSectionButton onPress={_onToggle}>
          <SSectionView>
            <STilteSection>{data?.label}</STilteSection>
            <Animated.View style={animatedIconStyle}>
              <SSectionIcon source={IC_DROPDOWN} />
            </Animated.View>
          </SSectionView>
        </SSectionButton>
        <Animated.View
          style={[
            animatedStyle,
            {
              width: Dimensions.get("window").width - 32,
              backgroundColor: "#e3e3e3",
              marginLeft: 16,
            },
          ]}
        />
      </>
    );
  }
);

interface ItemProps {
  bottomRef: RefObject<BottomSheetModal>;
  data: IOption;
  typeSelect?: "normal" | "border" | "tick";
  contentContainer?: ViewStyle | ViewStyle[];
  itemContainerStyle?: ViewStyle | ViewStyle[];
  itemTextStyle?: TextStyle | TextStyle[];
  renderIcon?: (icon: ImageSourcePropType) => React.ReactElement | null;
  multiple?: boolean;
  getIsSelected: (value: any) => boolean;
  onToggle: (value: any) => any;
  map?: any;
  lastElements?: IOption[];
}

const Item = memo((props: ItemProps) => {
  const {
    typeSelect = "tick",
    contentContainer,
    data,
    itemContainerStyle,
    renderIcon,
    itemTextStyle,
    multiple,
    bottomRef,
    getIsSelected,
    onToggle,
    lastElements,
  } = props;
  // const {getIsSelected, onToggle} = useMySelectorInput();
  const [, forceUpdate] = useState(false);

  const toggle = useCallback(
    (value: any) => {
      onToggle && onToggle(value);
      if (!multiple) bottomRef.current?.dismiss();
      forceUpdate((prev) => !prev);
    },
    [multiple, onToggle]
  );

  const isLast = useMemo(() => {
    if (lastElements) {
      return lastElements.some((element) => element === data);
    }

    return true;
  }, [lastElements, data]);

  const bgColor =
    typeSelect === "tick"
      ? {
          backgroundColor: getIsSelected(data.value) ? "#F5F5F5" : "white",
          paddingLeft: lastElements && lastElements.length > 0 ? 16 : 0,
        }
      : {};

  return (
    <>
      <View
        style={flatStyles(styles.optionContainer, bgColor, contentContainer)}
        key={data.value}
      >
        {typeSelect === "normal" ? (
          <SelectorItem
            itemContainerStyle={itemContainerStyle}
            option={data}
            renderIcon={renderIcon}
            selected={getIsSelected(data.value)}
            onSelect={toggle}
            itemTextStyle={itemTextStyle}
          />
        ) : typeSelect === "tick" ? (
          <SSelectorItemWithIcon
            itemContainerStyle={itemContainerStyle}
            option={data}
            renderIcon={renderIcon}
            selected={getIsSelected(data.value)}
            onSelect={toggle}
            itemTextStyle={itemTextStyle}
          />
        ) : (
          <SSelectorItem
            itemContainerStyle={itemContainerStyle}
            option={data}
            renderIcon={renderIcon}
            selected={getIsSelected(data.value)}
            onSelect={toggle}
            itemTextStyle={itemTextStyle}
          />
        )}
      </View>
      {isLast &&
      typeSelect === "tick" &&
      lastElements &&
      lastElements.length > 0 ? (
        <View
          style={{
            height: 1,
            width: Dimensions.get("window").width - 32,
            backgroundColor: "#e3e3e3",
            marginLeft: 16,
            marginTop: 8,
          }}
        />
      ) : null}
    </>
  );
});

