import React, { memo, useCallback, useMemo, useState } from "react";
import {
  AppInfo,
  Colors,
  EAuthStage,
  getFontSize,
  getHeight,
  getWidth,
  IC_2FA,
  IC_EMAIL,
  IC_EYE_CLOSE,
  IC_EYE_OPEN,
  IC_KEY,
  styled,
  styledValue,
  useAuthStore,
  useBoolean,
  useTheme,
  useWindow,
} from "@true/core";
import { LoginInput } from "./components/LoginInput";
import { useManualLogin } from "./hooks";
import { MyButton, MyButtonProvider } from "../Button";
import { StyleSheet } from "react-native";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { InitKeyboardView } from "./InitKeyboardView";
import { UITextInput } from "../UIInput";

//
// email: 'hoang.nguyen@platform.inc',
//     password: 'XNQFBQM54T',
export const LoginForm = memo(
  (props: { onGoBack?: () => void; socketLoading?: boolean }) => {
    const { onGoBack, socketLoading } = props;
    const { isLandscape } = useWindow();
    // thang.luong@platform.inc
    // Anhduongbhb123
    const [params, setParams] = useState({
      email: "",
      password: "",
      code_2fa: "",
    });
    const {
      onCreateClient,
      isVerifiedCaptcha,
      onGetRecaptcha,
      failSafeV3,
      loading,
      twoFA,
    } = useManualLogin(params, onGoBack);

    const { authStage } = useAuthStore();

    const onParamsChange = useCallback((keyName: string, value: any) => {
      setParams((prev) => ({
        ...prev,
        [keyName]: value,
      }));
    }, []);

    const [visiblePass, , , togglePass] = useBoolean(false);

    const theme = useTheme();
    const inputStyle = useMemo(
      () =>
        isLandscape
          ? {
              width: 500,
            }
          : {
              marginHorizontal: 30,
            },
      [isLandscape]
    );

    const { aBottom } = useAnimatedSafeAreaInsets();
    const animatedStyles = useAnimatedStyle(() => ({
      paddingBottom: aBottom.value + 16,
    }));

    return (
      <SContainer style={[{ flex: isLandscape ? 1 : 3 }, animatedStyles]}>
        <UITextInput
          // placeholder={"Email"}
          // iconLeft={IC_EMAIL}
          style={styles.input}
          keyName={"email"}
          onChangeValue={onParamsChange}
          title={"Email"}
          value={params.email}
          autoFocus={true}
          keyboardType={"email-address"}
          autoComplete={"email"}
        />
        <UITextInput
          // placeholder={"Password"}
          // iconLeft={IC_KEY}
          style={styles.input}
          keyName={"password"}
          onChangeValue={onParamsChange}
          title={"Password"}
          value={params.password}
          rightIconSource={visiblePass ? IC_EYE_OPEN : IC_EYE_CLOSE}
          onRightIconPress={togglePass}
          secureTextEntry={!visiblePass}
          autoComplete={"off"}
        />
        {twoFA ? (
          <UITextInput
            placeholder={"Two factor authentication code"}
            // iconLeft={IC_2FA}
            style={styles.input}
            keyName={"code_2fa"}
            onChangeValue={onParamsChange}
            title={"Two factor authentication code"}
            value={params.code_2fa}
            autoComplete={"off"}
          />
        ) : null}
        <SButtonView>
          <MyButtonProvider>
            <MyButton
              containerStyle={styles.input}
              title={"Login"}
              loading={
                loading || socketLoading || authStage === EAuthStage.CHECKING
              }
              onPress={onCreateClient}
              backgroundColor={theme.primaryColor}
              textColor={AppInfo.buttonTextColor || Colors.neutral1}
              disabled={!isVerifiedCaptcha && failSafeV3 === 1}
              disabledBackgroundColor={theme.neutral8}
              disabledTextColor={theme.neutral6}
            />
            {failSafeV3 === 1 && (
              <MyButton
                containerStyle={styles.input}
                title={
                  isVerifiedCaptcha ? "ReCaptcha verified" : "Get ReCaptcha"
                }
                disabled={isVerifiedCaptcha}
                onPress={onGetRecaptcha}
                backgroundColor={theme.primaryColor}
                disabledBackgroundColor={theme.neutral8}
                textColor={AppInfo.buttonTextColor || Colors.neutral1}
                disabledTextColor={theme.neutral6}
              />
            )}
          </MyButtonProvider>
        </SButtonView>

        <InitKeyboardView isInForm={true} />
      </SContainer>
    );
  }
);

const SErrorText = styled.Text`
  color: ${styledValue.neutral6};
  font-size: ${getFontSize(13)}px;
  margin-top: ${getHeight(8)}px;
`;

const SContainer = styled(Animated.View)`
  //align-items: center;
  padding: 0 16px;
`;

const SButtonView = styled.View`
  //align-items: center;
  margin-top: 32px;
`;

const styles = StyleSheet.create({
  input: {
    // width: getWidth(300),
    marginTop: 8,
  },
});
