  const [textSearch, setTextSearch] = useState("");
  const { ids, onDeleteFollower } = useNavigationParams<MembersModalProps>();
  const [followerIds] = useState(ids);

  // const [{ loading, error }, onDeleteFollower] = useAsyncFn(
