import React, { memo, useCallback, useEffect, useMemo, useState } from "react";
import {
  ActivityIndicator,
  Image,
  StyleSheet,
  TouchableOpacity,
} from "react-native";
import FastImage from "react-native-fast-image";
import { CustomRendererProps, TNode } from "react-native-render-html";
import { Colors, flatStyles, TPrevFn, useWindow } from "@true/core";
import { SvgUri } from "react-native-svg";
import { useHTMLImage } from "./htmlImageProvider";
import Animated, { runOnUI, useAnimatedStyle } from "react-native-reanimated";
import { TapGestureHandler } from "react-native-gesture-handler";

export interface ImageProps {
  onImagePress?: (uri: string) => void;
  setImages?: (uris: TPrevFn<string[]>) => void;
  images?: string[];
  imageScale?: number;
  imageOpacity?: number;
  imageTapHandlerWaitFor?: React.Ref<unknown> | React.Ref<unknown>[];
  isTapGestureHandler?: boolean;
}

export const ImageView = ({
  onImagePress,
  setImages,
  images,
  imageScale = 1,
  imageOpacity = 1,
  imageTapHandlerWaitFor,
  isTapGestureHandler,
}: ImageProps) =>
  memo((props: CustomRendererProps<TNode>) => {
    const uri = useMemo(
      () => props.tnode.attributes?.src || "",
      [props.tnode.attributes?.src]
    );
    const [isLoading, setLoading] = useState(true);

    const { width: windowWidth } = useWindow();
    const [widthImg, setWidthImg] = useState(100);
    const [heightImg, setHeightImg] = useState(100);
    const isSvg = useMemo(() => uri.includes(".svg"), [uri]);

    useEffect(() => {
      Image.getSize(
        uri,
        (_width, _height) => {
          _width > 0 && setWidthImg(_width);
          _height > 0 && setHeightImg(_height);
          setLoading(false);
        },
        (e) => {
          setLoading(false);
        }
      );
    }, [windowWidth]);

    const { TDefaultRenderer, tnode } = props;
    const tnodeStyles = useMemo(() => tnode.styles, [tnode.styles]);
    const _height = useMemo(
      () => (tnodeStyles.nativeBlockRet.height || heightImg) as number,
      [tnodeStyles.nativeBlockRet.height, heightImg]
    );
    const _width = useMemo(
      () => (tnodeStyles.nativeBlockRet.width || widthImg) as number,
      [tnodeStyles.nativeBlockRet.width, widthImg]
    );
    const width = useMemo(
      () => (_width > windowWidth - 64 ? windowWidth - 64 : _width),
      [_width, windowWidth]
    );
    const height = useMemo(
      () => (_height / _width) * width,
      [_height, _width, width]
    );

    useEffect(() => {
      !images?.includes(uri) && setImages?.((prev) => [...prev, uri]);
    }, [images?.length, uri]);

    const { imageLayout } = useHTMLImage();

    const uiCb = useCallback((uri: string, w: number, h: number) => {
      "worklet";
      imageLayout.value = {
        ...imageLayout.value,
        [uri]: { w, h },
      };
    }, []);

    useEffect(() => {
      if (height !== 100 && width !== 100) {
        runOnUI(uiCb)(uri, width, height);
      }
    }, [uri, height, width]);

    const aStyle = useAnimatedStyle(
      () => ({
        width: (imageLayout.value[uri]?.w || 100) * imageScale,
        height: (imageLayout.value[uri]?.h || 100) * imageScale,
      }),
      [uri, imageScale]
    );

    const pressImage = useCallback(() => {
      if (onImagePress) {
        onImagePress(uri);
      }
    }, [onImagePress, uri]);

    const imageStyle = useMemo(
      () =>
        flatStyles(
          tnodeStyles.nativeBlockFlow,
          tnodeStyles.nativeBlockRet,
          tnodeStyles.nativeTextFlow,
          tnodeStyles.nativeTextRet,
          tnodeStyles.webBlockRet as any,
          tnodeStyles.webTextFlow as any,
          {
            borderRadius: 4,
            height: height * imageScale,
            width: width * imageScale,
            opacity: imageOpacity,
          }
        ),
      [tnodeStyles, height, width, images]
    );

    const buttonStyle = useMemo(
      () => ({
        ...tnodeStyles.nativeBlockRet,
        backgroundColor: "#EBEBEB",
        marginVertical: 6,
        borderRadius: 4,
        alignItems: "center",
        justifyContent: "center",
      }),
      [tnodeStyles.nativeBlockRet]
    ) as any;

    const render = (
      <Animated.View style={StyleSheet.absoluteFillObject}>
        {isLoading && height && width ? (
          <ActivityIndicator style={{ top: "45%" }} color={Colors.blue1} />
        ) : isSvg ? (
          <SvgUri
            uri={uri}
            height={height * imageScale}
            width={width * imageScale}
          />
        ) : (
          <FastImage resizeMode={"cover"} style={imageStyle} source={{ uri }} />
        )}
      </Animated.View>
    );

    return (
      <TDefaultRenderer
        {...props}
        style={{ alignItems: "center", justifyContent: "center" }}
      >
        {isTapGestureHandler || imageTapHandlerWaitFor ? (
          <TapGestureHandler
            waitFor={imageTapHandlerWaitFor}
            onActivated={pressImage}
          >
            <Animated.View style={[buttonStyle, aStyle]}>
              {render}
            </Animated.View>
          </TapGestureHandler>
        ) : (
          <AButton style={[buttonStyle, aStyle]}>{render}</AButton>
        )}
      </TDefaultRenderer>
    );
  });

const AButton = Animated.createAnimatedComponent(TouchableOpacity);
