const [_AnimatedScrollableProvider, useAnimatedScrollableView] = constate(
  Context,
  (value) => value
);

const AnimatedScrollableProvider = memo((props: Props) => {
  return (
    <View style={{ flex: 1, overflow: "hidden" }} pointerEvents={"box-none"}>
      <_AnimatedScrollableProvider {...props} />
    </View>
  );
});

export { useAnimatedScrollableView };
export { useRequest } from "./hooks/useRequest";

interface _IScrollableTabView {
  Provider: typeof AnimatedScrollableProvider;
  TabBar: typeof AnimatedScrollableTabBar;
  CollapsableTabBar: typeof AnimatedCollapseScrollableTabBar;
  Content: typeof AnimatedScrollableContent;
  List: typeof ScrollableTabList;
  ScrollView: typeof AnimatedScrollView;
  TotalProvider: typeof TotalProvider;
}

export const ScrollableTabView: _IScrollableTabView = {
  Provider: AnimatedScrollableProvider,
  TabBar: AnimatedScrollableTabBar,
  CollapsableTabBar: AnimatedCollapseScrollableTabBar,
  Content: AnimatedScrollableContent,
  List: ScrollableTabList,
  ScrollView: AnimatedScrollView,
  TotalProvider: TotalProvider,
};

interface _ISingleTabView {
  Provider: typeof SingleTabProvider;
  TabBar: typeof SingleTabBar;
  CollapsableTabBar: typeof CollapsableSingleTabBar;
  Content: typeof Content;
  List: typeof SingleList;
  ScrollView: typeof _ScrollView;
}

export const SingleTabView: _ISingleTabView = {
  Provider: SingleTabProvider,
  TabBar: SingleTabBar,
  CollapsableTabBar: CollapsableSingleTabBar,
  Content: Content,
  List: SingleList,
  ScrollView: _ScrollView,
};

export const ScrollableList = _List;

