import React, { memo, useEffect, useState } from "react";
import Animated, {
  SharedValue,
  useAnimatedStyle,
} from "react-native-reanimated";
import { styled, translate } from "@true/core";
import { ActionProps } from "./FloatingModal";
import { TouchableWithoutFeedback as TouchableOpacityGes } from "react-native-gesture-handler";
import { SAnchor, SIcon, SText, STouchable } from "./IOSFloatingModal";

export const AndroidFloatingModal = memo(
  ({
    onClose,
    actions,
    _onPress,
    isPressed,
    value,
  }: {
    onClose: () => void;
    actions?: ActionProps[];
    _onPress: (name: string) => void;
    isPressed: boolean;
    value: SharedValue<number>;
  }) => {
    const [z, setZ] = useState<number | undefined>(0);
    useEffect(() => {
      isPressed
        ? setZ(undefined)
        : setTimeout(() => {
            setZ(0);
          }, 200);
    }, [isPressed]);

    const animatedOpacity = useAnimatedStyle(() => ({
      opacity: value.value,
    }));
    const animatedTransForm = useAnimatedStyle(() => ({
      transform: [{ scale: value.value }],
    }));

    return (
      <Animated.View style={animatedOpacity}>
        <SView style={{ width: z, height: z }}>
          <STouchableOpacityGes onPress={onClose} />
        </SView>

        <SWrapOptions style={animatedTransForm}>
          <SAnchor />
          {actions?.map((item) => {
            return (
              <STouchable onPress={() => _onPress(item?.name)}>
                <SIcon>{item.icon}</SIcon>
                <SText>{translate(item?.text || "")}</SText>
              </STouchable>
            );
          })}
        </SWrapOptions>
      </Animated.View>
    );
  }
);

const SWrapOptions = styled(Animated.View)`
  background-color: ${(p) => p.theme.backgroundColor};
  padding: 4px;
  border-radius: 8px;
  bottom: 70px;
  right: 0;
`;

const SView = styled.View`
  position: absolute;
  background-color: black;
  opacity: 0.4;
  top: -1000px;
  left: -1000px;
`;

const STouchableOpacityGes = styled(TouchableOpacityGes)`
  height: 2000px;
  width: 2000px;
`;
