import React, { useCallback, useEffect, useState } from "react";
import {
  getComments,
  getCommentsByQuery,
  getPosts,
  memoForwardRef,
  requestLoadComment,
  useAsyncFn,
  useAutoToastError,
  useCommentsByQuery,
  usePostsByQuery,
} from "@true/core";
import { CommentProviderProps } from "./Provider";
import { RawCommentListProps, UICommentList } from "./UIComps/List";
import { IOnReplyPress } from "./UIComps/Item";
import { useInvalid } from "./hooks/useInvalid";
import { IRequestLoad } from "@true/core";

interface _Props
  extends Omit<RawCommentListProps, "data" | "appName" | "type"> {
  appName?: string;
  type?: "comment" | "post";
  loading?: boolean;
}

const _CommentList = (props: _Props & CommentProviderProps, ref: any) => {
  const { type, hid, token, appName } = useInvalid(props);

  const comments = (type == "comment" ? useCommentsByQuery : usePostsByQuery)(
    hid
  ) as string[];

  const [mounted, setMounted] = useState(false);

  const _requestLoadComment = useCallback(
    async (params?: Omit<IRequestLoad, "hid" | "token">) => {
      return await requestLoadComment(
        { ...(params || {}), hid, token },
        appName,
        type
      );
    },
    [hid, token, appName, type]
  );

  const [{ loading: loadingFirst, error: firstError }, loadFirst] = useAsyncFn(
    _requestLoadComment,
    [_requestLoadComment]
  );
  const [{ loading: refreshing, error: refeshError }, refresh] = useAsyncFn(
    _requestLoadComment,
    [_requestLoadComment]
  );
  const [{ loading: loadingMore, error: loadMoreError }, loadMore] = useAsyncFn(
    _requestLoadComment,
    [_requestLoadComment]
  );

  const onRepLyPress = useCallback(
    ({ comment, onAddReplies, requestReply }: IOnReplyPress) => {
      if (comment) {
        requestReply(
          { hid: comment.hid, token: comment.token },
          appName,
          type
        ).then(() => {
          onAddReplies(getCommentsByQuery(comment.hid) as string[]);
        });
      }
    },
    [appName, type]
  );

  useEffect(() => {
    if (hid && token && !mounted) {
      loadFirst().then(() => {
        setMounted(true);
      });
    }
  }, [loadFirst]);

  useAutoToastError(firstError);
  useAutoToastError(loadMoreError);
  useAutoToastError(refeshError);

  const onLoadData = useCallback(
    (refreshing: boolean, loadingMore: boolean) => {
      if (refreshing) {
        refresh().then();
      }
      if (type === "post" && loadingMore) {
        const lastHid = comments[comments.length - 1];
        const last_id = getPosts(lastHid)?.id as unknown as string | undefined;
        loadMore({ last_id }).then();
      }
    },
    [refresh, type, comments, token, hid]
  );

  const onLoadMore = useCallback(() => {
    if (type == "comment") {
      const lastHid = comments[0];
      const position = getComments(lastHid)?.id as unknown as
        | string
        | undefined;
      loadMore({ method: "prev", position }).then();
    }
  }, [type, comments]);

  return (
    <UICommentList
      {...props}
      type={type}
      appName={appName}
      ref={ref}
      data={comments}
      onReplyPress={onRepLyPress}
      requestReply={requestLoadComment}
      loading={loadingFirst || props.loading}
      refreshing={refreshing}
      loadingMore={loadingMore}
      onLoadData={onLoadData}
      onLoadMore={onLoadMore}
    />
  );
};

export const CommentList = memoForwardRef(_CommentList) as typeof _CommentList;
