import _ from "lodash";
import React, { useCallback, useEffect, useMemo, useRef } from "react";
import { Animated, Pressable, StyleSheet, ViewStyle } from "react-native";
import { getHeight, styled, useTheme } from "@true/core";
import { UIButton } from "../UIButton";

export const Switch = ({
  keyName,
  onPress,
  inActiveTrackColor = "rgba(153, 153, 153, 1)",
  disabledColor = "rgba(153, 153, 153, 1)",
  thumbColor = "#fff",
  value,
  disabled,
  scale = getHeight(22),
  debounce,
  ...props
}: {
  keyName: string;
  onPress: (keyName: string, value: boolean) => void;
  activeTrackColor?: string;
  inActiveTrackColor?: string;
  disabledColor?: string;
  thumbColor?: string;
  value: boolean;
  disabled?: boolean;
  scale?: number;
  debounce?: boolean;
}) => {
  const theme = useTheme();
  const activeTrackColor = useMemo(
    () => props.activeTrackColor || theme.primaryColor,
    [theme.primaryColor]
  );
  const switchTranslate = useRef(new Animated.Value(0)).current;
  useEffect(() => {
    if (value) {
      Animated.spring(switchTranslate, {
        toValue: getRightSwitch(scale),
        mass: 1,
        damping: 15,
        stiffness: 120,
        overshootClamping: false,
        restSpeedThreshold: 0.001,
        restDisplacementThreshold: 0.001,
        useNativeDriver: false,
      }).start();
    } else {
      Animated.spring(switchTranslate, {
        toValue: 0,
        mass: 1,
        damping: 15,
        stiffness: 120,
        overshootClamping: false,
        restSpeedThreshold: 0.001,
        restDisplacementThreshold: 0.001,
        useNativeDriver: false,
      }).start();
    }
  }, [value, switchTranslate]);
  const interpolateBackgroundColor = {
    backgroundColor: switchTranslate.interpolate({
      inputRange: [0, getRightSwitch(scale)],
      outputRange: [
        inActiveTrackColor,
        disabled ? disabledColor : activeTrackColor,
      ],
    }),
  };
  const memoizedOnSwitchPressCallback = React.useCallback(() => {
    onPress && onPress(keyName, !value);
  }, [onPress, value, keyName]);

  return (
    <SPressable
      wait={debounce ? 300 : undefined}
      disabled={disabled}
      onPress={memoizedOnSwitchPressCallback}
    >
      <Animated.View
        style={[
          dynamicStyles.containerStyle(scale),
          interpolateBackgroundColor,
        ]}
      >
        <Animated.View
          style={[
            dynamicStyles.circleStyle(scale),
            { backgroundColor: thumbColor },
            {
              transform: [
                {
                  translateX: switchTranslate as any,
                },
              ],
            },
            styles.shadowValue,
          ]}
        />
      </Animated.View>
    </SPressable>
  );
};

const getSwitchWidth = (scale: number) => scale - 2 * getPadding(scale);
const getWidth = (scale: number) => Math.round((scale * 34) / 22);
const getPadding = (scale: number) => Math.round(scale / 11);
const getRightSwitch = (scale: number) =>
  getWidth(scale) - getSwitchWidth(scale) - 2 * getPadding(scale);

const SPressable = styled(UIButton)`
  padding: 16px;
  margin: -16px;
`;

const dynamicStyles: Record<string, (value: any) => ViewStyle> = {
  circleStyle: (scale: number) => ({
    width: getSwitchWidth(scale),
    height: getSwitchWidth(scale),
    borderRadius: getSwitchWidth(scale),
  }),
  containerStyle: (scale: number) => ({
    width: getWidth(scale),
    paddingVertical: getPadding(scale),
    paddingHorizontal: getPadding(scale),
    borderRadius: scale,
  }),
};

const styles = StyleSheet.create({
  shadowValue: {
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.23,
    shadowRadius: 2.62,
    elevation: 4,
  },
});
