import React, { ComponentProps, memo, ReactElement, useCallback } from "react";
import { StyleSheet } from "react-native";
import { EmptyListScrollView, ErrorView, RecycleList } from "../Common";
import { BottomBarView } from "../BottomBarView";
import { useSelector } from "react-redux";
import { normalizeStringForSearch } from "@true/core";
import { MEMBER_ROW_HEIGHT, MemberRow } from "./MemberRow";
import { memoMemberListSelectors } from "./selectors";

interface Props {
  search: string;
  onSelectItem?: (id: string, checked?: boolean) => void;
  hasExternalFooter?: boolean;
  userIds?: string[];
  rowRenderer?: (data: any, index?: number) => ReactElement;
  // @ts-ignore
  scrollProps?: ComponentProps<typeof RecycleList>["scrollProps"];
  refreshing?: boolean;
  onRefresh?: () => void;
  externalScrollView?: any;
  multiple?: boolean;
  selectedUserIds?: string[];
  defaultUsernames?: string[];
}

export const RecycleMemberList = memo((props: Props) => {
  const {
    search,
    // onSelectItem,
    hasExternalFooter,
    rowRenderer,
    userIds,
    onRefresh: onRawRefresh,
    refreshing,
    multiple,
    selectedUserIds,
    defaultUsernames,
  } = props;

  const usernames: string[] = useSelector((state: any) => {
    const ids = memoMemberListSelectors(
      state,
      normalizeStringForSearch(search),
      userIds || [],
      defaultUsernames
    );
    return multiple ? ids : ids.filter((id) => id != selectedUserIds?.[0]);
  });

  const _rowRenderer = useCallback(
    (data: any, index?: number) => {
      if (rowRenderer) {
        return rowRenderer(data, index);
      }
      // @ts-ignore
      return <MemberRow id={data} />;
    },
    [rowRenderer]
  );

  const onRefresh = useCallback(() => {
    onRawRefresh && onRawRefresh();
  }, [onRawRefresh]);

  const renderEmptyView = useCallback(() => {
    return (
      <EmptyListScrollView
        onRefresh={onRefresh}
        refreshing={!!refreshing}
        text={
          search.trim().length > 0 ? "common/no_match_found" : "common/no_data"
        }
      />
    );
  }, [onRefresh, refreshing, search]);

  const renderErrorView = useCallback(() => {
    return <ErrorView onRetry={onRefresh} />;
  }, [onRefresh]);

  const renderFooter = useCallback(() => {
    if (hasExternalFooter) return null;
    return <BottomBarView />;
  }, [hasExternalFooter]);

  // const { selectedIds } = UserPickerModalContext.useUsersTray();

  return (
    <>
      <RecycleList
        style={styles.list}
        data={usernames}
        itemHeight={MEMBER_ROW_HEIGHT}
        renderItem={_rowRenderer}
        loading={!!refreshing}
        isRefreshing={!!refreshing}
        isLoadMore={false}
        noMore={false}
        error={false}
        onLoadData={onRefresh}
        renderEmptyView={renderEmptyView}
        renderFooter={renderFooter}
        renderErrorView={renderErrorView}
        loadMorable={false}
        extendedState={undefined}
        scrollProps={props.scrollProps}
        externalScrollView={props.externalScrollView}
        refreshable={false}
      />
      {/*{multiple ? (*/}
      {/*  <AnimatedWrap*/}
      {/*    exitDuration={150}*/}
      {/*    enterOpacityDelay={300}*/}
      {/*    visible={selectedIds.size > 0}*/}
      {/*  >*/}
      {/*    <View style={{ height: getHeight(64) }} />*/}
      {/*  </AnimatedWrap>*/}
      {/*) : null}*/}
    </>
  );
});

const styles = StyleSheet.create({
  list: {
    flex: 1,
    // height: '100%',
    // width: '100%',
  },
});
