import PhotoViewContent from "./PhotoViewContent";
import * as React from "react";
import { ComponentProps, memo } from "react";
import { Platform } from "react-native";
import Modal, { ModalProps } from "react-native-modal";
import { styled, useStatusBarStackEntry } from "@true/core";
import ImageViewing from "./ImageViewing";
import { ImageSource } from "./ImageViewing/@types";

const SModal = styled(Modal)`
  margin: 0;
  width: 100%;
  height: 100%;
`;

type Props = {
  isVisible: boolean;
  onCloseRequest: () => any;
  images: ComponentProps<typeof PhotoViewContent>["images"];
  initialIndex?: ComponentProps<typeof PhotoViewContent>["initialIndex"];
  requestFn?: (image: string) => Promise<string>;
} & Partial<ModalProps>;

const PhotoViewModal = memo(
  ({
    isVisible,
    onCloseRequest,
    images,
    initialIndex,
    requestFn,
    ...props
  }: Props) => {
    useStatusBarStackEntry(isVisible, "#000000");

    if (Platform.OS === "ios") {
      return (
        <ImageViewing
          requestFn={requestFn}
          images={images.map((item) => ({
            uri: item,
          }))}
          imageIndex={initialIndex || 0}
          visible={isVisible}
          onRequestClose={onCloseRequest}
          swipeToCloseEnabled={Platform.OS === "ios"}
        />
      );
    }

    return (
      <SModal
        isVisible={isVisible}
        onBackdropPress={onCloseRequest}
        onDismiss={onCloseRequest}
        onBackButtonPress={onCloseRequest}
        onSwipeComplete={onCloseRequest}
        useNativeDriver={true}
        animationIn={"zoomIn"}
        animationOut={"zoomOut"}
        hideModalContentWhileAnimating={true}
        {...props}
      >
        <PhotoViewContent
          requestFn={requestFn}
          images={images}
          initialIndex={initialIndex}
          onCloseRequest={onCloseRequest}
        />
      </SModal>
    );
  }
);

PhotoViewModal.displayName = "PhotoViewModal";

export default PhotoViewModal;
