import {TableHeadOptions} from './types';
import {useEffect, useMemo, useState} from 'react';
import {decodeUnicode, encodeUnicode} from '@true/core';
import {ICustomFieldForm} from '../Inputs';

export const useTableHead = (
  valueHeader: string | undefined,
): TableHeadOptions[] => {
  if (!valueHeader) return [];
  return (valueHeader || '').split('--br--').map((item): TableHeadOptions => {
    let nameWithOptions = item.replace(/\(([0-9\.]+)\)$/, '').trim();
    const optionsMatch = nameWithOptions.match(/\[(.*)\]$/);
    const name = nameWithOptions.replace(/\[(.*)\]$/, '').trim();
    const options = optionsMatch
      ? optionsMatch[1]
          .split(',')
          .filter(option => !!option && option.trim().length >= 0)
          .map(option => option.trim())
      : undefined;
    const scaleMatch = item.match(/\(([0-9\.]+)\)$/);
    return {
      name,
      scale: scaleMatch ? Number(scaleMatch[1]) : undefined,
      type: options ? 'selector' : 'text',
      options,
    };
  });
};

const getTableDataFromForm = (
  form: ICustomFieldForm | string | undefined,
): any[] => {
  if (!form) return [];
  try {
    return JSON.parse(
      decodeUnicode(typeof form === 'string' ? form : form.value),
    );
  } catch (error) {
    return [];
  }
};

export const encodeTableData = (data: any[]): string => {
  return encodeUnicode(JSON.stringify(data));
};

export const useTableData = (valueRow: string | undefined): any[] => {
  return useMemo(() => getTableDataFromForm(valueRow), [valueRow]);
};

export const useTableDataState = (
  valueRow: string | string[][] | undefined,
): [string[][], React.Dispatch<React.SetStateAction<string[][]>>] => {
  const convertedData = useMemo(
    () =>
      typeof valueRow === 'string'
        ? getTableDataFromForm(valueRow)
        : valueRow || [],
    [valueRow],
  );
  const [state, setState] = useState(convertedData);
  useEffect(() => {
    setState(convertedData);
  }, [valueRow]);
  return [state, setState];
};
