import React, { memo, useMemo } from "react";
import { styled } from "@true/core";
import { IOnReplyPress, IRawComment, RawCommentItem } from "./Item";
import { useCommentsProvider } from "../Provider";
import { View } from "react-native";

interface Props {
  comment: IRawComment;
  onReplyPress?: (props: IOnReplyPress) => void;
  onLinkPress?: (href: string) => void;
  type: "comment" | "post";
  appName: string;
  index?: number;
  isLast?: boolean;
}

export const RawReplyItem = memo((props: Props) => {
  const { comment, onReplyPress, isLast, type } = props;
  const { itemLayout } = useCommentsProvider();

  const id = useMemo(
    () => (typeof comment !== "string" ? comment.hid : comment),
    [comment]
  );

  return (
    <SWrapper
      onLayout={(e) => {
        itemLayout.current = {
          ...itemLayout.current,
          [id]: e.nativeEvent.layout.height,
        };
      }}
    >
      <SLeft>
        {type === "comment" ? (
          <>
            {isLast ? null : <SDividerVertical />}
            <SCurveLine />
          </>
        ) : null}
      </SLeft>
      <RawCommentItem
        {...props}
        comment={comment}
        onReplyPress={onReplyPress as any}
        isReply={true}
        avatarSize={20}
        paddingLeft={0}
      />
    </SWrapper>
  );
});

const SWrapper = styled.View`
  z-index: 1000;
  flex-direction: row;
`;

const SLeft = styled.View`
  height: 100%;
  width: 32px;
  margin-right: 8px;
  margin-left: 16px;
  align-items: center;
`;

const SDividerVertical = styled.View`
  flex: 1;
  width: 1px;
  background-color: ${(p) => p.theme.neutral5};
`;

const SCurveLine = styled(View)`
  right: -6.5px;
  top: 4px;
  position: absolute;
  height: 16px;
  width: 30px;
  border-top-left-radius: 100px;
  border-left-width: 2px;
  border-top-width: 2px;
  border-color: ${(p) => p.theme.neutral5};
  transform: rotateZ(-90deg);
  z-index: 100;
`;
