import {ProfileModalContent} from './ProfileModalContent';
import * as React from 'react';
import {memo, PropsWithChildren} from 'react';
import Modal, {ModalProps} from 'react-native-modal';
import {useUser} from '@true/core';

interface Props extends Partial<ModalProps> {
  isVisible: boolean;
  userId: string;
  onCloseRequest?: () => void;
}

export const ProfileModal = memo(function ProfileModal({
  isVisible,
  userId,
  onCloseRequest,
  children,
  ...props
}: PropsWithChildren<Props>) {
  const user = useUser(userId);
  if (!user) return null;

  return (
    <Modal
      isVisible={isVisible}
      swipeDirection={'down'}
      onBackdropPress={onCloseRequest}
      onDismiss={onCloseRequest}
      onBackButtonPress={onCloseRequest}
      onSwipeComplete={onCloseRequest}
      useNativeDriver={true}
      animationIn={'zoomIn'}
      animationOut={'zoomOut'}
      hideModalContentWhileAnimating={true}
      {...props}>
      <ProfileModalContent userKey={userId} onCloseRequest={onCloseRequest}>
        {children}
      </ProfileModalContent>
    </Modal>
  );
});

ProfileModal.displayName = 'ProfileModal';
