import React, { memo, useCallback } from "react";
import { getHeight, getWidth, IC_DOCUMENT_OUTLINE, styled } from "@true/core";
import { GestureResponderEvent, TouchableOpacityProps } from "react-native";

const Wrapper = styled.TouchableOpacity`
  height: ${getHeight(56)}px;
  justify-content: center;
  padding: 0 ${getWidth(15)}px;
`;

export const BackButton = memo(function BackButton(
  props: TouchableOpacityProps
) {
  const onPress = useCallback(
    (event: GestureResponderEvent) => {
      if (props.onPress) {
        return props.onPress(event);
      }
      return;
    },
    [props.onPress]
  );

  return (
    <Wrapper onPress={onPress} {...props}>
      <BackImage source={IC_DOCUMENT_OUTLINE} />
    </Wrapper>
  );
});

const BackImage = styled.Image`
  tint-color: ${(props) => props.theme.neutral1};
`;
