import React, { memo, useCallback } from "react";
import {
  ImageSourcePropType,
  StyleSheet,
  TextInput,
  TouchableOpacity,
  ViewStyle,
} from "react-native";
import { getHeight, useCreateNavigate } from "@true/core";
import { RichTextEditorScreenProps } from "./RichTextEditorScreen";
import { HTMLRenderer } from "../../HTMLRender";
import { InputTemplate, SPlaceholder } from "../components/InputTemplate";

interface Props {
  innerRef?: React.RefObject<TextInput>;
  containerStyle?: ViewStyle;
  value: string;
  title: string;
  keyName: string;
  placeholder?: string;
  onValueChange: (keyName: string, value: string) => void;
  disabled?: boolean;
  iconRight?: ImageSourcePropType;
  onRightPress?: () => void;
  required?: boolean;
  type?: "normal" | "border";
  loading?: boolean;
  onSave?: (text: string) => void;
  showImageTabBar?: boolean;
}

export const MyRichTextInput = memo(function (props: Props) {
  // const inputRef = useRef<TextInput>(null);
  const {
    // innerRef = inputRef,
    placeholder,
    value,
    title,
    keyName,
    onValueChange,
    disabled,
    onSave,
  } = props;

  const navigation = useCreateNavigate<RichTextEditorScreenProps>(
    "RichTextEditorScreen"
  );

  const goToCompose = useCallback(() => {
    navigation({
      title,
      placeholder,
      value,
      onSubmit: (value) => {
        onValueChange(keyName, value);
        onSave?.(value);
      },
    });
  }, [onValueChange, keyName, onSave, value, title, placeholder]);

  return (
    <TouchableOpacity onPress={goToCompose} disabled={disabled}>
      <InputTemplate
        {...props}
        title={title}
        containerStyle={{ minHeight: 320 }}
      >
        {value ? (
          <HTMLRenderer
            htmlContent={value}
            containerStyle={styles.htmlContainer}
            // canEdit={true}
          />
        ) : (
          <SPlaceholder>{placeholder || "---"}</SPlaceholder>
        )}
      </InputTemplate>
    </TouchableOpacity>
  );
});

const styles = StyleSheet.create({
  htmlContainer: {
    marginTop: 5,
    flex: 1,
    marginBottom: getHeight(12),
    minHeight: 320,
  },
});

export * from "./RichTextEditorScreen";
