import React, { memo, useCallback, useRef, useState } from "react";
import { encodeTableData, useTableDataState, useTableHead } from "./hooks";
import { TableExample } from "./TableExample";
import { Alert, StyleSheet, View } from "react-native";
import { TableRowCreator } from "./TableRowCreator";
import {
  Colors,
  translate,
  useBoolean,
  useLatest,
  useNavigation,
  useNavigationParams,
  useTheme,
} from "@true/core";
import { MyButton } from "../Button";
import { ScreenWrapper } from "../ScreenWrap";
import { DynamicHeader } from "../Header";
import { TableHeadOptions } from "./types";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";

const AnimatedMyButton = Animated.createAnimatedComponent(MyButton);

const emptyArr: any[] = [];

interface Props {
  updateData: (keyName: string, value: string | string[][]) => void;
  valueHeader?: string | TableHeadOptions[];
  valueRow: string | string[][];
  returnType?: "string" | "array";
}

interface IFormId {
  formId: string;
}

interface IKeyName {
  keyName: string;
}

export type TableFieldEditorProps = Props & (IFormId | IKeyName);

export const TableFieldEditor = memo(function TableFieldEditor() {
  const {
    valueHeader,
    valueRow,
    updateData,
    returnType = "string",
    ...rest
  } = useNavigationParams<TableFieldEditorProps>();

  const { formId } = rest as IFormId;
  const { keyName } = rest as IKeyName;

  const theme = useTheme();
  const navigation = useNavigation();
  const goBack = useCallback(() => {
    navigation.goBack();
  }, []);

  const hasChange = useRef(false);

  const headOptions =
    typeof valueHeader === "string"
      ? useTableHead(valueHeader)
      : valueHeader || [];
  const [data, setData] = useTableDataState(valueRow);
  const latestData = useLatest(data);

  const [creatorVisible, showCreator, hideCreator] = useBoolean(false);
  const [formIndex, setFormIndex] = useState<number | undefined>(undefined);

  const onResetForm = useCallback(() => {
    setFormIndex(undefined);
  }, []);

  const onAddNewRow = useCallback(() => {
    onResetForm();
    showCreator();
  }, []);

  const onEdit = useCallback((index: number) => {
    setFormIndex(index);
    showCreator();
  }, []);

  const saveCall = useCallback(() => {
    const _data = returnType === "string" ? encodeTableData(data) : data;

    if (updateData) {
      updateData(keyName || `custom_${formId}`, _data);
      goBack();
    }
  }, [formId, updateData, data, keyName, returnType]);

  const onRemove = useCallback(
    (index: number) => {
      Alert.alert(
        translate("comp.updated_successfully"),
        translate("comp.delete_row_content", { item: index + 1 }),
        [
          {
            text: translate("comp.delete"),
            style: "destructive",
            onPress: () =>
              setData(latestData.current.filter((_, i) => i !== index)),
          },
          {
            text: translate("comp.cancel"),
            onPress: () => {},
          },
        ]
      );
    },
    [data]
  );

  const onUpdateRowData = useCallback((rowData: string[], index?: number) => {
    if (typeof index === "number") {
      const newData = [...latestData.current];
      newData[index] = rowData;
      setData(newData);
    } else {
      setData([...latestData.current, rowData]);
    }
    onResetForm();
    hideCreator();
    hasChange.current = true;
  }, []);

  const { aBottom } = useAnimatedSafeAreaInsets();
  const animatedStyles = useAnimatedStyle(() => ({
    marginBottom: aBottom.value + 16,
  }));

  return (
    <ScreenWrapper>
      <DynamicHeader
        title={
          valueRow == ""
            ? translate("comp.add_table")
            : translate("comp.edit_table")
        }
      />

      <TableExample
        headOptions={headOptions}
        indexVisible={true}
        data={data}
        contentContainerStyle={{ paddingBottom: 50 }}
        editable={true}
        onEdit={onEdit}
        onRemove={onRemove}
        onPressNewRow={onAddNewRow}
      />

      {creatorVisible && (
        <TableRowCreator
          visible={creatorVisible}
          index={formIndex}
          rowData={
            typeof formIndex === "number"
              ? data[formIndex] || emptyArr
              : emptyArr
          }
          onClose={hideCreator}
          headOptions={headOptions}
          onSave={onUpdateRowData}
        />
      )}

      <View style={styles.wrapButton}>
        <AnimatedMyButton
          style={animatedStyles}
          title={translate("comp.save")}
          backgroundColor={Colors.blue1}
          textColor={Colors.white}
          keyName={"1"}
          onPress={saveCall}
        />
      </View>
    </ScreenWrapper>
  );
});
const styles = StyleSheet.create({
  wrapButton: {
    paddingLeft: 16,
    paddingRight: 16,
  },
});

export * from "./TableFieldViewer";
export * from "./hooks";
export * from "./types";
