import { TextStyle } from "react-native";
import React, { memo } from "react";
import { styled } from "@true/core";
import moment from "moment";

interface Props {
  timeStamp: string | number | undefined;
  showHour?: number;
  format?: string;
  textStyle?: TextStyle;
  invalidTimeText?: string;
}

const Title = styled.Text`
  color: ${(props) => props.theme.neutral1};
  font-size: 16px;
`;

const SInvalidText = styled.Text`
  color: ${(props) => props.theme.neutral6};
  font-size: 16px;
`;

export const CreatedAt = memo((props: Props) => {
  const { timeStamp, textStyle, format, showHour, invalidTimeText } = props;

  if (!timeStamp) {
    return null;
  }
  return timeStamp != 0 ? (
    <Title style={textStyle}>
      {moment(timeStamp, "X").format(
        format || (showHour ? "DD/MM/YYYY HH:mm" : "DD/MM/YYYY")
      )}
    </Title>
  ) : (
    <SInvalidText>{invalidTimeText || ""}</SInvalidText>
  );
});
