import React, {memo, useCallback} from 'react';
import {MemberRow, MemberRowProps} from './MemberRow';
import {styled} from '@true/core';
import {DynamicCheckBox} from '../DynamicCheckbox';

const CheckboxWrapper = styled.View`
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  padding-left: 16px;
`;

export interface SelectableMemberRowProps extends MemberRowProps {
  selected: boolean;
  multiple: boolean;
  onChange: (newValue: boolean) => any;
}
export const SelectableMemberRow = memo(function SelectableMemberRow({
  selected,
  multiple,
  onChange,
  ...props
}: SelectableMemberRowProps) {
  const onPress = useCallback(() => {
    onChange(!selected);
  }, [selected, onChange]);

  return (
    <MemberRow
      onPress={onPress}
      selected={selected}
      multiple={multiple}
      {...props}
      rightElement={
        <CheckboxWrapper>
          <DynamicCheckBox
            disabled={true}
            value={selected}
            multiple={multiple}
          />
        </CheckboxWrapper>
      }
    />
  );
});
