import React, { memo } from "react";
import { styled, useAnimatedWindow } from "@true/core";
import { ViewProps, ViewStyle } from "react-native";
import Animated, { useAnimatedStyle } from "react-native-reanimated";

interface OwnProps {
  containerStyle?: ViewStyle | ViewStyle[];
  fullScreen?: boolean;
  children?: (JSX.Element | null) | (JSX.Element | null)[];
}

type Props = OwnProps & ViewProps;

const Container = styled(Animated.View)`
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  overflow: hidden;
  background-color: ${(p) => p?.theme?.backgroundColor || "#fff"};
`;

// const ContainerAnimated = Animated.createAnimatedComponent(Container);

export const BottomMenuContainer = memo(
  ({ children, containerStyle, fullScreen, ...rest }: Props) => {
    const { height } = useAnimatedWindow();
    const animatedStyles = useAnimatedStyle(() => ({
      maxHeight: height.value,
    }));
    return (
      <Container {...rest} style={[animatedStyles, containerStyle]}>
        {children}
      </Container>
    );
  }
);
