import React, { memo, useCallback } from "react";
import { TextStyle, TouchableOpacity, ViewStyle } from "react-native";
import {
  Fonts,
  getFontSize,
  getHeight,
  getWidth,
  IC_X,
  interactManager,
  KeyboardService,
  styled,
} from "@true/core";

const Container = styled.View`
  width: 100%;
`;

const ContentContainer = styled.View`
  width: 100%;
  height: ${getHeight(52)}px;
  flex-direction: row;
  align-items: center;
  padding: 0 ${getWidth(16)}px;
`;

const SText = styled.Text`
  flex: 1;
  font-size: ${getFontSize(17)}px;
  color: ${(p) => p?.theme?.neutral1 || "#111"};
  padding-right: ${getWidth(8)}px;
  font-family: ${Fonts.Medium};
`;

const SDivider = styled.View`
  width: 100%;
  height: 1px;
  background-color: ${(p) => p?.theme?.neutral5 || "#111111"};
`;

const Icon = styled.Image`
  width: ${getHeight(24)}px;
  height: ${getHeight(24)}px;
  tint-color: ${(p) => p?.theme?.neutral1 || "#111"};
`;

interface OwnProps {
  title: string;
  onClose?: () => void;
  noDivider?: boolean;
  containerStyle?: ViewStyle;
  textStyle?: TextStyle;
}

type Props = OwnProps;

export const BottomMenuHeader = memo(
  ({ title, onClose, textStyle, noDivider, containerStyle }: Props) => {
    const _onClose = useCallback(() => {
      KeyboardService.dismiss();
      interactManager(() => onClose?.(), KeyboardService.isShown ? 150 : 0);
    }, []);
    return (
      <Container>
        <ContentContainer style={containerStyle}>
          <SText numberOfLines={1} ellipsizeMode="tail" style={textStyle}>
            {title}
          </SText>
          <TouchableOpacity hitSlop={16} onPress={_onClose}>
            <Icon source={IC_X} />
          </TouchableOpacity>
        </ContentContainer>
        {!noDivider && <SDivider />}
      </Container>
    );
  }
);
