import React, { memo, useCallback, useMemo } from "react";
import {
  Fonts,
  getUser,
  getUserGroup,
  IC_TICK,
  stripSpecialCharacters,
  styled,
  useTheme,
} from "@true/core";
import { TouchableOpacity } from "react-native";
import { IListItem } from "../types";
import { UserAvatarItem } from "../UsersRow/UserAvatarItem";
import Animated, {
  useAnimatedStyle,
  useDerivedValue,
  withTiming,
} from "react-native-reanimated";
import { UICheckbox } from "../../UICheckbox";

const AnimatedBtn = Animated.createAnimatedComponent(TouchableOpacity);

export const Item = memo((props: IListItem) => {
  const { item, type, onPress, selectedIds, multiple, isHeaderItem } = props;

  const theme = useTheme();

  const userName = useMemo(
    () =>
      stripSpecialCharacters(
        type == "user" ? getUser(item)?.name : getUserGroup(item)?.name
      ),
    [type, item]
  );

  const subTitle = useMemo(() => {
    if (type == "user") {
      return stripSpecialCharacters(getUser(item)?.title);
    }
    const _userGroup = getUserGroup(item);
    const _user = getUser(_userGroup?.user_id || "");

    if (_userGroup?.metatype == "user")
      return stripSpecialCharacters(_user?.title || "");
    return stripSpecialCharacters(_user?.name || "");
  }, [type, item]);

  const _onPress = useCallback(() => {
    onPress?.(item);
  }, [item]);

  const animatedBackgroundStyle = useAnimatedStyle(
    () => ({
      backgroundColor:
        selectedIds.value.indexOf(item) !== -1
          ? withTiming(theme.neutral8, { duration: 100 })
          : withTiming(theme.backgroundColor, { duration: 100 }),
    }),
    [item, theme]
  );

  const isSelected = useDerivedValue(
    () => selectedIds.value.indexOf(item) !== -1,
    [item]
  );

  if (isHeaderItem && item == undefined) {
    return null;
  }

  return (
    <SContainer style={animatedBackgroundStyle} onPress={_onPress}>
      <UserAvatarItem type={type} id={item} size={32} />
      <SContentWrapper>
        <STitleWrapper>
          <STitle>{userName || ""}</STitle>
          {subTitle ? <SSubTitle>{subTitle || ""}</SSubTitle> : null}
        </STitleWrapper>
        {multiple ? (
          <UICheckbox value={isSelected} />
        ) : isHeaderItem ? (
          <SImage source={IC_TICK} />
        ) : null}
      </SContentWrapper>
    </SContainer>
  );
});

const SContainer = styled(AnimatedBtn)`
  flex-direction: row;
  padding: 0 16px;
  align-items: center;
  overflow: hidden;
`;
const SContentWrapper = styled.View`
  flex-direction: row;
  padding: 8px 0;
  margin-left: 8px;
  align-items: center;
  border-bottom-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
  min-height: 56px;
  flex: 1;
`;

const STitleWrapper = styled.View`
  justify-content: center;
  flex: 1;
`;

const STitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Medium};
  color: ${(p) => p.theme.neutral1};
`;
const SSubTitle = styled.Text`
  font-size: 12px;
  line-height: 16px;
  color: ${(p) => p.theme.neutral2};
`;

const SImage = styled.Image`
  tint-color: ${(p) => p.theme.primaryColor};
`;
