import { memoForwardRef, styled, useBoolean } from "@true/core";
import React, {
  RefObject,
  useCallback,
  useImperativeHandle,
  useMemo,
} from "react";
import { UIForm } from "../UIForm";
import { UIDateTimeInputProps } from "./types";
import DateTimePickerModal from "react-native-modal-datetime-picker";
import { ReText } from "react-native-redash";
import {
  runOnJS,
  SharedValue,
  useAnimatedReaction,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
} from "react-native-reanimated";
import moment from "moment";
import { MODE_FORMAT, MODE_ICON } from "./constant";
import { CONTENT_FONT_SIZE, CONTENT_LINE_HEIGHT } from "../UIForm/constant";

export interface UIDateTimeInput {
  onClearValue: () => void;
}

export const UIDateTimeInput = memoForwardRef(
  (props: UIDateTimeInputProps, ref: RefObject<UIDateTimeInput>) => {
    const {
      value = null,
      mode,
      format,
      onChangeValue,
      keyName,
      dateTimePickerProps,
      hideRightIconWhileTyping = true,
      hideRightIcon,
      params,
      placeholder,
    } = props;

    const [visible, show, hide] = useBoolean();

    /* -- set content by params if there 's params -- */
    const rawValue = params
      ? (useDerivedValue(
          () => params.value?.[keyName] || null,
          [keyName, params]
        ) as SharedValue<any>)
      : useSharedValue(value);

    const text = useSharedValue(
      moment(rawValue.value, "X").format(MODE_FORMAT[mode])
    );

    const setText = useCallback(
      (_val: number | null) => {
        const _m = moment(_val, "X");
        text.value = _m.isValid() ? _m.format(MODE_FORMAT[mode]) : "";
      },
      [mode]
    );

    useAnimatedReaction(
      () => rawValue.value,
      (_val) => {
        runOnJS(setText)(_val);
      },
      [mode]
    );

    const date = useMemo(() => {
      if (!value) {
        return new Date();
      }
      return moment(value, "X").toDate();
    }, [value]);

    const _onChangeValue = useWorkletCallback(
      (keyName: string, value: any) => {
        if (params) {
          params.value = { ...params.value, [keyName]: value };
        }
        onChangeValue?.(keyName, value);
      },
      [onChangeValue, params]
    );

    const onConfirm = useCallback(
      (date: Date) => {
        hide();
        requestAnimationFrame(() => {
          if (!params) rawValue.value = moment(date).unix();
          _onChangeValue(keyName, moment(date).unix());
        });
      },
      [mode, _onChangeValue, keyName, format, params]
    );

    const onClearDate = useCallback(() => {
      if (!params) rawValue.value = null;
      _onChangeValue(keyName, null);
    }, [rawValue, keyName, params]);

    // const mounted = useMounted(100)

    useImperativeHandle(ref, () => ({
      onClearValue: onClearDate,
    }));

    return (
      <>
        <UIForm.Input
          {...props}
          hideRightIconWhileTyping={hideRightIconWhileTyping}
          onPress={show}
          onClearValue={onClearDate}
          ContentElement={
            <SReText text={text} placeholder={placeholder || "---"} />
          }
          inputType={"selector"}
          textValue={text}
          rightIconSource={hideRightIcon ? null : MODE_ICON[mode]}
        />
        <DateTimePickerModal
          display={mode === "time" ? "spinner" : "inline"}
          mode={mode}
          isVisible={visible}
          onConfirm={onConfirm}
          onCancel={hide}
          date={date}
          {...dateTimePickerProps}
        />
      </>
    );
  }
);

const SReText = styled(ReText).attrs((p) => ({
  placeholderTextColor: p.theme.neutral3,
}))`
  color: ${(p) => p.theme.neutral1};
  font-size: ${CONTENT_FONT_SIZE}px;
  line-height: ${CONTENT_LINE_HEIGHT}px;
  padding: 0;
`;
