import { useCallback, useMemo, useRef } from "react";
import constate from "constate";
import {
  runOnJS,
  runOnUI,
  useAnimatedReaction,
  useSharedValue,
  useWorkletCallback,
} from "react-native-reanimated";
import { FileType, ToastService } from "@true/core";
import { UIAttachmentInputProps } from "./types";
import { BottomSheetModal } from "@gorhom/bottom-sheet";
import { MAX_FILE } from "../UIAttachmentInput";

const callbackOnJs = (fn: any) => {
  runOnJS(fn)();
};

const getRawValue = (value: FileType | FileType[] | undefined) =>
  Array.isArray(value)
    ? value
    : value !== undefined && value !== null
    ? [value]
    : [];

const _provider = (props: UIAttachmentInputProps) => {
  const {
    value,
    params,
    keyName,
    multiple,
    onChangeValue,
    maxFile = MAX_FILE,
  } = props;

  const bottomSheetRef = useRef<BottomSheetModal>(null);

  const files = useSharedValue(getRawValue(value));

  /* -- set files by params -- */
  if (params) {
    const setFiles = useCallback((_value: FileType | FileType[]) => {
      files.value = getRawValue(_value);
    }, []);

    useAnimatedReaction(
      () => params.value,
      (_params) => {
        runOnJS(setFiles)(_params?.[keyName]);
      },
      [keyName]
    );
  }

  const _onChangeValue = useWorkletCallback(
    (value: any) => {
      "worklet";
      if (params) {
        return (params.value = { ...params.value, [keyName]: value });
      }
      onChangeValue?.(keyName, value);
    },
    [keyName, onChangeValue, params]
  );

  const onDismiss = useCallback(() => {
    bottomSheetRef.current?.dismiss();
  }, [bottomSheetRef.current?.dismiss]);

  const onAddFile = useCallback(
    (_files: FileType[]) => {
      runOnUI((_files) => {
        "worklet";
        if (multiple) {
          const newFiles = [...files.value, ..._files];

          if (newFiles.length > maxFile) {
            return runOnJS(ToastService.show)(
              `Maximum files you can pick is ${maxFile}, try again`
            );
          }

          if (!params) files.value = newFiles;
          _onChangeValue(newFiles);
          return runOnJS(onDismiss)();
        }

        if (!params) files.value = _files;
        _onChangeValue(_files[0]);
        return runOnJS(onDismiss)();
      })(_files);
    },
    [_onChangeValue, params, multiple, bottomSheetRef, maxFile]
  );

  const onRemoveFile = useCallback(
    (_file: FileType) => {
      runOnUI((_file: FileType) => {
        "worklet";
        if (multiple) {
          const newFiles = files.value.filter((item) => item.uri !== _file.uri);
          if (!params) files.value = newFiles;
          return _onChangeValue(newFiles);
        }

        if (!params) files.value = [];
        return _onChangeValue(null);
      })(_file);
    },
    [multiple, _onChangeValue, params]
  );

  return useMemo(
    () => ({
      files,
      onAddFile,
      bottomSheetRef,
      onRemoveFile,
    }),
    [files, onAddFile, bottomSheetRef, onRemoveFile]
  );
};

export const [UIAttachmentProvider, useUIAttachment] = constate(
  _provider,
  (val) => val
);
