import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { EFlashViewTypes, IFlashItem, IFlashSection } from "./types";

interface Props<T, F> {
  data: T[];
  mapKey?: string;
  map?: Record<string, F[]>;
  renderSection?: (props: IFlashSection<T>) => JSX.Element | null;
  extraData?: any;
}

let INDEX = -1;

export const useUIFlashList = <T, F>({
  renderSection,
  data: pData,
  map,
  mapKey,
  extraData,
}: Props<T, F>) => {
  // const {  } = props;
  const [visible, setVisible] = useState<Record<string, any>>({});

  useEffect(() => {
    if (renderSection && pData) {
      if (pData.length && map === undefined) {
        throw new Error("SectionList must have 'map' props");
      }

      if (
        mapKey &&
        pData.length &&
        // @ts-ignore
        pData[0]?.[mapKey] === undefined
      )
        console.error(`Warning: there is no mapKey: '${mapKey}' in item`);
      let _visible: Record<string, any> = {};
      for (let section in pData) {
        _visible[section] = true;
      }

      setVisible(_visible);
    }
  }, [pData.length]);

  const toggle = useCallback((section: string) => {
    setVisible((prev) => ({
      ...prev,
      [section]: !prev?.[section],
    }));
  }, []);

  const hide = useCallback((section: string) => {
    setVisible((prev) => ({
      ...prev,
      [section]: false,
    }));
  }, []);

  const show = useCallback((section: string) => {
    setVisible((prev) => ({
      ...prev,
      [section]: true,
    }));
  }, []);

  const byKey = useRef<{ [id: string]: any }>({});

  /** Error: Blank space appears when data changes
   * Solve: add 1 any item at last of data and return
   * null if item === 'lastItem'*/
  const data = useMemo(() => {
    if (!Array.isArray(pData)) return [];
    if (pData.length === 0) return [];
    if (!renderSection) return [...pData];

    let result: any[] = [];
    for (let sectionIndex = 0; sectionIndex < pData.length; sectionIndex++) {
      const section = pData[sectionIndex];

      byKey.current = {
        ...byKey.current,
        [JSON.stringify(section)]: {
          data: section,
          type: EFlashViewTypes.SECTION,
          index: sectionIndex,
        },
      };
      result.push(section);

      const _key = mapKey
        ? // @ts-ignore
          `${section[mapKey]}`
        : `${section}`;

      const itemList = map?.[_key];
      if (itemList && visible?.[sectionIndex]) {
        itemList.forEach((item, _index) => {
          byKey.current = {
            ...byKey.current,
            [JSON.stringify(item)]: {
              data: item,
              type: EFlashViewTypes.ITEM,
              index: _index,
              isLast: _index === itemList.length - 1,
            },
          };
          result.push(item);
        });
      }
    }
    return [...result];
  }, [visible, pData, map]);

  const stickyHeaderIndices = useMemo(() => {
    return data
      .map((item: any, index: number) => {
        if (
          byKey.current?.[JSON.stringify(item)]?.type ===
          EFlashViewTypes.SECTION
        ) {
          return index;
        }
      })
      .filter((index: any) => index !== undefined) as number[];
  }, [data, extraData]);

  return useMemo(
    () => ({
      data,
      byKey,
      stickyHeaderIndices,
      visible,
      toggle,
      hide,
      show,
    }),
    [data, byKey, stickyHeaderIndices, visible, toggle, show, hide]
  );
};
