import React, { memo } from "react";
import { FullScreenWrapper } from "../../ScreenWrap";
import { Provider } from "../Provider";
import { useNavigationParams } from "@true/core";
import { PreviewCommentProps } from "./provider";
import { CommentList } from "../CommentList";
import { CommentInput } from "../CommentInput";
import { CommentBottomSheet } from "../CommentBottomSheet";
import { DynamicHeader } from "../../Header";

export const CommentScreen = memo(() => {
  const { hid, token, appName, type } =
    useNavigationParams<PreviewCommentProps>();

  return (
    <FullScreenWrapper>
      <Provider appName={appName} type={type} hid={hid} token={token}>
        <DynamicHeader title={type == "comment" ? "Comments" : "Discussions"} />
        <CommentList />
        <CommentInput />
        <CommentBottomSheet />
      </Provider>
    </FullScreenWrapper>
  );
});
