import React, {memo, PropsWithChildren} from 'react';
import {useKeyboard} from '@true/core';
import {AnimatedWrap} from '../AnimatedWrap';
import {Platform} from 'react-native';

interface Props extends PropsWithChildren {
  mode?: ('height' | 'opacity')[];
}

export const HideWhenAndroidKeyboardShowView = memo((props: Props) => {
  const {mode} = props;
  const {isShown} = useKeyboard();
  return Platform.OS === 'android' ? (
    <AnimatedWrap
      mode={mode}
      enterDuration={100}
      exitDuration={0}
      visible={!isShown}>
      {props.children}
    </AnimatedWrap>
  ) : (
    <>{props.children}</>
  );
});
