import React, { memo, useCallback } from "react";
import { StyleSheet, Text, View, ViewStyle } from "react-native";
import {
  Colors,
  getFontSize,
  styled,
  styledValue,
  translate,
  useCreateNavigate,
} from "@true/core";
import { ICustomFieldForm } from "../Inputs";

import { NavActionButton } from "../Button";
import { TableFieldEditorProps, TableHeadOptions } from "../TableFieldEditor";

interface CheckboxBottomProps {
  title: string;
  value: string | string[][];
  containerStyle?: ViewStyle;
  required?: boolean | undefined;
  onValueChange: (keyName: string, value: string | string[][]) => void;
  onSave?: (value: string | string[][]) => void;
  returnType?: "string" | "array";
}

interface IKeyName {
  keyName: string;
  header: string | TableHeadOptions[];
}

interface IItem {
  item: ICustomFieldForm;
}

type Props = CheckboxBottomProps & (IKeyName | IItem);

export const MyTableInput = memo(function ({
  title,
  value,
  containerStyle,
  required,
  onValueChange,
  returnType = "string",
  onSave,
  ...rest
}: Props) {
  const { keyName, header } = rest as IKeyName;
  const { item } = rest as IItem;
  const navigation =
    useCreateNavigate<TableFieldEditorProps>("TableFieldEditor");

  const onPress1 = useCallback(
    (keyName: string, value: any) => {
      onValueChange(keyName, value);
      onSave && onSave(value);
    },
    [onValueChange, onSave]
  );

  const onPress = useCallback(() => {
    navigation({
      formId: item?.id || "",
      keyName,
      valueHeader: item?.placeholder || header,
      valueRow: value,
      updateData: (keyName, value) => {
        onPress1(keyName, value);
      },
      returnType,
    });
  }, [value, item, onPress1, keyName, returnType]);

  return (
    <View style={containerStyle}>
      <ContainerButton onPress={onPress}>
        <Row>
          <View style={styles.btnViewTitle}>
            <SGrey3Text>
              {title}{" "}
              {required ? <Text style={{ color: Colors.red1 }}>*</Text> : ""}
            </SGrey3Text>
          </View>
        </Row>
        <NavActionButton
          title={
            value == ""
              ? translate("comp.add_table")
              : translate("comp.edit_table")
          }
          containerStyle={styles.textOpen}
          textStyle={{ color: Colors.blue1, marginLeft: -4 }}
          activeBold={true}
          disabled={true}
        />
      </ContainerButton>
    </View>
  );
});

const ContainerButton = styled.TouchableOpacity`
  min-height: 62px;
  border-width: 1px;
  border-color: ${Colors.neutral5};
  border-radius: 4px;
  padding-top: 2px;
  padding-left: 4px;
`;

const Row = styled.View`
  flex-direction: row;
`;

const SGrey3Text = styled.Text`
  color: ${styledValue.neutral1};
  font-size: ${getFontSize(14)}px;
  line-height: ${getFontSize(18)}px;
`;

const styles = StyleSheet.create({
  textOpen: {
    flex: 1,
    marginLeft: 16,
    marginBottom: 8,
  },
  btnViewTitle: {
    flex: 1,
    paddingTop: 8,
    paddingBottom: 8,
    paddingLeft: 12,
  },
});
