import create from 'zustand';

interface IFloatModal {
  routeState: {[name: string]: IRouteState}
  setState: (state: Partial<IRouteState>, name: string) => void
}

interface ActionProps {
  color?: string;
  icon?: any;
  name: string;
  text?: string;
  textBackground?: string;
  textColor?: string;
  component?: Function;
  animated?: boolean;
}

export interface IRouteState {
  isPressed: boolean;
  visible: boolean;
  actions?: ActionProps[];
  onPressItem?: (itemName: string) => void;
  hasTabBar?: boolean;
  floatingIcon?: any;
  isFirstPressed?: boolean
}


export const useFloatModalStore = create<IFloatModal>((set) => ({
  routeState: {},
  setState: (newState, keyName) => {
    set(state => ({
      ...state,
      routeState: {
        ...state.routeState,
        [keyName]: {
          ...state.routeState?.[keyName],
          ...newState
        }
      }
    }))
  },
}));
