import React, { memo, MutableRefObject, useMemo } from "react";
import { View } from "react-native";
import { EFlashViewTypes, IFlashItem, IFlashSection } from "./types";

interface ItemProps {
  renderItem: (props: IFlashItem<any>) => JSX.Element;
  renderSection?: (props: IFlashSection<any>) => JSX.Element | null;
  item: any;
  index: number;
  dataSize: number;
  toggle: (section: string) => void;
  show: (section: string) => void;
  hide: (section: string) => void;
  visible: { [id: string]: boolean };
  byKey: MutableRefObject<{ [id: string]: any }>;
  isLastItem?: boolean;
  mapKey?: string;
}

export const FlashItemRender = memo((props: ItemProps) => {
  const {
    renderItem,
    renderSection,
    index,
    item,
    toggle,
    visible,
    show,
    hide,
    byKey,
    dataSize,
    isLastItem,
    mapKey,
  } = props;

  if (item === "lastItem") return null;

  if (!renderSection) {
    return (
      <View key={index}>
        {renderItem({
          item,
          index,
          rawIndex: index,
          isLast: index === dataSize - 1,
        })}
      </View>
    );
  }

  const _item = useMemo(() => byKey.current[JSON.stringify(item)], [item]);

  if (_item.type === EFlashViewTypes.SECTION && renderSection) {
    return (
      <View key={index}>
        {renderSection({
          item: _item.data,
          toggle: () => toggle(_item.index),
          show: () => show(_item.index),
          hide: () => hide(_item.index),
          index: _item.index,
          rawIndex: index,
          visible: visible[_item.index],
        })}
      </View>
    );
  }

  if (_item.type === EFlashViewTypes.ITEM) {
    return (
      <View key={index}>
        {renderItem({
          item: _item.data,
          index: _item.index,
          rawIndex: index,
          isLast: isLastItem,
        })}
      </View>
    );
  }

  return null;
});
