import { useCallback, useMemo, useRef } from "react";
import { LayoutChangeEvent, NativeScrollEvent } from "react-native";
import { useAnimatedScrollableView } from "../index";
import { interactManager } from "@true/core";
import {
  runOnJS,
  useAnimatedReaction,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
} from "react-native-reanimated";
import { useSingleTabView } from "../SingleTabView/provider";

export const useAuToScroll = (props: {
  _ref: any;
  index: number;
  singleListIndex?: number;
}) => {
  const { index, _ref, singleListIndex } = props;

  const {
    tabBarHeight,
    headerHeight,
    onScroll: aOnScroll,
    level,
    pIndex,
    aLevelIndex,
    aScrollVal,
    scrollValMap,
    aHeight,
  } = useAnimatedScrollableView();

  // const { headerHeightMap, tabBarHeightMap, totalScrollMap, aHeightMap } =
  //   useTotalScrollableTabView();

  const { currentIndex, singleHeaderHeight, singleTabBarHeight } =
    useSingleTabView();

  /**
   * header style
   */
  // const _aHeight = useDerivedValue(() =>  aHeight.value, []);

  const _headerHeight = useDerivedValue(
    () => headerHeight.value + (singleHeaderHeight?.value || 0),
    []
  );

  const _minHeight = useDerivedValue(
    () => (tabBarHeight.value || 0) - (headerHeight.value || 0),
    []
  );

  // const _tabBarHeight = useDerivedValue(() => tabBarHeight.value || 0, []);

  const containerHeight = useSharedValue(0);

  const onContainerListLayout = useCallback((e: LayoutChangeEvent) => {
    containerHeight.value = e.nativeEvent.layout.height;
  }, []);

  /**
   * autoScroll
   */
  const levelIndex = useMemo(() => index.toString(), [level, index, pIndex]);

  const onAutoScroll = useCallback(
    (e: NativeScrollEvent) => {
      "worklet";
      if (
        aLevelIndex.value == levelIndex &&
        ((currentIndex?.value == undefined && singleListIndex == undefined) ||
          currentIndex.value == singleListIndex)
      ) {
        // console.log(index, singleListIndex)
        aOnScroll(index)(e);
      }
    },
    [index, aOnScroll, levelIndex, singleListIndex, currentIndex]
  );

  const scrollToTimeout = useRef<any>(null);

  const autoScrollTo = useCallback(
    (val: number, animated: boolean = false) => {
      interactManager(
        () => {
          _ref.current?.scrollToOffset?.({ offset: val, animated });
          _ref.current?.scrollTo?.({ y: val, animated });
        },
        100,
        true,
        scrollToTimeout
      );
    },
    [_ref.current, scrollToTimeout]
  );

  // useEffect(() => {
  //
  // }, [])

  // useAnimatedReaction(() => footerHeight.value, (_footer, _prevFooter) => {
  //   runOnJS(autoScrollTo)(Math.min(scrollValMap?.value, -_minHeight.value));
  // }, [])

  useAnimatedReaction(
    () => ({
      _scrollVal: aScrollVal.value,
      _aLevelIndex: aLevelIndex.value,
      _currentIndex: currentIndex?.value,
      // _totalScrollMap: totalScrollMap?.value,
      // _footer: footerHeight.value,
    }),
    (cur, prev) => {
      const { _scrollVal, _aLevelIndex, _currentIndex } = cur;
      if (
        _aLevelIndex !== levelIndex ||
        (_currentIndex !== undefined &&
          singleListIndex !== undefined &&
          _aLevelIndex === levelIndex &&
          _currentIndex !== singleListIndex)
      ) {
        // const _prevFooter =
        //   prev?._footer === undefined ? _footer : prev._footer;
        //  currentScrollVal;
        // if (level !== undefined && pIndex !== undefined) {
        //   currentScrollVal = totalScrollMap?.value?.[level]?.[pIndex]?.[index];
        // } else {
        const currentScrollVal = scrollValMap?.value?.[index];
        // }

        if (
          -aHeight.value >= currentScrollVal ||
          currentScrollVal <= -_minHeight.value ||
          currentScrollVal === undefined
        ) {
          if (_aLevelIndex !== levelIndex) {
            runOnJS(autoScrollTo)(Math.min(_scrollVal, -_minHeight.value));
          } else if (
            _currentIndex !== undefined &&
            singleListIndex !== undefined &&
            _aLevelIndex === levelIndex &&
            _currentIndex !== singleListIndex
          ) {
            const min = headerHeight.value + (singleTabBarHeight?.value || 0);

            runOnJS(autoScrollTo)(Math.min(_scrollVal, min));
          }
        } else {
          runOnJS(autoScrollTo)(
            currentScrollVal - aHeight.value + _minHeight.value
          );
        }
      }
    },
    [levelIndex, singleListIndex, index]
  );

  const onScrollBeginDrag = useWorkletCallback(() => {
    aLevelIndex.value = levelIndex;
  }, [levelIndex]);

  return useMemo(
    () => ({
      levelIndex,
      onAutoScroll,
      _headerHeight,
      onScrollBeginDrag,
      // onContentSizeChange,
      _minHeight,
      // _tabBarHeight,
      // footerHeight,
      // contentHeight,
      _aHeight: aHeight,
      onContainerListLayout,
      containerHeight,
    }),
    [
      levelIndex,
      onAutoScroll,
      _headerHeight,
      onScrollBeginDrag,
      // onContentSizeChange,
      _minHeight,
      aHeight,
      // _tabBarHeight,
      // footerHeight,
      // contentHeight,
      // _aHeight,
      onContainerListLayout,
      containerHeight,
    ]
  );
};
