import React, { memo } from "react";
import { ITab, useSingleTabView } from "./provider";
import { styled } from "@true/core";
import Animated, {
  SharedValue,
  useAnimatedProps,
  useAnimatedStyle,
} from "react-native-reanimated";

export const Content = memo(() => {
  const { tabs, currentIndex } = useSingleTabView();

  return (
    <SContainer>
      {tabs.map((item, index) => (
        <Item
          key={index}
          item={item}
          index={index}
          currentIndex={currentIndex}
        />
      ))}
    </SContainer>
  );
});

const SContainer = styled.View`
  flex: 1;
  width: 100%;
`;

const Item = memo(
  ({
    currentIndex,
    item,
    index,
  }: {
    item: ITab;
    index: number;
    currentIndex: SharedValue<number>;
  }) => {
    const styles = useAnimatedStyle(() => {
      return {
        opacity: index == currentIndex.value ? 1 : 0,
      };
    }, [index]);

    const aProps = useAnimatedProps(
      () => ({
        pointerEvents: index == currentIndex.value ? "auto" : "none",
      }),
      [index]
    ) as any;

    return (
      <SItemContainer animatedProps={aProps} style={styles}>
        {item.content({ singleListIndex: index })}
      </SItemContainer>
    );
  }
);

const SItemContainer = styled(Animated.View)`
  overflow: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
`;
