import {
  IComment,
  memoForwardRef,
  requestLoadComment,
  styled,
  useAutoToastError,
  useCommentsByQuery,
  usePostsByQuery,
} from "@true/core";
import React, {
  RefObject,
  useCallback,
  useEffect,
  useImperativeHandle,
  useMemo,
} from "react";
import { useAsyncFn } from "react-use";
import { RawCommentItem } from "../UIComps/Item";
import { usePreviewProps } from "./hooks";
import { PreviewCommentProps } from "./provider";
import { navigateToCommentScreen } from "./utils";
import { FlatList } from "react-native";

interface Props extends PreviewCommentProps {
  defaultItems?: IComment[];
  maxNum?: number;
}

export interface PreviewCommentList {
  refresh: () => Promise<any>;
}

export const PreviewCommentList = memoForwardRef(
  (props: Props, ref: RefObject<PreviewCommentList>) => {
    const { defaultItems, maxNum = 3, ...rest } = props;

    const { hid, token, appName, type } = usePreviewProps(rest);

    const _requestLoadComment = useCallback(async () => {
      if (hid && token && type) {
        return await requestLoadComment({ hid, token }, appName, type);
      }
    }, [hid, token, appName, type]);

    const [{ loading: loadingFirst, error: firstError }, loadFirst] =
      useAsyncFn(_requestLoadComment);

    const [{ loading: refreshing, error: refreshError }, refresh] =
      useAsyncFn(_requestLoadComment);

    if (hid && token && appName && type) {
      useEffect(() => {
        loadFirst().then();
      }, []);

      useAutoToastError(firstError);
    }

    const _items = (type == "comment" ? useCommentsByQuery : usePostsByQuery)(
      hid
    ) as string[];

    const data = useMemo(() => {
      const _data = defaultItems || _items;

      const slice =
        type === "comment"
          ? [_data.length - maxNum, _data.length]
          : [0, maxNum];

      return _data.slice(...slice);
    }, [defaultItems, _items, maxNum, type]);

    const _onReply = useCallback(() => {
      navigateToCommentScreen({
        hid,
        token,
        appName,
        type,
      });
    }, [hid, token, appName, type]);

    useImperativeHandle(ref, () => ({
      refresh,
    }));

    return (
      <SContainer>
        <FlatList
          scrollEnabled={false}
          data={data as any[]}
          renderItem={({ item }) => {
            return (
              <RawCommentItem
                onReplyPress={_onReply}
                appName={appName}
                comment={item}
                type={type}
              />
            );
          }}
        />
      </SContainer>
    );
  }
);

const SContainer = styled.View``;
