import React, { memo, useCallback, useEffect, useMemo } from "react";
import { ActivityIndicator, Platform, StyleSheet, View } from "react-native";
import {
  AppInfo,
  autoLogin,
  Colors,
  Core,
  createNavigate,
  DeviceInfo,
  EAuthStage,
  getWidth,
  replaceToMain,
  setAuthStage,
  styled,
  styledValue,
  useAnimatedWindow,
  useAuthStore,
  useAutoToastError,
  useSetupLanguage,
  useTheme,
} from "@true/core";
import { Logo } from "./components/Logo";
import Animated, {
  interpolate,
  interpolateColor,
  useAnimatedStyle,
  useSharedValue,
  withDelay,
  withTiming,
} from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { InitKeyboardView } from "./InitKeyboardView";
import { useAsyncFn } from "react-use";
import { UIButton } from "../UIButton";
import { UITextStyle } from "../UIText";

interface Props {
  socketLoading?: boolean;
  isPersistorLoading?: boolean;
}

const DELAY = Platform.OS === "ios" ? 600 : 300;
const SCALE = 0.4;

export const Preload = memo((props: Props) => {
  const { heightWindow } = useAnimatedWindow();

  const aScale = useSharedValue(1);

  useEffect(() => {
    aScale.value = withDelay(DELAY, withTiming(SCALE, { duration: 1200 }));
  }, []);

  const phoneStyle = useAnimatedStyle(
    () => ({
      bottom: interpolate(aScale.value, [1, SCALE], [0, 40]) + "%",
      transform: [{ scale: aScale.value }],
    }),
    []
  );

  const backgroundStyle = useAnimatedStyle(
    () => ({
      opacity: interpolate(aScale.value, [1, SCALE], [0, 1]),
    }),
    []
  );

  return (
    <SAnimatedView>
      <SAnimatedView
        onLayout={(e) => {
          if (heightWindow.value < e.nativeEvent.layout.height) {
            heightWindow.value = e.nativeEvent.layout.height;
          }
        }}
        style={backgroundStyle}
      >
        <Comp {...props} />
      </SAnimatedView>
      <SAbsoluteIconView pointerEvents={"none"} style={phoneStyle}>
        <SPhoneIcon
          style={{ tintColor: AppInfo.buttonTextColor }}
          source={AppInfo.preloadImage}
        />
      </SAbsoluteIconView>
    </SAnimatedView>
  );
});

const Comp = memo((props: Props) => {
  const { isPersistorLoading, socketLoading } = props;
  const title = AppInfo.title;
  const subtitle = AppInfo.subTitle;
  const textButtonColor = AppInfo.buttonTextColor;
  const { authStage } = useAuthStore();

  useSetupLanguage();

  const _socketLoading = useMemo(() => socketLoading, [socketLoading]);

  // get loading
  const isLoading = useMemo(
    () =>
      isPersistorLoading || authStage === EAuthStage.CHECKING || _socketLoading,
    [authStage, isPersistorLoading, _socketLoading]
  );

  const versionText = useMemo(
    () => DeviceInfo.appVersion + ` (${DeviceInfo.buildNumber})`,
    []
  );

  const { aBottom } = useAnimatedSafeAreaInsets();
  const animatedStyles = useAnimatedStyle(() => ({
    marginBottom: aBottom.value,
  }));

  const __autoLogin = useCallback(async () => {
    setAuthStage(EAuthStage.CHECKING);
    // auto login
    await autoLogin({
      access_token: Core.accessToken,
      client_key: Core.clientKey,
    });
    // set auth stage and event
    setAuthStage(EAuthStage.SIGNED);
  }, []);

  const [{ loading: autoLoading, error: autoError }, _autoLogin] =
    useAsyncFn(__autoLogin);

  // auto replace to main when signed
  if (!isPersistorLoading) {
    useEffect(() => {
      if (authStage === EAuthStage.SIGNED && !_socketLoading) {
        // console.log("Preload:", "replace to Main screen");
        // if (callback) callback();
        replaceToMain();
      }
    }, [authStage, _socketLoading]);

    useEffect(() => {
      if (autoError) {
        setAuthStage(EAuthStage.ERROR);
      }
    }, [autoError]);

    useAutoToastError(autoError);
  }

  return (
    <>
      <View style={{ flex: 1 }}>
        <View style={{ height: "40%" }} />
        <Logo title={title} subTitle={subtitle} />
        <SView />
        <View style={styles.container}>
          {isLoading ? (
            <ActivityIndicator size={"large"} color={textButtonColor} />
          ) : (
            <SButtonView>
              <SManualLoginButton
                title={"Manual Login"}
                onPress={() => createNavigate("LoginPreloadScreen")()}
                textStyle={
                  [
                    { color: AppInfo.primaryColor },
                    UITextStyle.button_large,
                  ] as any
                }
                style={{
                  backgroundColor: textButtonColor,
                }}
              />
              {Core.accessToken && Core.clientKey ? (
                <SAutoLoginButton
                  title={"Auto Login"}
                  onPress={_autoLogin}
                  textStyle={
                    [
                      { color: textButtonColor },
                      UITextStyle.button_large,
                    ] as any
                  }
                  loading={autoLoading}
                  style={{
                    borderColor: textButtonColor,
                  }}
                />
              ) : null}
            </SButtonView>
          )}
        </View>
        {isLoading && !isPersistorLoading ? <InitKeyboardView /> : null}
      </View>
      <Animated.View style={animatedStyles}>
        <SVersionText>{versionText}</SVersionText>
      </Animated.View>
    </>
  );
});

const SVersionText = styled.Text`
  color: ${styledValue.neutral2};
  text-align: center;
`;

const SView = styled.View`
  margin-top: 50px;
`;

const SManualLoginButton = styled(UIButton)`
  border-radius: 8px;
  padding: 12px;
  margin: 0 16px;
  align-items: center;
`;
const SAutoLoginButton = styled(UIButton)`
  align-items: center;
  margin: 8px 16px 0;
  border-width: 1px;
  border-radius: 8px;
  padding: 12px;
`;
const SButtonView = styled.View`
  width: 100%;
`;

const styles = StyleSheet.create({
  button: {
    width: getWidth(300),
    marginTop: 16,
  },
  container: {
    alignItems: "center",
  },
});

const SAnimatedView = styled(Animated.View)`
  flex: 1;
  background-color: ${(p) => p.theme.primaryColor};
`;

const SAbsoluteIconView = styled(Animated.View)`
  position: absolute;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
`;

const SPhoneIcon = styled.Image`
  height: 200px;
  width: 200px;
`;
