import React, { memo, PropsWithChildren, useCallback } from "react";
import Modal, { ModalProps } from "react-native-modal";
import { StyleSheet } from "react-native";
import {
  interactManager,
  KeyboardService,
  useStatusBarStackEntry,
} from "@true/core";

interface OwnProps extends Partial<ModalProps> {
  isVisible: boolean;
  onClose?: () => void;
  noChangeStatusbarColor?: boolean;
}

type Props = OwnProps;

export const BottomMenuModal = memo(
  ({
    onClose,
    children,
    noChangeStatusbarColor,
    ...rest
  }: PropsWithChildren<Props>) => {
    useStatusBarStackEntry(!noChangeStatusbarColor && rest.isVisible);

    const _onClose = useCallback(() => {
      KeyboardService.dismiss();
      interactManager(() => onClose?.(), KeyboardService.isShown ? 150 : 0);
    }, []);

    return (
      <Modal
        style={styles.modal}
        onBackButtonPress={_onClose}
        onBackdropPress={_onClose}
        backdropTransitionOutTiming={0}
        backdropOpacity={0.4}
        animationOut={"slideOutDown"}
        animationOutTiming={400}
        useNativeDriverForBackdrop={true}
        useNativeDriver={true}
        hideModalContentWhileAnimating={true}
        {...rest}
      >
        {children}
      </Modal>
    );
  }
);

const styles = StyleSheet.create({
  modal: {
    margin: 0,
    flex: 1,
    justifyContent: "flex-end",
    // alignItems: 'center',
  },
});
