import React, {
  memo,
  RefObject,
  useCallback,
  useImperativeHandle,
} from "react";
import Modal from "react-native-modal";
import {
  EReaction,
  memoForwardRef,
  Reaction,
  styled,
  useBoolean,
} from "@true/core";
import Animated, {
  FadeInDown,
  SharedValue,
  useAnimatedStyle,
} from "react-native-reanimated";
import { TouchableOpacity, View } from "react-native";

const AnimatedTouchableOpacity =
  Animated.createAnimatedComponent(TouchableOpacity);

export interface ReactionModalRef {
  present: () => void;
  dismiss: () => void;
}

interface Props {
  params: any;
  requestFn: (params: any) => Promise<void>;
  value_y: SharedValue<number>;
  reactionKey?: string;
  noWait?: boolean;
}

export const ReactionModal = memoForwardRef(
  (props: Props, ref: RefObject<ReactionModalRef>) => {
    const { params, requestFn, value_y, reactionKey, noWait } = props;

    const [modalVisible, showModal, hideModal] = useBoolean(false);

    useImperativeHandle(
      ref,
      () => ({
        present: showModal,
        dismiss: hideModal,
      }),
      [showModal, hideModal]
    );

    const animatedStyle = useAnimatedStyle(() => {
      return {
        top: value_y.value - 120,
      };
    });

    return (
      <Modal
        onBackdropPress={hideModal}
        backdropOpacity={0}
        isVisible={modalVisible}
        animationIn={"fadeIn"}
        animationOut={"fadeOut"}
      >
        <SWrapper style={animatedStyle}>
          <SSectionReaction>
            <ReactionButton
              type={EReaction.LIKE}
              duration={300}
              params={params}
              requestFn={requestFn}
              closeModal={hideModal}
              reactionKey={reactionKey}
              noWait={noWait}
            />
            <ReactionButton
              type={EReaction.LOVE}
              duration={350}
              params={params}
              requestFn={requestFn}
              closeModal={hideModal}
              reactionKey={reactionKey}
              noWait={noWait}
            />
            <ReactionButton
              type={EReaction.HAHA}
              duration={400}
              params={params}
              requestFn={requestFn}
              closeModal={hideModal}
              reactionKey={reactionKey}
              noWait={noWait}
            />
            <ReactionButton
              type={EReaction.CARE}
              duration={450}
              params={params}
              requestFn={requestFn}
              closeModal={hideModal}
              reactionKey={reactionKey}
              noWait={noWait}
            />
            <ReactionButton
              type={EReaction.WOW}
              duration={500}
              params={params}
              requestFn={requestFn}
              closeModal={hideModal}
              reactionKey={reactionKey}
              noWait={noWait}
            />
            <ReactionButton
              type={EReaction.SAD}
              duration={550}
              params={params}
              requestFn={requestFn}
              closeModal={hideModal}
              reactionKey={reactionKey}
              noWait={noWait}
            />
            <ReactionButton
              type={EReaction.ANGRY}
              duration={600}
              params={params}
              requestFn={requestFn}
              closeModal={hideModal}
              reactionKey={reactionKey}
              noWait={noWait}
            />
          </SSectionReaction>
        </SWrapper>
      </Modal>
    );
  }
);

const SWrapper = styled(Animated.View)`
  position: absolute;
  left: 0;
  right: 0;
  align-items: center;
  justify-content: center;
`;

const SSectionReaction = styled(Animated.View)`
  flex-direction: row;
  align-items: center;
  background-color: white;
  border: 1px solid ${(p) => p.theme.neutral8}
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  padding: 10px 10px 10px 2px;
`;

const ReactionButton = memo(function ({
  duration,
  closeModal,
  params,
  requestFn,
  type,
  reactionKey = "reaction",
  noWait,
}: {
  duration: number;
  params: any;
  type: EReaction;
  requestFn: (params: any) => Promise<void>;
  reactionKey?: string;
  closeModal: () => void;
  noWait?: boolean;
}) {
  const onPress = useCallback(async () => {
    if (noWait) closeModal();
    await requestFn({ ...params, [reactionKey]: type }).then();
    if (!noWait) closeModal();
  }, [params, requestFn, noWait]);
  return (
    <SReactionWrapper
      entering={FadeInDown.duration(duration)}
      onPress={onPress}
    >
      <View>
        <SIconReaction source={Reaction.getReactionImage(type)} />
      </View>
    </SReactionWrapper>
  );
});

const SReactionWrapper = styled(AnimatedTouchableOpacity)``;

const SIconReaction = styled.Image`
  width: 32px;
  height: 32px;
  margin-left: 8px;
`;
