import React, { memo, useCallback, useMemo } from "react";
import {
  ScrollView,
  StyleSheet,
  TouchableOpacity,
  View,
  ViewStyle,
} from "react-native";
import { Row, Table } from "react-native-table-component";
import {
  Colors,
  IC_EDIT,
  IC_TRASH_OUTLINE,
  styled,
  translate,
  useTheme,
} from "@true/core";
import { TableHeadOptions } from "./types";
import { MyButton } from "../Button";

interface Props {
  headOptions: TableHeadOptions[];
  indexVisible?: boolean;
  defaultColumnWidth?: number;
  indexColumnWidth?: number;
  data: any[];
  contentContainerStyle?: ViewStyle;
  editable?: boolean;
  onEdit?: (rowIndex: number) => void;
  onRemove?: (rowIndex: number) => void;
  onPressNewRow?: () => void;
}

export const TableExample = memo((props: Props) => {
  const {
    headOptions,
    indexVisible,
    data,
    contentContainerStyle,
    editable,
    onEdit,
    onRemove,
    onPressNewRow,
  } = props;

  const tableHead = useMemo(() => {
    const rawTableHead = headOptions.map((option) => option.name);
    return indexVisible
      ? editable
        ? ["", "#", ...rawTableHead]
        : ["#", ...rawTableHead]
      : rawTableHead;
  }, [headOptions, indexVisible, editable]);

  const defaultColumnWidth = props.defaultColumnWidth || 120;

  const widthArr = useMemo(() => {
    const rawWidthArr = headOptions.map((option) => {
      if (typeof option.scale === "number") {
        return defaultColumnWidth * option.scale;
      }
      return defaultColumnWidth;
    });
    return indexVisible
      ? editable
        ? [80, 40, ...rawWidthArr]
        : [40, ...rawWidthArr]
      : rawWidthArr;
  }, [headOptions, defaultColumnWidth, indexVisible, editable]);

  const actionButtons = useCallback(
    (index: number) => {
      return (
        <SActionButtonContainer>
          <SActionButton onPress={() => onEdit && onEdit(index)}>
            <SActionImage source={IC_EDIT} />
          </SActionButton>
          <SActionButton onPress={() => onRemove && onRemove(index)}>
            <SActionImage source={IC_TRASH_OUTLINE} />
          </SActionButton>
        </SActionButtonContainer>
      );
    },
    [onEdit, onRemove]
  );

  const dataWithIndex = useMemo(() => {
    if (indexVisible) {
      return data.map((rowData, index) =>
        editable
          ? [actionButtons(index), index + 1, ...rowData]
          : [index + 1, ...rowData]
      );
    }
    return data;
  }, [data, indexVisible, editable, actionButtons]);

  const theme = useTheme();

  return (
    <SContainer>
      <SScrollView contentContainerStyle={styles.list} horizontal={true}>
        <View>
          <Table borderStyle={styles.border}>
            <Row
              data={tableHead}
              widthArr={widthArr}
              style={{ backgroundColor: theme.neutral7 }}
              textStyle={{ ...styles.headerText, ...{ color: theme.neutral1 } }}
            />
          </Table>

          <ScrollView
            style={styles.dataWrapper}
            contentContainerStyle={contentContainerStyle}
          >
            <Table borderStyle={styles.border}>
              {dataWithIndex.map((rowData, index) => (
                <Row
                  key={index}
                  data={rowData}
                  widthArr={widthArr}
                  style={{
                    backgroundColor:
                      index % 2 ? theme.neutral7 : theme.backgroundColor,
                  }}
                  textStyle={{
                    ...styles.text,
                    ...{ color: theme.neutral1 },
                  }}
                />
              ))}
            </Table>
            {onPressNewRow && (
              <MyButton
                containerStyle={styles.button}
                backgroundColor={Colors.blue1}
                onPress={onPressNewRow}
                title={translate("comp.add_new_row")}
                textColor={Colors.white}
              />
            )}
          </ScrollView>
        </View>
      </SScrollView>
    </SContainer>
  );
});

const SContainer = styled.View`
  flex: 1;
  background-color: ${(p) => p.theme.backgroundColor};
  padding: 16px 0px;
`;

const SScrollView = styled.ScrollView`
  padding: 0px 16px;
`;

const styles = StyleSheet.create({
  list: { paddingRight: 32 },
  headerText: {
    textAlign: "center",
    fontWeight: "500",
    margin: 6,
  },
  text: { textAlign: "center", margin: 6 },
  dataWrapper: { marginTop: -1 },
  border: {
    borderWidth: 1,
    borderColor: "#BDBDBD",
  },
  button: {
    marginTop: 8,
    maxWidth: 200,
  },
});

const SActionButtonContainer = styled.View`
  flex-direction: row;
  flex: 1;
  align-items: center;
  justify-content: center;
  padding: 0 6px;
`;
const SActionButton = styled(TouchableOpacity)`
  flex: 1;
  align-items: center;
  justify-content: center;
`;
const SActionImage = styled.Image`
  tint-color: ${Colors.neutral3};
`;
