import { IOption, SelectorInputProps } from "./types";
import {
  runOnJS,
  useAnimatedReaction,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
} from "react-native-reanimated";
import { RefObject, useCallback, useMemo } from "react";
import { stripSpecialCharacters } from "@true/core";
import { BottomSheetModal } from "@gorhom/bottom-sheet";

interface Props {
  bottomSheetRef?: RefObject<BottomSheetModal>;
}

const getRawValue = (value: any) => (Array.isArray(value) ? value : [value]);

export const useSelector = (
  props: Omit<SelectorInputProps, "hasSearch"> & Props
) => {
  const {
    value,
    params,
    keyName,
    options: rawOptions,
    optionMap: rawOptionMap,
    multiple,
    onChangeValue,
    bottomSheetRef,
  } = props;

  const selectedIds = useSharedValue<any[]>(getRawValue(value));

  /* -- set selectedIds when params change -- */
  if (params) {
    const setSelectedIds = useCallback((_value: any) => {
      selectedIds.value = getRawValue(_value);
    }, []);

    useAnimatedReaction(
      () => params.value,
      (_params) => {
        runOnJS(setSelectedIds)(_params?.[keyName]);
      },
      [keyName]
    );
  }

  /** -- only get section if there are items -- */
  const options = useMemo(() => {
    let _options = rawOptions;
    if (rawOptions.length && typeof rawOptions[0] === "string") {
      _options = (rawOptions as string[])
        .filter(
          (item) => rawOptionMap == undefined || rawOptionMap[item]?.length
        )
        .map((item) => stripSpecialCharacters(item));

      return _options;
    }

    return (_options as IOption[]).map((item) => ({
      ...item,
      label: stripSpecialCharacters(item.label),
    }));
  }, [rawOptions, rawOptionMap]);

  /** -- convert items' label to stripSpecialCharacters -- */
  const optionMap = useMemo(() => {
    let _map: { [id: string]: IOption[] } = {};
    for (let key in rawOptionMap) {
      _map[key] = rawOptionMap[key].map((item) => ({
        ...item,
        label: stripSpecialCharacters(item.label),
      }));
    }
    return _map;
  }, [rawOptionMap]);

  /** -- selecting -- */
  const _onChangeValue = useWorkletCallback(
    (keyName: string, value: any) => {
      if (params) {
        params.value = { ...params.value, [keyName]: value };
      }
      onChangeValue?.(keyName, value);
    },
    [onChangeValue, params]
  );

  const onSelectItem = useCallback(
    (value: any) => {
      if (multiple) {
        if (selectedIds.value.indexOf(value) === -1) {
          const _newVals = [...selectedIds.value, value];

          if (!params) selectedIds.value = _newVals;
          return _onChangeValue(keyName, _newVals);
        }

        const _newVals = [...selectedIds.value].filter(
          (_value) => value !== _value
        );

        if (!params) selectedIds.value = _newVals;
        return _onChangeValue(keyName, _newVals);
      }

      if (!params) selectedIds.value = [value];
      _onChangeValue(keyName, value);
      bottomSheetRef?.current?.dismiss();
    },
    [multiple, keyName, bottomSheetRef, _onChangeValue, params]
  );

  const getIsSelected = (value: any) =>
    useDerivedValue(() => selectedIds.value.indexOf(value) !== -1, []);

  /** -- clearValue -- */
  const onClearValue = useCallback(() => {
    if (!params) selectedIds.value = [];
    _onChangeValue(keyName, multiple ? [] : null);
  }, [keyName, multiple, _onChangeValue, params]);

  return useMemo(
    () => ({
      options,
      optionMap,
      selectedIds,
      onSelectItem,
      getIsSelected,
      onClearValue,
    }),
    [options, optionMap, selectedIds, onSelectItem, getIsSelected, onClearValue]
  );
};
