import { ParamListBase } from "@react-navigation/native";
import * as React from "react";
import { useEffect } from "react";
import type {
  HoldMenuDescriptorMap,
  HoldMenuNavigationConfig,
  HoldMenuNavigationHelpers,
  HoldMenuNavigationProp,
  HoldMenuNavigationState,
} from "./type";
import { goBack } from "@true/core";
import { HoldItemModal, HoldItemModalProps } from "react-native-hold-menu";

type BottomSheetModalScreenProps = HoldItemModalProps & {
  navigation: HoldMenuNavigationProp<ParamListBase>;
};

function HoldMenuModalScreen(props: BottomSheetModalScreenProps) {
  const ref = React.useRef<HoldItemModal>(null);
  useEffect(() => {
    ref.current?.present();
  }, []);

  const isMounted = React.useRef(true);
  React.useEffect(() => {
    return () => {
      isMounted.current = false;
    };
  }, []);

  const onDismiss = React.useCallback(() => {
    // BottomSheetModal will call onDismiss on unmount, be we do not want that since
    // we already popped the screen.
    if (isMounted.current) {
      goBack();
      props.onDismiss?.();
    }
  }, [props.onDismiss]);

  return <HoldItemModal ref={ref} {...props} onDismiss={onDismiss} />;
}

type Props = HoldMenuNavigationConfig & {
  state: HoldMenuNavigationState<ParamListBase>;
  navigation: HoldMenuNavigationHelpers;
  descriptors: HoldMenuDescriptorMap;
};

export function HoldMenuView({ state, descriptors }: Props) {
  // Avoid rendering provider if we only have one screen.
  const shouldRenderProvider = React.useRef(false);
  shouldRenderProvider.current =
    shouldRenderProvider.current || state.routes.length > 1;

  const firstScreen = descriptors[state.routes[0].key];

  return (
    <>
      {firstScreen.render()}
      {shouldRenderProvider.current
        ? state.routes.slice(1).map((route) => {
            const { options, navigation, render } = descriptors[route.key];

            // return null;
            if (!(route.params as any)?.name) return null;
            return (
              <HoldMenuModalScreen
                {...options}
                key={route.key}
                name={(route.params as any)?.name}
                navigation={navigation}
                MenuElement={render()}
                children={(route.params as any)?.children}
              />
            );
          })
        : null}
    </>
  );
}
