import React, {
  RefObject,
  useCallback,
  useImperativeHandle,
  useMemo,
  useRef,
} from "react";
import {
  ActivityIndicator,
  StyleSheet,
  Text,
  TouchableOpacity,
  View,
} from "react-native";
import { ErrorView } from "../ErrorView";
import { Colors, memoForwardRef } from "@true/core";
import { BaseRefreshControl } from "../BaseRefreshControl";
import { FlashList, FlashListProps } from "@shopify/flash-list";
import Animated from "react-native-reanimated";
import { BottomSheetFlashList } from "../../MyBottomSheet";
import { useUIFlashList } from "./hooks";
import { UIFlastListProps } from "./types";
import { FlashItemRender } from "./FlashItemRender";

const AnimatedFlashList =
  Animated.createAnimatedComponent<FlashListProps<any>>(FlashList);

/** Item cannot rerender as usually,
 * solve: use context (constate, useContext, ...)
 * to set data of each item */
// @ts-ignore
const _Comp = <T, F>(
  {
    refreshable,
    data,
    map,
    mapKey,
    loading,
    onLoadData,
    error,
    noMore,
    loadMorable,
    loadingMore,
    renderRefreshControl,
    refreshControl,
    refreshing,
    onRefresh,
    progressViewOffset,
    renderErrorView,
    loadingViewStyle,
    ListEmptyComponent,
    ListFooterComponent,
    isBottomSheet,
    renderItem,
    renderSection,
    isStickyHeader,
    scrollEventThrottle,
    extraData,
    ...rest
  }: UIFlastListProps<T, F>,
  ref: RefObject<FlashList<T>>
) => {
  // const {
  //
  // } = props;

  const {
    data: _data,
    stickyHeaderIndices,
    visible,
    toggle,
    show,
    hide,
    byKey,
  } = useUIFlashList<T, F>({
    data,
    renderSection,
    map,
    mapKey,
    extraData,
  });

  const _onRefresh = useCallback(() => {
    if (onRefresh) return onRefresh();
    if (!refreshable) return;
    if (loading) return;
    if (!!onLoadData) {
      onLoadData(true, false);
    }
  }, [refreshable, loading, onLoadData, onRefresh]);

  const _onRetry = useCallback(() => {
    if (loading) return;
    if (!!onLoadData) {
      onLoadData(false, false);
    }
  }, [loading, onLoadData]);

  const _onLoadMore = useCallback(() => {
    if (!loadMorable) return;
    if (loadingMore) return;
    if (!!onLoadData) {
      onLoadData(false, true);
    }
  }, [loadMorable, loadingMore, onLoadData]);

  const _onEndReached = useCallback(() => {
    if (error || noMore || loading) return;
    _onLoadMore();
  }, [error, noMore, loading, _onLoadMore]);

  const _renderRefreshControl = useCallback(() => {
    if (!refreshable) return undefined;
    if (renderRefreshControl) {
      return renderRefreshControl();
    }
    if (refreshControl) {
      return refreshControl;
    }
    if (refreshing !== undefined) {
      return (
        <BaseRefreshControl
          progressViewOffset={progressViewOffset}
          refreshing={refreshing}
          onRefresh={_onRefresh}
        />
      );
    }
  }, [
    _onRefresh,
    progressViewOffset,
    refreshable,
    renderRefreshControl,
    refreshControl,
    refreshing,
  ]);

  const _renderErrorView: () => JSX.Element | null = useCallback(() => {
    if (renderErrorView) {
      return renderErrorView();
    }
    return <ErrorView containerStyle={loadingViewStyle} onRetry={_onRetry} />;
  }, [loadingViewStyle, renderErrorView, _onRetry]);

  // @ts-ignore
  const renderEmptyView: () => JSX.Element | null = useCallback(() => {
    if (loading) return null;
    if (ListEmptyComponent) {
      return ListEmptyComponent || null;
    }
  }, [loading, ListEmptyComponent]);

  const renderFooterContent: () => JSX.Element | null = useCallback(() => {
    if (error) {
      return (
        <TouchableOpacity style={styles.footerRetry} onPress={_onLoadMore}>
          <Text style={styles.footerRetryText}>{"Failed press to retry"}</Text>
        </TouchableOpacity>
      );
    }
    if (noMore) {
      return <Text style={styles.noMoreCommentText}>{"No more"}</Text>;
    }

    if (loadingMore) {
      return <ActivityIndicator style={{ marginTop: 10 }} color="#000" />;
    }
    return null;
  }, [error, _onLoadMore, noMore, loadingMore]);

  const _renderFooter: () => JSX.Element | null = useCallback(() => {
    if (_data.length === 0) return null;
    if (!loadMorable) return null;

    return <View style={styles.footerView}>{renderFooterContent()}</View>;
  }, [_data.length, loadMorable, renderFooterContent]);

  const renderFooter: () => JSX.Element | null = useCallback(() => {
    return (
      <>
        {ListFooterComponent || null}
        {_renderFooter()}
      </>
    );
  }, [ListFooterComponent, _renderFooter]);

  const _renderItem = useCallback(
    ({ item, index }: { index: number; item: any }) => {
      return (
        <FlashItemRender
          toggle={toggle}
          key={index.toString()}
          renderItem={renderItem}
          item={item}
          index={index}
          renderSection={renderSection}
          visible={visible}
          show={show}
          hide={hide}
          byKey={byKey}
          dataSize={data.length}
          mapKey={mapKey}
          isLastItem={byKey.current?.[JSON.stringify(item)]?.isLast}
        />
      );
    },
    [renderItem, visible, renderSection, byKey.current, data.length, mapKey]
  );

  const _FlashList = useMemo(
    () => (isBottomSheet ? BottomSheetFlashList : AnimatedFlashList),
    [isBottomSheet]
  );

  if (error) {
    return _renderErrorView();
  }

  const render = () => {
    return (
      <_FlashList
        onEndReachedThreshold={0.4}
        stickyHeaderIndices={isStickyHeader ? stickyHeaderIndices : undefined}
        keyExtractor={(_: any, index: number) => index.toString()}
        {...rest}
        extraData={extraData}
        onEndReached={_onEndReached}
        scrollEventThrottle={scrollEventThrottle || 16}
        ListFooterComponent={renderFooter}
        refreshControl={_renderRefreshControl()}
        ListEmptyComponent={renderEmptyView()}
        //@ts-ignore
        ref={ref}
        data={_data}
        renderItem={_renderItem}
      />
    );
  };

  return <View style={{ flex: 1, overflow: "hidden" }}>{render()}</View>;
};

export const UIFlashList = memoForwardRef(
  _Comp as any
) as unknown as typeof _Comp;

export * from "./types";

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
    minHeight: 1,
    minWidth: 1,
  },
  list: {
    flex: 1,
    width: "100%",
  },
  footerView: {
    alignItems: "center",
    justifyContent: "center",
    width: "100%",
  },
  noMoreCommentText: {
    fontSize: 12,
    color: Colors.neutral6,
  },
  footerRetry: {
    width: "100%",
    height: "100%",
    justifyContent: "center",
    alignItems: "center",
  },
  footerRetryText: {
    fontSize: 12,
    color: Colors.neutral6,
  },
});
