import React, { useCallback } from "react";
import {
  Image,
  ImageSourcePropType,
  ScrollView,
  StyleSheet,
  ViewStyle,
} from "react-native";
import {
  flatStyles,
  getHeight,
  IC_EMPTY_LIST,
  styled,
  styledValue,
  useTheme,
} from "@true/core";
import { BaseRefreshControl } from "./BaseRefreshControl";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";

interface OwnProps {
  text?: string;
  containerStyle?: ViewStyle;
  refreshing?: boolean;
  onRefresh?: () => void;
  icon?: ImageSourcePropType;
}

type Props = OwnProps;

const SScrollView = styled(ScrollView)`
  background-color: ${(props) => props.theme.backgroundColor};
`;

export const EmptyListScrollView = React.memo(
  ({ containerStyle, refreshing, onRefresh, icon, ...restProps }: Props) => {
    const theme = useTheme();
    const { aBottom } = useAnimatedSafeAreaInsets();
    const animatedHeight = useSharedValue(getHeight(150));
    const animatedStyles = useAnimatedStyle(() => ({
      marginBottom: aBottom.value + animatedHeight.value,
    }));

    const onRefreshCB = useCallback(() => {
      if (onRefresh) {
        onRefresh();
      }
    }, [onRefresh]);
    return (
      <SScrollView
        style={flatStyles(styles.svContainer, { backgroundColor: theme.neutral8 })}
        contentContainerStyle={styles.svContentContainer}
        refreshControl={
          <BaseRefreshControl
            refreshing={refreshing || false}
            onRefresh={onRefreshCB}
          />
        }
      >
        <Animated.View
          style={flatStyles(styles.container, animatedStyles, containerStyle)}
        >
          <Image source={icon || IC_EMPTY_LIST} />
          <SText style={styles.text}>{restProps?.text || "No data"}</SText>
        </Animated.View>
      </SScrollView>
    );
  }
);

const SText = styled.Text`
  color: ${styledValue.neutral1};
`;

const styles = StyleSheet.create({
  svContainer: { width: "100%", flex: 1 },
  svContentContainer: {
    width: "100%",
    minHeight: "100%",
  },
  container: {
    width: "100%",
    flex: 1,
    alignItems: "center",
    justifyContent: "center",
  },
  image: {
    width: 113,
    height: 116,
    resizeMode: "contain",
  },
  text: {
    marginTop: 20,
    fontSize: 15,
    lineHeight: 18,
  },
});
