import { IReaction, memoForwardRef, styled } from "@true/core";
import { MyBottomSheet, BottomSheetFlashList } from "../../MyBottomSheet";
import { BottomMenuHeader } from "../../BottomMenu";
import { BottomSheetView,BottomSheetModal } from "@gorhom/bottom-sheet";
import React, { memo } from "react";
import { Text } from "react-native";
import { ReactionRow } from "./ReactionRow";

interface Props {
  data: IReaction[]
}


export const DetailModal = memoForwardRef<Props, BottomSheetModal>((props, ref) => {
  const {data} = props

  return <MyBottomSheet
    ref={ref}
    name={"detail-reaction-list"}
    snapPoints={["100%"]}
  >
        <BottomSheetView >
          <BottomMenuHeader title={"People who react emotions"} onClose={() => ref.current?.dismiss()} />
        </BottomSheetView>
    <BottomSheetFlashList
      data={data}
      estimatedItemSize={50}
      renderItem={renderItem}
    />
  </MyBottomSheet>
})


const renderItem = (props: {item: IReaction, index: number}) => <ReactionRow   reaction={props.item} />

const Item = memo(({item, index}: {item: IReaction, index: number}) => {
  return <SItemContainer>
    <Text>
      {item.user_id}
    </Text>
  </SItemContainer>
})

const SItemContainer = styled.View`
  
`
