import { UIUserPickerInputProps } from "./types";
import { useCallback, useMemo, useRef, useState } from "react";
import {
  runOnJS,
  useAnimatedReaction,
  useSharedValue,
  useWorkletCallback,
} from "react-native-reanimated";
import constate from "constate";
import {
  getUser,
  getUserByQuery,
  getUserGroup,
  getUserGroupByQuery,
} from "@true/core";
import { BottomSheetModal } from "@gorhom/bottom-sheet";
import { getMatch } from "./utils";

const getRawValue = (
  value: string | string[] | undefined,
  type: "user" | "group"
) => {
  const __list = Array.isArray(value)
    ? value
    : value !== undefined && value !== null
    ? [value]
    : [];
  return __list.filter((id) =>
    type == "user"
      ? getUser(id)?.username !== undefined
      : getUserGroup(id)?.name !== undefined
  );
};

const _provider = (props: UIUserPickerInputProps) => {
  const {
    defaultUsers,
    type,
    multiple,
    onChangeValue,
    keyName,
    value,
    params,
  } = props;

  const bottomSheetRef = useRef<BottomSheetModal>(null);

  const validValue = useMemo(() => getRawValue(value, type), [value, type]);

  const selectedIds = useSharedValue<string[]>(validValue);

  if (params) {
    const setSelectedIds = useCallback(
      (_value: string | string[] | undefined) => {
        selectedIds.value = getRawValue(_value, type);
      },
      [type]
    );

    useAnimatedReaction(
      () => params.value,
      (_value) => {
        runOnJS(setSelectedIds)(_value?.[keyName]);
      },
      [keyName, type]
    );
  }

  const _data = useMemo(
    () =>
      defaultUsers ||
      ((type == "user"
        ? getUserByQuery("allIds")
        : getUserGroupByQuery("allIds")) as string[]),
    [defaultUsers, type]
  );

  const [data, setData] = useState<string[]>(_data);

  /** -- remove selected item in single mode -- */
  if (!multiple) {
    useAnimatedReaction(
      () => selectedIds.value[0],
      (_id) => {
        runOnJS(setData)(_data.filter((_item) => _item != _id));
      },
      [_data]
    );
  }

  /** -- searching -- */
  const onSearchChange = useCallback(
    (text: string) => {
      if (type == "user") {
        return setData(
          _data.filter(
            (item) =>
              (multiple || selectedIds.value[0] != item) &&
              getMatch(getUser(item)?.name || "", text)
          )
        );
      }
      setData(
        _data.filter(
          (item) =>
            (multiple || selectedIds.value[0] != item) &&
            getMatch(getUserGroup(item)?.name || "", text)
        )
      );
    },
    [_data, multiple]
  );

  const _onChangeValue = useWorkletCallback(
    (keyName: string, value: any) => {
      if (params) {
        params.value = { ...params.value, [keyName]: value };
      }
      onChangeValue?.(keyName, value);
    },
    [onChangeValue, params]
  );

  const onItemPress = useCallback(
    (item: string) => {
      if (multiple) {
        if (selectedIds.value.indexOf(item) === -1) {
          const newVal = [...selectedIds.value, item];

          if (!params) selectedIds.value = newVal;
          return _onChangeValue(keyName, newVal);
        }

        const newVal = selectedIds.value.filter((_item) => item !== _item);

        if (!params) selectedIds.value = newVal;
        return _onChangeValue(keyName, newVal);
      }

      if (!params) selectedIds.value = [item];
      _onChangeValue(keyName, item);
      bottomSheetRef.current?.dismiss();
    },
    [keyName, _onChangeValue, bottomSheetRef, multiple, params]
  );

  const onClearIds = useCallback(() => {
    if (!params) selectedIds.value = [];
    return _onChangeValue(keyName, multiple ? [] : "");
  }, [_onChangeValue, keyName, multiple, params]);

  return useMemo(
    () => ({
      selectedIds,
      data,
      bottomSheetRef,
      onItemPress,
      onClearIds,
      onSearchChange,
    }),
    [selectedIds, data, bottomSheetRef, onItemPress, onClearIds, onSearchChange]
  );
};

export const [UserPickerInputProvider, useUserPickerInput] = constate(
  _provider,
  (val) => val
);
