import React, { RefObject, useCallback, useMemo } from "react";
import { memoForwardRef } from "@true/core";
import { NativeScrollEvent } from "react-native";
import Animated from "react-native-reanimated";
import {
  AnimatedScrollableTabCommonProps,
  useAnimatedScrollableView,
} from "./index";
import { FlashList } from "@shopify/flash-list";
import { _List } from "./_List";
import { useRequest } from "./hooks/useRequest";
import { IFlashItem, IFlashSection } from "../Common";

export interface ITab extends AnimatedScrollableTabCommonProps {
  title: string;
  map?: Record<string, (string | number)[]>;
  onDataChange?: (val: (number | string)[]) => void;
  mapKey?: string;
}

interface Props extends AnimatedScrollableTabCommonProps {
  index: number;
  tab?: string;
  map?: Record<string, (string | number)[]>;
  onDataChange?: (val: (number | string)[]) => void;
  mapKey?: string;
  ref?: any;
  onScroll?: (event: NativeScrollEvent) => void;
  setData?: (prevData: (string | number)[]) => (string | number)[];
  // tabs?: ITab[];
}

export const ScrollableTabList = memoForwardRef(
  (props: Props, ref: RefObject<FlashList<any>>) => {
    /**
     * get props
     */
    const {
      tab,
      index,
      params: bParams,
      requestFn: bRequestFn,
      query: bQuery,
      getItemByQuery: bGetItemByQuery,
      useItemByQuery: bUseItemByQuery,
      renderItem: bRenderItem,
      noItemImageSource: bNoItemImageSource,
      disabledLoadData: bDisabledLoadData,
      onScroll: bOnScroll,
      itemHeight: bItemHeight,
      numColumns: bNumColumns,
      ListHeaderComponent: bListHeaderComponent,
      ListEmptyComponent: bListEmptyComponent,
      map: bMap,
      mapKey: bMapKey,
      renderSection: bRenderSection,
      onDataChange: bOnDataChange,
      loadMoreParams: bLoadMoreParams,
      setData,
      setMap: bSetMap,
      loadMorable: bLoadMorable,
      showsVerticalScrollIndicator: bShowsVerticalScrollIndicator,
      // tabs,
    } = props;

    /**
     * get value from Context
     */
    const {
      params: aParams,
      requestFn: aRequestFn,
      query: aQuery = "",
      mainKey = "tab",
      getItemByQuery: aGetItemByQuery,
      useItemByQuery: aUseItemByQuery,
      renderItem: aRenderItem,
      renderSection: aRenderSection,
      noItemImageSource: aNoItemImageSource,
      onCollapse,
      disabledLoadData: aDisabledLoadData,
      itemHeight: aItemHeight,
      numColumns: aNumColumns,
      ListHeaderComponent: aListHeaderComponent,
      ListEmptyComponent: aListEmptyComponent,
      loadMoreParams: aLoadMoreParams,
      setMap: aSetMap,
      loadMorable: aLoadMorable,
      showsVerticalScrollIndicator: aShowsVerticalScrollIndicator,
    } = useAnimatedScrollableView();

    // const {
    //   headerHeightMap,
    //   aHeightMap,
    //   tabBarHeightMap,
    //   totalScrollMap,
    // } = useTotalScrollableTabView();

    /**
     * set props by values in props or values in Context
     */
    const params = useMemo(() => bParams || aParams, [bParams, aParams]);

    const requestFn = useMemo(
      () => bRequestFn || aRequestFn,
      [bRequestFn, aRequestFn]
    );

    const query = useMemo(() => bQuery || aQuery, [bQuery, aQuery]);

    const getItemByQuery = useMemo(
      () => bGetItemByQuery || aGetItemByQuery,
      [bGetItemByQuery, aGetItemByQuery]
    );

    const useItemByQuery = useMemo(
      () => bUseItemByQuery || aUseItemByQuery,
      [bUseItemByQuery, aUseItemByQuery]
    );

    const renderItem = useMemo(
      () => bRenderItem || aRenderItem,
      [bRenderItem || aRenderItem]
    );

    const noItemImageSource = useMemo(
      () => bNoItemImageSource || aNoItemImageSource,
      [bNoItemImageSource, aNoItemImageSource]
    );

    const disabledLoadData = useMemo(
      () => bDisabledLoadData || aDisabledLoadData,
      [bDisabledLoadData, aDisabledLoadData]
    );

    const itemHeight = useMemo(
      () => bItemHeight || aItemHeight,
      [bItemHeight, aItemHeight]
    );

    const numColumns = useMemo(
      () => bNumColumns || aNumColumns,
      [bNumColumns, aNumColumns]
    );

    const ListHeaderComponent = useMemo(
      () => bListHeaderComponent || aListHeaderComponent,
      [bListHeaderComponent, aListHeaderComponent]
    );

    const ListEmptyComponent = useMemo(
      () => bListEmptyComponent || aListEmptyComponent,
      [bListEmptyComponent, aListEmptyComponent]
    );

    const loadMoreParams = useMemo(
      () => bLoadMoreParams || aLoadMoreParams,
      [bLoadMoreParams, aLoadMoreParams]
    );
    const setMap = useMemo(() => bSetMap || aSetMap, [bSetMap, aSetMap]);

    const map = useMemo(() => bMap, [bMap]);

    const mapKey = useMemo(() => bMapKey, [bMapKey]);

    const renderSection = useMemo(
      () => bRenderSection || aRenderSection,
      [bRenderSection, aRenderSection]
    );
    const loadMorable = useMemo(
      () => bLoadMorable || aLoadMorable,
      [bLoadMorable, aLoadMorable]
    );
    const showsVerticalScrollIndicator = useMemo(
      () => bShowsVerticalScrollIndicator || aShowsVerticalScrollIndicator,
      [bShowsVerticalScrollIndicator, aShowsVerticalScrollIndicator]
    );

    const onDataChange = useMemo(() => bOnDataChange, [bOnDataChange]);

    if (!renderItem) throw new Error("ScrollableTabList: renderItem is null");

    /**
     * request hook
     */
    const {
      loadingFirst,
      refreshing,
      changeParamsLoading,
      itemList,
      mounted,
      onLoadData,
      loadingMore,
    } = useRequest({
      // currentIndex,
      index,
      params,
      requestFn,
      query,
      getItemByQuery,
      useItemByQuery,
      noItemImageSource,
      disabledLoadData,
      itemHeight,
      numColumns,
      ListHeaderComponent,
      renderItem,
      onDataChange,
      tab,
      mainKey,
      loadMoreParams,
    });

    /**
     * for preventing press while scrolling
     */
    // const pressable = useSharedValue(true);

    // const animatedProps = useAnimatedProps(() => {
    //   return {
    //     pointerEvents:
    //       pressable.value && animatedIndex.value % 1 == 0 ? "auto" : "none",
    //   } as any;
    // });

    const _renderItem = useCallback(
      ({ item, index }: IFlashItem<string | number>) => {
        // return null;
        return (
          <Animated.View>
            {renderItem(item, index, index == itemList.length - 1)}
          </Animated.View>
        );
      },
      [itemList.length]
    );

    const _renderSection = useCallback(
      ({ item, index, toggle }: IFlashSection<any>) => {
        return (
          <Animated.View>{renderSection(item, toggle, index)}</Animated.View>
        );
      },
      [itemList.length]
    );

    return (
      <_List
        {...props}
        index={index}
        ref={ref}
        ListHeaderComponent={ListHeaderComponent}
        ListEmptyComponent={ListEmptyComponent}
        data={setMap?.(itemList)?.data || setData?.(itemList) || itemList}
        mapKey={mapKey}
        map={setMap?.(itemList)?.map || map}
        renderSection={renderSection ? _renderSection : undefined}
        renderItem={_renderItem}
        onScroll={bOnScroll}
        estimatedItemSize={itemHeight || 100}
        numColumns={numColumns}
        loading={loadingFirst || !mounted}
        changeParamsLoading={changeParamsLoading}
        onLoadData={onLoadData}
        refreshing={refreshing}
        loadingMore={loadingMore}
        onCollapse={onCollapse}
        loadMorable={loadMorable}
        showsVerticalScrollIndicator={showsVerticalScrollIndicator}
      />
    );
  }
);
