import React, { ComponentType, memo, useRef } from "react";
import { EReaction, Reaction, styled } from "@true/core";
import { View } from "react-native";
import { globalStyles } from "../utils";
import FastImage from "react-native-fast-image";
import { DetailModal } from "./DetailModal";
import { BottomSheetModal } from "@gorhom/bottom-sheet";

interface IReaction {
  user_id: string;
  reaction: EReaction;
}

interface ReactionListProps {
  withNumber: boolean;
  likes?: number;
  reactions?: IReaction[];
}

export const ReactionList = memo(function (props: ReactionListProps) {
  const { withNumber, likes, reactions } = props;
  const listTypeReactions = reactions?.map((item) => item.reaction);
  const _listReactions = [...new Set(listTypeReactions)];
  const modalRef = useRef<BottomSheetModal>(null);

  return (
    <SContainer hitSlop={16} onPress={() => modalRef.current?.present()}>
      <SContentContainer>
        {withNumber && (
          <View style={globalStyles.mr4}>
            <SNumberReactions>{likes}</SNumberReactions>
          </View>
        )}
        <FlexCenter>
          {_listReactions?.slice(0, 3).map((item: EReaction, index: number) => {
            return (
              <SIcon key={index}>
                <StyledReactionImage
                  // @ts-ignore
                  source={Reaction.getReactionImage(item)}
                />
              </SIcon>
            );
          })}
        </FlexCenter>
        {!withNumber && (
          <View style={globalStyles.ml4}>
            <SText>{`${likes}`}</SText>
          </View>
        )}
      </SContentContainer>
      <DetailModal ref={modalRef} data={reactions} />
    </SContainer>
  );
});
const SContainer = styled.TouchableOpacity`
  border-radius: 40px;
`;

const SContentContainer = styled.View`
  flex-direction: row;
  align-items: center;
`;
const SIcon = styled.View`
  align-items: center;
  justify-content: center;
  border: 1px solid #ffffff;
  border-radius: 10px;
`;

const SText = styled.Text`
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  color: ${(p) => p.theme.neutral2};
`;
const SNumberReactions = styled.Text`
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  color: ${(p) => p.theme.neutral2};
`;
const StyledReactionImage = styled(FastImage)`
  width: 14px;
  height: 14px;
` as ComponentType<{ width: number; height: number; as: ComponentType<any> }>;

const FlexCenter = styled.View`
  flex-direction: row;
  align-items: center;
`;
