import React, { memo, useCallback, useMemo } from "react";
import { Fonts, IC_X, styled, translate } from "@true/core";
import striptags from "striptags";
import Animated, {
  SharedValue,
  useAnimatedStyle,
  withTiming,
} from "react-native-reanimated";
import FastImage from "react-native-fast-image";

const timing = (value: number) => {
  "worklet";
  return withTiming(value, { duration: 200 });
};

export const getStrippedTagContent = (content: string | undefined) => {
  const strippedContent =
    typeof content !== "undefined"
      ? striptags(content, [], " ").replace(/\s+/g, " ").trim()
      : undefined;
  return strippedContent;
};

const Wrapper = styled.View`
  align-items: center;
  flex-direction: row;
  padding: 8px 16px;
`;

const TextContainer = styled.View`
  flex: 1;
  justify-content: center;
`;

const Title = styled.Text`
  font-size: 12px;
  color: ${(props) => props.theme.neutral1};
`;

const Bold = styled.Text`
  font-family: ${Fonts.Bold};
  color: ${(props) => props.theme.neutral1};
`;

const Content = styled.Text`
  font-size: 12px;
  color: ${(props) => props.theme.neutral2};
  padding-top: 2px;
`;

const CloseTouchable = styled.TouchableOpacity`
  width: 20px;
`;

const CloseIcon = styled.Image`
  width: 20px;
  height: 20px;
  tint-color: ${(p) => p.theme.neutral2};
`;

interface Props {
  visible: SharedValue<boolean>;
  username: string;
  attachment?: { uri: string; name: string; isImage: boolean };
  content?: string;
  onClose?: () => void;
}
export const ReplyView = memo<Props>(
  ({ username, attachment, content, visible, onClose }) => {
    const _content = useMemo(() => {
      return getStrippedTagContent(content || "") || attachment.name || "";
    }, [content, attachment.name]);

    const animatedStyle = useAnimatedStyle(() => {
      return {
        height: visible.value
          ? timing(attachment?.isImage ? 60 : 50)
          : timing(0),
        opacity: visible.value ? timing(1) : timing(0),
      };
    }, [attachment?.isImage]);

    const _clear = useCallback(() => {
      onClose?.();
      visible.value = false;
    }, [onClose]);

    return (
      <Animated.View style={[animatedStyle, { justifyContent: "center" }]}>
        <Wrapper>
          <TextContainer>
            <Title numberOfLines={1}>
              <>
                {translate("Replying")} <Bold>{username}</Bold>
              </>
            </Title>
            {attachment?.isImage ? (
              <Photo source={{ uri: attachment.uri }} resizeMode={"contain"} />
            ) : (
              <Content numberOfLines={1}>{_content}</Content>
            )}
          </TextContainer>
          <CloseTouchable onPress={_clear} hitSlop={8}>
            <CloseIcon source={IC_X} />
          </CloseTouchable>
        </Wrapper>
      </Animated.View>
    );
  }
);

const Photo = styled(FastImage)`
  width: 36px;
  height: 36px;
  border-radius: 4px;
  border-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
`;

class ImageHelper {
  static uriSize(
    uri: string,
    size = 0,
    params: { [x: string]: any } = {}
  ): string {
    if (!uri) return uri;
    if (!uri.startsWith("http")) return decodeURIComponent(uri);
    let output = uri.replace(/\/([^\/]+)$/g, `/${size}.$1`);

    const keys = Object.keys(params) || [];

    return keys.length
      ? output +
          "?" +
          keys
            .map(
              (key) =>
                encodeURIComponent(key) + "=" + encodeURIComponent(params[key])
            )
            .join("&")
      : output;
  }
}
