import { IC_ATTACHMENT, memoForwardRef, ToastService } from "@true/core";
import { UIForm } from "../UIForm";
import React, { RefObject } from "react";
import { UIAttachmentInputProps } from "./types";
import { useDerivedValue, useSharedValue } from "react-native-reanimated";
import { PickFileBottomSheet } from "./BottomSheet";
import { UIAttachmentProvider, useUIAttachment } from "./provider";
import { SelectedFilesView } from "./SelectedFilesView";

interface UIAttachmentInput {}

export const MAX_FILE = 3;

const _UIAttachmentInput = memoForwardRef(
  (props: UIAttachmentInputProps, ref: RefObject<UIAttachmentInput>) => {
    const { maxFile = MAX_FILE, multiple } = props;

    const text = useSharedValue("");

    const { bottomSheetRef, files } = useUIAttachment();

    const title = useDerivedValue(
      () =>
        "Attachment" +
        " (" +
        files.value.length +
        "/" +
        (!multiple ? 1 : maxFile) +
        ")",
      [multiple, maxFile]
    );

    return (
      <>
        <UIForm.Input
          {...props}
          title={title}
          ContentElement={<SelectedFilesView />}
          textValue={text}
          onPress={() => {
            if (files.value.length >= maxFile)
              return ToastService.show(
                `Maximum files you can pick is ${maxFile}, remove some files`
              );
            bottomSheetRef.current?.present();
          }}
          inputType={"attachment"}
          onClearValue={() => {}}
          hideClearButton={true}
          leftIconSource={IC_ATTACHMENT}
        />
        <PickFileBottomSheet ref={bottomSheetRef} {...props} />
      </>
    );
  }
);

export const UIAttachmentInput = memoForwardRef(
  (props: UIAttachmentInputProps, ref: RefObject<UIAttachmentInput>) => {
    return (
      <UIAttachmentProvider {...props}>
        <_UIAttachmentInput {...props} ref={ref} />
      </UIAttachmentProvider>
    );
  }
);
