import React, {
  memo,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import {interactManager, styled} from '@true/core';
import {Emoji, EmojiGroup} from './types';
import {ViewProps} from 'react-native';
import PagerWrapper from './PagerWrapper';
import BottomBar from './BottomBar';
import Sticker from './Sticker';
import {getBottomSpace} from 'react-native-iphone-x-helper';
import {getBaseGifs} from './gif/functions';
import {BottomSheetFlashList} from '@true/ui';
import ViewPager from 'react-native-pager-view';
import {BottomSheetFooter} from '@gorhom/bottom-sheet';
import {SBottomFooterView} from './GifScreen';
import {SharedValue} from 'react-native-reanimated';

const Header = styled.View`
  height: ${32 + getBottomSpace()}px;
  width: 100%;
  flex-direction: row;
  align-items: center;
  padding-bottom: ${getBottomSpace()}px;
  background-color: ${p => p.theme.backgroundColor};
`;

let emoji: Record<string, EmojiGroup> = {};

export interface StickerBoxContentProps {
  onStickerPress: (sticker: string) => void;
  animatedBottomFooter: SharedValue<number>;
}

export const StickerBoxContent = memo(
  ({
    onStickerPress,
    animatedBottomFooter,
  }: StickerBoxContentProps & ViewProps) => {
    const [stickersByKey, setStickersByKey] = useState<typeof emoji>({});
    const [activeGroupIndex, setActiveGroupIndex] = useState(0);

    const pagerRef = useRef<ViewPager>(null);

    const onBottomPress = useCallback(
      (index: number) => {
        setActiveGroupIndex(index);
        pagerRef.current?.setPageWithoutAnimation(index);
      },
      [pagerRef],
    );

    const stickers = useMemo(() => {
      return Object.values(stickersByKey).filter(
        item => item.type !== 'popular',
      );
    }, [stickersByKey]);

    useEffect(() => {
      if (Object.keys(emoji).length === 0) {
        emoji = getBaseGifs();
      }
      setStickersByKey(emoji);
    }, []);

    const setActiveGroup = useCallback(
      (index: number) => {
        const maxLength = stickers.length;
        if (index < 0) return setActiveGroupIndex(0);
        if (index > maxLength) return setActiveGroupIndex(maxLength);
        return setActiveGroupIndex(index);
      },
      [stickers],
    );

    return (
      <>
        <PagerWrapper
          ref={pagerRef}
          currentIndex={activeGroupIndex}
          onScrollEnd={setActiveGroup}>
          {stickers.map((group, index) => {
            return (
              <Item
                onStickerPress={onStickerPress}
                data={group.item}
                index={index}
                activeIndex={activeGroupIndex}
              />
            );
          })}
        </PagerWrapper>
        <BottomSheetFooter animatedFooterPosition={animatedBottomFooter}>
          <SBottomFooterView>
            <Header>
              <BottomBar
                stickers={stickers}
                setActiveGroupIndex={onBottomPress}
                activeGroupIndex={activeGroupIndex}
              />
            </Header>
          </SBottomFooterView>
        </BottomSheetFooter>
      </>
    );
  },
);

const Item = memo(
  ({
    data,
    activeIndex,
    index,
    onStickerPress,
  }: {
    data: Emoji[];
    activeIndex: number;
    index: number;
    onStickerPress: (sticker: string) => void;
  }) => {
    const [mounted, setMounted] = useState(false);
    useEffect(() => {
      if (activeIndex === index) interactManager(() => setMounted(true), 100);
    }, [activeIndex, index]);

    if (!mounted) return null;

    return (
      <BottomSheetFlashList
        data={data}
        renderItem={({item}) => (
          <Sticker onPress={onStickerPress} key={item.url} item={item} />
        )}
        numColumns={5}
      />
    );
  },
);
