import React, { FunctionComponent, memo } from "react";
import { ActivityIndicator, StyleSheet, View, ViewStyle } from "react-native";
import { flatStyles, styled } from "@true/core";

interface OwnProps {
  containerStyle?: ViewStyle;
}

type Props = OwnProps;

const SActivityIndicator = styled(ActivityIndicator).attrs((props) => ({
  color: props.theme.neutral1,
}))``;

const LoadingView: FunctionComponent<Props> = memo((props) => {
  return (
    <View
      style={StyleSheet.flatten(
        flatStyles(styles.container, props.containerStyle)
      )}
    >
      <SActivityIndicator />
    </View>
  );
});

const MemoLoadingView = React.memo(LoadingView);
export { MemoLoadingView as LoadingView };

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
    alignItems: "center",
    paddingTop: 10,
  },
});
