import React, { memo, useMemo } from "react";
import { Text, TextStyle, View, ViewStyle } from "react-native";
import FastImage from "react-native-fast-image";
import { Colors, flatStyles, Fonts, IC_USER } from "@true/core";

interface AvatarStackImageProps {
  uri?: string;
  radius: number;
  overlap: number;
  borderColor: string;
  borderWidth: number;
}

export const AvatarStackImage = memo(
  ({
    uri,
    radius,
    overlap,
    borderColor,
    borderWidth,
  }: AvatarStackImageProps) => {
    const size = radius * 2;
    const width = size - overlap;
    const source = uri
      ? {
          uri,
          height: size,
          width: size,
          borderRadius: radius,
        }
      : IC_USER;
    return (
      <View style={{ width, height: size }}>
        <View
          style={{
            height: size,
            width: size,
            borderRadius: radius,
            borderWidth,
            borderColor,
            overflow: "hidden",
            backgroundColor: Colors.neutral7,
          }}
        >
          <FastImage
            style={{ height: size - borderWidth, width: size - borderWidth }}
            source={source}
            resizeMode={FastImage.resizeMode.cover}
          />
        </View>
      </View>
    );
  }
);

export interface NonUriAvatarStackProps
  extends Omit<AvatarStackImageProps, "uri"> {}

interface OwnProps extends NonUriAvatarStackProps {
  limit?: number;
  sumViewStyle?: ViewStyle;
  sumTextStyle?: TextStyle;
  containerStyle?: ViewStyle;
  data: any[];
  renderAvatar: (
    item: any,
    avatarProps: NonUriAvatarStackProps
  ) => React.ReactElement | null;
}

type Props = OwnProps;

export const AvatarStack = memo((props: Props) => {
  const {
    radius = 14,
    borderWidth = 1,
    limit = 3,
    sumViewStyle,
    sumTextStyle,
    containerStyle,
    data,
    overlap,
    renderAvatar,
    borderColor,
  } = props;

  const avatarProps = useMemo<NonUriAvatarStackProps>(
    () => ({
      radius,
      overlap,
      borderColor,
      borderWidth,
    }),
    [radius, overlap, borderColor, borderWidth]
  );

  const size = radius * 2;

  return (
    <View
      style={flatStyles(
        {
          flexDirection: "row",
          minWidth: size,
          paddingRight: size - overlap,
        },
        containerStyle
      )}
    >
      {data.slice(0, limit).map((item) => {
        return renderAvatar(item, avatarProps);
      })}
      {data.length > limit && (
        <View style={{ height: size, width: size - overlap }}>
          <View
            style={flatStyles(
              {
                height: size,
                width: size,
                borderRadius: radius,
                borderWidth,
                borderColor,
                backgroundColor: Colors.neutral7,
                overflow: "hidden",
                justifyContent: "center",
                alignItems: "center",
              },
              sumViewStyle
            )}
          >
            <Text
              style={flatStyles(
                {
                  color: Colors.neutral3,
                  fontSize: 11,
                  fontFamily: Fonts.Bold,
                  lineHeight: 16,
                  textAlign: "center",
                },
                sumTextStyle
              )}
            >{`+${data.length - limit}`}</Text>
          </View>
        </View>
      )}
    </View>
  );
});

