import {memoForwardRef, styled} from "@true/core";
import React, { RefObject, useCallback, useRef } from "react";
import { BottomSheetModal, BottomSheetView } from "@gorhom/bottom-sheet";
import { SearchBar, SearchBarRef } from "../../SearchBar";
import { MyBottomSheet, useAnimatedSnapPoints } from "../../MyBottomSheet";
import { UIFlashList } from "../../Common";
import { BottomMenuHeader } from "../../BottomMenu";
import { IListItem, IListSection, SelectorInputProps } from "./types";
import { ListItem } from "./ListItem";
import { ListSection } from "./ListSection";
import { useSelectorInput } from "./provider";
import { AnimatedBottomSpace } from "../../AnimatedBottomSpace";

export const BottomSheet = memoForwardRef(
  (props: SelectorInputProps, ref: RefObject<BottomSheetModal>) => {
    const { title, hasSearch, optionMap, renderBottomSheet } = props;

    const {
      animatedSnapPoints,
      onViewLayout,
      onListContentSize,
      onListLayout
    } =
      useAnimatedSnapPoints();

    const {
      bottomSheetRef,
      selectedIds,
      selectedText,
      data,
      map,
      onSearchChange,
      onSelectItem,
      getIsSelected,
      onClearValue,
    } = useSelectorInput();

    const renderItem = useCallback(
      (props: IListItem) => (
        <ListItem
          {...props}
          onPress={onSelectItem}
          isSelected={getIsSelected(props.item.value)}
        />
      ),
      [onSelectItem]
    );
    const renderSection = useCallback(
      (props: IListSection) => <ListSection {...props} />,
      []
    );

    const searchRef = useRef<SearchBarRef>(null);

    return (
      <MyBottomSheet
        ref={ref}
        onDismiss={() => {
          onSearchChange("");
        }}
        snapPoints={animatedSnapPoints}
        hasFlashList={true}
      >
        <BottomSheetView onLayout={onViewLayout("header")}>
          {title ? (
            <BottomMenuHeader
              title={title as string}
              onClose={ref.current?.dismiss}
            />
          ) : null}
          {hasSearch ? (
            <SSearchBar
              ref={searchRef}
              containerStyle={{ marginTop: 8 }}
              isOnModal={true}
              onSearchTextChange={onSearchChange}
            />
          ) : null}
        </BottomSheetView>
        {renderBottomSheet ? (
          renderBottomSheet({
            bottomSheetRef,
            selectedIds,
            selectedText,
            data,
            map,
            onSearchChange,
            onSelectItem,
            getIsSelected,
            onClearValue,
            onViewLayout,
            onListContentSize,
            onListLayout
          })
        ) : (
          <UIFlashList
            data={data}
            map={optionMap ? map : undefined}
            keyboardShouldPersistTaps={"handled"}
            renderItem={renderItem}
            renderSection={optionMap ? renderSection : undefined}
            estimatedItemSize={52}
            onContentSizeChange={onListContentSize}
            isBottomSheet={true}
            ListFooterComponent={<AnimatedBottomSpace />}
          />
        )}
      </MyBottomSheet>
    );
  }
);

const SSearchBar = styled(SearchBar)`
  margin: 0 16px 8px 16px;
`
