import React from "react";
import { Core } from "@true/core";
import ConfirmGoogleCaptcha from "react-native-google-recaptcha-v2";
import RootSiblings from "react-native-root-siblings";

// 6Lebk2wiAAAAADtMuDjPRTSA9OD2edmDFzxTz3vg

export const RecaptchaV2 = {
  get _siteKey() {
    return (
      Core.recaptchaV2SiteKey || "6LdhEzUgAAAAACXAMpX4jeJ-M7wnvGgeHwS48EOp"
    );
  },

  get _baseUrl() {
    return `https://${Core?.sysUrl}`;
  },

  requestToken: async (params?: { baseUrl?: string }) => {
    const { baseUrl } = params || {};
    return await new Promise<string>((resolve, reject) => {
      const onMessage = (event: { nativeEvent: { data: string } }) => {
        const {
          nativeEvent: { data },
        } = event;

        if (data === "cancel") {
          return sibling.destroy(() => reject("RECAPTCHA_V2_CANCELLED"));
        }

        if (data === "error") {
          return sibling.destroy(() => reject("RECAPTCHA_V2_ERROR"));
        }

        if (data === "expired") {
          return sibling.destroy(() => reject("RECAPTCHA_V2_EXPIRED"));
        }

        return sibling.destroy(() => resolve(data));
      };

      const onRef = (ref: any) => ref?.show?.();

      const sibling = new RootSiblings(
        (
          <ConfirmGoogleCaptcha
            ref={onRef}
            siteKey={RecaptchaV2._siteKey}
            baseUrl={baseUrl || RecaptchaV2._baseUrl}
            languageCode={"en"}
            onMessage={onMessage}
            cancelButtonText={"cancel"}
          />
        )
      );
    });
  },
};
