import React, {RefObject, useCallback} from 'react';
import {View} from 'react-native';
import {MyBottomSheet, useAnimatedSnapPoints,} from '../../MyBottomSheet';
import {BottomMenuHeader} from '../../BottomMenu';
import {BottomSheetModal, BottomSheetView} from "@gorhom/bottom-sheet";
import {SharedValue} from "react-native-reanimated";
import {memoForwardRef} from "@true/core";

interface Props {
  keyName: string;
  title: string;
  withDivider?: boolean;
  headerComponent?: React.ReactElement;
  snapPoints?: (number|string)[] | SharedValue<(number|string)[]>
  children?: any;
  onDismiss?: () => void;
  hasFlashList?: boolean;
}

export const BottomSheetTemplate = memoForwardRef((props: Props, ref: RefObject<BottomSheetModal>) => {
  const {keyName, title, headerComponent, withDivider = false, snapPoints, hasFlashList} = props;

  const {onViewLayout, animatedSnapPoints} = useAnimatedSnapPoints()

  const onHide = useCallback(() => {
    ref.current?.dismiss()
  }, [ref.current])

  const headerRender = useCallback(() => {
    return title ? (
      <BottomMenuHeader
        title={title}
        onClose={onHide}
        noDivider={!withDivider}
      />
    ) : headerComponent ? (
      headerComponent
    ) : (
      <View style={{height: 8}} />
    )
  }, [title, onHide, withDivider, headerComponent])

  return (
    <MyBottomSheet
      ref={ref}
      name={keyName}
      onDismiss={props.onDismiss}
      snapPoints={snapPoints || animatedSnapPoints}
      hasFlashList={hasFlashList}
    >
      {!snapPoints ? <BottomSheetView onLayout={onViewLayout('header')}>
        {headerRender()}
      </BottomSheetView>
      : headerRender()}

      {!snapPoints
        ? <BottomSheetView onLayout={onViewLayout('body')}>{props.children}</BottomSheetView>
        : <>{props.children}</>}
    </MyBottomSheet>
  );
});
