import "react-native-gesture-handler";
import {
  Platform,
  StatusBar,
  Text,
  TextInput,
  TouchableOpacity,
} from "react-native";
import {
  AnimatedKeyboardProvider,
  AnimatedWindowProvider,
  Colors,
  DeviceInfo,
  ETheme,
  Fonts,
  setCustomText,
  setupLanguage,
  store,
  useThemeStore,
  AppInfo,
  useMounted,
} from "@true/core";
import * as React from "react";
import {
  memo,
  NamedExoticComponent,
  PropsWithChildren,
  useEffect,
  useState,
} from "react";
import { MyThemeProvider } from "./MyThemeProvider";
import { Provider } from "react-redux";
import { PersistGate } from "redux-persist/integration/react";
import { GestureHandlerRootView } from "react-native-gesture-handler";
import { SafeAreaProvider } from "react-native-safe-area-context";
import { RootSiblingParent } from "react-native-root-siblings";
import { AuthWrapper } from "./AuthWrapper";
import { Persistor } from "redux-persist";
import { BottomSheetModalProvider } from "@gorhom/bottom-sheet";
import { RootStack, RootStackComponent } from "../routes";
import { PreloadScreen } from "./Preload";
import { LinkingOptions } from "@react-navigation/native";
import { LoginScreen } from "./Preload/LoginScreen";
import { KeyboardProvider } from "react-native-keyboard-controller";
import { HTMLImageProvider } from "./HTMLRender/htmlImageProvider";
import { HoldMenuProvider } from "react-native-hold-menu";

// @ts-ignore
TouchableOpacity.defaultProps = {
  // @ts-ignore
  ...(TouchableOpacity.defaultProps || {}),
  activeOpacity: 0.6,
};
// @ts-ignore
Text.defaultProps = {
  // @ts-ignore
  ...(Text.defaultProps || {}),
  maxFontSizeMultiplier: 1.3,
};
// @ts-ignore
TextInput.defaultProps = {
  // @ts-ignore
  ...(TextInput.defaultProps || {}),
  maxFontSizeMultiplier: 1.3,
};

setupLanguage().then();

interface Props extends PropsWithChildren {
  persistor?: Persistor;
  primaryColor?: string;
  CustomWrapper?: JSX.Element | null;
}

const _MyApp = memo((props: Props) => {
  const { themeName } = useThemeStore();
  const [mounted, setMounted] = useState(false);

  useEffect(() => {
    setCustomText({
      style: {
        fontFamily: Fonts.Normal,
      },
    });
  }, []);

  const mountedColor = useMounted(2000);

  useEffect(() => {
    (async () => {
      await DeviceInfo.setupDeviceInfo();
      setMounted(true);
    })();
  }, []);

  const renderChild = () => {
    return (
      <GestureHandlerRootView
        style={{
          flex: 1,
          backgroundColor: !mountedColor
            ? AppInfo.primaryColor
            : themeName === ETheme.light
            ? Colors.white
            : Colors.neutral1,
        }}
      >
        <KeyboardProvider statusBarTranslucent={true}>
          <AnimatedWindowProvider>
            <AnimatedKeyboardProvider>
              <SafeAreaProvider>
                <MyThemeProvider primaryColor={props.primaryColor || "#242424"}>
                  <StatusBar
                    barStyle={
                      themeName === ETheme.light
                        ? "dark-content"
                        : "light-content"
                    }
                    backgroundColor={"transparent"}
                    translucent={true}
                  />
                  <RootSiblingParent>
                    <HTMLImageProvider>
                      <HoldMenuProvider>
                        <BottomSheetModalProvider>
                          {mounted && props.children}
                          <AuthWrapper />
                          {props.CustomWrapper ? props.CustomWrapper : null}
                        </BottomSheetModalProvider>
                      </HoldMenuProvider>
                    </HTMLImageProvider>
                  </RootSiblingParent>
                </MyThemeProvider>
              </SafeAreaProvider>
            </AnimatedKeyboardProvider>
          </AnimatedWindowProvider>
        </KeyboardProvider>
      </GestureHandlerRootView>
    );
  };

  return (
    <Provider store={store}>
      {props.persistor ? (
        <PersistGate persistor={props.persistor}>{renderChild()}</PersistGate>
      ) : (
        renderChild()
      )}
    </Provider>
  );
});

interface AppProps extends Props {
  component: NamedExoticComponent<object>;
  noPreload?: boolean;
  linking?: LinkingOptions<ReactNavigation.RootParamList>;
  preloadComp?: NamedExoticComponent<object>;
}

/* -- Mini App -- */
export const MyApp = memo(
  ({
    component,
    preloadComp,
    persistor,
    primaryColor,
    noPreload,
    linking,
    CustomWrapper,
  }: AppProps) => {
    return (
      <_MyApp
        persistor={persistor}
        primaryColor={primaryColor || AppInfo.primaryColor}
        CustomWrapper={CustomWrapper}
      >
        <RootStackComponent linking={linking}>
          {!noPreload ? (
            <RootStack.Group
              screenOptions={{
                animation:
                  Platform.OS === "ios"
                    ? "slide_from_bottom"
                    : "fade_from_bottom",
              }}
            >
              <RootStack.Screen
                name={"PreloadScreen"}
                component={preloadComp || PreloadScreen}
              />
              <RootStack.Screen
                name={"LoginPreloadScreen"}
                component={LoginScreen}
              />
            </RootStack.Group>
          ) : null}
          <RootStack.Screen name={"Main"} component={component} />
          <RootStack.Screen name={"LoginScreen"} component={LoginScreen} />
        </RootStackComponent>
      </_MyApp>
    );
  }
);
