import { styled } from "@true/core";
import Modal from "react-native-modal";
import * as React from "react";
import { memo, PropsWithChildren, useEffect } from "react";
import {
  ActivityIndicatorProps,
  ImageSourcePropType,
  ImageStyle,
  Keyboard,
} from "react-native";
import { Icon } from "../Icon";
import { ModalContentContainer } from "../SafeView";

const SWrapper = styled(Modal)`
  margin: 0;
  justify-content: flex-end;
`;

const SContainer = styled(ModalContentContainer)`
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  background-color: ${(props) => props.theme.backgroundColor};
`;

interface Props {
  isVisible: boolean;
  onCloseRequest: () => any;
}
export const ActionSheetWrapper = memo(
  ({ isVisible, onCloseRequest, children }: PropsWithChildren<Props>) => {
    useEffect(() => {
      isVisible && Keyboard.dismiss();
    }, [isVisible]);

    return (
      <SWrapper
        isVisible={isVisible}
        swipeDirection={"down"}
        onSwipeComplete={onCloseRequest}
        onDismiss={onCloseRequest}
        onBackdropPress={onCloseRequest}
        onBackButtonPress={onCloseRequest}
        backdropOpacity={0.4}
        animationIn="slideInUp"
        animationOut="slideOutDown"
        backdropTransitionOutTiming={0}
        avoidKeyboard={true}
      >
        <SContainer>{children}</SContainer>
      </SWrapper>
    );
  }
);

const SHeader = styled.View`
  height: 44px;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
  border-bottom-width: 0.5px;
  border-bottom-color: ${(props) => props.theme.neutral6};
`;
const STitle = styled.Text`
  font-size: 17px;
  color: ${(props) => props.theme.neutral1};
  flex: 1;
`;

const SCloseIcon = styled(Icon)`
  color: ${(props) => props.theme.neutral6};
  font-size: 24px;
  height: 40px;
  line-height: 40px;
  margin-left: 16px;
`;
interface ActionSheetHeaderProps {
  title: string;
  onCloseRequest?: () => any;
}
export const ActionSheetHeader = memo(
  ({ title, onCloseRequest }: ActionSheetHeaderProps) => {
    return (
      <SHeader>
        <STitle numberOfLines={1}>{title}</STitle>
        {!!onCloseRequest && (
          <SCloseIcon name={"close"} onPress={onCloseRequest} />
        )}
      </SHeader>
    );
  }
);

const SRow = styled.TouchableOpacity`
  flex-flow: row;
  align-items: center;
  height: 44px;
  padding: 0 16px;
  margin-bottom: 8px;
`;

const SIcon = styled.Image`
  width: 24px;
  height: 24px;
  margin-right: 16px;
`;

const SText = styled.Text`
  flex: 1;
  font-size: 16px;
  color: ${(props) => props.theme.neutral1};
`;

const LoadingIndicator = styled.ActivityIndicator.attrs(
  ({ theme }): ActivityIndicatorProps => ({
    size: 24,
    color: theme.primaryColor,
  })
)`
  margin-right: 8px;
`;

interface ActionSheetRowProps {
  icon: ImageSourcePropType;
  text: string;
  onPress: () => void;
  isLoading?: boolean;
  iconStyle?: ImageStyle;
}
export const ActionSheetRow = memo(
  ({ icon, iconStyle, text, onPress, isLoading }: ActionSheetRowProps) => {
    return (
      <SRow onPress={onPress}>
        {isLoading ? (
          <LoadingIndicator />
        ) : (
          <SIcon style={iconStyle} source={icon} />
        )}
        <SText numberOfLines={1}>{text}</SText>
      </SRow>
    );
  }
);
