import { memoForwardRef, styled, useMounted } from "@true/core";
import { UIUserPickerInputProps } from "../types";
import React, { memo, RefObject, useCallback, useRef } from "react";
import { BottomSheetModal } from "@gorhom/bottom-sheet";
import { BottomMenuHeader } from "../../../BottomMenu";
import { IFlashItem, UIFlashList } from "../../../Common";
import { MyBottomSheet } from "../../../MyBottomSheet";
import { MyKeyboardAvoidingView } from "../../../MyKeyboardAvoidingView";
import { SearchBar } from "../../../SearchBar";
import { useUserPickerInput } from "../provider";
import { Item } from "./Item";
import { BottomUserRow } from "../UsersRow/BottomUserRow";
import { FlashList } from "@shopify/flash-list";

export const BottomSheet = memoForwardRef(
  (props: UIUserPickerInputProps, ref: RefObject<BottomSheetModal>) => {
    const { keyName } = props;

    const { data, onItemPress, selectedIds, onSearchChange } =
      useUserPickerInput();

    return (
      <MyBottomSheet name={keyName} ref={ref} snapPoints={["100%"]}>
        <Child
          bottomSheetRef={ref}
          data={data}
          onItemPress={onItemPress}
          selectedIds={selectedIds}
          onSearchChange={onSearchChange}
          {...props}
        />
      </MyBottomSheet>
    );
  }
);

interface Props extends UIUserPickerInputProps {
  bottomSheetRef: RefObject<BottomSheetModal>;
  data: any[];
  onItemPress: any;
  selectedIds: any;
  onSearchChange: any;
}

const Child = memo((props: Props) => {
  const {
    title,
    type,
    multiple,
    bottomSheetRef,
    data,
    onItemPress,
    selectedIds,
    onSearchChange,
  } = props;

  const renderItem = useCallback(
    (p: IFlashItem<string>) => {
      return (
        <Item
          type={type}
          {...p}
          onPress={onItemPress}
          selectedIds={selectedIds}
          multiple={multiple}
        />
      );
    },
    [type, selectedIds, multiple]
  );

  const scrollRef = useRef<FlashList<any>>(null);

  const mounted = useMounted(100);

  return (
    <MyKeyboardAvoidingView disableOnAndroid={true}>
      {title ? (
        <BottomMenuHeader
          title={title as string}
          onClose={() => bottomSheetRef.current?.dismiss()}
          noDivider={true}
        />
      ) : null}
      <SSearchBar
        containerStyle={{ marginTop: 8 }}
        onSearchTextChange={onSearchChange}
        isOnModal={true}
      />
      {!multiple ? (
        <Item
          type={type}
          item={selectedIds.value[0]}
          onPress={onItemPress}
          selectedIds={selectedIds}
          multiple={multiple}
          isHeaderItem={true}
          index={0}
          rawIndex={0}
        />
      ) : null}
      {mounted ? (
        <UIFlashList
          ref={scrollRef}
          data={data}
          renderItem={renderItem}
          estimatedItemSize={50}
          keyboardShouldPersistTaps={"handled"}
          isBottomSheet={true}
        />
      ) : null}
      {multiple ? (
        <BottomUserRow
          type={type}
          ids={selectedIds}
          onClearPress={onItemPress}
          onItemPress={(id) => {
            scrollRef.current?.scrollToItem({ item: id, animated: true });
          }}
        />
      ) : null}
    </MyKeyboardAvoidingView>
  );
});

const SDivider = styled.View`
  height: 1px;
  background-color: ${(p) => p.theme.neutral7};
`;

const SSearchBar = styled(SearchBar)`
  margin: 0 16px 8px 16px;
`;
