import {useCallback, useMemo} from "react";
import { useCommentsProvider } from "../Provider";
import { useKeyboardAvoidingView } from "../../AnimatedKeyboardAvoidingView";

export const useUiComment = () => {
  const { keyboardRef, map } = useCommentsProvider();
  const { scrollRef } = useKeyboardAvoidingView();

  const onScroll = useCallback(
    (e: any) => {
      keyboardRef.current?.onScroll(e);
    },
    [keyboardRef],
  );

  return useMemo(
    () => ({
      map,
      scrollRef,
      onScroll
    }),
    [keyboardRef, map, scrollRef]
  );
};
