import React, {memo} from 'react';
import {TextStyle, ViewStyle} from 'react-native';
import {Switch} from '../Switch';
import {InputTemplate} from './components/InputTemplate';

interface Props {
  containerStyle?: ViewStyle;
  value: boolean;
  title: string;
  keyName: string;
  onValueChange: (keyName: string, value: boolean) => void;
  disabled?: boolean;
  required?: boolean;
  type?: 'normal' | 'border';
  activeTrackColor?: string;
  inActiveTrackColor?: string;
  disabledColor?: string;
  thumbColor?: string;
  titleStyle?: TextStyle;
}

export const MySwitch = memo(function UITextInput(props: Props) {
  const {
    value,
    title,
    keyName,
    disabled,
    onValueChange,
    activeTrackColor,
    inActiveTrackColor,
    disabledColor,
    thumbColor,
  } = props;

  return (
    <InputTemplate {...props} title={title} flexDirection={'row'}>
      <Switch
        keyName={keyName}
        disabled={disabled}
        onPress={onValueChange}
        value={value}
        activeTrackColor={activeTrackColor}
        inActiveTrackColor={inActiveTrackColor}
        disabledColor={disabledColor}
        thumbColor={thumbColor}
      />
    </InputTemplate>
  );
});
