import React, { memo, useEffect, useMemo, useState } from "react";
import { styled } from "@true/core";
import { View } from "react-native";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  withDelay,
  withTiming,
} from "react-native-reanimated";

interface Props {
  visible: boolean;
  mode?: ("height" | "opacity")[];
  children?: any;
  enterDuration?: number;
  exitDuration?: number;
  enterHeightDelay?: number;
  exitHeightDelay?: number;
  enterOpacityDelay?: number;
  exitOpacityDelay?: number;
}

export const AnimatedWrap = memo(
  ({
    visible,
    children,
    enterDuration = 150,
    exitDuration = 150,
    enterHeightDelay = 0,
    exitHeightDelay = 0,
    enterOpacityDelay = 0,
    exitOpacityDelay = 0,
    mode = ["height"],
  }: Props) => {
    const heightValue = useSharedValue(0);
    const opacityValue = useSharedValue(0);
    const [height, setHeight] = useState(0);

    const isOpacity = useMemo(() => mode?.includes("opacity"), [mode]);
    const isHeight = useMemo(() => mode?.includes("height"), [mode]);

    useEffect(() => {
      if (visible) {
        if (isOpacity)
          opacityValue.value = withDelay(
            enterOpacityDelay,
            withTiming(1, {
              duration: enterDuration,
            })
          );

        if (isHeight)
          heightValue.value = withDelay(
            enterHeightDelay,
            withTiming(height, {
              duration: enterDuration,
            })
          );
      } else {
        if (isHeight)
          heightValue.value = withDelay(
            exitHeightDelay,
            withTiming(0, {
              duration: exitDuration,
            })
          );

        if (isOpacity)
          opacityValue.value = withDelay(
            exitOpacityDelay,
            withTiming(0, {
              duration: exitDuration,
            })
          );
      }
    }, [visible, isHeight, isOpacity, height]);

    const animatedStyles = useAnimatedStyle(
      () => ({
        height: isHeight ? heightValue.value : height,
        opacity: isOpacity ? opacityValue.value : 1,
      }),
      [isOpacity, isHeight, opacityValue, heightValue, height]
    );

    return (
      <SContainer style={animatedStyles}>
        <View
          onLayout={(e) => {
            if (height === 0) setHeight(e.nativeEvent.layout.height);
          }}
        >
          {children}
        </View>
      </SContainer>
    );
  }
);

const SContainer = styled(Animated.View)`
  overflow: scroll;
`;
