import React, { memo, useCallback, useMemo } from "react";
import { View, StyleSheet, LayoutChangeEvent } from "react-native";
import { IOnReplyPress, IRawComment, RawCommentItem } from "./Item";
import { useCommentsProvider } from "../Provider";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";
import { styled, useComments, usePosts } from "@true/core";

interface Props {
  comment: IRawComment;
  onReplyPress: (props: IOnReplyPress) => void;
  onToggle?: () => void;
  onHide?: () => void;
  onShow?: () => void;
  requestReply?: (...params: any) => Promise<any>;
  type: "comment" | "post";
  appName: string;
  visible?: boolean;
  refreshing?: boolean;
  index?: number;
}

export const RawComment = memo((props: Props) => {
  const { comment, type } = props;
  const { mountedItem, repliedItem } = useCommentsProvider();
  const height = useSharedValue(0);

  const id = useMemo(
    () => (typeof comment !== "string" ? comment.hid : comment),
    [comment]
  );

  /* -- determine if this is a comment or post -- */
  const _comment = type == "comment" ? useComments(id) : usePosts(id);

  const onLayout = useCallback(
    ({
      nativeEvent: {
        layout: { height: _height },
      },
    }: LayoutChangeEvent) => {
      height.value = _height;
    },
    []
  );

  const animatedStyle = useAnimatedStyle(() => {
    return {
      opacity:
        mountedItem.value?.[id] &&
        (repliedItem.value?.[id] || (_comment && _comment?.stats?.comments > 0))
          ? 1
          : 0,
      height: height.value - 32 - 16,
    };
  }, [id, _comment?.stats?.comments]);

  return (
    <>
      {type === "comment" ? (
        <SDividerVertical
          style={[StyleSheet.absoluteFillObject, animatedStyle]}
        />
      ) : null}
      <View onLayout={onLayout}>
        <RawCommentItem {...props} />
      </View>
    </>
  );
});

const SDividerVertical = styled(Animated.View)`
  background-color: ${(p) => p.theme.neutral5};
  width: 1px;
  z-index: 100;
  right: 16px;
  margin-left: 32px;
  margin-top: 56px;
`;
