import {
  NativeScrollEvent,
  NativeSyntheticEvent,
  Text,
  View,
} from "react-native";
import {
  interactManager,
  memoForwardRef,
  styled,
  useCommentLength,
} from "@true/core";
import { TopInsetTagUserView, useCommentsProvider } from "../Provider";
import React, { memo, useCallback, useEffect, useMemo } from "react";
import { useKeyboardAvoidingView } from "../../AnimatedKeyboardAvoidingView";
import { IOnReplyPress, IRawComment, RawCommentItem } from "./Item";
import { RawReplyItem } from "./Reply";
import { UIFlashList, IFlashItem, IFlashSection } from "../../Common";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from "react-native-reanimated";
import { UIButton } from "../../UIButton";
import { RawComment } from "./Comment";
import SkeletonPlaceholder from "react-native-skeleton-placeholder";

const DEFAULT_DATA_LENGHT = 10;

export interface RawCommentListProps {
  data: IRawComment[];
  ListHeaderComponent?: JSX.Element;
  index?: number;
  singleListIndex?: number;
  onScroll?: (event: NativeSyntheticEvent<NativeScrollEvent>) => void;
  onReplyPress?: (props: IOnReplyPress) => void;
  requestReply?: (...params: any) => Promise<any>;
  type: "comment" | "post";
  appName: string;
  refreshing?: boolean;
  loading?: boolean;
  loadingMore?: boolean;
  onLoadData?: (isRefreshing: boolean, isLoadMore: boolean) => void;
  onLoadMore?: () => void;
}

export const UICommentList = memoForwardRef(
  (props: RawCommentListProps, ref: any) => {
    const {
      data,
      ListHeaderComponent,
      index,
      singleListIndex,
      onScroll,
      onReplyPress,
      requestReply,
      type,
      appName,
      refreshing,
      loading,
      loadingMore,
      onLoadData,
      onLoadMore,
    } = props;

    const { keyboardRef, map, listHeight, itemLayout } = useCommentsProvider();

    const { scrollRef, enableAutoScroll } = useKeyboardAvoidingView();

    // const [map, setMap] = useState<Record<string, IRawComment[]>>({});

    const renderSection = useCallback(
      ({
        item,
        toggle,
        rawIndex,
        index,
        visible,
        hide,
        show,
      }: IFlashSection<IRawComment>) => {
        if (item === "skeleton_1") return <SkeletonItem type={1} />;
        if (item === "skeleton_2") return <SkeletonItem type={2} />;
        if (item === "skeleton_3") return <SkeletonItem type={3} />;

        return (
          <RawComment
            comment={item}
            onReplyPress={onReplyPress}
            onToggle={toggle}
            onHide={hide}
            onShow={show}
            requestReply={requestReply}
            type={type}
            appName={appName}
            visible={visible}
            refreshing={refreshing}
            index={rawIndex}
          />
        );
      },
      [onReplyPress, type, appName, refreshing]
    );

    const endScrollVal = useSharedValue<number | null>(null);
    const scrollVal = useSharedValue(0);

    const _onScroll = useCallback(
      (e: NativeSyntheticEvent<NativeScrollEvent>) => {
        onScroll?.(e);
        keyboardRef.current?.onScroll(e);
        endScrollVal.value =
          e.nativeEvent.contentSize.height -
          e.nativeEvent.layoutMeasurement.height;
        scrollVal.value = e.nativeEvent.contentOffset.y;
      },
      [onScroll, keyboardRef]
    );

    /* -- scroll to end when there new comment -- */
    useEffect(() => {
      if (
        endScrollVal.value !== null &&
        Math.round(scrollVal.value - endScrollVal.value) >= -60
      ) {
        interactManager(
          () => scrollRef.current?.scrollToEnd({ animated: true }),
          300
        );
      }
    }, [data.length]);

    const renderItem = useCallback(
      ({ item, rawIndex, index, isLast }: IFlashItem<IRawComment>) => {
        return (
          <RawReplyItem
            type={type}
            appName={appName}
            comment={item}
            onReplyPress={onReplyPress}
            index={rawIndex}
            isLast={isLast}
          />
        );
      },
      [onReplyPress, type, appName]
    );

    return index === undefined ? (
      <View style={{ flex: 1, overflow: "hidden" }}>
        <TopInsetTagUserView />
        {/*<LoadingView loading={loading} />*/}
        <UIFlashList
          ListHeaderComponent={
            <>
              {ListHeaderComponent}
              <LoadMoreBtn onPress={onLoadMore} loadingMore={loadingMore} />
            </>
          }
          data={loading ? ["skeleton_1", "skeleton_3", "skeleton_2"] : data}
          estimatedItemSize={100}
          renderItem={renderItem}
          map={map}
          onScrollBeginDrag={() => {
            keyboardRef.current?.onBeginDrag();
          }}
          renderSection={renderSection}
          loading={loading || false}
          refreshing={refreshing || false}
          loadingMore={loadingMore || false}
          onLoadData={onLoadData}
          refreshable={true}
          loadMorable={true}
          keyboardShouldPersistTaps={"handled"}
          ref={scrollRef}
          onScroll={_onScroll}
          onLayout={(e) => {
            listHeight.value = e.nativeEvent.layout.height;
          }}
          contentContainerStyle={{
            paddingBottom: 8,
          }}
        />
      </View>
    ) : null;
  }
);

const LoadMoreBtn = memo(
  ({ onPress, loadingMore }: { onPress: () => void; loadingMore: boolean }) => {
    const dataLength = useCommentLength();

    return (
      <SLoadMoreBtn
        disabled={dataLength < DEFAULT_DATA_LENGHT}
        title={dataLength < DEFAULT_DATA_LENGHT ? "" : "Load previous comments"}
        onPress={onPress}
        loading={loadingMore}
        hideContentWhileLoading={true}
      />
    );
  }
);

const SLoadMoreBtn = styled(UIButton).attrs((p) => ({
  textStyle: {
    color: p.disabled ? p.theme.neutral3 : p.theme.neutral2,
    fontSize: 12,
    lineHeight: 20,
  },
}))`
  padding: 8px 16px;
  align-items: center;
`;

const timing = (toValue: number) => {
  "worklet";
  return withTiming(toValue, { duration: 200 });
};

const LoadingView = memo(({ loading }: { loading: boolean }) => {
  const aStyle = useAnimatedStyle(
    () => ({
      opacity: loading ? timing(1) : timing(0),
    }),
    [loading]
  );

  return (
    <SLoadingView style={aStyle} pointerEvents={"none"}>
      <SActivityIndicator />
    </SLoadingView>
  );
});

const SLoadingView = styled(Animated.View)`
  position: absolute;
  width: 100%;
  z-index: 99999;
  top: 50%;
`;
const SActivityIndicator = styled.ActivityIndicator.attrs((p) => ({
  color: p.theme.neutral1,
}))`
  position: absolute;
  width: 100%;
  z-index: 99999;
  bottom: -30px;
`;

const SkeletonItem = memo(({ type = 1 }: { type?: 1 | 2 | 3 }) => {
  const height = useMemo(() => {
    if (type == 1) return 80;
    if (type == 2) return 100;
    return 120;
  }, [type]);

  return (
    <View style={{ paddingHorizontal: 16, marginTop: 14 }}>
      <SkeletonPlaceholder borderRadius={4}>
        <View style={{ flexDirection: "row" }}>
          <View
            style={{
              height: 32,
              width: 32,
              borderRadius: 20,
              marginRight: 8,
            }}
          />
          <View style={{ height, flex: 1, borderRadius: 8 }} />
        </View>
      </SkeletonPlaceholder>
    </View>
  );
});
