import React, { memo, useCallback, useEffect, useMemo } from "react";
import { InteractionManager, Platform, StatusBar } from "react-native";
import { useUpdateEffect } from "react-use";
import { usePrevious } from "react-use";
import { IOSFloatingModal } from "./IOSFloatingModal";
import { AndroidFloatingModal } from "./AndroidFloatingModal";
import { useSharedValue, withTiming } from "react-native-reanimated";

export interface ActionProps {
  color?: string;
  icon?: any;
  name: string;
  text?: string;
  textBackground?: string;
  textColor?: string;
  component?: Function;
  animated?: boolean;
}
interface Props {
  isPressed: boolean;
  onClose: () => void;
  actions?: ActionProps[];
  onPressItem?: (itemName: string) => void;
  routeName?: string;
  paddingBottom?: number;
}

export const FloatingModal = memo(
  ({
    onClose,
    actions,
    onPressItem,
    isPressed,
    routeName,
    paddingBottom,
  }: Props) => {
    const isSingle = useMemo(
      () => !actions || actions?.length == 0 || actions[0].name == "single",
      [actions]
    );
    // const isManager = useMemo(
    //   () => !actions || actions?.length == 1,
    //   [actions]
    // );
    const value = useSharedValue(0);
    const prevRouteName = usePrevious(routeName);

    useEffect(() => {
      let stack = isPressed
        ? StatusBar.pushStackEntry({
            showHideTransition: "fade",
            networkActivityIndicatorVisible: true,
            animated: true,
            backgroundColor: "rgba(0,0,0,0)",
            hidden: false,
            barStyle: "light-content",
          })
        : null;
      return () => {
        stack && StatusBar.popStackEntry(stack);
      };
    }, [isPressed]);

    useUpdateEffect(() => {
      if (prevRouteName == routeName) {
        if (isPressed) {
          value.value = withTiming(1, { duration: 200 });
        } else {
          value.value = withTiming(0, { duration: 200 });
        }
      }
    }, [isPressed, routeName]);

    const _onPress = useCallback(
      (itemName: string) => {
        onClose();
        InteractionManager.runAfterInteractions(() => {
          setTimeout(() => {
            onPressItem?.(itemName);
          }, 300);
        });
      },
      [onPressItem, onClose]
    );

    return (
      <>
        {!isSingle ? (
          Platform.OS == "ios" ? (
            <IOSFloatingModal
              value={value}
              actions={actions}
              onClose={onClose}
              _onPress={_onPress}
            />
          ) : (
            <AndroidFloatingModal
              value={value}
              actions={actions}
              onClose={onClose}
              _onPress={_onPress}
              isPressed={isPressed}
            />
          )
        ) : null}
      </>
    );
  }
);
