import { useSelector } from "react-redux";
import { useCallback, useMemo } from "react";
import { ToastService, useNavigationParam, useUserByQuery } from "@true/core";

export const useSelectedUserIds = (
  value: any,
  _valueType?: "username" | "userId"
) => {
  const valueType = useNavigationParam("valueType", _valueType || "username");
  const userUsers = useSelector((state) => state?.users.byKey);
  const allUserIds = useUserByQuery("allIds");
  const selectedUserIds = useMemo(() => {
    if (typeof value === "string") {
      return value
        .split(" ")
        .filter(Boolean)
        .map((_username) => {
          const username = _username.trim().replace(/^@/, "");
          return userUsers?.[username]?.id || "";
        });
    }

    if (typeof value === "number") {
      return [value];
    }

    if (Array.isArray(value)) {
      return value.map((item) => {
        return userUsers?.[item]?.id || "";
      });
    }
    return [];
  }, [value, userUsers]);

  const returnType:
    | "usernameString"
    | "usernameArray"
    | "userIdArray"
    | "userId"
    | "undefined" = useMemo(() => {
    if (typeof value === "string") return "usernameString";
    if (typeof value === "number") return "userId";

    if (Array.isArray(value)) {
      if (valueType == "username") return "usernameArray";
      if (valueType == "userId") return "userIdArray";
    }

    return "undefined";
  }, [value, valueType]);

  const emptyValue = useMemo(
    () =>
      returnType === "usernameString"
        ? ""
        : returnType === "usernameArray" || returnType === "userIdArray"
        ? []
        : undefined,
    [returnType]
  );

  const showError = useCallback(() => {
    ToastService.simpleShow(
      "cannot set value because default value is undefined"
    );
  }, []);

  return {
    useUsers: userUsers,
    selectedUserIds,
    returnType,
    emptyValue,
    showError,
    allUserIds,
  };
};
