import React from "react";
import RecaptchaV3Module from "@haskkor/react-native-recaptchav3";
import RootSiblings from "react-native-root-siblings";
import { Core } from "@true/core";

// 6Le7y2wiAAAAAA-oBaqq682pwxfJ_ZC3r9qejpPa

export const RecaptchaV3 = {
  get _siteKey() {
    return Core.recaptchaV3SiteKey || "6LchEjUgAAAAAEoI7egtpkeB2xtFeMKeV5C1QJ-B"
  },

  get _baseUrl() {
    return `https://${Core.sysUrl}`;
  },

  requestToken: async (params?: { baseUrl?: string }) => {
    const { baseUrl } = params || {};
    return await new Promise<string>((resolve, reject) => {
      const timeout = setTimeout(
        () => rootSibling?.destroy(() => reject("RECAPTCHA_V3_TIMED_OUT")),
        10000
      );

      const onReceiveToken = (token: any) =>
        rootSibling.destroy(() => {
          resolve(token);
          clearTimeout(timeout);
        });

      const rootSibling = new RootSiblings(
        (
          <RecaptchaV3Module
            captchaDomain={baseUrl || RecaptchaV3._baseUrl}
            siteKey={RecaptchaV3._siteKey}
            onReceiveToken={onReceiveToken}
            action={"login"}
          />
        )
      );
    });
  },
};
