import constate from "constate";
import { useSelectorInput } from "../hook";

// interface IOption {
//   label: string;
//   value: any;
//   disabled?: boolean;
// }

interface Props {
  value: any;
  keyName: string;
  onValueChange: (keyName: string, value: any) => void;
  onSave?: (value: any) => void;
  multiple?: boolean;
  options: {
    label: string;
    value: any;
    disabled?: boolean;
  }[];
  returnType?: "string" | "array";
  isSubmitButton?: boolean;
  defaultOptionLength?: number;
}

const useConstate = (props: Props) => {
  return useSelectorInput(props);
};

const [_MySelectorInputProvider, _useMySelectorInput] = constate(
  useConstate,
  (value) => value
);

export const MySelectorInputProvider: typeof _MySelectorInputProvider =
  _MySelectorInputProvider;
export const useMySelectorInput: () => ReturnType<typeof useSelectorInput> =
  _useMySelectorInput;
