import React, { memo } from "react";
import Animated, {
  SharedValue,
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";
import { useAnimatedScrollableView } from "./index";
import { useSingleTabView } from "./SingleTabView/provider";
import { ActivityIndicator } from "react-native";

export const StickyHeader = memo(
  (props: {
    index: number;
    singleListIndex?: number;
    loadingFirst?: boolean;
    _headerHeight: SharedValue<number>;
    listHeaderHeight?: SharedValue<number>;
  }) => {
    const { index, loadingFirst, _headerHeight, singleListIndex } = props;

    const {
      tabs,
      aHeight,
      aScrollVal,
      headerHeight,
      aLevelIndex,
      scrollValMap,
    } = useAnimatedScrollableView();
    const { tabs: singTabs, singleTabBarHeight } = useSingleTabView();

    const dumbHeight = useSharedValue(0);

    const stickyStyle = useAnimatedStyle(() => {
      let min = Math.min(aHeight.value, -aScrollVal.value);

      if (
        aLevelIndex.value != index.toString() &&
        (scrollValMap.value?.[index] === undefined ||
          -aHeight.value >= scrollValMap.value?.[index])
      ) {
        min = aHeight.value;
      }

      const maxTop = -headerHeight.value - (singleTabBarHeight?.value || 0);
      const max = Math.max(min, maxTop);
      const top = max + _headerHeight.value;

      return {
        top: top,
      };
    }, []);

    const dumbStyle = useAnimatedStyle(
      () => ({
        height: dumbHeight.value,
      }),
      []
    );

    /**
     * indicatorStyle
     */
    const indicatorStyle = useAnimatedStyle(
      () => ({
        top:
          60 + (aHeight?.value || 0) + _headerHeight.value + dumbHeight.value,
        // (listHeaderHeight?.value || 0),
      }),
      []
    );

    return (
      <>
        {loadingFirst ? (
          <Animated.View
            pointerEvents={"none"}
            style={[
              indicatorStyle,
              { position: "absolute", width: "100%", zIndex: 1 },
            ]}
          >
            <ActivityIndicator />
          </Animated.View>
        ) : null}
        {(singleListIndex !== undefined &&
          singTabs?.[singleListIndex]?.stickyHeaderRender) ||
        tabs[index].stickyHeaderRender ? (
          <>
            <Animated.View style={dumbStyle} />
            <Animated.View
              onLayout={(e) => {
                dumbHeight.value = e.nativeEvent.layout.height;
              }}
              style={[
                { position: "absolute", left: 0, right: 0, zIndex: 1 },
                stickyStyle,
              ]}
            >
              {singleListIndex !== undefined &&
              singTabs?.[singleListIndex]?.stickyHeaderRender
                ? singTabs[singleListIndex].stickyHeaderRender
                : tabs[index].stickyHeaderRender}
            </Animated.View>
          </>
        ) : null}
      </>
    );
  }
);
