import React, { memo, useCallback, useMemo, useState } from "react";
import { FullScreenWrapper } from "../ScreenWrap";
import { DynamicHeader, HeaderActionText } from "../Header";
import { SearchBar } from "../SearchBar";
import {
  IUser,
  styled,
  translate,
  useMe,
  useNavigationParams,
} from "@true/core";
import { RecycleMemberList } from "../Member/RecycleMemberList";
import { UserPickerModalContext, UserPickerModalProvider } from "./context";
import { useNavigation } from "@react-navigation/native";
import { IUserRole, UserPickerModalProps } from "./types";
import { HeaderSection } from "./HeaderSection";
import { UserRow } from "./UserRow";
import { UsersTray } from "./UsersTray";
import { StyleSheet } from "react-native";
import { Switch } from "../Switch";
import { useSelectedUserIds } from "./hook";
import { MyKeyboardAvoidingView } from "../MyKeyboardAvoidingView";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";

const SearchBarWrapper = styled.View`
  flex-direction: row;
  border-bottom-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
`;

const _UserPickerModal = memo(function UserPickerModal() {
  const {
    title,
    updateText,
    multiple,
    onGoBack,
    selectManager,
    onlySelectManager,
    defaultUsernames,
    onNoAssigneePress,
    value,
    onSubmit,
    isMetaChangeable,
    onSave,
  } = useNavigationParams<UserPickerModalProps>();
  const navigation = useNavigation();
  const me = useMe();
  const { useUsers, allUserIds, selectedUserIds, emptyValue } =
    useSelectedUserIds(value);
  const [searchText, setSearchText] =
    UserPickerModalContext.useSearchTextState();

  const picker = useMemo(
    () => (selectManager ? "manager" : ""),
    [selectManager]
  );

  const userNames = useMemo(() => {
    const result: string[] = [];

    for (let id of allUserIds) {
      const user: IUser | undefined = useUsers[id];

      if (user.id) result.push(user?.id);
    }
    return result;
  }, [useUsers]);

  // @ts-ignore
  const userOwnerIds = useMemo<string[]>(() => {
    return (
      me &&
      me?.cached_managers &&
      me.cached_managers
        .map((username: string) => useUsers?.[username]?.id)
        .filter((id: string) => !!id)
    );
  }, [me, useUsers]);
  const defaultUserIds = useMemo(
    () =>
      defaultUsernames
        ?.map((username) => useUsers?.[username]?.id)
        .filter((id) => !!id) || [],
    [defaultUsernames, useUsers]
  );

  const [allowExtendMembers, setAllowExtendMembers] = useState(false);

  const allManager = useMemo(() => {
    if (defaultUsernames && !allowExtendMembers) {
      return defaultUserIds;
    }
    if (onlySelectManager) {
      if (picker == "manager")
        return [...new Set(["manager", ...userOwnerIds])];
      return [...new Set(userOwnerIds)];
    }
    if (picker == "manager")
      return [...new Set(["manager", ...userOwnerIds, "member", ...userNames])];
    return [...new Set([...userOwnerIds, ...userNames])];
  }, [
    userOwnerIds,
    userNames,
    onlySelectManager,
    defaultUsernames,
    allowExtendMembers,
    picker,
  ]);

  const rowRenderer = useCallback(
    (id: string) => {
      if (id == IUserRole.manager || id == IUserRole.member) {
        const name =
          id == IUserRole.manager
            ? translate("comp.direct_managers")
            : translate("comp.members");
        return <HeaderSection title={name} />;
      }
      return <UserRow id={id} multiple={multiple} />;
    },
    [multiple]
  );

  const onPressSubmit = UserPickerModalContext.useOnPressSubmit();

  const _onNoAssigneePress = useCallback(() => {
    if (!multiple) {
      onSave?.(emptyValue);
      onSubmit(emptyValue);
      onNoAssigneePress && onNoAssigneePress();
      onGoBack ? onGoBack() : navigation.goBack();
    }
  }, [onNoAssigneePress, onSubmit, multiple, onGoBack, emptyValue, onSave]);

  const renderHeader = useCallback(() => {
    return (
      <UserRow
        id={(selectedUserIds?.[0] || "") as any}
        multiple={multiple}
        onPress={_onNoAssigneePress}
      />
    );
  }, [_onNoAssigneePress, selectedUserIds, multiple]);

  const onChangeMetaPress = useCallback((_: string, value: boolean) => {
    setAllowExtendMembers(value);
  }, []);

  const { aBottom } = useAnimatedSafeAreaInsets();
  const animatedStyles = useAnimatedStyle(() => ({
    paddingBottom: aBottom.value + +16,
  }));

  return (
    <FullScreenWrapper>
      <DynamicHeader
        hideSeparator={true}
        onGoBack={onGoBack}
        title={translate(title || "comp.pick_user")}
        rightComponent={
          multiple ? (
            <HeaderActionText
              textProps={{
                numberOfLines: 1,
              }}
              text={updateText || translate("comp.save")}
              onPress={onPressSubmit}
            />
          ) : null
        }
      />
      <SearchBarWrapper>
        <SSearchBar
          containerStyle={styles.search}
          onSearchTextChange={setSearchText}
          // autoFocus={true}
        />
        {/*<SearchBar*/}
        {/*  value={searchText}*/}
        {/*  debounceTime={0}*/}
        {/*  onSearchTextChange={setSearchText}*/}
        {/*/>*/}
      </SearchBarWrapper>

      <MyKeyboardAvoidingView>
        {!multiple && (selectedUserIds?.length || 0) > 0 && renderHeader()}
        <RecycleMemberList
          search={searchText}
          rowRenderer={rowRenderer}
          defaultUsernames={defaultUsernames}
          userIds={
            (allManager && picker == "manager") || defaultUsernames
              ? allManager
              : []
          }
          multiple={multiple}
          selectedUserIds={selectedUserIds as any}
        />
        {multiple && <UsersTray isMetaChangeable={isMetaChangeable} />}
      </MyKeyboardAvoidingView>
      {isMetaChangeable && (
        <SChangeMetaBtn style={animatedStyles}>
          <SChangeMetaText>
            {translate("comp.allow_extended_members")}
          </SChangeMetaText>
          <Switch
            keyName={""}
            onPress={onChangeMetaPress}
            value={allowExtendMembers}
          />
        </SChangeMetaBtn>
      )}
    </FullScreenWrapper>
  );
});

export const UserPickerModal = () => {
  return (
    // <TransitionContextView transitionKey={'UserPickerModal'} style={{flex: 1}}>

    <UserPickerModalProvider>
      <_UserPickerModal />
    </UserPickerModalProvider>

    // </TransitionContextView>
  );
};

const SChangeMetaBtn = styled(Animated.View)`
  flex-direction: row;
  padding: 16px;
  border-top-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
`;

const SChangeMetaText = styled.Text`
  color: ${(p) => p.theme.neutral3};
  flex: 1;
`;

const styles = StyleSheet.create({
  search: {
    flex: 1,
    height: 40,
    marginTop: 4,
  },
});

const SSearchBar = styled(SearchBar)`
  margin: 0 16px 8px 16px;
`;

export * from "./types";
export * from "./context";
export * from "./hook";
