import React, { memo, useCallback } from "react";
import { IC_TICK, styled, useTheme } from "@true/core";
import { IListItem } from "./types";
import Animated, {
  SharedValue,
  useAnimatedStyle,
} from "react-native-reanimated";
import { TouchableOpacity } from "react-native";

const AnimatedBtn = Animated.createAnimatedComponent(TouchableOpacity);

export const ListItem = memo(
  (
    props: IListItem & {
      onPress: (item: any) => void;
      isSelected: SharedValue<boolean>;
    }
  ) => {
    const { item, onPress, isSelected } = props;
    const theme = useTheme();

    const _onPress = useCallback(() => {
      onPress(item.value);
    }, [item.value]);

    const animatedStyle = useAnimatedStyle(
      () => ({
        backgroundColor: isSelected.value ? theme.primaryColor + 20 : "white",
      }),
      [item.value]
    );

    const animatedIconStyle = useAnimatedStyle(
      () => ({
        opacity: isSelected.value ? 1 : 0,
      }),
      [item.value]
    );

    return (
      <SContainer style={animatedStyle} onPress={_onPress}>
        <STextWrapper>
          <SText>{item.label}</SText>
        </STextWrapper>
        <SIconWrapper style={animatedIconStyle}>
          <SIcon source={IC_TICK} />
        </SIconWrapper>
      </SContainer>
    );
  }
);

const SContainer = styled(AnimatedBtn)`
  flex-direction: row;
  margin-bottom: 4px;
`;
const STextWrapper = styled.View`
  margin: 12px 0 12px 16px;
  flex: 1;
`;
const SText = styled.Text`
  color: ${(p) => p.theme.neutral1};
  font-size: 16px;
  line-height: 24px;
`;
const SIconWrapper = styled(Animated.View)`
  margin-right: 16px;
  justify-content: center;
`;
const SIcon = styled.Image`
  tint-color: ${(p) => p.theme.primaryColor};
`;
