import { memoForwardRef, setMultiRef, styled } from "@true/core";
import {
  NativeScrollEvent,
  NativeSyntheticEvent,
  Platform,
  ScrollViewProps,
} from "react-native";
import { ScrollView } from "react-native-gesture-handler";
import { useAnimatedScrollableView } from "../index";
import React, { RefObject, useCallback, useMemo, useRef } from "react";
import Animated, {
  AnimateProps,
  interpolate,
  useAnimatedReaction,
  // useAnimatedRefScrollHandler,
  useAnimatedScrollHandler,
  useAnimatedStyle,
} from "react-native-reanimated";
import { useAuToScroll } from "../hooks/useAutoScroll";
import { StickyHeader } from "../StickyHeader";
import { useSingleTabView } from "./provider";

const _AnimatedScrollView = Animated.createAnimatedComponent(
  ScrollView
) as unknown as typeof Animated.ScrollView;

export const _ScrollView = memoForwardRef(
  (
    props: {
      singleListIndex: number;
      refreshControl?: (p: { progressViewOffset: number }) => JSX.Element;
      onScroll?: (e: NativeScrollEvent) => void;
    } & Omit<AnimateProps<ScrollViewProps>, "refreshControl" | "onScroll">,
    ref: RefObject<Animated.ScrollView>
  ) => {
    const {
      singleListIndex,
      onScroll: propsOnScroll,
      refreshControl: bRefreshControl,
      ...rest
    } = props;

    const {
      onCollapse,
      refreshControl: aRefreshControl,
      aHeight,
    } = useAnimatedScrollableView();

    const { index } = useSingleTabView();

    const refreshControl = useMemo(
      () => bRefreshControl || aRefreshControl,
      [bRefreshControl, aRefreshControl]
    );

    /**
     * auto scroll other tab when current tab is dragging
     */
    const _ref = useRef<Animated.ScrollView>(null);

    const {
      _headerHeight,
      _minHeight,
      // _aHeight,
      onScrollBeginDrag,
      onAutoScroll,
      onContainerListLayout,
      containerHeight,
    } = useAuToScroll({
      singleListIndex,
      _ref,
      index,
    });

    /**
     * style header if there is collapsable comp
     */
    const headerStyle = useAnimatedStyle(() => {
      return {
        height: _headerHeight.value,
      };
    }, []);

    // const { height: windowHeight } = useAnimatedWindow();

    const containerStyle = useAnimatedStyle(() => {
      return {
        minHeight: containerHeight.value - _minHeight.value,
      };
    }, []);

    /**
     * return animatedValue in onCollapse
     */
    useAnimatedReaction(
      () => aHeight.value,
      (_height) => {
        onCollapse?.(interpolate(_height, [0, _minHeight.value], [0, 1]));
      },
      [onCollapse]
    );

    const _onScroll =
      // Platform.OS == "android" && refreshControl
      //   ? useCallback(
      //       (e: NativeSyntheticEvent<NativeScrollEvent>) => {
      //         propsOnScroll?.(e.nativeEvent);
      //       },
      //       [propsOnScroll]
      //     )
      //   :
      useAnimatedScrollHandler(
        {
          onScroll: (e: any) => {
            propsOnScroll?.(e);
            onAutoScroll(e);
          },
          onBeginDrag: onScrollBeginDrag,
        },
        [propsOnScroll, onAutoScroll, onScrollBeginDrag]
      );

    // if (Platform.OS == "android" && refreshControl) {
    //   useAnimatedRefScrollHandler(
    //     _ref,
    //     {
    //       onScroll: onAutoScroll,
    //       onBeginDrag: onScrollBeginDrag,
    //     },
    //     []
    //   );
    // }

    return (
      <SContainer onLayout={onContainerListLayout}>
        <StickyHeader index={index} _headerHeight={_headerHeight} />
        <_AnimatedScrollView
          ref={setMultiRef(_ref, ref)}
          scrollEventThrottle={16}
          {...rest}
          refreshControl={refreshControl?.({
            progressViewOffset: _headerHeight.value,
          })}
          onScrollBeginDrag={
            Platform.OS == "android" && refreshControl
              ? onScrollBeginDrag
              : undefined
          }
          onScroll={_onScroll}
        >
          <Animated.View style={containerStyle}>
            <>
              <Animated.View
                style={[headerStyle, { justifyContent: "flex-end" }]}
              />
              {props.children}
            </>
          </Animated.View>
        </_AnimatedScrollView>
      </SContainer>
    );
  }
);

const SContainer = styled.View`
  flex: 1;
`;
