import ViewPager, { PagerViewProps } from "react-native-pager-view";
import React, { PropsWithChildren, RefObject, useCallback, useRef } from "react";
import { memoForwardRef } from "@true/core";

type Props = {
  currentIndex: number;
  onScrollEnd: (index: number) => any;
};

export default memoForwardRef(
  ({onScrollEnd, currentIndex, children}: PropsWithChildren<Props>, ref: RefObject<ViewPager>) => {
    const pagerRef = ref || useRef<ViewPager>(null);

    const onPageScroll: PagerViewProps['onPageSelected'] = useCallback(
      ({nativeEvent}: any) => {
        onScrollEnd(nativeEvent.position);
      },
      [onScrollEnd],
    );

    return (
      <ViewPager
        style={{flex: 1}}
        ref={pagerRef}
        initialPage={currentIndex}
        onPageSelected={onPageScroll}>
        {children}
      </ViewPager>
    );
  },
);
