import { memoForwardRef } from "@true/core";
import React, { RefObject } from "react";
import { SelectorInputProps } from "./types";
import { SelectorProvider } from "./provider";
import { Input } from "./Input";

export interface UISelectorInput {
  onClearValue: () => void;
}

export const UISelectorInput = memoForwardRef(
  (props: SelectorInputProps, ref: RefObject<UISelectorInput>) => {
    return (
      <SelectorProvider {...props}>
        <Input {...props} ref={ref} />
      </SelectorProvider>
    );
  }
);
