import {PreviewCommentProps, usePreview} from "./provider";

export const usePreviewProps = (props: PreviewCommentProps) => {
  const provider = usePreview()

  const hid = props.hid || provider.hid
  const  token= props.token || provider.token
  const  appName= props.appName || provider.appName
  const  type= props.type || provider.type

  if (!appName) throw new Error('PreviewComment: there no appName')
  if (!type) throw new Error('PreviewComment: there no type')

  return {
    hid,
    token,
    appName,
    type,
  }
}
