import React, { memo, PropsWithChildren, useCallback } from "react";
import Animated, {
  AnimateProps,
  SharedValue,
  useAnimatedReaction,
  useAnimatedStyle,
  useDerivedValue,
  useSharedValue,
  withTiming,
} from "react-native-reanimated";
import { LayoutChangeEvent, StyleSheet, View, ViewProps } from "react-native";

const timing = (value: number) => {
  "worklet";
  return withTiming(value, { duration: 200 });
};

type Props = AnimateProps<ViewProps> &
  PropsWithChildren & {
    visible: SharedValue<boolean>;
    behavior?: "margin" | "height";
  };

export const CollapseView = memo<Props>(
  ({ children, behavior = "margin", visible, ...rest }) => {
    const fixHeight = useSharedValue(0);
    const isNextPressed = useSharedValue<0 | 1>(0);

    const onLayout = useCallback(
      (e: LayoutChangeEvent) => {
        /* only set fix height when it hasn't pressed or visible
         * This condition help fix height not reset when margin is changing */
        if (behavior == "margin" && (!isNextPressed.value || visible.value)) {
          fixHeight.value = e.nativeEvent.layout.height;
          return;
        }

        /* set _height, if _height == fixHeight then run animation,
         * else set new height to fix height */
        if (behavior == "height") {
          fixHeight.value = e.nativeEvent.layout.height;
          _height.value = timing(e.nativeEvent.layout.height);
        }
      },
      [behavior]
    );

    const top = useDerivedValue(() => {
      if (!isNextPressed.value && !visible.value) {
        return fixHeight.value;
      }

      return !visible.value ? timing(fixHeight.value) : timing(0);
    }, [behavior]);

    const _height = useSharedValue(0);

    const height = useDerivedValue(() => {
      // if (behavior === "margin") {
      //   return !visible.value && !isNextPressed.value
      //     ? fixHeight.value
      //     : undefined;
      // }

      /* if _height !== fixHeight then set new height to fix height,
       * else run animation */
      if (
        _height.value !== fixHeight.value &&
        (visible.value || isNextPressed.value)
      ) {
        return undefined;
      }

      return visible.value ? timing(fixHeight.value) : timing(0);
    }, [behavior]);

    const animatedStyle = useAnimatedStyle(() => {
      return {
        ...(behavior === "margin"
          ? {
              marginTop: -top.value,
            }
          : {}),
        ...(behavior === "height"
          ? {
              height: height.value,
            }
          : {}),
        opacity: visible.value ? timing(1) : timing(0),
      };
    }, [behavior]);

    /* set isNextPressed when visible first changing */
    useAnimatedReaction(
      () => visible.value,
      (_visible, _prevVisible) => {
        if (isNextPressed.value) return;
        /* if behavior is margin and init value is true, then set isNextPress is true */
        if (behavior == "margin" && _visible) return (isNextPressed.value = 1);
        if (_prevVisible === null || _prevVisible === undefined) return;
        if (_visible === _prevVisible) return;

        isNextPressed.value = 1;
      },
      [behavior]
    );

    return (
      <Animated.View
        {...rest}
        style={[rest.style, animatedStyle, styles.aView]}
      >
        <View style={styles.fixView} onLayout={onLayout}>
          {children}
        </View>
      </Animated.View>
    );
  }
);

const styles = StyleSheet.create({
  aView: {
    zIndex: -1,
    overflow: "scroll",
  },

  fixView: {
    overflow: "scroll",
  },
});
