import React, { memo, useCallback, useMemo } from "react";
import {
  AvatarStack,
  AvatarStackImage,
  NonUriAvatarStackProps,
} from "./AvatarStack";
import {
  InteractionManager,
  StyleSheet,
  TextStyle,
  TouchableOpacity,
  ViewStyle,
} from "react-native";
import {
  EImageSize,
  flatStyles,
  ImageHelper,
  styled,
  useCreateNavigate,
  useUser,
  useUserByQuery,
} from "@true/core";

interface OwnProps {
  data: string[];
  size?: number;
  limit?: number;
  type?: "member" | "employee";
  containerStyle?: ViewStyle;
  onPress?: () => void;
}

type Props = OwnProps;

const MAX_AVATAR_STACK_SIZE = 5;

const SAvatarStack = styled(AvatarStack).attrs((p) => ({
  borderColor: p.theme.neutral5,
}))``;

export const MembersAvatarStack = memo(
  ({
    data,
    size,
    limit = MAX_AVATAR_STACK_SIZE,
    containerStyle,
    type,
    onPress,
  }: Props) => {
    const navigation = useCreateNavigate("MembersModal");
    const users: (string | number)[] = useUserByQuery("allIds");
    const renderAvatar = useCallback(
      (username: string, props: NonUriAvatarStackProps) => {
        return <Avatar key={username} userKey={username} {...props} />;
      },
      []
    );

    const sumTextStyle = useMemo<TextStyle>(
      () => ({
        fontSize: 15,
      }),
      []
    );

    const onPressCB = useCallback(() => {
      if (onPress) {
        onPress && onPress();
      } else {
        InteractionManager.runAfterInteractions(() => {
          if (!data.length) {
            return;
          }

          navigation({ ids: data });
        });
      }
    }, [type, users, data, onPress]);

    return (
      <TouchableOpacity
        style={flatStyles(styles.container, containerStyle)}
        onPress={onPressCB}
      >
        <SAvatarStack
          data={type && type === "employee" ? users : data}
          limit={limit}
          radius={size ? size / 2 : 20}
          overlap={13}
          borderWidth={1}
          renderAvatar={renderAvatar}
          sumTextStyle={sumTextStyle}
        />
      </TouchableOpacity>
    );
  }
);

interface AvatarProps extends NonUriAvatarStackProps {
  userKey: string;
}

const Avatar = memo(({ userKey, ...restProps }: AvatarProps) => {
  const user = useUser(userKey);

  if (!user) return null;

  return (
    <AvatarStackImage
      key={userKey}
      {...restProps}
      uri={ImageHelper(user.gavatar).getImageSize(EImageSize.SIZE_2)}
    />
  );
});

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: "center",
    marginLeft: 16,
  },
});
