import React, { memo, useCallback, useMemo, useState } from "react";
import {
  Keyboard,
  StyleSheet,
  TouchableOpacity,
  View,
  ViewStyle,
} from "react-native";
import DateTimePickerModal, {
  ReactNativeModalDateTimePickerProps,
} from "react-native-modal-datetime-picker";
import { useBoolean } from "@true/core";
import { styled } from "@true/core";
import moment from "moment";
import { getFontSize, getHeight, getWidth } from "@true/core";
import { IC_CALENDAR, IC_CLOCK, IC_X, Fonts } from "@true/core";

interface IProps {
  title: string;
  placeholder?: string;
  value: string; // html DD/MM/YYYY    HH:mm DD/MM/YYYY
  keyName: string;
  mode: "date" | "time" | "datetime";
  containerStyle?: ViewStyle;
  onValueChange: (keyName: string, value: string) => void;
  dateTimePickerProps?: ReactNativeModalDateTimePickerProps;
  required?: boolean;
  format?: string;
  type?: "border" | "normal";
  showClearButton?: boolean;
  onPress?: () => void;
  onClearButton?: () => void;
  onSave?: (date: Date | string) => void;
}

const formatByMode = {
  date: "DD/MM/YYYY",
  time: "HH:mm",
  datetime: "HH:mm DD/MM/YYYY",
};

const ContainerButton = styled.View`
  min-height: ${getHeight(62)}px;
  border-width: 1px;
  border-color: ${(p) => p.theme.neutral5};
  border-radius: 4px;
  padding-top: ${getHeight(4)}px;
`;

const ContainerNormal = styled.View`
  min-height: ${getHeight(62)}px;
  border-bottom-width: 1px;
  border-bottom-color: ${(p) => p?.theme?.neutral5 + 80 || "#CECECE"};
  padding-top: ${getHeight(4)}px;
`;

const TitleText = styled.Text`
  font-size: ${getFontSize(14)}px;
  line-height: ${getFontSize(18)}px;
  color: ${(p) => p.theme.neutral2};
`;
const TitleTextNormal = styled.Text`
  font-size: ${getFontSize(14)}px;
  line-height: ${getFontSize(18)}px;
  color: ${(p) => p.theme.neutral2};
`;

const ValueText = styled.Text`
  font-size: ${getFontSize(15)}px;
  line-height: ${getFontSize(18)}px;
  padding-top: ${getHeight(4)}px;
  color: ${(p) => p.theme.neutral1};
  font-family: ${Fonts.Bold};
`;

const Placeholder = styled.Text`
  font-size: ${getFontSize(14)}px;
  line-height: ${getFontSize(18)}px;
  padding-top: ${getHeight(4)}px;
  color: ${(p) => p.theme.neutral6};
  font-family: ${Fonts.Bold};
`;

const IconClear = styled.Image`
  width: ${getHeight(10)}px;
  height: ${getHeight(10)}px;
`;

const IconCalendar = styled.Image`
  width: ${getHeight(24)}px;
  height: ${getHeight(24)}px;
  margin-right: ${getWidth(12)}px;
  tint-color: ${(p) => p.theme.neutral3};
`;

const Row = styled.TouchableOpacity`
  flex-direction: row;
`;

const BtnViewTitle = styled.TouchableOpacity`
  flex: 1;
  padding-top: ${getHeight(8)}px;
`;

const BtnViewBorderTitle = styled.View`
  flex: 1;
  padding-top: ${getHeight(8)}px;
  padding-left: ${getWidth(16)}px;
  margin-right: ${getWidth(4)}px;
`;
const STextRequired = styled.Text`
  font-size: ${getFontSize(14)}px;
  color: #fb4444;
`;

const _MyDateTimeInput = memo(function UIDateTimeInput(props: IProps) {
  const {
    title,
    value,
    keyName,
    placeholder,
    onValueChange,
    containerStyle,
    mode,
    dateTimePickerProps,
    required,
    format,
    type,
    showClearButton,
    onPress,
    onClearButton,
    onSave,
  } = props;
  const [isModalVisible, showModal, hideModal] = useBoolean();

  const onChange = useCallback(
    (value: string) => {
      onValueChange(keyName, value);
    },
    [onValueChange, keyName]
  );

  const onConfirm = useCallback(
    (date: Date) => {
      hideModal();
      requestAnimationFrame(() => {
        onChange?.(moment(date).format(format || formatByMode[mode]));
        onSave && onSave(date);
      });
    },
    [mode, onChange, onSave, hideModal, format]
  );

  const onClearSelect = useCallback(() => {
    onClearButton ? onClearButton() : null;
    onValueChange && onValueChange(keyName, "");
    onSave && onSave("");
  }, [onValueChange, keyName, onClearButton, onSave]);

  const date = useMemo(() => {
    if (!value) {
      return new Date();
    }
    const momentTime = moment(value, format || formatByMode[mode]);
    return momentTime.isValid() ? momentTime.toDate() : new Date();
  }, [value, mode, format]);

  const _onPress = useCallback(() => {
    onPress ? onPress() : showModal();
    Keyboard.dismiss();
  }, [onPress]);

  if (type === "normal") {
    return (
      <ContainerNormal style={containerStyle}>
        <Row>
          <BtnViewTitle onPress={_onPress}>
            <TitleTextNormal>
              {title}
              {required ? <STextRequired> *</STextRequired> : null}
            </TitleTextNormal>
            {value ? (
              <View>
                <ValueText>{value}</ValueText>
              </View>
            ) : (
              <Placeholder numberOfLines={1}>
                {placeholder || "---"}
              </Placeholder>
            )}
          </BtnViewTitle>
          <View style={styles.viewDrop}>
            {value && showClearButton && `${value}`.trim() !== "" ? (
              <TouchableOpacity onPress={onClearSelect} style={styles.btnClear}>
                <ViewIcon>
                  <IconClear source={IC_X} />
                </ViewIcon>
              </TouchableOpacity>
            ) : (
              <IconCalendar source={mode == "time" ? IC_CLOCK : IC_CALENDAR} />
            )}
          </View>
        </Row>
        <DateTimePickerModal
          mode={mode}
          isVisible={isModalVisible}
          onConfirm={onConfirm}
          onCancel={hideModal}
          date={date}
          {...dateTimePickerProps}
        />
      </ContainerNormal>
    );
  }

  return (
    <ContainerButton style={containerStyle}>
      <Row onPress={_onPress}>
        <BtnViewBorderTitle>
          <TitleText>
            {title}
            {required ? <STextRequired> *</STextRequired> : null}
          </TitleText>
          {value ? (
            <ValueText>{value}</ValueText>
          ) : (
            <Placeholder numberOfLines={1}>{placeholder || "---"}</Placeholder>
          )}
        </BtnViewBorderTitle>
        <View style={styles.viewDrop}>
          {value && showClearButton && `${value}`.trim() !== "" ? (
            <TouchableOpacity onPress={onClearSelect} style={styles.btnClear}>
              <ViewIcon>
                <IconClear source={IC_X} />
              </ViewIcon>
            </TouchableOpacity>
          ) : (
            <IconCalendar source={mode == "time" ? IC_CLOCK : IC_CALENDAR} />
          )}
        </View>
      </Row>
      <DateTimePickerModal
        display={mode === "date" ? "inline" : "spinner"}
        mode={mode}
        isVisible={isModalVisible}
        onConfirm={onConfirm}
        onCancel={hideModal}
        date={date}
        {...dateTimePickerProps}
      />
    </ContainerButton>
  );
});

// @ts-ignore
interface Props extends Omit<IProps, "mode"> {
  mode: "date" | "time" | "datetime" | "row-datetime";
}

export const MyDateTimeInput = memo((props: Props) => {
  const { mode, keyName, onValueChange, containerStyle } = props;
  const [date, setDate] = useState({ date: "", time: "" });
  const onDateChange = useCallback(
    (_keyName: string, value: string) => {
      setDate((prev) => {
        const _time = _keyName === "time" ? value : prev.time;
        const _date = _keyName === "date" ? value : prev.date;
        onValueChange(keyName, _time + " " + _date);
        return {
          ...prev,
          [_keyName]: value,
        };
      });
    },
    [keyName]
  );

  return mode === "row-datetime" ? (
    <SRow style={containerStyle}>
      <_MyDateTimeInput
        containerStyle={styles.leftInput}
        title={"Date"}
        value={date.date}
        keyName={"date"}
        mode={"date"}
        onValueChange={onDateChange}
      />
      <_MyDateTimeInput
        containerStyle={styles.rightInput}
        title={"Time"}
        value={date.time}
        keyName={"time"}
        mode={"time"}
        onValueChange={onDateChange}
      />
    </SRow>
  ) : (
    // @ts-ignore
    <_MyDateTimeInput {...props} />
  );
});

const SRow = styled.View`
  flex-direction: row;
`;

const ViewIcon = styled.View`
  background-color: ${(props) => props.theme.neutral7};
  width: ${getHeight(14)}px;
  height: ${getHeight(14)}px;
  border-radius: ${getHeight(7)}px;
  align-items: center;
  justify-content: center;
`;

const styles = StyleSheet.create({
  viewDrop: {
    flexDirection: "row",
    marginVertical: getWidth(16),
  },
  btnClear: {
    paddingRight: getWidth(22),
    marginTop: -getHeight(16),
    marginBottom: -getHeight(24),
    alignItems: "center",
    justifyContent: "center",
  },
  leftInput: {
    marginRight: getWidth(8),
    flex: 1,
  },
  rightInput: {
    flex: 1,
  },
});
