import { memo, useCallback, useEffect, useRef, useState } from "react";
import { AppState, AppStateStatus } from "react-native";
import {
  AppInfo,
  autoLogin,
  BasicProjectSInfo,
  Core,
  EAuthStage,
  interactManager,
  replaceToMain,
  resetAllStore,
  resetAuth,
  resetPreload,
  useAsyncFn,
  useAuthStore,
  useUpdateEffect,
} from "@true/core";

export const AuthWrapper = memo(() => {
  const { setAuthStage, authStage } = useAuthStore();
  const appState = useRef(AppState.currentState);
  const mounted = useRef(false);
  const {
    data: { currentEmail, allEmails },
  } = BasicProjectSInfo.useStore();

  const handleAppStateChange = useCallback(
    (nextState: AppStateStatus) => {
      if (
        appState.current.match(/inactive|background/) &&
        nextState === "active"
      ) {
        startCheck().then();
      }
      appState.current = nextState;
    },
    [appState.current]
  );

  const [{ error }, startCheck] = useAsyncFn(async () => {
    // get SInfo data
    const SInfoData = await BasicProjectSInfo.getItem();
    const _currentEmail = SInfoData.currentEmail;
    const byEmail = SInfoData.byEmail;

    const currentUserSInfo = byEmail?.[_currentEmail];
    // auto login only when Core.accessToken !== currentUserSInfo.access_token...
    if (
      currentUserSInfo?.access_token &&
      currentUserSInfo?.client_key &&
      Core.accessToken !== currentUserSInfo.access_token &&
      Core.clientKey !== currentUserSInfo.client_key
    ) {
      Core.reset();

      setAuthStage(EAuthStage.CHECKING);
      Core.accessToken = currentUserSInfo.access_token;
      Core.clientKey = currentUserSInfo.client_key;
      Core.clientSecret = currentUserSInfo.client_secret;
      // auto login
      await autoLogin({
        access_token: currentUserSInfo.access_token,
        client_key: currentUserSInfo.client_key,
      });
      // set auth stage and event
      setAuthStage(EAuthStage.SIGNED);
      if (mounted.current) {
        replaceToMain();
      }
      await AppInfo.preSyncData();
    }
  }, [mounted.current]);

  // first check
  useEffect(() => {
    startCheck().then(() => {
      mounted.current = true;
    });
  }, []);

  // check when signed and currentEmail changed
  useUpdateEffect(() => {
    if (authStage === EAuthStage.SIGNED) startCheck().then();
  }, [authStage, currentEmail]);

  // check when app state change
  useEffect(() => {
    const handler = AppState.addEventListener("change", handleAppStateChange);
    return () => handler.remove();
  }, []);

  // check all emails in SInfo and replace to preload screen if no email
  useEffect(() => {
    if (!allEmails.length && authStage === EAuthStage.SIGNED) {
      Core.reset();
      resetAuth();
      resetAllStore();
      interactManager(resetPreload);
      // console.log("Auth:", "there 's no account in system");
    }
  }, [allEmails.length, authStage]);

  // check error
  useEffect(() => {
    if (error?.message) {
      console.log("autoLoginError", error.message);
      setAuthStage(EAuthStage.ERROR);
    }
  }, [error?.message]);

  return null;
});
