import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import constate from "constate";
import { IOption, SelectorInputProps } from "./types";
import {
  runOnJS,
  useAnimatedReaction,
  useDerivedValue,
  useSharedValue,
} from "react-native-reanimated";
import { BottomSheetModal } from "@gorhom/bottom-sheet";
import {interactManager, useDepsCompare} from "@true/core";
import { getMatch } from "./utils";
import { useSelector } from "./hooks";

const _provider = (props: SelectorInputProps) => {
  const { options: rawOptions, optionMap: rawOptionMap } = props;

  const bottomSheetRef = useRef<BottomSheetModal>(null);

  const {
    selectedIds,
    options,
    optionMap,
    onSelectItem,
    onClearValue,
    getIsSelected,
  } = useSelector({ ...props, bottomSheetRef });

  /* -- return selected text -- */
  const optionDict = useMemo(() => {
    let r: { [id: string]: string } = {};
    if (rawOptionMap !== undefined) {
      for (let _section in optionMap) {
        for (let _item of optionMap[_section]) {
          r[_item.value] = _item.label;
        }
      }
      return r;
    }
    for (let _item of options as IOption[]) {
      r[_item.value] = _item.label;
    }
    return r;
  }, [options, rawOptionMap, optionMap]);

  const selectedText = useDerivedValue(
    () => selectedIds.value?.map((id) => optionDict[id]).join(", "),
    [optionDict]
  );

  const [data, _setData] = useState(options);
  const [map, _setMap] = useState(optionMap);

  useEffect(() => {
    _setData(options);
    _setMap(optionMap);
  }, [rawOptions, rawOptionMap].map(useDepsCompare));

  /** -- searching -- */
  const timeout = useRef(null);

  const setData = useCallback(
    (text: string) => {
      interactManager(
        () => {
          /** filter optionMap */
          if (rawOptionMap !== undefined) {
            /** if there's match data */
            let _data: { [id: string]: 0 | 1 } = {};
            for (let val of options as string[]) {
              if (getMatch(val, text)) {
                _data[val] = 1;
              }
            }

            /** filter optionMap:
             * if there are searched sections return items in its.
             * if not return searched items*/
            let _map: { [id: string]: IOption[] } = {};
            let _dup: { [id: string]: 1 } = {};
            for (let key in optionMap) {
              _map[key] = optionMap[key].filter((_item) => {
                /** if section is same to item
                 * return only searched items */
                if (_item.label == key) {
                  _dup[key] = 1;
                }
                return (
                  getMatch(_item.label, text) ||
                  (_data[key] == 1 && _dup[key] !== 1)
                );
              });
            }
            _setMap(_map);
            return _setData(
              (options as string[]).filter(
                (_item) => _map?.[_item]?.length || getMatch(_item, text)
              )
            );
          }

          /** filter options */
          const _data = (options as IOption[]).filter((_item) =>
            getMatch(_item.label, text)
          );

          _setData(_data);
        },
        50,
        true,
        timeout
      );
    },
    [timeout, optionMap, options]
  );

  const searchText = useSharedValue("");

  const onSearchChange = useCallback((text: string) => {
    searchText.value = text;
  }, []);

  useAnimatedReaction(
    () => searchText.value,
    (_text, _prevText) => {
      // console.log(_text, _prevText);
      if (_text != _prevText) {
        runOnJS(setData)(_text);
      }
    },
    [setData]
  );

  return useMemo(
    () => ({
      bottomSheetRef,
      selectedIds,
      selectedText,
      data,
      map,
      onSearchChange,
      onSelectItem,
      getIsSelected,
      onClearValue,
    }),
    [
      selectedIds,
      selectedText,
      bottomSheetRef,
      data,
      map,
      onSearchChange,
      onSelectItem,
      getIsSelected,
      onClearValue,
    ]
  );
};

export const [SelectorProvider, useSelectorInput] = constate(
  _provider,
  (val) => val
);
