import { useRoute } from '@react-navigation/native';
import {
  IRouteState,
  useFloatModalStore,
} from './store';
import { useEffect } from 'react';

export const useSetFloatBtnState = (newState: Partial<IRouteState>) => {
  const route = useRoute();
  const { setState } = useFloatModalStore();
  useEffect(() => {
    setState(newState, route.name);
  }, [route.name, ...Object.values(newState)]);
};
