  // const _data = useMemo(() => {
  //   if (!Array.isArray(props.data)) return [];
  //   if (props.data.length === 0) return [];
  //   if (!props.renderSection) return [...props.data, "lastItem"];
  //
  //   let result: any[] = [];
  //   for (let section = 0; section < props.data.length; section++) {
  //     result.push({
