import React, { memo } from 'react';
import { FloatingButton, useFloatModalStore } from './index';

export const TabBarFloatingButton = memo(
  ({ routeName }: { routeName: string }) => {
    const { routeState } = useFloatModalStore();
    return (
      <FloatingButton {...routeState[routeName || '']} routeName={routeName} />
    );
  }
);
