import React, { RefObject } from "react";
import { IOption } from "./index";
import { BottomSheetTemplate } from "../components/BottomSheetTemplate";
import { SharedValue } from "react-native-reanimated";
import { memoForwardRef } from "@true/core";
import { BottomSheetModal } from "@gorhom/bottom-sheet";

export type IRenderMenu = () => JSX.Element | null;

interface Props {
  options: IOption[];
  title?: string;
  withDivider?: boolean;
  headerComponent?: React.ReactElement;
  value: any;
  onValueChange: (keyName: string, value: any) => void;
  keyName: string;
  multiple?: boolean;
  renderMenu?: IRenderMenu;
  onSave?: (value: any) => void;
  defaultOptionLength?: number;
  snapPoints?: (number | string)[] | SharedValue<(number | string)[]>;
  onDismiss?: () => void;
  hasFlashList?: boolean;
}

export const BottomMenu = memoForwardRef(
  (props: Props, ref: RefObject<BottomSheetModal>) => {
    const { renderMenu, title } = props;

    return (
      <BottomSheetTemplate ref={ref} {...props} title={title || ""}>
        {renderMenu?.() || null}
      </BottomSheetTemplate>
    );
  }
);
