import { FileType, styled } from "@true/core";
import React, { memo, useCallback, useState } from "react";
import { useUIAttachment } from "./provider";
import { runOnJS, useAnimatedReaction } from "react-native-reanimated";
import { IMapItem, MapList } from "../../MapList";
import { CONTENT_LINE_HEIGHT, TITLE_FONT_SIZE } from "../UIForm/constant";
import { ClearIcon } from "../ClearIcon";
import { UIButton } from "../../UIButton";

export const SelectedFilesView = memo(() => {
  const { files: aFiles, onRemoveFile } = useUIAttachment();

  const [files, setFiles] = useState(aFiles.value);

  useAnimatedReaction(
    () => aFiles.value,
    (_files) => {
      runOnJS(setFiles)(_files);
    },
    []
  );

  const renderItem = useCallback((p: IMapItem<FileType>) => {
    return <Item key={p.index} {...p} onPress={onRemoveFile} />;
  }, []);

  if (!files.length) return null;

  return (
    <SContainer>
      <MapList data={files} renderItem={renderItem} />
    </SContainer>
  );
});

const SContainer = styled.View``;

const Item = memo(
  ({
    item,
    index,
    onPress,
  }: IMapItem<FileType> & { onPress: (item: FileType) => void }) => {
    return (
      <SItemContainer>
        <SFileName numberOfLines={1}>{item.name}</SFileName>
        <UIButton isGestureHandler={true} onPress={() => onPress(item)}>
          <ClearIcon size={TITLE_FONT_SIZE} />
        </UIButton>
      </SItemContainer>
    );
  }
);

const SItemContainer = styled.View`
  height: ${CONTENT_LINE_HEIGHT}px;
  flex-direction: row;
  align-items: center;
  margin-top: 4px;
`;

const SFileName = styled.Text`
  line-height: ${CONTENT_LINE_HEIGHT}px;
  font-size: ${TITLE_FONT_SIZE}px;
  color: ${(p) => p.theme.neutral2}
  flex: 1;
`;
