import {
  File,
  IC_DOWNLOAD,
  IC_SHARE,
  IC_X,
  styled,
  useAnimatedWindow,
  useBoolean,
} from "@true/core";
import * as React from "react";
import { memo, useCallback, useEffect, useMemo, useRef, useState } from "react";
import {
  Dimensions,
  NativeScrollEvent,
  NativeSyntheticEvent,
  ScrollView,
  View,
} from "react-native";
import PhotoView from "react-native-photo-view";
import SimpleToast from "react-native-simple-toast";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { useAsyncFn } from "react-use";

const SWrapper = styled(Animated.View)`
  background-color: #000;
  flex: 1;
`;

const SSingleImageWrapper = styled(Animated.View)`
  flex: 1;
  align-items: center;
  justify-content: center;
  background-color: #000;
`;

const AnimatedPhotoView = Animated.createAnimatedComponent(PhotoView);

const SImage = styled(AnimatedPhotoView)`
  flex: 1 1;
  width: 100%;
`;

const SLoadingIndicator = styled.ActivityIndicator`
  margin: auto;
  width: 30px;
  height: 30px;
  position: absolute;
`;

const SHeader = styled.View`
  flex-direction: row;
  justify-content: space-between;
  padding: 8px 16px;
  left: 0;
  width: 100%;
  z-index: 1;
  background-color: #00000080;
`;

const SHeaderLeft = styled.View`
  flex-direction: row;
  align-items: center;
`;

