const PhotoViewSingleImage = memo(
  ({
    image,
    requestFn,
    checkFileLocal,
    onDownload,
    onCloseRequest,
    onShare,
  }: {
    image: string;
    requestFn?: (image: string) => Promise<string>;
    checkFileLocal: boolean;
    onCloseRequest: () => void;
    onDownload: (img: string) => void;
    onShare: (img: string) => void;
  }) => {
    // const [loading, , setLoadingFalse] = useBoolean(true);

    const [{ value }, onRequestFn] = useAsyncFn(requestFn || (async () => {}));
    const [loading, setLoading] = useState(!!requestFn);

    if (requestFn) {
      useEffect(() => {
        if (loading)
          onRequestFn(image as string).then(() => {
            setLoading(false);
          });
      }, [image]);
    }

    const { width } = useAnimatedWindow();
    const animatedStyles = useAnimatedStyle(() => ({
      width: width.value,
    }));

    return (
      <View style={{ flex: 1 }}>
        <SHeader>
          <SHeaderLeft>
            {!checkFileLocal && (
              <SCloseButton
                onPress={() =>
                  onDownload(requestFn ? (value as string) : image)
                }
              >
                <Icon source={IC_DOWNLOAD} />
              </SCloseButton>
            )}
            <SShareButton
              onPress={() => onShare(requestFn ? (value as string) : image)}
            >
              <Icon source={IC_SHARE} />
            </SShareButton>
          </SHeaderLeft>
          <SCloseButton onPress={onCloseRequest}>
            <Icon source={IC_X} />
          </SCloseButton>
        </SHeader>
        <SSingleImageWrapper style={animatedStyles}>
          {loading || (!requestFn && !image) || (requestFn && !value) ? (
            <SLoadingIndicator />
          ) : (
            <SImage
              style={animatedStyles}
              source={{ uri: requestFn ? (value as string) : image }}
              minimumZoomScale={1}
              maximumZoomScale={3}
              androidScaleType="fitCenter"
              // onLoadEnd={setLoadingFalse}
            />
          )}
        </SSingleImageWrapper>
      </View>
    );
  }
);

interface Props {
  images: string[];
  initialIndex?: number;
  onCloseRequest: () => any;
  requestFn?: (image: string) => Promise<string>;
}
const PhotoViewContent = memo(
  ({ initialIndex = 0, images, onCloseRequest, requestFn }: Props) => {
    const currentPage = useRef(initialIndex);
    const scrollView = useRef<ScrollView>(null);
    const [renderedPages, setRenderedPages] = useState<Set<number>>(
      () => new Set([initialIndex])
    );

    useEffect(() => {
      if (!initialIndex && scrollView.current) return;
      const dimensions = Dimensions.get("window");

      let timeout = setTimeout(() => {
        // @ts-ignore
        scrollView.current.scrollTo({
          x: dimensions.width * initialIndex,
          y: 0,
          animated: false,
        });
      });

      return () => {
        return clearTimeout(timeout);
      };
    }, [scrollView, initialIndex]);

    const onScrollEnd: ScrollView["props"]["onMomentumScrollEnd"] = useCallback(
      (event: NativeSyntheticEvent<NativeScrollEvent>) => {
        const { contentOffset, layoutMeasurement } = event.nativeEvent;

        const pageNum = Math.floor(contentOffset.x / layoutMeasurement.width);

        currentPage.current = pageNum;

        setRenderedPages((renderedPages) => {
          const newState = new Set<number>(renderedPages);
          newState.add(pageNum);
          return newState;
        });
      },
      []
    );

    const actions = useMemo(() => {
      return {
        share: (url: string) =>
          File.share({
            url,
            cacheDownload: true
          }),
        download: async (url: string) => {
          // console.log(url);
          await File.download({
            url,
          });
          // SimpleToast.show("Photo downloaded successfully");
        },
      };
    }, [currentPage, images]);

    const checkFileLocal = useMemo(() => {
      return (
        images[currentPage.current].includes("file:///") ||
        images[currentPage.current].includes("content:/")
      );
    }, [currentPage, images]);

    const { aBottom, aTop } = useAnimatedSafeAreaInsets();
    const animatedStyle = useAnimatedStyle(() => ({
      paddingBottom: aBottom.value,
    }));

    const { width } = useAnimatedWindow();
    const animatedItemStyle = useAnimatedStyle(() => ({
      width: width.value,
    }));

    return (
      <SWrapper style={animatedStyle}>
        <ScrollView
          ref={scrollView}
          pagingEnabled={true}
          horizontal={true}
          onMomentumScrollEnd={onScrollEnd}
        >
          {images.map((image, index) =>
            renderedPages.has(index) ? (
              <PhotoViewSingleImage
                key={image}
                image={image}
                requestFn={requestFn}
                onDownload={actions.download}
                onShare={actions.share}
                onCloseRequest={onCloseRequest}
                checkFileLocal={checkFileLocal}
              />
            ) : (
              <SSingleImageWrapper style={animatedItemStyle} key={image} />
            )
          )}
        </ScrollView>
      </SWrapper>
    );
  }
);

const Icon = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: #fff;
`;

const SCloseButton = styled.TouchableOpacity`
  height: 32px;
  width: 32px;
  align-items: center;
  justify-content: center;
`;

const SShareButton = styled(SCloseButton)`
  margin-left: 16px;
`;

PhotoViewContent.displayName = "PhotoViewContent";

export default PhotoViewContent;
