import { memoForwardRef } from "@true/core";
import React, { RefObject } from "react";
import { UIForm } from "../UIForm";
import { UICheckboxInputProps } from "./types";
import { CheckboxContent } from "./CheckboxContent";

export interface UICheckboxInput {}

export const UICheckboxInput = memoForwardRef(
  (props: UICheckboxInputProps, ref: RefObject<UICheckboxInput>) => {
    return (
      <UIForm.Input
        {...props}
        inputType={"checkbox"}
        ContentElement={<CheckboxContent {...props} ref={ref} />}
      />
    );
  }
);
