import React, { memo } from "react";
import { useTableData, useTableHead } from "./hooks";
import { TableExample } from "./TableExample";
import { DynamicHeader } from "../Header";
import { translate, useNavigationParams } from "@true/core";
import { ScreenWrapper } from "../ScreenWrap";

export interface TableFieldViewerProps {
  value: string;
  placeholder: string;
}

export const TableFieldViewer = memo(function TableFieldViewer() {
  const { value, placeholder } = useNavigationParams<TableFieldViewerProps>();

  const headOptions = useTableHead(placeholder);
  const data = useTableData(value);
  return (
    <ScreenWrapper>
      <DynamicHeader title={translate("common/detail")} />
      <TableExample headOptions={headOptions} indexVisible={true} data={data} />
    </ScreenWrapper>
  );
});
