import * as React from "react";
import {
  RefObject,
  useCallback,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
} from "react";
import {
  ImageStyle,
  Platform,
  StyleSheet,
  TextInput,
  TouchableOpacity,
  ViewStyle,
} from "react-native";
import { BottomSheetTextInput } from "@gorhom/bottom-sheet";
import {
  flatStyles,
  IC_SEARCH,
  IC_X,
  interactManager,
  memoForwardRef,
  styled,
} from "@true/core";
import _ from "lodash";
import Animated, {
  useAnimatedProps,
  useAnimatedStyle,
  useSharedValue,
} from "react-native-reanimated";

const GrayPlaceholder = styled(TextInput).attrs((props) => ({
  placeholderTextColor: props.theme.neutral3,
  selectionColor: "#007AFF",
}))`
  color: ${(props) => props.theme.neutral1};
`;

const ModalGrayPlaceholder = styled(BottomSheetTextInput).attrs((props) => ({
  placeholderTextColor: props.theme.neutral3,
  selectionColor: "#007AFF",
}))`
  color: ${(props) => props.theme.neutral1};
`;

const AModalGrayPlaceholder =
  Animated.createAnimatedComponent(ModalGrayPlaceholder);

const AGrayPlaceholder = Animated.createAnimatedComponent(GrayPlaceholder);

interface OwnProps {
  containerStyle?: ViewStyle | ViewStyle[];
  iconStyle?: ImageStyle | ImageStyle[];
  inputContainerStyle?: ViewStyle | ViewStyle[];
  style?: ViewStyle | ViewStyle[];
  placeholder?: string;
  onSearchTextChange?: (text: string) => void;
  autoFocus?: boolean;
  value?: string;
  onFocus?: () => void;
  onEndEditing?: () => void;
  rightComponent?: React.ReactElement;
  isOnModal?: boolean;
}

type Props = OwnProps;

const SDarkIcon = styled.Image`
  tint-color: ${(props) => props.theme.neutral2};
`;

const SSectionHeaderView = styled.View`
  height: 40px;
  //margin: 0 16px 8px 16px;
  padding: 0 4px 0 10px;
  flex-direction: row;
  align-items: center;
  border-radius: 8px;
  background-color: ${(p) => p.theme.neutral8};
`;

const IconClose = styled.Image`
  width: 24px;
  height: 24px;
  tint-color: ${(p) => p.theme.neutral8};
`;

const ViewIcon = styled.View`
  background-color: ${(props) => props.theme.neutral3};
`;

export interface SearchBarRef {
  clearText: () => void;
}

export const SearchBar = memoForwardRef(
  (props: Props, ref: RefObject<SearchBarRef>) => {
    const text = useSharedValue(props.value || "");
    const setText = useCallback((_text: string) => {
      text.value = _text;
    }, []);

    const inputRef = useRef<TextInput>(null);

    useEffect(() => {
      if (props.autoFocus && props.isOnModal) {
        interactManager(() => {
          inputRef?.current?.focus();
        }, 300);
      }
    }, [props.autoFocus, props.isOnModal]);

    const onSearchTextChange = useCallback(
      _.debounce((_text: string) => {
        if (props.onSearchTextChange) {
          props.onSearchTextChange(_text.toLowerCase());
        }
      }, 300),
      [props.onSearchTextChange]
    );

    const onChangeText = useCallback(
      (_text: string) => {
        if (_text !== text.value) {
          setText(_text);
          onSearchTextChange(_text);
        }
      },
      [props.value, onSearchTextChange]
    );

    const onClearText = useCallback(() => {
      onChangeText("");
    }, [onChangeText]);

    useImperativeHandle(
      ref,
      () => ({
        clearText: onClearText,
      }),
      []
    );

    const DynamicTextInput = useMemo(
      () =>
        props.isOnModal && Platform.OS === "ios"
          ? AModalGrayPlaceholder
          : AGrayPlaceholder,
      [props.isOnModal]
    ) as any;

    const placeholder = useMemo(
      () => (props.placeholder ? props.placeholder : "Search"),
      [props.placeholder]
    );

    const animatedProps = useAnimatedProps(
      () => ({
        text: text.value,
      }),
      []
    );

    const clearBtnStyle = useAnimatedStyle(
      () => ({
        opacity: text.value ? 1 : 0,
      }),
      []
    );

    const clearBtnProps = useAnimatedStyle(
      () =>
        ({
          pointerEvents: text.value ? "auto" : "none",
        } as any),
      []
    );

    return (
      <SSectionHeaderView
        {...props}
        style={[props.containerStyle, props.style]}
      >
        <SDarkIcon
          style={flatStyles(styles.searchIcon, props.iconStyle)}
          source={IC_SEARCH}
        />
        <DynamicTextInput
          {...{ animatedProps }}
          ref={inputRef as any}
          keyboardType={Platform.OS === "android" ? "default" : "ascii-capable"}
          style={flatStyles(styles.input, props.inputContainerStyle)}
          placeholder={placeholder}
          // value={text}
          onChangeText={onChangeText}
          autoCorrect={false}
          autoFocus={props.isOnModal ? false : props.autoFocus}
          onEndEditing={props.onEndEditing}
          onFocus={props.onFocus}
        />
        {text && !props.rightComponent ? (
          <Animated.View animatedProps={clearBtnProps} style={clearBtnStyle}>
            <TouchableOpacity
              onPress={onClearText}
              hitSlop={8}
              style={styles.btnClear}
            >
              <ViewIcon style={styles.viewIcon}>
                <IconClose style={styles.iconClear} source={IC_X} />
              </ViewIcon>
            </TouchableOpacity>
          </Animated.View>
        ) : null}
        {props.rightComponent}
      </SSectionHeaderView>
    );
  }
);

const styles = StyleSheet.create({
  searchIcon: {
    width: 24,
    height: 24,
    resizeMode: "contain",
  },
  input: {
    flex: 1,
    fontSize: 15,
    height: "100%",
    paddingBottom: 0,
    paddingTop: 0,
    paddingLeft: 10,
  },
  btnClear: {
    padding: 13,
    paddingRight: 8,
    alignItems: "center",
    justifyContent: "center",
  },
  iconClear: {
    width: 10,
    height: 10,
  },
  viewIcon: {
    width: 14,
    height: 14,
    borderRadius: 7,
    alignItems: "center",
    justifyContent: "center",
  },
});
