import React, { memo, useMemo } from "react";
import { TextAvatar } from "./TextAvatar";
import { useTheme, useUserGroup } from "@true/core";
import { UserAvatar } from "../../../Avatar";

interface Props {
  type: "user" | "group";
  id: string;
  size: number;
}

export const UserAvatarItem = memo(({ type, id, size }: Props) => {
  const theme = useTheme();
  const userGroup = useUserGroup(id);

  if (type == "group") {
    const isUser = useMemo(
      () => userGroup?.metatype == "user",
      [userGroup?.metatype]
    );

    if (!userGroup?.name) return null;

    if (isUser) {
      return (
        <UserAvatar
          userKey={userGroup.user_id}
          size={size}
          style={{
            borderWidth: 1,
            borderColor: theme.backgroundColor,
          }}
        />
      );
    }

    return (
      <TextAvatar
        size={size}
        text={userGroup?.name}
        style={{
          borderWidth: 1,
          borderColor: theme.backgroundColor,
        }}
      />
    );
  }

  return (
    <UserAvatar
      userKey={id || ""}
      size={size}
      style={{
        borderWidth: 1,
        borderColor: theme.backgroundColor,
      }}
    />
  );
});
