import React, { memo, useCallback } from "react";
import { Fonts, styled, useMe } from "@true/core";
import { PreviewCommentProps } from "./provider";
import { usePreviewProps } from "./hooks";
import { navigateToCommentScreen } from "./utils";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { Avatar } from "../../Avatar";
import { UIText } from "../../UIText";

export const PreviewCommentInput = memo((props: PreviewCommentProps) => {
  const { hid, token, appName, type } = usePreviewProps(props);
  const { aBottom } = useAnimatedSafeAreaInsets();
  const me = useMe();

  const animatedStyle = useAnimatedStyle(() => ({
    paddingBottom: aBottom.value,
  }));

  const _onReply = useCallback(() => {
    navigateToCommentScreen({
      hid,
      token,
      appName,
      type,
    });
  }, [hid, token, appName, type]);

  return (
    <SWrapper style={animatedStyle}>
      <SContainer>
        <Avatar userKey={me?.id || ""} size={32} />
        <SInputWrapper onPress={_onReply}>
          <STextInput>Write your comments</STextInput>
        </SInputWrapper>
      </SContainer>
    </SWrapper>
  );
});

const STextInput = styled(UIText.BodyMedium400)`
  color: ${(p) => p.theme.neutral2};
  flex: 1;
`;

const SInputWrapper = styled.TouchableOpacity`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-radius: 400px;
  background-color: ${(p) => p.theme.neutral8};
  overflow: hidden;
  flex: 1;
  margin-left: 8px;
  padding: 8px 16px;
`;

const SWrapper = styled(Animated.View)`
  background-color: ${(p) => p.theme.backgroundColor};
`;

const SContainer = styled(Animated.View)`
  padding: 8px 16px 4px 16px;
  border-top-width: 1px;
  border-color: ${(p) => p.theme.neutral4};
  flex-direction: row;
`;
