import { memoForwardRef, styled, useTheme } from "@true/core";
import React, {
  RefObject,
  useCallback,
  useImperativeHandle,
  useMemo,
  useState,
} from "react";
import { TextInput, TextInputProps } from "react-native";
import Animated, {
  SharedValue,
  useAnimatedProps,
  useAnimatedStyle,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
} from "react-native-reanimated";
import { UIForm } from "../UIForm";
import { UITextInputProps } from "./types";
import { CONTENT_FONT_SIZE, CONTENT_LINE_HEIGHT } from "../UIForm/constant";
import { BottomSheetTextInput } from "@gorhom/bottom-sheet";

const AnimatedTextInput = Animated.createAnimatedComponent(TextInput);
const AnimatedBottomTextInput =
  Animated.createAnimatedComponent(BottomSheetTextInput);

export interface UITextInput extends TextInput {
  onClearValue: () => void;
  setValue: (value: string) => void;
}

export const UITextInput = memoForwardRef(
  (
    props: UITextInputProps & Omit<TextInputProps, "onChangeText">,
    ref: RefObject<UITextInput>
  ) => {
    const {
      keyName,
      value = "",
      onChangeValue,
      textStyle,
      params,
      placeholder,
      isBottomSheetTextInput,
      containerStyle,
      ...rest
    } = props;

    const theme = useTheme();

    const [_value, _setValue] = useState(params?.value[keyName] || value);

    /* -- set content by params if there 's params -- */
    const content = params
      ? (useDerivedValue(
          () => params.value?.[keyName] || "",
          [params, keyName]
        ) as unknown as SharedValue<any>)
      : useSharedValue(value);
    const focused = useSharedValue(false);

    const animatedProps = useAnimatedProps(
      () => ({
        text: content.value.toString(),
      }),
      []
    ) as any;

    const _onChangeValue = useWorkletCallback(
      (keyName: string, value: any) => {
        if (params) {
          params.value = { ...params.value, [keyName]: value };
        }
        onChangeValue?.(keyName, value);
        _setValue(value);
      },
      [onChangeValue, params]
    );

    const onChangeText = useCallback(
      (text: string) => {
        if (!params) content.value = text;
        _onChangeValue(keyName, text);
      },
      [keyName, _onChangeValue, params]
    );

    const onClearText = useCallback(() => {
      if (!params) content.value = "";
      _onChangeValue(keyName, "");
    }, [_onChangeValue, params]);

    const onFocus = useCallback(
      (e: any) => {
        focused.value = true;
        props.onFocus?.(e);
      },
      [props.onFocus]
    );

    const onEndEditing = useCallback(
      (e: any) => {
        focused.value = false;
        props.onEndEditing?.(e);
      },
      [props.onEndEditing]
    );

    const animatedStyle = useAnimatedStyle(
      () => ({
        borderColor: focused.value ? theme.primaryColor : theme.neutral5,
      }),
      [theme]
    );

    const setValue = useCallback(
      (value: string) => {
        if (!params) content.value = value;
        _onChangeValue(keyName, "");
      },
      [_onChangeValue, params]
    );

    useImperativeHandle(
      ref,
      () =>
        ({
          ...ref.current,
          onClearValue: onClearText,
          setValue,
        } as any)
    );

    const _TextInput = useMemo(
      () => (isBottomSheetTextInput ? SBottomTextInput : STextInput),
      [isBottomSheetTextInput]
    );

    return (
      <UIForm.Input
        {...rest}
        inputType={"text"}
        textValue={content}
        onClearValue={onClearText}
        containerStyle={[animatedStyle, containerStyle]}
        ContentElement={
          <_TextInput
            {...rest}
            {...{ animatedProps }}
            ref={ref}
            value={_value}
            onFocus={onFocus}
            onEndEditing={onEndEditing}
            onChangeText={onChangeText}
            placeholder={placeholder || "---"}
            style={textStyle}
          />
        }
      />
    );
  }
);

const STextInput = styled(AnimatedTextInput).attrs((p) => ({
  placeholderTextColor: p.theme.neutral3,
}))`
  color: ${(p) => p.theme.neutral1};
  font-size: ${CONTENT_FONT_SIZE}px;
  //line-height: ${CONTENT_LINE_HEIGHT}px;
  padding: 28px 32px 8px 16px;
` as any;

const SBottomTextInput = styled(AnimatedBottomTextInput).attrs((p) => ({
  placeholderTextColor: p.theme.neutral3,
}))`
  color: ${(p) => p.theme.neutral1};
  font-size: ${CONTENT_FONT_SIZE}px;
  //line-height: ${CONTENT_LINE_HEIGHT}px;
  padding: 28px 32px 8px 16px;
` as any;
