import React, { RefObject, useCallback, useImperativeHandle } from "react";
import { memoForwardRef, styled } from "@true/core";
import { useSelectorInput } from "./provider";
import { ReText } from "react-native-redash";
import { BottomSheet } from "./BottomSheet";
import { SelectorInputProps } from "./types";
import { UISelectorInput } from "./index";
import { UIForm } from "../UIForm";
import { IC_DROPDOWN } from "../UIForm/assets";
import { CONTENT_FONT_SIZE, CONTENT_LINE_HEIGHT } from "../UIForm/constant";
import { SelectedItems } from "./SelectedItems";

export const Input = memoForwardRef(
  (props: SelectorInputProps, ref: RefObject<UISelectorInput>) => {
    const {
      selectedText,
      bottomSheetRef,
      onClearValue,
      selectedIds,
      onSelectItem,
    } = useSelectorInput();

    const onPress = useCallback(() => {
      bottomSheetRef.current?.present();
    }, [bottomSheetRef]);

    useImperativeHandle(ref, () => ({
      onClearValue,
    }));

    return (
      <>
        <UIForm.Input
          {...props}
          inputType={"selector"}
          onPress={onPress}
          onClearValue={onClearValue}
          ContentElement={
            props.multiple ? (
              <SelectedItems
                selectedIds={selectedIds}
                onClearItem={onSelectItem}
              />
            ) : (
              <SReText
                text={selectedText}
                placeholder={props.placeholder || "---"}
              />
            )
          }
          rightIconSource={IC_DROPDOWN}
          textValue={selectedText}
        />
        <BottomSheet ref={bottomSheetRef} {...props} />
      </>
    );
  }
);

const SReText = styled(ReText).attrs((p) => ({
  placeholderTextColor: p.theme.neutral3,
}))`
  color: ${(p) => p.theme.neutral1};
  font-size: ${CONTENT_FONT_SIZE}px;
  line-height: ${CONTENT_LINE_HEIGHT}px;
  padding: 0;
`;
