import React, { memo, useCallback } from "react";
import { PickFileContentProps } from "./types";
import {
  File,
  FileType,
  IC_ATTACHMENT,
  IC_CAMERA,
  IC_IMAGE,
  IC_RECORD,
  styled,
  translate,
} from "@true/core";
import { ActionSheetRow } from "../ActionSheet";
import SimpleToast from "react-native-simple-toast";

