const ContentContainer = styled.View`
  width: 100%;
  padding: 16px 0;
`;
const PickFileContent = memo(
  ({
    onFilePicked,
    pickFileOptions,
    pickImageOptions,
    takeCameraOptions,
    includeTakeCamera = true,
    includePickFile = true,
  }: PickFileContentProps) => {
    // const [visible, setVisibleTrue, setVisibleFalse] = useBoolean(false);

    // const onPressTakeCameraAndroid = useCallback(async () => {
    //   await PermissionsAndroid.request(PermissionsAndroid.PERMISSIONS.CAMERA);
    //   await requestWriteStoragePermission();
    //   setVisibleTrue();
    // }, []);

    const onCallbackFile = useCallback(
      (files: FileType[]) => {
        let list_files: FileType[] = [];
        let maxSize = true;
        files.map((item: FileType) => {
          if (item?.size) {
            maxSize = parseFloat(String(item.size)) / 1000000 < 25;
          }

          if (maxSize) {
            list_files.push(item);
          }
        });
        if (!maxSize) {
          SimpleToast.show(translate("ui/max_25mb"), 5, false, true);
        }
        onFilePicked(list_files);
      },
      [onFilePicked]
    );

    const onPressTakeCamera = useCallback(async () => {
      const files = await File.takeCamera(takeCameraOptions || {});
      onCallbackFile(files);
    }, [onFilePicked]);

    const onSelectFromGallery = useCallback(async () => {
      const files = await File.pickImage(pickImageOptions || {});
      onCallbackFile(files);
    }, [onFilePicked]);

    const onSelectVideoFromGallery = useCallback(async () => {
      const files = await File.pickImage({
        ...(pickImageOptions || {}),
        mediaType: "video",
      });
      onCallbackFile(files);
    }, [onFilePicked]);

    const onPickFile = useCallback(async () => {
      const files = await File.pick({
        multiple: true,
        ...(pickFileOptions || {}),
      });

      onCallbackFile(files);
    }, [onFilePicked]);

    return (
      <ContentContainer>
        {!!includePickFile && (
          <ActionSheetRow
            onPress={onPickFile}
            icon={IC_ATTACHMENT}
            text={translate("uikit/selectAttachment")}
          />
        )}
        <ActionSheetRow
          onPress={onSelectFromGallery}
          icon={IC_IMAGE}
          text={translate("comp.selectPhoto")}
        />
        <ActionSheetRow
          onPress={onSelectVideoFromGallery}
          icon={IC_RECORD}
          text={translate("comp.selectVideo")}
        />
        {!!includeTakeCamera && (
          <ActionSheetRow
            onPress={
              // Platform.OS === 'android'
              //   ? onPressTakeCameraAndroid
              onPressTakeCamera
            }
            icon={IC_CAMERA}
            text={translate("comp.takePhoto")}
          />
        )}
        {/*{Platform.OS === 'android' && (*/}
        {/*  <SWrapper*/}
        {/*    isVisible={visible}*/}
        {/*    onDismiss={setVisibleFalse}*/}
        {/*    onBackdropPress={setVisibleFalse}*/}
        {/*    onBackButtonPress={setVisibleFalse}*/}
        {/*    animationIn="fadeIn"*/}
        {/*    animationOut="fadeOut"*/}
        {/*    backdropTransitionOutTiming={0}*/}
        {/*    avoidKeyboard={true}>*/}
        {/*    <SContainer>*/}
        {/*      <TakePhoto*/}
        {/*        onCloseRequest={setVisibleFalse}*/}
        {/*        onFilePicked={onFilePicked}*/}
        {/*      />*/}
        {/*    </SContainer>*/}
        {/*  </SWrapper>*/}
        {/*)}*/}
      </ContentContainer>
    );
  }
);

PickFileContent.displayName = "PickFileContent";

export default PickFileContent;

