import React, { memo, PropsWithChildren } from "react";
import { ViewStyle } from "react-native";
import { Shadow, ShadowProps } from "react-native-shadow-2";
import { Colors, ETheme, flatStyles, styled, useThemeStore } from "@true/core";

interface Props extends PropsWithChildren {
  style?: ViewStyle | ViewStyle[];
  size?: number;
}

export const MyShadow = memo((props: Props) => {
  const { style, size = 4 } = props;
  const { themeName } = useThemeStore();

  return themeName === ETheme.light ? (
    <Shadow
      {...button}
      offset={[0, size]}
      distance={size}
      style={flatStyles(style)}
      containerStyle={flatStyles(style)}
    >
      {props.children}
    </Shadow>
  ) : (
    <SView style={flatStyles(style)}>{props.children}</SView>
  );
});

const button = {
  startColor: Colors.neutral6 + "50",
} as ShadowProps;

const SView = styled.View`
  border-width: 1px;
  border-color: ${Colors.white};
`;
