import React, { memo, useState } from "react";
import { styled } from "@true/core";
import { UIButton } from "../../UIButton";
import { MapList } from "../../MapList";
import {
  runOnJS,
  SharedValue,
  useAnimatedReaction,
} from "react-native-reanimated";
import { ClearIcon } from "../ClearIcon";
import { ReText } from "react-native-redash";
import { CONTENT_FONT_SIZE, CONTENT_LINE_HEIGHT } from "../UIForm/constant";
import { Platform } from "react-native";

export const SelectedItems = memo(
  ({
    selectedIds,
    maxNumber = 2,
    onClearItem,
  }: {
    selectedIds: SharedValue<any[]>;
    maxNumber?: number;
    onClearItem?: (item: any) => void;
  }) => {
    const [ids, setIds] = useState(selectedIds.value);

    useAnimatedReaction(
      () => selectedIds.value,
      (_ids) => {
        runOnJS(setIds)(_ids);
      },
      []
    );

    if (!ids.length)
      return <SPlaceholder text={"" as any} placeholder={"---"} />;

    return (
      <SContainer>
        <MapList
          data={ids.slice(0, maxNumber)}
          renderItem={({ item, index }) => (
            <Item key={index} item={item} onPress={onClearItem} />
          )}
        />
        {ids.length > maxNumber ? (
          <SCountItem>
            <SCountText>+{ids.length - maxNumber}</SCountText>
          </SCountItem>
        ) : null}
      </SContainer>
    );
  }
);

const SContainer = styled.View`
  flex-direction: row;
  margin-top: ${Platform.OS == "android" ? 8 : 0}px;
`;

const Item = memo(
  ({
    item,
    hideClearBtn,
    onPress,
  }: {
    item: any;
    hideClearBtn?: boolean;
    onPress?: (item: any) => void;
  }) => {
    return (
      <SContainerItem>
        <SText>{item.toString()}</SText>
        {!hideClearBtn ? (
          <UIButton isGestureHandler={true} onPress={() => onPress?.(item)}>
            <ClearIcon size={12} />
          </UIButton>
        ) : null}
      </SContainerItem>
    );
  }
);

const SContainerItem = styled.View`
  border-width: 1px;
  padding: 0 ${(CONTENT_LINE_HEIGHT / 24) * 12}px;
  height: ${CONTENT_LINE_HEIGHT}px;
  border-color: ${(p) => p.theme.neutral5};
  border-radius: 100px;
  margin-right: 4px;
  flex-direction: row;
  align-items: center;
`;

const SCountItem = styled.View`
  border-width: 1px;
  height: ${CONTENT_LINE_HEIGHT}px;
  width: ${CONTENT_LINE_HEIGHT}px;
  border-color: ${(p) => p.theme.neutral5};
  border-radius: 100px;
  align-items: center;
  justify-content: center;
`;

const SText = styled.Text`
  color: ${(p) => p.theme.neutral1};
  font-size: 12px;
  line-height: 16px;
  margin-right: 4px;
`;
const SCountText = styled.Text`
  color: ${(p) => p.theme.neutral2};
  font-size: 12px;
  line-height: 16px;
`;

const SPlaceholder = styled(ReText).attrs((p) => ({
  placeholderTextColor: p.theme.neutral3,
}))`
  color: ${(p) => p.theme.neutral3};
  font-size: ${CONTENT_FONT_SIZE}px;
  line-height: ${CONTENT_LINE_HEIGHT}px;
  padding: 0;
`;
