import React, { memo, useCallback } from "react";
import { styled } from "@true/core";
import { usePrevious, useUpdateEffect } from "react-use";
import { isIphoneX } from "react-native-iphone-x-helper";
import Animated, {
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from "react-native-reanimated";

interface Props {
  isPressed: boolean;
  onPress: (isPressed: boolean) => void;
  floatingIcon?: any;
  isSingle: boolean;
  routeName?: string;
}

export const _FloatingButton = memo(
  ({ isPressed, onPress, floatingIcon, isSingle, routeName }: Props) => {
    const rotate = useSharedValue("0deg");
    const prevRouteName = usePrevious(routeName);

    useUpdateEffect(() => {
      if (prevRouteName == routeName) {
        if (isPressed) {
          rotate.value = withTiming("45deg", { duration: 100 });
        } else {
          rotate.value = withTiming("0deg", { duration: 100 });
        }
      }
    }, [isPressed, routeName]);

    const _press = useCallback(() => {
      onPress(!isPressed);
    }, [isPressed]);

    const animatedStyles = useAnimatedStyle(() => ({
      transform: [{ rotate: rotate.value }],
    }));

    return (
      <SContainer activeOpacity={0.9} onPress={_press}>
        <SWrap>
          <SBtnBg source={require("../assets/ic_floating_bg.png")} />
          {floatingIcon ? (
            floatingIcon
          ) : (
            <SPlus
              style={!isSingle ? animatedStyles : {}}
              source={require("../assets/ic_floating_plus.png")}
            />
          )}
        </SWrap>
      </SContainer>
    );
  }
);

const SWrap = styled.View`
  width: 60px;
  height: 60px;
`;

const SContainer = styled.TouchableOpacity`
  padding: 20px 20px ${!isIphoneX() ? 8 : 20}px 20px;
`;

const SBtnBg = styled.Image`
  position: absolute;
  left: -15px;
  top: -8px;
`;

const SPlus = styled(Animated.Image)`
  position: absolute;
  left: 18px;
  top: 18px;
`;
