export interface SelectorOption {
  value: string | number | undefined;
  label: string;
  noTranslate?: boolean;
  icon?: ImageSourcePropType;
  color?: string;
  style?: ViewStyle;
}

export interface SelectorItemProps {
  option: SelectorOption;
  onSelect?: (value: any) => void;
  selected: boolean;
  renderIcon?: (icon: ImageSourcePropType) => React.ReactElement | null;
  itemContainerStyle?: ViewStyle | ViewStyle[];
  itemTextStyle?: TextStyle | TextStyle[];
}

export const SelectorItem = memo(function SelectorItem({
  onSelect,
  option,
  selected,
  renderIcon,
  itemContainerStyle,
  itemTextStyle,
}: SelectorItemProps) {
  const onPress = useCallback(() => {
    onSelect?.(option.value);
  }, [option, onSelect]);
  return (
    <TouchableOpacity
      style={flatStyles(styles.menuItem, itemContainerStyle, option.style)}
      onPress={onPress}
    >
      <STranslatedText
        style={flatStyles(itemTextStyle)}
        isSelected={selected}
        color={option.color || ""}
        numberOfLines={1}
      >
        {StringHelper(option?.label || "")
          .stripHtml({ trimWhiteSpaces: false })
          .toString()}
      </STranslatedText>
      {option.icon &&
        selected &&
        (typeof renderIcon === "function" ? (
          renderIcon(option.icon)
        ) : (
          <IconImage color={option.color || ""} source={option.icon} />
        ))}

      {/*{selected && (*/}
      {/*    <ViewCheckBox>*/}
      {/*        <CheckIcon*/}
      {/*            resizeMode="contain"*/}
      {/*            style={styles.selectedIcon}*/}
      {/*            source={require('./assets/check.png')}*/}
      {/*        />*/}
      {/*    </ViewCheckBox>*/}
      {/*)}*/}
    </TouchableOpacity>
  );
});

const styles = StyleSheet.create({
  menuItem: {
    minHeight: 44,
    flexDirection: "row",
    alignItems: "center",
    paddingHorizontal: 16,
  },
  selectedIcon: {
    width: 16,
    height: 16,
  },
  icon: {
    width: 24,
    height: 24,
    marginRight: 16,
    tintColor: "#333",
  },
});
