import React, {memo, useCallback} from 'react';
import {Emoji} from './types';
import {styled} from '@true/core';
import {TouchableOpacity} from 'react-native';
import FastImage from 'react-native-fast-image';

const ItemWrapper = styled.View`
  width: 80px;
  height: 80px;
`;

const ItemImage = styled(FastImage)`
  width: 68px;
  height: 68px;
`;

const STICKER_MATCH = /(\w+)\/(\w+)\.(\w+)$/;

const Sticker = memo(({item, onPress}: {item: Emoji; onPress: (name: string) => void}) => {
  const _onPress = useCallback(() => {
    // const match = item.url.match(STICKER_MATCH);
    // if (!match) return;
    // const name = `${match[1]}-${match[2]}.${match[3]}`;

    onPress(item.url)
  }, [item, onPress]);

  return (
    <TouchableOpacity onPress={_onPress}>
      <ItemWrapper>
        <ItemImage source={{uri: item.url}} />
      </ItemWrapper>
    </TouchableOpacity>
  );
});

export default Sticker;
