import React, { PropsWithChildren, RefObject } from "react";
import { FlatList, Platform, ScrollView, ViewStyle } from "react-native";
import { flatStyles, memoForwardRef } from "@true/core";
import { useAnimatedSafeAreaInsets } from "react-native-safe-area-context";
import { SharedValue, useDerivedValue } from "react-native-reanimated";
import {
  AnimatedKeyboardAvoidingView,
  AnimatedKeyboardAvoidingViewRef,
} from "../AnimatedKeyboardAvoidingView";
import { FlashList } from "@shopify/flash-list";

interface Props extends PropsWithChildren {
  isOffset?: boolean;
  style?: ViewStyle | ViewStyle[];
  scrollInputAdditionalOffset?: SharedValue<number> | number;
  hasCollapseMethod?: boolean;
  animatedIndex?: SharedValue<number>;
  animatedCurrentIndex?: SharedValue<number>;
  scrollRef?: React.RefObject<ScrollView | FlashList<any> | FlatList>;
  keyboardOffset?: number;
  disableOnAndroid?: boolean;
}

export const MyKeyboardAvoidingView = memoForwardRef(
  (props: Props, ref: RefObject<AnimatedKeyboardAvoidingViewRef>) => {
    const {
      isOffset = true,
      style,
      scrollInputAdditionalOffset,
      animatedIndex,
      animatedCurrentIndex,
      scrollRef,
      keyboardOffset,
      disableOnAndroid,
    } = props;
    const { aBottom, aTop } = useAnimatedSafeAreaInsets();
    const negativeBottom = useDerivedValue(
      () =>
        isOffset
          ? -aBottom.value + (keyboardOffset ? aTop.value + keyboardOffset : 0)
          : 0,
      [aBottom, isOffset]
    );

    const _scrollInputAdditionalOffset = useDerivedValue(() => {
      if (scrollInputAdditionalOffset) {
        const _offset =
          typeof scrollInputAdditionalOffset === "number"
            ? scrollInputAdditionalOffset
            : scrollInputAdditionalOffset.value;
        return _offset - (_offset ? aBottom.value : 0);
      }
      return 0;
    }, [scrollInputAdditionalOffset]);

    return (
      <AnimatedKeyboardAvoidingView
        ref={ref}
        scrollRef={scrollRef}
        behavior={
          disableOnAndroid && Platform.OS == "android" ? undefined : "padding"
        }
        style={flatStyles({ flex: 1 }, style)}
        keyboardVerticalOffset={negativeBottom}
        scrollInputAdditionalOffset={_scrollInputAdditionalOffset}
        animatedIndex={animatedIndex}
        animatedCurrentIndex={animatedCurrentIndex}
      >
        {props.children}
      </AnimatedKeyboardAvoidingView>
    );
  }
);
