import React from "react";
import {FlashList, FlashListProps} from "@shopify/flash-list";
import Animated from "react-native-reanimated";

const F2 = React.forwardRef((props: any, ref) => {
  return (
    <FlashList
      {...props}
      overrideProps={{
        ref,
      }}
      renderScrollComponent={Animated.ScrollView}
    />
  );
});

export const AnimatedFlashList = Animated.createAnimatedComponent<FlashListProps<any>>(
  F2 as any,
) as typeof FlashList;
