import { FlashListProps } from "@shopify/flash-list";
import {
  NativeScrollEvent,
  NativeSyntheticEvent,
  ViewStyle,
} from "react-native";

export enum EFlashViewTypes {
  SECTION = "section",
  ITEM = "item",
}

export interface IFlashItem<F> {
  item: F;
  index: number;
  rawIndex: number;
  isLast: boolean;
}

export interface IFlashSection<T> {
  item: T;
  toggle: () => void;
  show: () => void;
  hide: () => void;
  index: number;
  rawIndex: number;
  visible: boolean;
}

interface OwnProps<T, F>
  extends Omit<
    FlashListProps<any[] | Record<string, any>>,
    "data" | "renderItem"
  > {
  data: T[];
  mapKey?: string;
  map?: Record<string, F[]>;
  renderItem: (props: IFlashItem<F>) => JSX.Element | null;
  renderSection?: (props: IFlashSection<T>) => JSX.Element | null;
  loading?: boolean;
  refreshing?: boolean;
  loadingMore?: boolean;
  noMore?: boolean;
  error?: boolean;
  onLoadData?: (refreshing: boolean, loadingMore: boolean) => void;
  renderErrorView?: () => JSX.Element | null;
  renderRefreshControl?: () => JSX.Element;
  refreshable?: boolean;
  loadMorable?: boolean;
  onScroll?: (event: NativeSyntheticEvent<NativeScrollEvent>) => void;
  style?: ViewStyle;
  loadingViewStyle?: ViewStyle;
  isStickyHeader?: boolean;
  scrollEventThrottle?: number;
  progressViewOffset?: number;
  ref?: any;
  isBottomSheet?: boolean;
}

export type UIFlastListProps<T, F> = OwnProps<T, F>;
