import {createSelector} from '@reduxjs/toolkit';
import {IUser, matchFilter} from '@true/core';

enum EUserRole {
  manager = 'manager',
  member = 'member',
}

const _getData = (
  _: any,
  textSearch: string,
  ids: string[],
  defaultUsernames?: string[],
) => ({
  textSearch,
  ids,
  defaultUsernames,
});

const userSelector = (state: RootState) => state.users.byKey;

const userQueriesSelector = (state: RootState) =>
  state.users.query?.allIds || [];

const memberSelector = (
  userSelector: {[id: string]: IUser},
  userQueriesSelector: string[],
  data: {textSearch: string; ids: string[]; defaultUsernames?: string[]},
) => {
  const {ids, textSearch, defaultUsernames} = data;
  const allIds = userQueriesSelector;

  let result: string[] = [];

  const searchIds = ids && (ids.length || defaultUsernames) ? ids : allIds;

  for (let i = 0; i < searchIds.length; i++) {
    const userId = searchIds[i];

    const newUserId = userSelector?.[userId]?.id;

    const user: IUser | undefined = userSelector?.[newUserId || userId];
    if (
      userId != EUserRole.manager &&
      userId != EUserRole.member &&
      (!user || typeof user?.id === 'undefined')
    ) {
      continue;
    }

    if (textSearch.length === 0) {
      result.push(newUserId || userId);
    } else {
      const isMatch = matchFilter(textSearch, user, ['name'], ['id']);
      if (isMatch === 'isMatch') {
        result.push(newUserId || userId);
      }
      if (isMatch === 'isUnique') {
        result = [];
        result.push(newUserId || userId);
      }
    }
  }
  return result;
};

// @ts-ignore
export const memoMemberListSelectors = createSelector(
  userSelector,
  userQueriesSelector,
  _getData,
  memberSelector,
);
