import React, { memo, useCallback } from "react";
import { SingleTabBar } from "./SingleTabBar";
import { View } from "react-native";
import { useSingleTabView } from "./provider";
import Animated, { useAnimatedStyle } from "react-native-reanimated";
import { styled } from "@true/core";
import { useAnimatedScrollableView } from "../index";
import { AnimatedScrollableTabBarProps } from "../AnimatedScrollableTabBar";

export const CollapsableSingleTabBar = memo(
  (props: AnimatedScrollableTabBarProps) => {
    const { onHeaderLayout, index, onTabBarLayout } = useSingleTabView();

    const {
      headerHeight: rHeaderHeight,
      // tabBarHeight: rTabBarHeight,
      aHeight,
      scrollValMap,
      aScrollVal,
      aLevelIndex,
    } = useAnimatedScrollableView();

    const aHeaderStyle = useAnimatedStyle(() => {
      // && (scrollValMap.value?.[index] === undefined || -aHeight.value <= scrollValMap.value?.[index])
      if (
        aLevelIndex.value != index.toString() &&
        (scrollValMap.value?.[index] === undefined ||
          -aHeight.value >= scrollValMap.value?.[index])
      ) {
        // console.log(-aHeight.value, scrollValMap.value?.[index.toString()], index)
        return {
          top: aHeight.value + rHeaderHeight.value,
        };
      }

      return {
        top: Math.min(aHeight.value, -aScrollVal.value) + rHeaderHeight.value,
      };
    }, [index]);

    const _onLayout = useCallback(
      (e: any) => {
        onTabBarLayout(e);
        props.onLayout?.(e);
      },
      [props.onLayout]
    );

    return (
      <SBg style={aHeaderStyle} pointerEvents={"box-none"}>
        <View
          style={{ overflow: "scroll" }}
          onLayout={onHeaderLayout}
          pointerEvents={"box-none"}
        >
          <View pointerEvents={"box-none"}>{props.children}</View>
          <SingleTabBar {...props} onLayout={_onLayout} />
        </View>
      </SBg>
    );
  }
);

const SBg = styled(Animated.View)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: scroll;
  z-index: 1;
`;
