import React, {memo, useCallback} from 'react';
import {SelectableMemberRow} from '../Member/SelectableMemberRow';
import {UserPickerModalContext} from './context';

export interface UserRowProps {
  id: string;
  multiple: boolean;
  onPress?: () => void;
}
export const UserRow = memo(function UserRow({
  id,
  multiple,
  onPress,
}: UserRowProps) {
  const isSelected = UserPickerModalContext.useIsUserSelected();

  const onUserPress = UserPickerModalContext.useOnUserPress();

  const onChange = useCallback(() => {
    onPress ? onPress() : onUserPress(id);
  }, [onUserPress, id, onPress]);

  return (
    <SelectableMemberRow
      id={id}
      selected={isSelected(id)}
      onChange={onChange}
      multiple={multiple}
    />
  );
});
