import React, { memo, useCallback, useRef } from "react";
import { ButtonProps, UIButton } from "../UIButton";
import { ReactionModal, ReactionModalRef } from "./ReactionModal";
import { runOnJS, useSharedValue } from "react-native-reanimated";
import {
  Gesture,
  GestureDetector,
  GestureStateChangeEvent,
  LongPressGestureHandlerEventPayload,
  TapGestureHandlerEventPayload,
} from "react-native-gesture-handler";
import { EReaction } from "@true/core";
import { GestureResponderEvent, Insets } from "react-native";

interface Props
  extends Omit<ButtonProps, "onPress" | "onLongPress" | "hitSlop"> {
  params: any;
  requestFn: (params: any) => Promise<any>;
  reactionKey?: string;
  onLongPress?: (
    event: GestureStateChangeEvent<LongPressGestureHandlerEventPayload>
  ) => void;
  onPress?: (
    event: GestureStateChangeEvent<TapGestureHandlerEventPayload>
  ) => void;
  hitSlop?: null | Insets | number;
  noWait?: boolean;
}

export const ReactionModalButton = memo((props: Props) => {
  const {
    params,
    requestFn,
    reactionKey,
    onLongPress,
    onPress,
    hitSlop,
    noWait,
    ...rest
  } = props;
  const value_y = useSharedValue(0);

  const ref = useRef<ReactionModalRef>(null);

  const _onLongPress = useCallback(
    (event: GestureStateChangeEvent<LongPressGestureHandlerEventPayload>) => {
      onLongPress?.(event);
      ref.current?.present();
    },
    [onLongPress, ref.current]
  );

  const _onPress = useCallback(
    (e: GestureResponderEvent) => {
      onPress?.(e as any);
      requestFn({ ...params, reaction: EReaction.LIKE });
    },
    [params]
  );

  const LongPressGesture = Gesture.LongPress()
    .hitSlop(hitSlop)
    .onStart((event) => {
      value_y.value = event.absoluteY;
      runOnJS(_onLongPress)(event);
    })
    .onTouchesDown(() => {});

  // const TapGesture = Gesture.Tap()
  //   .onStart((event) => {
  //     if (onPress) {
  //       runOnJS(_onPress)(event);
  //     }
  //   })
  //   .onTouchesDown(() => {});

  // const composeGesture = Gesture.Race(TapGesture, LongPressGesture);

  return (
    <>
      <GestureDetector gesture={LongPressGesture}>
        <UIButton {...rest} onPress={_onPress} hitSlop={hitSlop} />
      </GestureDetector>
      <ReactionModal
        ref={ref}
        params={params}
        requestFn={requestFn}
        value_y={value_y}
        reactionKey={reactionKey}
        noWait={noWait}
      />
    </>
  );
});
