import React, {memo, useCallback, useState} from 'react';
import {
  ImageSourcePropType,
  StyleSheet,
  TouchableOpacity,
  ViewStyle,
} from 'react-native';
import {RadioBox} from '../RadioBox';
import {translate, styledValue, styled} from '@true/core';
import {useSelectorInput} from './hook';
import {InputTemplate} from './components/InputTemplate';

interface IOption {
  label: string;
  value: string | number | undefined;
  disabled?: boolean;
}

type IValue = any;

interface Props {
  title: string;
  value: IValue;
  keyName: string;
  containerStyle?: ViewStyle;
  onValueChange: (keyName: string, value: IValue) => void;
  required?: boolean;
  type?: 'border' | 'normal';
  activeIcon?: ImageSourcePropType;
  options: IOption[];
  onSave?: (value: IValue) => void;
  disabled?: boolean;
  multiple?: boolean;
  returnType?: 'string' | 'array';
}

export const MyCheckbox = memo(function (props: Props) {
  const {title, value, disabled, options, activeIcon, multiple} = props;

  const {onToggle: _onToggle, getIsSelected} = useSelectorInput(props);
  const [, forceUpdate] = useState(false);
  const onToggle = useCallback(
    (value: any) => {
      _onToggle(value);
      forceUpdate(p => !p);
    },
    [_onToggle],
  );

  const renderOption = useCallback(
    (item: IOption) => {
      return (
        <TouchableOpacity
          disabled={disabled || item?.disabled}
          onPress={() => onToggle(item?.value)}
          style={styles.checkboxWithLabel}>
          <RadioBox
            activeIcon={activeIcon}
            multiple={multiple}
            disabled={disabled}
            active={getIsSelected(item?.value)}
          />
          <SGrey1Text style={styles.checkboxLabel}>
            {translate(`${item?.label}`)}
          </SGrey1Text>
        </TouchableOpacity>
      );
    },
    [disabled, activeIcon, value, onToggle, multiple],
  );

  return (
    <InputTemplate {...props} title={title}>
      <ValueViewBorder>
        {options.map(item => renderOption(item))}
      </ValueViewBorder>
    </InputTemplate>
  );
});

const SGrey1Text = styled.Text`
  color: ${styledValue.neutral1};
`;

const ValueViewBorder = styled.View`
  margin-top: 4px;
  align-items: flex-end;
  flex: 1;
`;

const styles = StyleSheet.create({
  checkboxWithLabel: {
    width: '100%',
    flexDirection: 'row',
    alignItems: 'center',
    paddingVertical: 12,
  },
  checkboxLabel: {
    flex: 1,
    marginLeft: 10,
    fontSize: 15,
  },
});
