import {useCallback, useEffect, useRef, useState} from 'react';
import {
  Keyboard,
  NativeScrollEvent,
  NativeSyntheticEvent,
  ScrollView,
} from 'react-native';
import constate from 'constate';

const useScrollProvider = () => {
  const ref = useRef<ScrollView>(null);
  const posInput = useRef<Record<string, number>>({});
  const keyboardHeight = useRef(0);
  const [extraHeight, setExtraHeight] = useState(0);
  const scrollPos = useRef(0);
  const screenY = useRef(0);
  const itemHeight = useRef<Record<string, number>>({});
  useEffect(() => {
    const show = Keyboard.addListener('keyboardWillShow', e => {
      keyboardHeight.current = e.endCoordinates.height;
      screenY.current = e.endCoordinates.screenY;
    });
    return () => show.remove();
  }, []);
  const onInputLayout = useCallback(
    (keyName: string, e: any) => {
      e.target?.measure(
        (
          x: number,
          y: number,
          w: number,
          h: number,
          px: number,
          py: number,
        ) => {
          itemHeight.current[keyName] = h;
          posInput.current[keyName] = py;
        },
      );
    },
    [posInput.current, itemHeight.current],
  );
  const onInputFocus = useCallback(
    (keyName: string) => {
      setTimeout(() => {
        const inputPosVal =
          posInput.current?.[keyName] -
          (screenY.current - extraHeight - itemHeight.current[keyName]);

        screenY.current != 0 &&
          inputPosVal > scrollPos.current &&
          ref.current?.scrollTo(inputPosVal);
      }, 300);
    },
    [
      ref.current,
      posInput.current,
      keyboardHeight.current,
      extraHeight,
      scrollPos.current,
      screenY.current,
      itemHeight.current,
    ],
  );
  const onScroll = useCallback(
    (e: NativeSyntheticEvent<NativeScrollEvent>) => {
      scrollPos.current = e.nativeEvent.contentOffset.y;
    },
    [scrollPos.current],
  );

  return {
    scrollView: {
      ref,
      posInput,
      onInputLayout,
      onInputFocus,
      setExtraHeight,
      onScroll,
    },
  };
};

const [UIScrollProvider, useScrollView] = constate(
  useScrollProvider,
  val => val.scrollView,
);

export {UIScrollProvider, useScrollView};
